import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class DelayNode extends Node {
  delayedValues = [];

  constructor() {
    super();
    this.title = NODE_INFO.streams["stream-delay"].title;
    this.description = NODE_INFO.streams["stream-delay"].description;
    this.addInput('input');
    this.addInputWithSettings('delay', Type.NUMBER, 1000, 'Delay (millis)', false);
    this.addOutput('output');

    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [{ delay: { weight: 2 }, time: {} }],
    });
  }

  onAdded() {
    this.inputs[1]['name'] = `[delay] (${this.settings['time'].value})`;
  }

  onAfterSettingsChange() {
    this.inputs[1]['name'] = `[delay] (${this.settings['time'].value})`;
  }

  onInputUpdated() {
    if (this.inputs[0].updated)
      this.delayedValues.push({
        val: this.inputs[0].data,
        time: Date.now(),
      });
  }

  onExecute() {
    if (this.delayedValues.length == 0) return;
    let delay = this.getInputData(1);
    delay = TimeUtils.timeConvert(delay, this.settings['time'].value);
    const val = this.delayedValues[0];
    if (Date.now() - val.time >= delay) {
      this.delayedValues.shift();
      this.setOutputData(0, val.val);
      return;
    }
  }
}
Container.registerNodeType('streams/stream-delay', DelayNode);
