import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

export class DateNode extends Node {
  interval = 5000;
  timeoutFunc;
  constructor() {
    super();
    this.title = NODE_INFO.time.date.title;
    this.description = NODE_INFO.time.date.description;
    this.addOutput('dateString', Type.STRING);
    this.addOutput('dayString', Type.STRING);
    this.addOutput('dayOfWeek', Type.NUMBER);
    this.addOutput('date', Type.NUMBER);
    this.addOutput('month', Type.NUMBER);
    this.addOutput('year', Type.NUMBER);
  }

  onAdded() {
    clearInterval(this.timeoutFunc);
    this.timeoutFunc = setInterval(() => {
      this.recalculate();
    }, this.interval);
    this.recalculate();
  }

  recalculate() {
    let now = new Date();
    const day = now.getDay();
    let dayString: string = null;
    switch (day) {
      case 0:
        dayString = 'Sunday';
        break;
      case 1:
        dayString = 'Monday';
        break;
      case 2:
        dayString = 'Tuesday';
        break;
      case 3:
        dayString = 'Wednesday';
        break;
      case 4:
        dayString = 'Thursday';
        break;
      case 5:
        dayString = 'Friday';
        break;
      case 6:
        dayString = 'Saturday';
        break;
    }
    this.setOutputData(0, now.toLocaleDateString(), true);
    this.setOutputData(1, dayString, true);
    this.setOutputData(2, day, true);
    this.setOutputData(3, now.getDate(), true);
    this.setOutputData(4, now.getMonth() + 1, true);
    this.setOutputData(5, now.getFullYear(), true);
  }
}
Container.registerNodeType('time/date', DateNode);
