import { isNull, isString } from '../../../utils/helper';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';

class StringCaseChangeNode extends Node {
  conversionFunction;
  constructor() {
    super();
    this.title = NODE_INFO.string["string-case"].title;
    this.description = NODE_INFO.string["string-case"].description;
    this.addInput('input', Type.STRING);
    this.addOutput('output', Type.STRING);
    this.setOutputData(0, null);
    this.settings['function'] = NodeSetting.create({
      description: 'Select Case',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'lower', text: 'Lower Case' },
          { value: 'upper', text: 'Upper Case' },
          { value: 'first_upper', text: 'First Characters to Upper Case' },
        ],
      },
      value: 'lower',
    });
  }

  onAdded() {
    this.setTitleAndConversionFunction();
    this.onInputUpdated();
  }

  toTitleCase = (phrase: string): string => {
    if (isString(phrase) && !isNull(phrase)) {
      return phrase
        .toLowerCase()
        .split(' ')
        .map(word => word.charAt(0).toUpperCase() + word.slice(1))
        .join(' ');
    } else return null
  };

  onInputUpdated() {
    const input = this.getInputData(0);
    if (!Utils.hasInput(input)) {
      this.setOutputData(0, null, true);
      return;
    }
    this.setOutputData(0, this.conversionFunction(input), true);
  }

  onAfterSettingsChange() {
    this.setTitleAndConversionFunction();
    this.onInputUpdated();
  }

  setTitleAndConversionFunction() {
    const inputType = this.settings['function'].value;
    switch (inputType) {
      case 'first_upper':
        this.title = 'String Case (First Character to Upper)';
        this.conversionFunction = s1 => {
          return this.toTitleCase(s1);
        };
        break;
      case 'lower':
        this.title = 'String Case (Lower)';
        this.conversionFunction = s1 => {
          return s1.toLowerCase();
        };
        break;
      case 'upper':
        this.title = 'String Case (Upper)';
        this.conversionFunction = s1 => {
          return s1.toUpperCase();
        };
        break;
      default:
        this.title = 'String Case (Lower)';
        this.conversionFunction = s1 => {
          return s1.toLowerCase();
        };
    }
  }
}
Container.registerNodeType('string/string-case', StringCaseChangeNode);
