import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

const PID = require('../../lib/pid-controller');

class PIDFunctionNode extends Node {
  lastTime: number;
  startTime: number;
  lastResetVal: boolean = false;
  pid = new PID(1, 1, 1, 0, 0, 'reverse');

  constructor() {
    super();

    this.title = NODE_INFO.hvac.PID.title;
    this.description = NODE_INFO.hvac.PID.description;

    this.addInput('enable', Type.BOOLEAN); // 0
    this.addInput('processVariable', Type.NUMBER); // 1
    this.addInputWithSettings('setpoint', Type.NUMBER, 1, 'setpoint'); // 2
    this.addInputWithSettings('minOut', Type.NUMBER, 0, 'minOut'); // 3
    this.addInputWithSettings('maxOut', Type.NUMBER, 100, 'maxOut'); // 4
    this.addInputWithSettings('inP', Type.NUMBER, 1, 'P'); // 5
    this.addInputWithSettings('inI', Type.NUMBER, 0, 'I'); // 6
    this.addInputWithSettings('inD', Type.NUMBER, 0, 'D'); // 7
    //this.addInputWithSettings('direction', Type.BOOLEAN, true, 'Direction (OFF = Reverse, ON = Direct)'); // Direct or Reverse // 8
    this.addInputWithSettings(
      'direction',
      Type.BOOLEAN,
      true,
      'Direction (OFF = Reverse, ON = Direct)',
    ); // Direct or Reverse // 8
    this.addInputWithSettings('interval', Type.NUMBER, 1, 'Interval (min 500ms)'); // 9
    this.addInputWithSettings('bias', Type.NUMBER, 0, 'Bias'); // 10
    this.addInputWithSettings('manual', Type.NUMBER, 0, "Manual ('output' value on disabled)"); // 11
    this.addInput('reset', Type.BOOLEAN); // 12
    this.addOutput('output', Type.NUMBER); // 0
    this.settings['time'] = NodeSetting.create({
      description: 'Time',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [
        { minOut: {}, maxOut: {} },
        { inP: {}, inI: {}, inD: {} },
        { time: {}, interval: { weight: 2 } },
      ],
    });
    this.startTime = Date.now();
    this.pid.setSampleTime(1000);
    this.pid.setOutputLimits(0, 100);
    this.pid.setMode('MANUAL');
    this.pid.setOutput(0);
    this.pid.setBias(0);
  }

  onAdded() {
    this.inputs[9]['name'] = `[interval] (${this.settings['time'].value})`;
    this.onInputUpdated();
  }

  onExecute() {
    if (this.pid.compute()) this.setOutputData(0, this.pid.getOutput().toFixed(1), true);
  }

  onInputUpdated() {
    const reset = this.getInputData(12);
    if (reset && !this.lastResetVal) this.pid.initialize();
    this.lastResetVal = reset;

    let enable = this.getInputData(0);
    if (!enable) {
      this.pid.setMode('MANUAL');
      const manual = this.getInputData(11) || 0;
      this.setOutputData(0, manual.toFixed(1));
      this.pid.setOutput(manual);
      return;
    }

    let processVariable = this.getInputData(1);
    if (
      processVariable === null ||
      processVariable === undefined ||
      typeof processVariable !== Type.NUMBER
    ) {
      this.pid.setMode('MANUAL');
      const manual = this.getInputData(11) || 0;
      this.setOutputData(0, manual.toFixed(1), true);
      this.pid.setOutput(manual);
      return;
    }

    const setpoint = this.getInputData(2);
    const minOut = this.getInputData(3);
    const maxOut = this.getInputData(4);
    const inP = this.getInputData(5);
    const inI = this.getInputData(6);
    const inD = this.getInputData(7);
    let direction = this.getInputData(8);
    if (direction == true) direction = 'direct';
    else direction = 'reverse';
    let interval = this.getInputData(9);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    interval = Math.max(500, interval);
    const bias = this.getInputData(10);

    this.pid.setBias(bias);
    this.pid.setSampleTime(interval);
    this.pid.setOutputLimits(minOut, maxOut);
    this.pid.setPoint(setpoint);
    this.pid.setTunings(inP, inI, inD);
    this.pid.setControllerDirection(direction);
    this.pid.setMode('auto');
    this.pid.setInput(processVariable);
    this.pid.compute();
    this.setOutputData(0, this.calculatePidOutput(), true);
  }

  onAfterSettingsChange() {
    this.inputs[9]['name'] = `[interval] (${this.settings['time'].value})`;
    this.onInputUpdated();
  }

  private calculatePidOutput(): number {
    return this.pid.getOutput().toFixed(1);
  }
}
Container.registerNodeType('hvac/PID', PIDFunctionNode);
