import { DispatcherRegistry } from './DispatcherRegistry';
import { DispatchNode } from './DispatchNode';
import { IPubSub, Store } from './IPubSub';

/**
 * Example:
 * {
 *   "protocol1": {
 *     "points": {
 *       "point1": { msg: <any>, name: 'Point1', listener: [{node: node1, soft: true}, {node: node2, soft: false}, ...]},
 *       "point2": { msg: <any> , name: 'Point2', listener: [{node: node1, soft: true}, {node: node2, soft: false}, ...]}
 *     },
 *   },
 *   "protocol2": {
 *     "points": {
 *       "point1": { msg: <any>, name: 'Point1', listener: [{node: node1, soft: true}, {node: node2, soft: false}, ...]},
 *       "point2": { msg: <any>, name: 'Point2', listener: [{node: node1, soft: true}, {node: node2, soft: false}, ...]}
 *     },
 *   }
 * }
 */
class PubSub implements IPubSub {
  store: Store = {};

  public getStore(): Store {
    return this.store;
  }

  public addPoint(protocol: string, point: string, name: string, soft: boolean): void {
    if (!this.store[protocol]) {
      this.store[protocol] = { points: {} };
    }
    if (!this.store[protocol].points[point]) {
      this.store[protocol].points[point] = { msg: {}, name, listeners: [], soft };
    } else {
      // Point exist already and soft is true and now it's inserted in real
      if (this.store[protocol].points[point].soft && !soft) {
        this.store[protocol].points[point].soft = soft;
      }
    }
    this.updateProtocols();
  }

  public removePoint(protocol: string, point: string): void {
    if (this.store[protocol]?.points?.[point]) {
      delete this.store[protocol].points[point];
    }
    if (Object.keys(this.store[protocol]?.points || {})?.length === 0) {
      delete this.store[protocol];
    }
    this.updateProtocols();
  }

  public renamePoint(protocol: string, point: string, name: string): void {
    if (this.store?.[protocol]?.points?.[point]) {
      this.store[protocol].points[point].name = name;
    }
    this.updateProtocols();
  }

  public dispatch(protocol: string, point: string, value: any): void {
    if (this.store[protocol]?.points?.[point]) {
      this.store[protocol].points[point].msg = value;
      this.store[protocol].points[point].listeners.forEach(node => {
        node.subscribe(value);
      });
    }
  }

  public connect(protocol: string, point: string, name: string, node: DispatchNode): void {
    // Remove if it's subscribing to any
    Object.keys(this.store).forEach(protocol => {
      Object.keys(this.store[protocol].points).forEach(point => {
        this.store[protocol].points[point].listeners = this.store[protocol].points[point].listeners.filter(
          n => !(n.cid === node.cid && n.id === node.id),
        );
      });
    });
    if (!(protocol && point && name)) return;
    this.addPoint(protocol, point, name, true);
    this.store[protocol].points[point].listeners.push(node);
    node.subscribe(this.store[protocol].points[point].msg);
  }

  public disconnect(protocol: string, point: string, node: DispatchNode): void {
    if (this.store[protocol]?.points?.[point]) {
      const storePoint = this.store[protocol].points[point];
      storePoint.listeners = storePoint.listeners.filter(n => !(n.cid === node.cid && n.id === node.id));
    }
  }

  private updateProtocols() {
    DispatcherRegistry.instance().list().forEach(n => n.updateProtocols());
  }
}

export const pubSubSingleton: IPubSub = new PubSub();
