import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class StringJoinNode extends FlexibleNode {
  constructor() {
    super();
    this.dynamicInputsType = Type.STRING;
    super.dynamicInputsStartName = 'string';
    this.title = NODE_INFO.string["string-join"].title;
    this.description = NODE_INFO.string["string-join"].description;
    this.addOutput('output', Type.STRING);
    this.setOutputData(0, null);
  }

  onAdded() {
    this.size = this.computeSize();
    this.onInputUpdated();
  }

  onInputUpdated() {
    const inputs = this.getInputsCount();
    let output = '';
    for (let i = this.dynamicInputStartPosition; i < inputs; i++) {
      output = output.concat(this.getInputData(i));
    }
    this.setOutputData(0, output, true);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}

Container.registerNodeType('string/string-join', StringJoinNode);
