import { PubSubHelpers } from './PubSubHelpers';

export const Map = (target: any, propertyKey: string, descriptor: any) => {
  const fn = descriptor.value!;
  descriptor.value = function DescriptorValue(...args: any[]) {
    PubSubHelpers.map(this);
    return fn.apply(this, args);
  };
  return descriptor;
};

export const UnMap = (target: any, propertyKey: string, descriptor: any) => {
  const fn = descriptor.value!;
  descriptor.value = function DescriptorValue(...args: any[]) {
    PubSubHelpers.unMap(this);
    return fn.apply(this, args);
  };
  return descriptor;
};

export const Rename = (target: any, propertyKey: string, descriptor: any) => {
  const fn = descriptor.value!;
  descriptor.value = function DescriptorValue(...args: any[]) {
    PubSubHelpers.renameFunction(this);
    fn.apply(this, args);
  };
  return descriptor;
};

export const Dispatch = (target: any, propertyKey: string, descriptor: any) => {
  const fn = descriptor.value!;
  descriptor.value = function DescriptorValue(...args: any[]) {
    const value: any = fn.apply(this, args);
    PubSubHelpers.dispatch(this, value);
  };
  return descriptor;
};

export const Connect = (target: any, propertyKey: string, descriptor: any) => {
  const fn = descriptor.value;
  descriptor.value = function DescriptorValue(...args: any[]) {
    fn.apply(this, args);
    PubSubHelpers.connect(this);
  };
  return descriptor;
};

export const Disconnect = (target: any, propertyKey: string, descriptor: any) => {
  const fn = descriptor.value;
  descriptor.value = function DescriptorValue(...args: any[]) {
    fn.apply(this, args);
    PubSubHelpers.disconnect(this);
  };
  return descriptor;
};
