import { Constructor, isEmpty, isFunction, isJSON } from '../../../../utils/helper';
import { IdGenerator } from '../../../../utils/id-generator';
import {
  HasCentralizedListener,
  ICentralizedListener,
  IMessageConverter,
  IMessageSubscriber,
  MockCentralizedListener,
  NodeStoreItem,
} from '../../../../utils/pattern';
import { Container } from '../../../container';
import { Node } from '../../../node';
import { Side } from '../../../node-interface';
import { NodeInput, NodeSetting, Type } from '../../../node-io';
import { AbleEnableNode, NodeInputEvaluation } from '../../node-mixin';
import { DependantConnectionNode, DependantConnectionNodeMixin } from '../connection-node-mixin';
import {
  MQTT_CLIENT_NODE,
  PUBLISH_MQTT_DATA,
  REGISTER_MQTT_PUBLISHER,
  REGISTER_MQTT_SUBSCRIBER,
  UNREGISTER_MQTT_PUBLISHER,
  UNREGISTER_MQTT_SUBSCRIBER,
  UPDATE_MQTT_PUBLISHER,
  UPDATE_MQTT_SUBSCRIBER,
} from './mqtt-client-node';

function MqttGenericNodeMixin<TBase extends Constructor<Node>>(Base: TBase) {
  abstract class MqttGenericNodeMixinBase extends AbleEnableNode(Base)
    implements NodeInputEvaluation, HasCentralizedListener {
    protected readonly _iTopic = 'topic';

    onAdded(): void {
      if (this.side !== Side.server) {
        return;
      }
      this.handleOnUpdate(this.createPayload());
      this.updateTitle();
    }

    onAfterSettingsChange(oldSettings, oldName?): void {
      if (this.side !== Side.server) {
        return;
      }
      this.handleOnUpdate(this.createPayload(), this.createPayload(oldSettings));
      this.updateTitle();
    }

    onInputUpdated(): void {
      if (this.side !== Side.server) {
        return;
      }
      let prev = this.createPayload();
      this.reEvaluateSettingByInput(this.inputs, this.settings);
      this.handleOnUpdate(this.createPayload(), prev);
      this.updateTitle();
    }

    onRemoved() {
      if (this.listener() === MockCentralizedListener.LISTENER) {
        return;
      }
      this.listener().listen({ action: this.unregisterAction(), payload: this.createPayload() });
    }

    reEvaluateSettingByInput(inputs: { [p: number]: NodeInput }, settings: { [name: string]: NodeSetting }) {
      settings[this._iTopic].value = inputs[this.topicInputIdx()].updated ? inputs[this.topicInputIdx()].data
                                                                          : settings[this._iTopic].value;
      if (isFunction(super['reEvaluateSettingByInput'])) {
        super['reEvaluateSettingByInput'](inputs, settings);
      }
    }

    listener(): ICentralizedListener {
      return MockCentralizedListener.cast(this.getParentNode());
    }

    protected abstract topicInputIdx(): number;

    protected abstract updateTitle(): void;

    protected abstract registerAction(): string;

    protected abstract updateAction(): string;

    protected abstract unregisterAction(): string;

    protected abstract doAfterUpdate(current: NodeStoreItem, prev?: NodeStoreItem): any;

    protected mixinMqttTopicInputSetting() {
      this.addInputWithSettings(this._iTopic, Type.STRING, '', 'MQTT Topic');
    }

    protected handleOnUpdate(current: NodeStoreItem, prev?: NodeStoreItem): any {
      if (!prev?.identifier && !current?.identifier) {
        return;
      }
      if (isEmpty(current.identifier)) {
        throw new Error('Must define MQTT topic');
      }
      if (this.shouldRegister(prev)) {
        this.listener().listen({ action: this.registerAction(), payload: current });
      } else if (current.identifier !== prev.identifier || current.enabled !== prev.enabled) {
        current.prev = prev;
        this.listener().listen({ action: this.updateAction(), payload: current });
      }
      this.doAfterUpdate(current, prev);
    }

    protected createPayload(settings?: NodeSetting): NodeStoreItem {
      let identifier = (settings ?? this.settings)['topic']?.value;
      let enabled = settings ? settings[this.enableSettingKey()]?.value : this.isEnabled();
      return {
        identifier: identifier,
        nodeId    : IdGenerator.createNodeId(this.cid, this.id),
        enabled   : enabled,
      } as NodeStoreItem;
    }

    protected shouldRegister(prev: NodeStoreItem) {
      return !prev || !prev.identifier;
    }

  }

  return MqttGenericNodeMixinBase;
}

// Need to mixin to keep call chain to update status
abstract class MqttGenericNode extends DependantConnectionNodeMixin(MqttGenericNodeMixin(Node))
  implements DependantConnectionNode, HasCentralizedListener {
  protected _title: string;

  protected constructor(title: string, description: string) {
    super();
    this._title = this.title = title;
    this.description = description;
    this.mixinEnableInputSetting();
    this.mixinConnectionStatusOutput();
    this.mixinMqttTopicInputSetting();
  }

  protected statusOutputIdx(): number {
    return 0;
  }

  protected enableDescription(): string {
    return `Enable ${this._title}`;
  }

  protected updateTitle() {
    this.title = `${this._title} (${this.settings['topic'].value})`;
  }

  protected topicInputIdx() {
    return 1;
  }

  protected shouldRegister(prev: NodeStoreItem) {
    return super.shouldRegister(prev) || this.getConnectionStatus().isError();
  }

  protected handleError(err: Error, func: string) {
    super.handleError(err, func);
  }
}

class MqttPublisherNode extends MqttGenericNode implements IMessageConverter {

  constructor() {
    super('MQTT Publisher', `MQTT Publisher Node is used to publish MQTT message for generic purpose`);
    this.addInputWithSettings('is-json', Type.BOOLEAN, false, 'MQTT Message is JSON type');
    this.addInputWithSettings('message', Type.ANY, '', 'MQTT Message');
  }

  onConvertMessage(data: any): string {
    return isJSON(data) ? JSON.stringify(data) : data?.toString();
  }

  reEvaluateSettingByInput(inputs: { [p: number]: NodeInput }, settings: { [name: string]: NodeSetting }) {
    super.reEvaluateSettingByInput(inputs, settings);
    this.settings['is-json'].value = this.inputs[2].updated ? this.inputs[2].data : this.settings['is-json'].value;
    this.settings['message'].value = this.inputs[3].updated ? this.inputs[3].data : this.settings['message'].value;
  }

  protected registerAction(): string {
    return REGISTER_MQTT_PUBLISHER;
  }

  protected updateAction(): string {
    return UPDATE_MQTT_PUBLISHER;
  }

  protected unregisterAction(): string {
    return UNREGISTER_MQTT_PUBLISHER;
  }

  protected createPayload(settings?: NodeSetting): NodeStoreItem {
    let payload = super.createPayload(settings);
    payload.callback = data => this.onConvertMessage(data);
    payload.data = this.settings['message'].value;
    return payload;
  }

  protected doAfterUpdate(current: NodeStoreItem, prev?: NodeStoreItem): any {
    if (isEmpty(current?.data) && (prev?.identifier && current.identifier !== prev.identifier || isEmpty(prev?.data))) {
      return current;
    }
    if (current?.enabled) {
      if (this.settings['is-json'].value && !isJSON(current.data)) {
        try {
          current.data = isEmpty(current.data) ? {} : JSON.parse(current.data);
        } catch (err) {
          this.debug(err);
          throw new Error(`Payload is invalid JSON in MQTT publisher on topic ${current.identifier}`);
        }
      }
      return this.listener().listen({ action: PUBLISH_MQTT_DATA, payload: current });
    }
  }

}

class MqttSubscriberNode extends MqttGenericNode implements IMessageSubscriber {
  constructor() {
    super('MQTT Subscriber', `MQTT Subscriber Node is used for generic purpose`);
    this.addOutput('out', Type.ANY);
  }

  onReceiveMessage(incomingMessage: any): void {
    this.setOutputData(this.errorOutputIdx() + 1, incomingMessage);
  }

  protected registerAction(): string {
    return REGISTER_MQTT_SUBSCRIBER;
  }

  protected updateAction(): string {
    return UPDATE_MQTT_SUBSCRIBER;
  }

  protected unregisterAction(): string {
    return UNREGISTER_MQTT_SUBSCRIBER;
  }

  protected createPayload(settings?: NodeSetting): NodeStoreItem {
    let payload = super.createPayload(settings);
    payload.callback = (out: any) => this.onReceiveMessage(out);
    return payload;
  }

  protected doAfterUpdate(current: NodeStoreItem, prev?: NodeStoreItem): any {

  }
}

Container.registerNodeType('protocols/mqtt/mqtt-subscriber', MqttSubscriberNode, MQTT_CLIENT_NODE);
Container.registerNodeType('protocols/mqtt/mqtt-publisher', MqttPublisherNode, MQTT_CLIENT_NODE);
