import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';

class StringIndexOfNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.string["string-index-of"].title;
    this.description = NODE_INFO.string["string-index-of"].description;
    this.addInputWithSettings('input', Type.STRING, '', 'Input Value', false);
    this.addInputWithSettings('searchFor', Type.STRING, '', 'Search For Value', false);
    this.addOutput('startIndex', Type.NUMBER);
    this.addOutput('endIndex', Type.NUMBER);
    this.setOutputData(0, null);
    this.setOutputData(1, null);
    this.settings['input'] = NodeSetting.create({
      description: 'string1 Value',
      value: '',
      type: SettingType.STRING,
    });
    this.settings['searchFor'] = NodeSetting.create({
      description: 'Search For Value',
      value: '',
      type: SettingType.STRING,
    });
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    const input = this.getInputData(0);
    const searchFor = this.getInputData(1);
    const index = input.indexOf(searchFor);
    const length = searchFor.length;
    if (index < 0) {
      this.setOutputData(0, null, true);
      this.setOutputData(1, null, true);
    } else {
      this.setOutputData(0, index, true);
      this.setOutputData(1, index + length, true);
    }
  }

  onAfterSettingsChange() {
    if (this.side !== Side.server) return;
    this.onInputUpdated();
  }
}
Container.registerNodeType('string/string-index-of', StringIndexOfNode);
