import AuthUserDatabase from './database/AuthUserDatabase';
import DashboardDatabase from './database/DashboardDatabase';
import HistorianDatabase from './database/HistorianDatabase';
import NodeDatabase from './database/NodeDatabase';
import ScheduleDatabase from './database/ScheduleDatabase';
import UserDatabase from './database/UserDatabase';

export interface Database extends AuthUserDatabase, DashboardDatabase, NodeDatabase, ScheduleDatabase,
                                  HistorianDatabase, UserDatabase {
}

export class AsyncDbHelper {

  static noArgs<R>(db: Database, func: Function): Promise<R> {
    return new Promise<R>((resolve, reject) => func.call(db, this.cbHandler(reject, resolve)));
  }

  static oneArgs<R>(db: Database, func: Function, param: any): Promise<R> {
    return new Promise<R>((resolve, reject) => func.call(db, param, this.cbHandler(reject, resolve)));
  }

  static twoArgs<R>(db: Database, func: Function, param1: any, param2: any): Promise<R> {
    return new Promise<R>((resolve, reject) => func.call(db, param1, param2, this.cbHandler(reject, resolve)));
  }

  static threeArgs<R>(db: Database, func: Function, param1: any, param2: any, param3: any): Promise<R> {
    return new Promise<R>((resolve, reject) => func.call(db, param1, param2, param3, this.cbHandler(reject, resolve)));
  }

  private static cbHandler<R>(reject: (reason?: any) => void, resolve: (value?: (PromiseLike<R> | R)) => void) {
    return (err?: Error, result?: R) => {
      if (err) {
        reject(err);
      } else {
        resolve(result);
      }
    };
  }
}
