import { isNull } from 'util';
import { Connect, Disconnect } from '../../../pub-sub/Decorators';
import { DispatchNode } from '../../../pub-sub/DispatchNode';
import { Store } from '../../../pub-sub/IPubSub';
import { pubSubSingleton } from '../../../pub-sub/PubSub';
import { Container } from '../../container';
import { BROADCAST } from '../../node';
import Icons from '../../node-icons';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';

const icon = Icons.aiIcon;

export class DispatchListener extends DispatchNode {
  originalHeaderColor: string = '';

  constructor() {
    super();
    this.originalHeaderColor = this.headerColor;
    this.title = NODE_INFO.connection['dispatch-listener'].title;
    this.description = NODE_INFO.connection['dispatch-listener'].description;

    this.iconImageUrl = icon;
    this.addOutput('output', Type.ANY);
    this.settings['protocol'] = NodeSetting.create({
      description: 'Protocol',
      config: { items: [] },
      value: '',
      type: SettingType.TREE_SELECT,
    });
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.settings['blockNull'] = NodeSetting.create({
      description: 'Block Null',
      value: false,
      type: SettingType.BOOLEAN,
    });

    this.setSettingsConfig({
      groups: [{ enable: { weight: 1 }, blockNull: { weight: 1 } }],
    });
  }

  async onAdded() {
    super.onAdded();
    this.onAfterSettingsChange();
    if (this.side === Side.editor) {
      this.sendMessageToServerSide(null); // for title update it will call onGetMessageToServerSide
    }
  }

  @Disconnect
  onRemoved() {
    super.onRemoved();
  }

  @Connect
  onAfterSettingsChange(oldSettings = {}, oldName = '') {
    if (this.side !== Side.server) return;
    this.updateSubscriptionTree();
    this.updateTitle();
    this.broadcastSettingsToClients();
    this.broadcastTitleToClients();
  }

  updateTitle() {
    this.title = `${NODE_INFO.connection['dispatch-listener'].title} [${this.getSelectedPointText()}]`;
  }

  updateSubscriptionTree() {
    const store: Store = pubSubSingleton.getStore();
    let items = Object.keys(store).map(protocol => {
      return {
        text: protocol,
        items: Object.keys(store[protocol].points)
          .filter(point => !store[protocol].points[point].soft)
          .map(point => {
            return {
              text: `${store[protocol].points[point].name} (${point})`,
              name: store[protocol].points[point].name,
              value: point,
              protocol,
            };
          }),
      };
    });
    items = items.filter(item => item.items.length); // removed protocol which has soft only
    this.settings['protocol'].config.items = items;
  }

  getSelectedProtocol(): string {
    const matchProtocolItem = this.getMatchProtocolItem();
    return matchProtocolItem?.protocol || '';
  }

  getSelectedPoint(): string {
    return this.settings['protocol'].value;
  }

  getSelectedPointName(): string {
    const matchProtocolItem = this.getMatchProtocolItem();
    return matchProtocolItem?.name || '';
  }

  getSelectedPointText(): string {
    const matchProtocolItem = this.getMatchProtocolItem();
    return matchProtocolItem?.text || '';
  }

  getMatchProtocolItem(): any {
    const protocol = this.settings['protocol'].value;
    const protocolsItems = this.settings['protocol'].config.items;
    for (let protocolsItem of protocolsItems) {
      const protocolItems = protocolsItem.items;
      for (let protocolItem of protocolItems) {
        if (protocolItem.value === protocol) {
          return protocolItem;
        }
      }
    }
    return null;
  }

  onGetMessageToServerSide() {
    this.broadcastTitleToClients(false); // false since it needs to reflect even on sub-containers
  }

  subscribe(value: any) {
    // this.debugInfo(`Subscribed value: ${value}`);
    const enable: boolean = this.settings['enable'].value;
    const blockNull: boolean = this.settings['blockNull'].value;
    if (enable && !(isNull(value) && blockNull)) {
      this.setOutputData(0, value);
    }
  }

  updateProtocols() {
    this.updateSubscriptionTree();
    // this.debugInfo(`Updating protocol to: ${this.settings['protocol'].config.items}`);
    this.updateTitle();
    this.broadcastSettingsToClients(false);
    this.broadcastTitleToClients(false);
  }

  onGetMessageToEditorSide({ action, payload, key }: { action: any; payload: any; key: any }) {
    if (action === BROADCAST.UPDATE_TITLE) {
      this.title = payload;
      this.size = this.computeSize();
      if (payload === `${NODE_INFO.connection['dispatch-listener'].title} []`) {
        this.headerColor = '#A44';
      } else {
        this.headerColor = this.originalHeaderColor;
      }
    } else if (action === BROADCAST.UPDATE_SETTINGS) {
      // Sorting only in frontend, don't pressurized the backend much
      const items = payload['protocol'].config.items;
      items.sort((item1, item2) => (item1.text.toUpperCase() > item2.text.toUpperCase() ? 1 : -1));
      items.forEach(item =>
        item.items.sort((item1, item2) => (item1.text.toUpperCase() > item2.text.toUpperCase() ? 1 : -1)),
      );
      payload['protocol'].config.items = items;
      this.settings = payload;
    } else {
      super.onGetMessageToEditorSide({ action, payload, key });
    }
  }
}
Container.registerNodeType('connection/dispatch-listener', DispatchListener);
