export default class NodeColours {
  /**
   * Changes the node colour for the user
   * NORMAL, light grey
   * INFO, light green
   * WARNING, light orange
   * ERROR, light red
   * @param self {string} pass in this instance
   * @param NodeState {string} pass in the colour to use
   */
    static nodeColourChange(self, NodeState) {
        self.setNodeState(NodeState);
    }
}
