import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class TimeFrequencyMeterNode extends Node {
  count = 0;
  countWas = 0;
  lastReset: boolean;

  constructor() {
    super();
    this.title = NODE_INFO.streams["frequency-meter"].title;
    this.description = NODE_INFO.streams["frequency-meter"].description;
    this.addInput('input');
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('events/sec', Type.NUMBER, undefined, { data: 0 });
    this.lastReset = false;

    setInterval(() => {
      if (this.count != this.countWas) {
        this.setOutputData(0, this.count);
      }
      this.countWas = this.count;
      this.count = 0;
    }, 1000);
  }

  onInputUpdated() {
    const reset = this.getInputData(1);
    if (reset && !this.lastReset) {
      this.count = 0;
      this.countWas = 0;
      this.setOutputData(0, null);
      this.lastReset = reset;
      return;
    } else if (!reset && this.lastReset) {
      this.lastReset = reset;
      return;
    }

    this.count++;
  }
}
Container.registerNodeType('streams/frequency-meter', TimeFrequencyMeterNode);
