import axios from 'axios';
import { IPoint } from 'influx';
import * as moment from 'moment-timezone';
import config from '../../../config';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import SettingUtils from '../../utils/setting-utils';
import TimeUtils, { TIME_TYPE } from '../../utils/time-utils';
import SystemUtils from '../system/system-utils';

const Influx = require('influx');

enum HistoryMode {
  COV = 0,
  PERIODIC = 1,
  TRIGGER_ONLY = 2,
}

enum DataBaseType {
  POSTGRES = 0,
  InfluxDB = 1,
}

export default class HistoryBase extends Node {
  // dynamically calculate position when starting
  histTriggerInput: number;
  clearStoredHisInput: number;
  tableNameInput: number;
  pointNameInput: number;
  histErrorOutput: number;
  storedHistCountOutput: number;
  lastHistExportOutput: number;
  // configurable parameters from each extended node for extracting values to store on DB
  valueInput: number;
  takeValueFromInput: boolean;
  // extra parameter for nodes
  interval: number = -1;
  timeoutFunc: any;
  dynamicInputStartPosition: number = 2;
  alarmCount: number = 0;
  tagCount: number = 0;
  lastTrigger: boolean = false;

  addHistoryConfiguration() {
    this.addInput('histTrigger', Type.BOOLEAN);
    this.addInput('clearStoredHis', Type.BOOLEAN);
    this.addOutput('histError', Type.ANY);
    this.addOutput('storedHistCount', Type.NUMBER);
    this.addOutput('lastHistExport', Type.STRING);

    /**
     * History settings
     */
    this.settings['history_group'] = NodeSetting.create({
      description: 'History Settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['databaseType'] = NodeSetting.create({
      description: 'Database Type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: DataBaseType.POSTGRES, text: 'Postgres' },
          { value: DataBaseType.InfluxDB, text: 'InfluxDB' },
        ],
      },
      value: DataBaseType.POSTGRES,
    });
    this.settings['enable'] = NodeSetting.create({
      description: 'History Enable',
      value: false,
      type: SettingType.BOOLEAN,
    });

    // THESE SETTINGS FOR INFLUX HISTORY STORAGE -----------------
    this.settings['tableName'] = NodeSetting.create({
      description: 'Measurement Name',
      value: '',
      type: SettingType.STRING,
    });
    // END OF INFLUX SPECIFIC SETTINGS ---------------------------

    this.addInputWithSettings('pointName', Type.STRING, '', 'Point Name');
    this.settings['historyMode'] = NodeSetting.create({
      description: 'History Logging Mode',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: HistoryMode.COV, text: 'Change Of Value (COV)' },
          { value: HistoryMode.PERIODIC, text: 'Periodic' },
          { value: HistoryMode.TRIGGER_ONLY, text: 'Trigger Only' },
        ],
      },
      value: HistoryMode.COV,
    });
    this.settings['dataType'] = NodeSetting.create({
      description: 'Data type for storing',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: Type.NUMBER, text: 'Number' },
          { value: Type.BOOLEAN, text: 'Boolean' },
          { value: Type.STRING, text: 'String' },
        ],
      },
      value: Type.NUMBER,
    });
    this.settings['threshold'] = NodeSetting.create({
      description: 'COV Threshold',
      value: 0,
      type: SettingType.NUMBER,
    });
    this.settings['period'] = NodeSetting.create({
      description: 'Logging Interval',
      value: 15,
      type: SettingType.NUMBER,
    });
    this.settings['periodUnits'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: TIME_TYPE.MILLISECONDS, text: 'Milliseconds' },
          { value: TIME_TYPE.SECONDS, text: 'Seconds' },
          { value: TIME_TYPE.MINUTES, text: 'Minutes' },
          { value: TIME_TYPE.HOURS, text: 'Hours' },
        ],
      },
      value: TIME_TYPE.MINUTES,
    });
    this.settings['storage-limit'] = NodeSetting.create({
      description: 'Local Storage Limit (Max 50)',
      value: 25,
      type: SettingType.NUMBER,
    });
    this.settings['minuteRoundValue'] = NodeSetting.create({
      description: 'Round minutes up in increments of',
      value: 0,
      type: SettingType.NUMBER,
    });
    this.settings['decimals'] = NodeSetting.create({
      description: 'Decimal Places (Limit 5)',
      value: 3,
      type: SettingType.NUMBER,
    });
    this.settings['timeAsString'] = NodeSetting.create({
      description: 'Display Timestamp as',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: false, text: 'Numeric' },
          { value: true, text: 'String' },
        ],
      },
      value: true,
    });

    /**
     * Alarm settings
     */
    this.settings['alarm_group'] = NodeSetting.create({
      description: 'Alarm Settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['alarms_count'] = NodeSetting.create({
      description: 'Alarms Count (Max 10)',
      value: 0,
      type: SettingType.NUMBER,
    });

    /**
     * Tag settings
     */
    this.settings['tags_group'] = NodeSetting.create({
      description: 'Tag Settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['tags_count'] = NodeSetting.create({
      description: 'Tag Count (Max 10)',
      value: 0,
      type: SettingType.NUMBER,
    });
    this.properties['obj'] = [];
  }

  setup() {
    this.addHistoryConfiguration();
  }

  init(properties) {
    this.historyFunctionsForAfterSettingsChange(properties['settings'], false);
    this.lastTrigger = false;
  }

  /**
   * When databaseType is selected as POSTGRES it needs to have an extra input tableName
   * @param settings {object} settings configuration
   */
  private changeInputDynamically(settings) {
    this.assignInputsOutputs();
    if (this.tableNameInput === -1 && settings['databaseType'].value === DataBaseType.InfluxDB) {
      if (this.pointNameInput) this.removeInput(this.pointNameInput);
      this.addInput('[tableName]', Type.STRING, { exist: true, nullable: false, hidden: false });
      this.addInput('[pointName]', Type.STRING, { exist: true, nullable: false, hidden: false });
    } else if (this.tableNameInput !== -1 && settings['databaseType'].value === DataBaseType.POSTGRES) {
      if (this.pointNameInput) this.removeInput(this.pointNameInput);
      this.removeInput(this.tableNameInput);
      this.addInput('[pointName]', Type.STRING, { exist: true, nullable: false, hidden: false });
    }
  }

  onAdded() {
    this.assignInputsOutputs();
    this.resetOutputs();
    this.doPeriodicHistoryFunctions();
  }

  async onInputUpdated() {
    if (this.getInputData(this.clearStoredHisInput)) {
      this.properties['obj'] = [];
      this.updateHistoryCountOutput();
    }
    await this.doNonPeriodicHistoryFunctions();
  }

  async onAfterSettingsChange() {
    this.historyFunctionsForAfterSettingsChange();
    await this.doNonPeriodicHistoryFunctions();
    this.doPeriodicHistoryFunctions();
  }

  onRemoved() {
    clearInterval(this.timeoutFunc);
  }

  assignInputsOutputs() {
    this.tableNameInput = -1;
    this.pointNameInput = -1;
    for (let input in this.inputs) {
      if (this.inputs.hasOwnProperty(input)) {
        if (this.inputs[input].name == 'histTrigger') this.histTriggerInput = Number(input);
        if (this.inputs[input].name == 'clearStoredHis') this.clearStoredHisInput = Number(input);
        if (this.inputs[input].name == '[tableName]') this.tableNameInput = Number(input);
        if (this.inputs[input].name == '[pointName]') this.pointNameInput = Number(input);
      }
    }

    for (let output in this.outputs) {
      if (this.outputs.hasOwnProperty(output)) {
        if (this.outputs[output].name == 'histError') this.histErrorOutput = Number(output);
        else if (this.outputs[output].name == 'storedHistCount') this.storedHistCountOutput = Number(output);
        else if (this.outputs[output].name == 'lastHistExport') this.lastHistExportOutput = Number(output);
      }
    }
  }

  addHistorySettingsConfig(valueInput: number = 0, takeValueFromInput: boolean = true) {
    if (!this.settingConfigs.hasOwnProperty('groups') || !this.settingConfigs.hasOwnProperty('conditions')) {
      this.setSettingsConfig({
        groups: this.settingConfigs['groups'] || [],
        conditions: this.settingConfigs['conditions'] || {},
      });
    }
    this.settingConfigs.groups.push({ period: { weight: 2 }, periodUnits: {} });
    this.settingConfigs.conditions['tableName'] = setting => {
      return setting['databaseType'].value === DataBaseType.InfluxDB;
    };
    this.settingConfigs.conditions['threshold'] = setting => {
      return setting['historyMode'].value === HistoryMode.COV;
    };
    this.settingConfigs.conditions['period'] = setting => {
      return setting['historyMode'].value === HistoryMode.PERIODIC;
    };
    this.settingConfigs.conditions['periodUnits'] = setting => {
      return setting['historyMode'].value === HistoryMode.PERIODIC;
    };
    this.settingConfigs.conditions['alarms_count'] = setting => {
      return setting['databaseType'].value === DataBaseType.InfluxDB;
    };
    this.settingConfigs.conditions['tags_count'] = setting => {
      return setting['databaseType'].value === DataBaseType.InfluxDB;
    };

    this.valueInput = valueInput;
    this.takeValueFromInput = takeValueFromInput;
  }

  resetOutputs() {
    this.updateHistoryCountOutput();
  }

  async doNonPeriodicHistoryFunctions() {
    if (this.side !== Side.server) return;
    if (!this.settings['enable'].value) return;
    let input = null;
    if (this.takeValueFromInput) {
      input = this.getInputData(this.valueInput);
    } else {
      input = this.outputs[this.valueInput].data;
    }

    if (this.checkTriggered()) {
      await this.storeLogEntry(input);
    } else if (this.checkCOV(input)) {
      await this.storeLogEntry(input);
    }

    if (this.properties['obj'].length > 0) await this.trySendStoredData();
  }

  async trySendStoredData() {
    let decimals = this.settings['decimals'].value;
    decimals = Utils.clamp(decimals, 0, 5);

    const points: IPoint[] = [];
    this.properties['obj'].forEach(log => {
      const dataType = this.settings['dataType'].value;
      log.payload = this.convertInput(log.payload, dataType, decimals);

      const tagList = {};
      tagList['point'] = this.getInputData(this.pointNameInput) || 'undefined';
      Object.keys(log).map(key => {
        if (key !== 'payload' && key !== 'timestamp' && key !== 'tags') {
          tagList[key] = log[key];
        }
      });
      for (let i = 0; i < 10; i++) {
        tagList['tag' + (i + 1)] = log['tags'][i] || null;
      }
      points.push({
        measurement: this.getInputData(this.tableNameInput),
        fields: { val: log.payload },
        timestamp: moment(log.timestamp).valueOf() * 1000000, // it only supports unix nano seconds dates
        tags: tagList,
      });
    });

    //////////////////////////////////////////////////////////////////////////
    // This section should be replaced with the history storage mode of choice.
    let errorFlag = false;
    if (this.settings['databaseType'].value === DataBaseType.POSTGRES) {
      // For Multiple points in 1 push
      let clientIdFromPlat;
      let deviceIdFromPlat;
      try {
        clientIdFromPlat = await SystemUtils.getClientIdDb();
      } catch (e) {
        clientIdFromPlat = 'unknownClientID';
      }
      try {
        deviceIdFromPlat = await SystemUtils.getDeviceIdDb();
      } catch (e) {
        deviceIdFromPlat = 'unknownDeviceID';
      }
      const multiPointPost = points.map(point => ({
        deviceid: deviceIdFromPlat,
        clientid: clientIdFromPlat,
        val: point.fields.val,
        point: point.tags.point,
        ts: moment(Number(point.timestamp) / 1000000).toISOString(), // we can do better than this
      }));

      const that = this;
      const pgUrl = config.pg.baseURL;
      try {
        await axios({
          method: 'post',
          url: pgUrl,
          data: multiPointPost,
        });
      } catch (error) {
        this.setOutputData(that.histErrorOutput, String(error));
        errorFlag = true;
      }
    } else if (DataBaseType.InfluxDB === DataBaseType.InfluxDB) {
      const writeOptions = {};

      const { protocol, host, port, databaseName, username, password } = config.influxDb;
      const client = new Influx.InfluxDB({
        host,
        port,
        protocol: protocol === 'https' ? 'https' : 'http',
        database: databaseName,
        username,
        password,
      });
      try {
        await client.writePoints(points, writeOptions);
      } catch (err) {
        this.setOutputData(this.histErrorOutput, String(err));
        this.debugErr(err);
        errorFlag = true;
      }
    }
    /////////////////////////////////////////////////////////////

    if (!errorFlag) {
      this.properties['obj'] = [];
      this.setOutputData(this.histErrorOutput, '');
      this.settings['timeAsString'].value
        ? this.setOutputData(this.lastHistExportOutput, moment().format(), true)
        : this.setOutputData(this.lastHistExportOutput, moment().valueOf(), true);
      this.updateHistoryCountOutput();
    }
  }

  /**
   * After settings changes this method needs to call
   * @param configSettings {object}, if we want to set different settings name in input
   * @param save {boolean}, need to persist the changes or not; when save = false, it needs to save later
   */
  historyFunctionsForAfterSettingsChange(configSettings = null, save: boolean = true) {
    this.changeInputDynamically(configSettings || this.settings);
    this.assignInputsOutputs();
    if (this.settings['databaseType'].value === DataBaseType.POSTGRES) {
      this.settings['alarms_count'].value = 0;
      this.settings['tags_count'].value = 0;
    }
    this.dynamicInputStartPosition = this.pointNameInput + 1;
    this.changeAlarmsCount(this.settings['alarms_count'].value);
    this.renameAlarmInputs(configSettings);
    this.changeTagsCount(this.settings['tags_count'].value);
    if (save) {
      this.persistProperties(true, false, true, false);
    }
  }

  async storeLogEntry(input) {
    if (input == null) return;
    // cap the amount of data that can be stored
    const storageLimit = Utils.clamp(this.settings['storage-limit'].value, 0, 50);
    const minuteRound = Utils.clamp(this.settings['minuteRoundValue'].value, 0, 60);
    const now = minuteRound ? TimeUtils.nearestFutureMinutes(minuteRound, moment()) : moment();
    // object data
    const msg = {
      payload: input,
      timestamp: now.toISOString(),
    };
    // add alarm values
    for (let i = this.dynamicInputStartPosition; i < this.dynamicInputStartPosition + this.alarmCount; i++) {
      let alarmNum = i - this.dynamicInputStartPosition + 1;
      let alarmName = this.settings['alarm' + alarmNum].value || 'alarm' + alarmNum;
      msg[alarmName] = this.getInputData(i) || 'null';
    }
    // add tags
    const tags = [];
    let j = 1;
    while (j <= this.tagCount) {
      if (this.settings['tag' + j].value) tags.push(this.settings['tag' + j].value);
      j++;
    }
    msg['tags'] = tags;

    // build an object
    this.properties['obj'].push(msg);
    while (this.properties['obj'].length > storageLimit) {
      this.properties['obj'].shift();
    }
    this.properties['lastHistoryValue'] = input;
    this.persistProperties(false, true, false, false);
    this.updateHistoryCountOutput();
  }

  checkTriggered() {
    const trigger = this.getInputData(this.histTriggerInput) || false;
    const result = this.inputs[this.histTriggerInput].updated && trigger && !this.lastTrigger;
    this.lastTrigger = trigger;
    return result;
  }

  checkCOV(input) {
    if (this.settings['historyMode'].value === HistoryMode.COV) {
      const threshold = this.settings['threshold'].value;
      if (!isNaN(input) && !isNaN(this.properties['lastHistoryValue'])) {
        return Math.abs(Number(input) - Number(this.properties['lastHistoryValue'])) >= threshold;
      } else {
        return input !== this.properties['lastHistoryValue'];
      }
    }
    return false;
  }

  /**
   * Sets values in periodic period when HistoryMode is `PERIODIC`
   */
  doPeriodicHistoryFunctions() {
    if (this.side !== Side.server) return;
    const enable = this.settings['enable'].value;
    const isPeriodic = this.settings['historyMode'].value === HistoryMode.PERIODIC;
    if (!enable || !isPeriodic) {
      if (this.timeoutFunc) {
        this.interval = -1;
        clearInterval(this.timeoutFunc);
        this.debugInfo('Clearing Periodic interval data insertion...');
      }
    }
    if (enable && isPeriodic) {
      const interval = TimeUtils.timeConvert(this.settings['period'].value, this.settings['periodUnits'].value);
      if (this.interval !== interval) {
        clearInterval(this.timeoutFunc);
        this.timeoutFunc = setInterval(async () => {
          let input = null;
          if (this.takeValueFromInput) input = this.getInputData(this.valueInput);
          else input = this.outputs[this.valueInput].data;
          await this.storeLogEntry(input);
          if (this.properties['obj'].length > 0) await this.trySendStoredData();
        }, interval);
        this.interval = interval;
        this.debugInfo(`Setting Periodic Period of: ${interval} ms`);
      }
    }
  }

  changeAlarmsCount(targetCount) {
    targetCount = Utils.clamp(targetCount, 0, 10);
    this.settings['alarms_count'].value = targetCount;
    let diff = targetCount - this.alarmCount;
    if (diff == 0) return;

    // add inputs
    this.changeInputsCount(targetCount + this.dynamicInputStartPosition, Type.STRING);

    // change settings for topics
    const alarmsToAdd = {};
    if (diff > 0) {
      for (let i = this.alarmCount + 1; i <= targetCount; i++) {
        alarmsToAdd['alarm' + i] = {
          description: 'Alarm ' + i,
          value: '',
          type: SettingType.STRING,
        };
      }
      // for making things consistent on UI settings side
      this.settings = SettingUtils.insertIntoObjectAtPosition(
        this.settings,
        alarmsToAdd,
        this.alarmCount == 0 ? 'alarms_count' : 'alarm' + this.alarmCount,
      );
    } else if (diff < 0) {
      for (let i = this.alarmCount; i > targetCount; i--) {
        delete this.settings['alarm' + i];
      }
    }
    this.alarmCount = targetCount;
  }

  renameAlarmInputs(configSettings) {
    for (let i = 1; i <= this.alarmCount; i++) {
      let alarm = this.settings['alarm' + i].value;
      if (!alarm && configSettings) {
        alarm = (configSettings['alarm' + i] && configSettings['alarm' + i].value) || '';
      }
      if (alarm.length > 20) alarm = '...' + alarm.substr(alarm.length - 20, 20);
      this.inputs[this.dynamicInputStartPosition + i - 1].name = 'alarm' + i + ' | ' + alarm;
    }
  }

  changeTagsCount(targetCount) {
    targetCount = Utils.clamp(targetCount, 0, 10);
    this.settings['tags_count'].value = targetCount;
    let diff = targetCount - this.tagCount;
    if (diff == 0) return;

    // change settings for topics
    const tagsToAdd = {};
    if (diff > 0) {
      for (let i = this.tagCount + 1; i <= targetCount; i++) {
        tagsToAdd['tag' + i] = {
          description: 'Tag ' + i,
          value: '',
          type: SettingType.STRING,
        };
      }
      // for making things consistent on UI settings side
      this.settings = SettingUtils.insertIntoObjectAtPosition(
        this.settings,
        tagsToAdd,
        this.tagCount === 0 ? 'tags_count' : 'tag' + this.tagCount,
      );
    } else if (diff < 0) {
      for (let i = this.tagCount; i > targetCount; i--) {
        delete this.settings['tag' + i];
      }
    }
    this.tagCount = targetCount;
  }

  updateHistoryCountOutput() {
    this.setOutputData(this.storedHistCountOutput, (this.properties['obj'] && this.properties['obj'].length) || 0);
  }
}
