import { AbstractRegistry } from '../nodes/registry';
import { IdGenerator } from '../utils/id-generator';
import { DispatchNode } from './DispatchNode';

export class DispatcherRegistry extends AbstractRegistry<DispatchNode> {
  private static _instance: DispatcherRegistry;

  private constructor() {
    super();
  }

  public static instance(): DispatcherRegistry {
    if (!DispatcherRegistry._instance) {
      DispatcherRegistry._instance = new DispatcherRegistry();
    }
    return DispatcherRegistry._instance;
  }

  protected createKey(node: DispatchNode): string | number {
    return IdGenerator.createNodeId(node.cid, node.id);
  }

  protected itemName(): string {
    return 'Dispatcher Node';
  }
}
