import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

export class NumSeriesGenerator extends FlexibleNode {
  constructor() {
    super();
    this.dynamicOutputsExist = true;
    this.dynamicInputsExist = false;
    this.dynamicSettingsExist = true;
    super.dynamicOutputsType = Type.NUMBER;
    this.title = NODE_INFO.miscellaneous["num-series-generator"].title;
    this.description = NODE_INFO.miscellaneous["num-series-generator"].description; 
    this.addInputWithSettings('startVal', Type.NUMBER, 0, 'Start Value');
    this.addInputWithSettings('addVal', Type.NUMBER, 1, 'Added Value ');
    this.settings['factor'] = NodeSetting.create({
      description: 'Multiply factor',
      value: 0,
      type: SettingType.NUMBER,
    });
  }

  onInputUpdated() {
    let input = this.getInputData(0);
    let factor = this.settings['factor'].value; 
    if (typeof input === 'undefined') return;
    if (typeof input === 'number') {
      const len = this.getOutputsCount();
      let diff = this.getInputData(1);
      if (typeof diff !== 'number') diff = 1;
      for (let i = 0; i < len; i++) {
        this.setOutputData(i, i + (input * diff) + (i * factor));
      }
    }
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated()
  }
}
Container.registerNodeType('miscellaneous/num-series-generator', NumSeriesGenerator);
