import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class FiltersOnlyTrueNode extends Node {
  constructor() {
    super();

    this.title = NODE_INFO.filter["only-true"].title;
    this.description = NODE_INFO.filter["only-true"].description;

    this.addInput('input', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
  }

  onAdded(){
    this.setOutputData(0, null);
    this.onInputUpdated();
  }

  onInputUpdated() {
    if (this.getInputData(0) === true) this.setOutputData(0, true);
  }
}
Container.registerNodeType('filter/only-true', FiltersOnlyTrueNode);
