import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class TypeCheckNode extends Node {

  constructor() {
    super();

    this.title = NODE_INFO.compare["type-check"].title;
    this.description = NODE_INFO.compare["type-check"].description;

    this.addInput('input', Type.ANY);
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('isType', Type.STRING);

    this.settings['type'] = NodeSetting.create({
      description: 'Type to check',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: Type.STRING, text: Type.STRING },
          { value: Type.NUMBER, text: Type.NUMBER },
          { value: Type.BOOLEAN, text: Type.BOOLEAN },
          { value: Type.JSON, text: Type.JSON },
        ],
      },
      value: Type.STRING,
    });
  }

  onAdded() {
 
    this.updateTitle();
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let data = this.getInputData(0);
    if (data === undefined) {
      this.setOutputData(0, null);
      this.setOutputData(1, null);
    }
    const type = this.settings['type'].value;
    const dataType = typeof data;
    this.setOutputData(1, dataType);
    switch (type) {
      case Type.STRING:
        this.setOutputData(0, typeof data === Type.STRING);
        break;
      case Type.NUMBER:
        this.setOutputData(0, typeof data === Type.NUMBER);
        break;
      case Type.BOOLEAN:
        this.setOutputData(0, typeof data === Type.BOOLEAN);
        break;
      case Type.JSON:
        if (typeof data === 'string') {
          try {
            JSON.parse(data);
            this.setOutputData(0, true);
          } catch (e) {
            this.setOutputData(0, false);
          }
        } else if (typeof data === 'object') {
          try {
            JSON.parse(JSON.stringify(data));
            this.setOutputData(0, true);
          } catch (e) {
            this.setOutputData(0, false);
          }
        }
        this.setOutputData(0, data === null);
        break;
    }
  }

  onAfterSettingsChange() {
    this.updateTitle();
    this.onInputUpdated();
  }

  updateTitle() {
    const type = this.settings['type'].value;
    switch (type) {
      case Type.STRING:
        this.title = 'Type Check (string)';
        break;
      case Type.NUMBER:
        this.title = 'Type Check (number)';
        break;
      case Type.BOOLEAN:
        this.title = 'Type Check (boolean)';
        break;
      case Type.JSON:
        this.title = 'Type Check (json)';
        break;
    }
  }
}

Container.registerNodeType('compare/type-check', TypeCheckNode);
