import * as _ from 'lodash';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

export class JSONBuilderNode extends Node {
  filterKeys: string[] = [];

  constructor() {
    super();
    this.title = NODE_INFO.json['json-builder'].title;
    this.description = NODE_INFO.json['json-builder'].description;
    this.settings['definition'] =NodeSetting.create({
      description: 'payload definition (JSON)',
      type: SettingType.STRING,
      value: '',
    });
    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('output', Type.JSON);
  }

  init() {
    this.addInputs();
  }

  onAdded() {
    this.filterKeys = this.getFilterKeys();
  }

  onInputUpdated() {
    let out = {};
    for (let i = this.dynamicOutputStartPosition(); i < this.getInputsCount(); i++) {
      out = { ...out, ...{ [this.filterKeys[i - 1]]: this.inputs[i].data } };
    }
    this.setOutputData(0, out);
  }

  onAfterSettingsChange() {
    this.updateInputs();
    this.onInputUpdated();
  }

  private updateInputs() {
    const filterKeys = this.getFilterKeys();
    let diffKeyFromPos = Math.min(this.filterKeys.length, filterKeys.length);
    for (let i = 0; i < filterKeys.length; i++) {
      if (this.filterKeys?.[i] != filterKeys[i]) {
        diffKeyFromPos = i;
        break;
      }
    }
    const inputsCount = this.getInputsCount();
    for (let i = diffKeyFromPos + this.dynamicOutputStartPosition(); i < inputsCount; i++) {
      this.removeInput(i);
    }
    this.addInputs(diffKeyFromPos);
  }

  private addInputs(fromPos = 0) {
    this.filterKeys = this.getFilterKeys();
    for (let i = fromPos; i < this.filterKeys.length; i++) {
      let name = this.filterKeys[i];
      this.addInput(name.toString(), Type.ANY);
    }
  }

  private getFilterKeys(): string[] {
    return this.settings['definition'].value
      .replace(/\s+/g, '')
      .split(',')
      .filter(x => !!x);
  }

  private dynamicOutputStartPosition() {
    return 1;
  }
}
Container.registerNodeType('json/json-builder', JSONBuilderNode);
