import app from '../app';

const schemaMiddleware = (schema, property) => {
  return (req, res, next) => {
    const { error } = schema.validate(req[property]);
    const valid = error == null;
    if (valid) {
      next();
    } else {
      const { details } = error;
      const message = details.map(i => i.message).join(',');
      res.status(422).json({ error: message });
    }
  };
};

const isAuthenticated = async (token: string): Promise<boolean> => {
  try {
    await app.db.isAuthenticated(token);
    return true;
  } catch (e) {
    return false;
  }
};

const authMiddleware = () => {
  return async (req, res, next) => {
    const token = req.headers['token'];
    if (!token) {
      res.status(403).json({ error: 'Please provide token' });
      return;
    }
    if (await isAuthenticated(token)) {
      next();
    } else {
      res.status(403).json({ error: 'Please provide valid token' });
    }
  };
};

export { schemaMiddleware, authMiddleware };
