import { Container } from './container';
import { Node } from './node';
import {
  ContainerRegistryMetadata,
  GlobalNodeRegistryMetadata,
  IContainer, IContainerNode,
  INode,
  isContainerNode,
  isNode,
  SerializedContainerNode,
  SerializedNode,
} from './node-interface';
import { NodeTypeRegistry } from './node-type-registry';

export class NodeFactory {

  static create(serializedNode: SerializedNode, isNew: boolean = true, containerRegistry?: ContainerRegistryMetadata,
                globalRegistry?: GlobalNodeRegistryMetadata): Node {
    if (isContainerNode(serializedNode)) {
      const { ContainerNode } = require('./container-node');
      return ContainerNode.create(<SerializedContainerNode>serializedNode, isNew, containerRegistry, globalRegistry);
    }
    const node = new Node().deserialize(serializedNode, isNew);
    if (isContainerNode(node)) {
      const cNode = <IContainerNode><unknown>node;
      cNode.sub_container = Container.create(node.side, (<IContainerNode>serializedNode).sub_container, containerRegistry, globalRegistry);
    }
    return node;
  }

  static createTempNode(type: string, position: [number, number]): Node {
    const node_class = NodeTypeRegistry.instance().get(type);
    if (node_class) {
      const node: Node = new node_class();
      node.type = type;
      node.pos = position;
      node.size = node.computeSize(true);
      return node;
    } else {
      throw `Can't create node of type [${type}]. Make sure it is registered through registerNodeType`;
    }
  }

  static standardize(parent: IContainer, nodes: (number | SerializedNode | INode)[]): INode[] {
    return nodes.map(n => {
      const nn: INode = isNode(n) ? <INode>n : <INode>{ id: <number>n, cid: parent.id };
      nn.container = nn.container ?? parent;
      return nn;
    });
  }

}
