const SerialPort = require('serialport');

export default class SerialUtils {
  /**
   * Get a list of active serial ports on linux
   * @returns {object}
   */

  static listPorts() {
    return new Promise<Array<any>>((resolve, reject) => {
      try {
        let portList = [];
        SerialPort.list().then(ports => {
          ports.forEach(port => {
            portList.push(port);
          });
          resolve(portList);
        });
      } catch (err) {
        reject(err);
      }
    });
  }
}
