import { isNull } from '../../../utils/helper';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';

export class PointPersistence extends Node {
  lastInterval = 1000;
  runState: boolean = true;
  timeoutFunc;
  lastTrigger;

  constructor() {
    super();
    this.title = NODE_INFO.history.persistence.title;
    this.description = NODE_INFO.history.persistence.description;
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable value resend timer');
    this.addInput('input', Type.ANY);
    this.addInputWithSettings('interval', Type.NUMBER, 30, 'Interval');
    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('output', Type.ANY);
    this.properties['output'] = null;
    this.settings['preventDuplicates'] = NodeSetting.create({
      description: 'Prevent Duplicates)',
      value: true,
      type: SettingType.BOOLEAN,
    });
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
  }

  onAdded() {
    clearInterval(this.timeoutFunc);
    this.inputs[2]['name'] = `[interval] (${this.settings['time'].value})`;
    const enable = this.getInputData(0);
    if (!enable) {
      this.setOutputData(0, this.properties['output']);
      this.runState = false;
      return;
    }
    let interval = this.getInputData(2);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    this.timeoutFunc = setInterval(() => {
      const message = this.getInputData(1);
      this.setOutputData(0, this.properties['output']);
    }, interval);
    this.onInputUpdated();
  }

  onInputUpdated() {
    // trigger
    let trigger = this.getInputData(3);
    const message = this.getInputData(1); //data input
    if (message !== undefined && message !== null && message.length) {
      this.properties['output'] = message;
      this.setOutputData(0, this.properties['output']);
    }

    if (trigger) {
      if (
        this.properties['output'] !== undefined &&
        this.properties['output'] !== null &&
        this.properties['output'].length
      ) {
        this.setOutputData(0, this.properties['output']);
      }
    }

    //pass data on input updated
    if (this.inputs[1].updated) {
      const preventDuplicates = this.settings['preventDuplicates'].value
      if(!isNull(message)){
        this.properties['output'] = message;
        this.setOutputData(0, this.properties['output'], preventDuplicates);
      }
    }

    // interval
    const enable = this.getInputData(0);
    if (!enable && this.runState) {
      clearInterval(this.timeoutFunc);
      this.setOutputData(0, this.properties['output']);
      this.runState = false;
      return;
    } else if (!enable && !this.runState) {
      this.setOutputData(0, this.properties['output']);
      this.runState = false;
      return;
    } else if (enable) {
      let interval = this.getInputData(2);
      interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
      if (this.runState) {
        if (interval != this.lastInterval) {
          this.lastInterval = interval;
          clearInterval(this.timeoutFunc);
          this.timeoutFunc = setInterval(() => {
            this.setOutputData(0, this.properties['output']);
          }, interval);
        }
        return;
      } else if (!this.runState) {
        this.runState = true;
        this.timeoutFunc = setInterval(() => {
          this.setOutputData(0, this.properties['output']);
        }, interval);
      }
    }
  }

  onAfterSettingsChange() {
    this.inputs[2]['name'] = `[interval] (${this.settings['time'].value})`;
    this.onInputUpdated();
  }
}
Container.registerNodeType('history/persistence', PointPersistence);
