import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils, { TIME_TYPE } from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class OneShotNode extends Node {
  lastFireValue: boolean;
  enabled: boolean;
  lastResetValue: boolean;
  timeoutFunc;
  remainingFunc;
  timeUnits;
  remainingUpdateMillis;
  remainingUpdateSize;


  constructor() {
    super();
    this.title = NODE_INFO.boolean["one-shot"].title;
    this.description = NODE_INFO.boolean["one-shot"].description;

    this.addInput('trigger', Type.BOOLEAN);
    this.addInput('reset', Type.BOOLEAN);
    this.addInputWithSettings('interval', Type.NUMBER, 1, 'Interval (Max is 597 Hours)');
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('remaining', Type.NUMBER);
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: TIME_TYPE.MILLISECONDS, text: 'Milliseconds' },
          { value: TIME_TYPE.SECONDS, text: 'Seconds' },
          { value: TIME_TYPE.MINUTES, text: 'Minutes' },
          { value: TIME_TYPE.HOURS, text: 'Hours' },
        ],
      },
      value: TIME_TYPE.SECONDS,
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });

    this.settings['retrigger'] = NodeSetting.create({
      description: "Retrigger While Output is 'true'?",
      value: true,
      type: SettingType.BOOLEAN,
    });

  }

  onCreated() {
  }

  onAdded() {
    this.clearTimers();
    this.setOutputData(0, false);
    this.setOutputData(1, 0);
    this.lastFireValue = false;
    this.lastResetValue = false;
    this.enabled = false;
    this.onAfterSettingsChange();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onAfterSettingsChange() {
    this.timeUnits = this.settings['time'].value;
    this.inputs[2]['name'] = `[interval] (${this.settings['time'].value})`;
    this.outputs[1]['name'] = `[remaining] (${this.timeUnits})`;
    this.onInputUpdated();
  }

  onInputUpdated() {
    let reset = this.getInputData(1);
    if (reset == null) reset = false;
    if (reset == true && reset != this.lastResetValue) {
      this.clearTimers();
      this.setOutputData(0, false);
      this.setOutputData(1, 0);
      this.enabled = false;
    }
    this.lastResetValue = reset;

    let fire = this.getInputData(0);
    if (fire == null) fire = false;
    if (fire == true && fire != this.lastFireValue) {
      const retrigger = this.settings['retrigger'].value;
      if (retrigger || this.enabled === false) {
        const delay = this.getInputData(2);
        const delayMilli = TimeUtils.timeConvert(delay, this.timeUnits);
        this.clearTimers();
        this.setOutputData(0, true);
        this.setOutputData(1, delay);
        this.enabled = true;
        this.timeoutFunc = setTimeout(() => {
          this.enabled = false;
          this.setOutputData(0, false);
          this.setOutputData(1, 0);
          clearInterval(this.remainingFunc);
        }, delayMilli);
        switch(this.timeUnits){
          case TIME_TYPE.MILLISECONDS:
            this.remainingUpdateMillis = 500;
            this.remainingUpdateSize = 500;
            break;
          case TIME_TYPE.SECONDS:
            this.remainingUpdateMillis = 1000;
            this.remainingUpdateSize = 1;
            break;
          case TIME_TYPE.MINUTES:
            this.remainingUpdateMillis = 6000;
            this.remainingUpdateSize = 0.1;
            break;
          case TIME_TYPE.HOURS:
            this.remainingUpdateMillis = 360000;
            this.remainingUpdateSize = 0.1;
            break;
        }
        this.remainingFunc = setInterval(() => {
          const remaining = this.outputs[1].data - this.remainingUpdateSize;
          this.setOutputData(1, remaining);
        }, this.remainingUpdateMillis);
      }
    }
    this.lastFireValue = fire;
    this.setOutputData(0, this.outputs[0].data, true);
    this.setOutputData(1, this.outputs[1].data, true);
  }

  private clearTimers() {
    if (this.timeoutFunc) clearTimeout(this.timeoutFunc);
    if (this.remainingFunc) clearInterval(this.remainingFunc);
  }
  
}
Container.registerNodeType('boolean/one-shot', OneShotNode);
