import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import { NODE_INFO } from '../../node-info';

//Numeric Constant
class NumConstantNode extends Node {

  constructor() {
    super();
    this.title = NODE_INFO.point["num-constant"].title;
    this.description = NODE_INFO.point["num-constant"].description; 
    this.settings['value'] = NodeSetting.create({ description: 'Value', value: null, type: SettingType.NUMBER });
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
 
    this.onAfterSettingsChange();
  }

  onExecute(){
    // this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    const val = this.settings['value'].value;
    this.setOutputData(0, Utils.parseValue(val, Type.NUMBER));
  }
}
Container.registerNodeType('point/num-constant', NumConstantNode);
