import config from '../../../config';

export const bsaClientConfig = path => {
  let cfg = config.bsa.paths[path];
  if (!cfg) {
    return null;
  }
  return {
    baseURL: config.bsa.baseURL,
    auth: {
      username: config.bsa.username,
      password: config.bsa.password,
    },
    url: cfg.path,
    headers: {
      'Content-Type': cfg.contentType ?? 'application/json',
      'Accept': cfg.accept ?? 'application/json',
    },
  };
};
