import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { NODE_INFO } from '../../node-info';
import { Type } from '../../node-io';

class ScheduleMergeNode extends FlexibleNode {
  constructor() {
    super();
    this.dynamicSettingsExist = false;
    this.dynamicInputsType = Type.STRING;
    this.title = NODE_INFO.schedule["schedule-merge"].title;
    this.description = NODE_INFO.schedule["schedule-merge"].description;

    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('error', Type.STRING);
  }

  onAdded() {
    this.onAfterSettingsChange();
  }

  onInputUpdated() {
    var weeklyOutput = {};
    var eventOutput = {};
    var inputString;
    var inputObject;
    for (var i = 0; i < this.getInputsCount(); i++) {
      inputString = this.getInputData(i);
      if (!inputString) continue;
      try {
        inputObject = JSON.parse(inputString);
        for (var weeklySched in inputObject.weekly) {
          weeklyOutput[weeklySched] = inputObject.weekly[weeklySched];
        }
        for (var eventSched in inputObject.events) {
          weeklyOutput[eventSched] = inputObject.events[eventSched];
        }
        this.setOutputData(0, { weekly: weeklyOutput, events: eventOutput });
      } catch (e) {
        this.setOutputData(1, e);
      }
    }
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}

Container.registerNodeType('schedule/schedule-merge', ScheduleMergeNode);
