import {
  ContainerRegistryMetadata,
  IContainer,
  isSerializedContainer,
  SerializedContainer,
} from '../nodes/node-interface';
import { NotFoundError } from '../utils/errors';
import { isNull } from '../utils/helper';

const Joi = require('@hapi/joi');

export class NodeValidator {
  static NodeArrayValidator() {
    return Joi.array().items(Joi.object().keys({ type: Joi.string().required() }));
  }
}

export class ContainerRegistryValidator {

  static check = (cId: number | string | SerializedContainer | IContainer,
                  registry: ContainerRegistryMetadata): IContainer => {
    const container = registry.get(isSerializedContainer(cId) ? (<SerializedContainer>cId).id : <number | string>cId);
    if (isNull(container)) {
      throw new NotFoundError(`Not found container id ${cId}`);
    }
    return container;
  };

  static mustExisted = (cId: number | string | SerializedContainer | IContainer,
                        registry: ContainerRegistryMetadata): Promise<IContainer> => {
    return Promise.resolve(() => ContainerRegistryValidator.check(cId, registry)).then(f => f());
  };
}

export class ListValidator {

  static notEmpty = <T>(items: T[], msg: string): Promise<T[]> => {
    if (items.length > 0) {
      return Promise.resolve(items);
    }
    return Promise.reject(msg);
  };

}
