import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { Type } from '../../node-io';

let moment = require('moment-timezone');

//https://github.com/travishorn/csval
//https://github.com/C2FO/fast-csv

class CSVMergeColumnsNode extends Node {
  obj = [];
  constructor() {
    super();
    this.title = NODE_INFO.csv["csv-merge-columns"].title;
    this.description = NODE_INFO.csv["csv-merge-columns"].description;
    this.addInput('original', Type.STRING);
    this.addInput('merge', Type.STRING);
    this.addOutput('output', Type.STRING);
  }

  onAdded() {
    this.setOutputData(0, '');
    this.onInputUpdated();
  }

  onInputUpdated() {
    if (this.side !== Side.server) return; // only execute on server
    if (!this.getInputData(0) || !this.getInputData(1)) return;

    let originalColumnsArray = this.getInputData(0).split('\n');
    let mergeColumnsArray = this.getInputData(1).split('\n');
    let outputCSV = '';
    const originalColumnsCount = originalColumnsArray[0].split(',').length;
    const mergeColumnsCount = mergeColumnsArray[0].split(',').length;

    var i = 0;
    for (i = 0; i < Math.min(mergeColumnsArray.length, originalColumnsArray.length) - 1; i++) {
      outputCSV += originalColumnsArray[i] + ', ' + mergeColumnsArray[i] + '\n';
    }
    //if(i < originalColumnsArray.length){
    var x = 0;
    while (i < originalColumnsArray.length - 1) {
      outputCSV += originalColumnsArray[i];
      x = originalColumnsCount;
      while (x < originalColumnsCount + mergeColumnsCount) {
        outputCSV += ', ';
        x++;
      }
      outputCSV += '\n';
      i++;
    }
    while (i < mergeColumnsArray.length - 1) {
      x = mergeColumnsCount;
      while (x < originalColumnsCount + mergeColumnsCount) {
        outputCSV += ', ';
        x++;
      }
      outputCSV += mergeColumnsArray[i];
      outputCSV += '\n';
      i++;
    }
    this.setOutputData(0, outputCSV);
  }
}
Container.registerNodeType('csv/csv-merge-columns', CSVMergeColumnsNode);
