import { Node } from '../../node';
import { Type } from '../../node-io';
import { Container } from '../../container';
import { NODE_INFO } from '../../node-info';

export class HexStringToBuffer extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.conversion["hex-string-to-buffer"].title;
    this.description = NODE_INFO.conversion["hex-string-to-buffer"].description;

    this.addInput('input', Type.STRING);
    this.addOutput('output', Type.ANY);
  }

  onInputUpdated() {
    let str = this.getInputData(0);

    if (!str) {
      return;
    }

    // console.log(str);
    let buffer = Buffer.from(str, 'hex');
    // console.log(buffer);
    this.setOutputData(0, buffer);
  }
}
Container.registerNodeType('conversion/hex-string-to-buffer', HexStringToBuffer);
