import { ErrorHandler } from '../utils/decorators';
import { isFunction } from '../utils/helper';
import { Node } from './node';

//TODO must remove by event lifecycle
export class NodeHookHelper {

  @ErrorHandler
  static execute(node: Node, funcName: string, ...args): Node {
    if (isFunction(node[funcName])) {
      node[funcName](args);
    }
    return node;
  }
}
