import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class FiltersOnlyFromRangeNode extends Node {
  constructor() {
    super();

    this.title = NODE_INFO.filter["only-between"].title;
    this.description = NODE_INFO.filter["only-between"].description;

    this.addInput('value', Type.NUMBER);
    this.addInputWithSettings('min', Type.NUMBER, 0, 'min', false);
    this.addInputWithSettings('max', Type.NUMBER, 10, 'max', false);
    this.addOutput('value', Type.NUMBER);
    this.properties['value'];
  }

  onAdded(){
    this.setOutputData(0, this.properties['value'] || null);
    this.onInputUpdated();
  }

  onAfterSettingsChange(){
    this.onInputUpdated();
  }

  onInputUpdated() {
    const val = this.getInputData(0);
    const min = this.getInputData(1);
    const max = this.getInputData(2);

    if (val == null || min == null || max == null) {
      this.setOutputData(0, null);
    } else if (val >= min && val <= max) {
      this.setOutputData(0, val);
      this.properties['value'] = val;
      this.persistProperties(false, true);
    }
  }
}
Container.registerNodeType('filter/only-between', FiltersOnlyFromRangeNode);
