import { Colors, create } from 'logplease';
import * as Bacnet from 'node-bacnet';
import { PromiseActor } from '../../../../promise-actor';
import {
  ADD_DEVICE,
  ADD_POINT,
  GET_BACNET_CLIENT,
  GET_NETWORK_NODE,
  GET_POINTS,
  GET_PRESENT_VALUE,
  REMOVE_DEVICE,
  REMOVE_POINT,
  SEND_PAYLOAD_TO_CHILD,
  SEND_POINT_VALUE,
  WRITE_PRESENT_VALUE,
} from './bacnet-constant';
import { DeviceObject } from './interface';

const logger = create('bacnet-utils', { color: Colors.Grey });

export const singleton = new PromiseActor();

export default class BacnetUtils {
  static getBacnetClient(networkNode): Bacnet {
    if (networkNode && networkNode['subscribe']) {
      return networkNode['subscribe'](this.createMessage(GET_BACNET_CLIENT));
    } else {
      logger.error('Network Node is not available on getBacnetClient');
      return null;
    }
  }

  static getPoints(deviceNode): Array<DeviceObject> {
    if (deviceNode && deviceNode['subscribe']) {
      return deviceNode['subscribe'](this.createMessage(GET_POINTS));
    } else {
      logger.error('Device Node is not available on getPoints');
      return [];
    }
  }

  static getPresentValue(deviceNode, payload): Promise<any> {
    if (deviceNode && deviceNode['subscribe']) {
      return deviceNode['subscribe'](this.createMessage(GET_PRESENT_VALUE, payload));
    } else {
      logger.error('Device Node is not available on getPresentValue');
      return null;
    }
  }

  static writePresentValue(deviceNode, payload) {
    if (deviceNode && deviceNode['subscribe']) {
      deviceNode['subscribe'](this.createMessage(WRITE_PRESENT_VALUE, payload));
    } else {
      logger.error('Device Node is not available on writePresentValue');
    }
  }

  static getNetworkNode(parentNode): any {
    if (parentNode && parentNode['subscribe']) {
      return parentNode['subscribe'](this.createMessage(GET_NETWORK_NODE));
    } else {
      logger.error('Parent Node is not available on getNetworkNode');
      return null;
    }
  }

  static sendPayloadToChild(childNode, payload) {
    if (childNode && childNode['subscribe']) {
      childNode['subscribe'](this.createMessage(SEND_PAYLOAD_TO_CHILD, payload));
    } else {
      logger.error('Child Node is not available on sendPayloadToChild');
    }
  }

  static sendPointValue(pointNode, payload) {
    if (pointNode && pointNode['subscribe']) {
      pointNode['subscribe'](this.createMessage(SEND_POINT_VALUE, payload));
    } else {
      logger.error('Point Node is not available on sendPointValue');
    }
  }

  static addDevice(networkNode, payload) {
    if (networkNode && networkNode['subscribe']) {
      networkNode['subscribe'](this.createMessage(ADD_DEVICE, payload));
    } else {
      logger.error('Network Node is not available on addDevice');
    }
  }

  static removeDevice(networkNode, payload) {
    if (networkNode && networkNode['subscribe']) {
      networkNode['subscribe'](this.createMessage(REMOVE_DEVICE, payload));
    } else {
      logger.error('Network Node is not available on removeDevice');
    }
  }

  static addPoint(deviceNode, payload) {
    if (deviceNode && deviceNode['subscribe']) {
      deviceNode['subscribe'](this.createMessage(ADD_POINT, payload));
    } else {
      logger.error('Device Node is not available on addPoint');
    }
  }

  static removePoint(deviceNode, payload) {
    if (deviceNode && deviceNode['subscribe']) {
      deviceNode['subscribe'](this.createMessage(REMOVE_POINT, payload));
    } else {
      logger.error('Device Node is not available on removePoint');
    }
  }

  private static createMessage(action, payload = null): any {
    return { action, payload };
  }
}
