//update wires by calling a bash script
router.get('/wires/update', async function (req, res) {
  const url: string = PLAT.wiresPlatUrlBuilder(PLAT.EPWiresUpdate);
  try {
    console.log(`WIRES-UPDATE: api call ${url}`)
    await PLAT.get(url).then(e => {
      res.status(200).send(`API CALL WAS SUCCESSFUL ${e.data}`);
    }).catch(err => {
      console.log(`WIRES-UPDATE-ERROR: ${err}`)
      res.status(500).send(err);
    })
  } catch (err) {
    console.log(`WIRES-UPDATE-ERROR: main try/catch error: ${err}`)
    res.status(500).send(err);
  }
});

//download nodes
router.get('/wires/download/nodes', async function (req, res) {
  try {
    const container = ContainerRegistry.instance().rootContainer();
    container.db.getNodes((err, docs) => {
      if (!err) {
        res.json(JSON.stringify(docs));
      } else {
        res.status(500);
      }
    });
  } catch (e) {
    res.status(500).send(e);
  }
});

