// update eth
router.put('/host/ip/eth', async function ({ body }, res) {
  try {
    await PlatformDataSource.updateEth(body);
    const interface1Eth = 'eth0'; //eth0 (ETH1 on the RC)
    const dhcp1Eth = parseBool(body.dhcpEth1)
    const eth1Ip = body.eth1Ip;
    const eth1Sub = body.eth1Sub;
    const eth1Gate = body.eth1Gate;
    const eth1NameServer = body.eth1NameServer;

    const interface2Eth = 'eth1'; //eth1 (ETH2 on the RC)
    const dhcp2Eth = parseBool(body.dhcpEth2)
    const eth2Ip = body.eth2Ip;
    const eth2Sub = body.eth2Sub;
    const eth2Gate = body.eth2Gate;
    const eth2NameServer = body.eth2NameServer;
    console.log(1111)
    console.log(body)
    console.log(1111)
    IpAddressUtils.updateIpRubixCompute(interface1Eth, dhcp1Eth, eth1Ip, eth1Sub, eth1Gate, eth1NameServer, interface2Eth, dhcp2Eth, eth2Ip, eth2Sub, eth2Gate, eth2NameServer).then(e => {
      console.log('UPDATE IP ETH')
      res.status(201).send('UPDATE IP ETH');
    }).catch(err => {
      console.log('UPDATE IP ETH ERR')
      res.status(500).send(err);
    })
  } catch (err) {
    res.status(500).send(err);
  }
});


