import { Colors, create } from 'logplease';
import { PointModel, PointOutput, PointValue, PointValueCreator } from '../../../../backend/models/point-model';
import { ErrorHandler } from '../../../../utils/decorators';
import { Constructor, isEmpty, isFunction, range } from '../../../../utils/helper';
import { IMessageSubscriber, Observer } from '../../../../utils/pattern';
import { Node, NodeSettingConfig } from '../../../node';
import { Side } from '../../../node-interface';
import { InputSetting, NodeInput, NodeSetting, SettingType, Type } from '../../../node-io';
import { NodeInputEvaluation } from '../../node-mixin';
import { InputMethod } from './point-node-utils';

const logger = create('observer', { color: Colors.Yellow });

/**
 * The logical handler manages data point state that can be changed by UI or external system.
 *
 * It is business logic that separated to UI component to easy extendable and testable
 */
export interface DataPointFlowHandler<T extends PointModel> extends IMessageSubscriber {

  /**
   * Init point data by setting object model
   * @param settings Node Settings
   * @return point data
   */
  initializePointBySettingObject(settings?: { [name: string]: NodeSetting }): T;

  /**
   * Init point data by each setting input
   * @param settings Node Settings
   * @return point data
   */
  initializePointBySettingInput(settings?: { [name: string]: NodeSetting }): T;

  /**
   * Compare previous point and current point then transfer COV to server/client
   * @param current current point data
   * @param prev previous point data
   * @return the updated point with COV
   */
  handleOnUpdate(current: T, prev?: T): T;

  /**
   * Listen a changes from external system
   * @param incomingMessage point data
   * @return {any}
   */
  onReceiveMessage(incomingMessage: any): void;

  /**
   * Notify COV to observers
   * @param point point data
   * @param observers list of observer that watch an output change
   */
  notifyOutput(point: T, observers: Observer<PointOutput<T>>[]): void;

}

/**
 * Defines list observers that watch an output change on point
 */
export interface HasPointOutputObservers<T extends PointModel> {

  pointObservers(): Observer<PointOutput<T>>[];

}

export class MockHasPointOutputObservers<T extends PointModel> implements HasPointOutputObservers<T> {
  private static readonly MOCK = new MockHasPointOutputObservers();

  static cast(object: any) {
    if (isEmpty(object)) {
      return MockHasPointOutputObservers.MOCK;
    }
    if (isFunction(object['pointObservers'])) {
      return object;
    }
    return MockHasPointOutputObservers.MOCK;
  }

  pointObservers(): Observer<PointOutput<T>>[] {
    return [];
  }

  private MockHasPointOutputObservers() {
  }
}

/**
 * Defines object must apply DataPointFlowHandler
 */
export interface HasDataPointFlowHandler<T extends PointModel> {

  flowHandler(): DataPointFlowHandler<T>;

}

/**
 * Defines self observer to update Node Output by point data
 */
export class PointOutputObserver<T extends PointModel> implements Observer<PointOutput<T>> {

  node: Node;
  outputSlots: { output: number, priority: number, priorityArray: number };

  constructor(node: Node, outputSlots: { output: number, priority: number, priorityArray: number }) {
    this.node = node;
    this.outputSlots = outputSlots;
  }

  update(data: PointOutput<T>): void {
    logger.debug(
      `Node ${this.node.getReadableId()} receives point: ${data.point.identifier()} - ${data.connStatus.status.label}`);
    this.node.setOutputData(this.outputSlots.output, data.point?.pointValue?.presentValue);
    this.node.setOutputData(this.outputSlots.priority, data.point?.pointValue?.priority);
    this.node.setOutputData(this.outputSlots.priorityArray, data.point?.pointValue?.priorityArray);
  }
}

export abstract class CentralizedPointOutputObserver<T extends PointModel> implements Observer<PointOutput<T>> {

  protected node: Node;
  protected outputSlot: number;

  constructor(node: Node, outputSlot: number) {
    this.node = node;
    this.outputSlot = outputSlot;
  }

  update(data: PointOutput<T>): void {
    logger.debug(
      `Node ${this.node.getReadableId()} receives point: ${data.point.identifier()} - ${data.connStatus.status.label}`);
    this.node.setOutputData(this.outputSlot,
                            { ...(this.node.outputs[this.outputSlot]?.data ?? {}), ...this.convert(data) });
  }

  protected abstract convert(data: PointOutput<T>): object;
}

/**
 * Define mixin to implement common point data workflow
 * @param {TBase} Base
 * @return {PointNodeMixinBase}
 * @constructor
 */
export function PointNodeMixin<TBase extends Constructor<Node>, T extends PointModel>(Base: TBase) {
  abstract class PointNodeMixinBase extends Base
    implements NodeInputEvaluation, HasPointOutputObservers<T>, HasDataPointFlowHandler<T> {
    private readonly _iPresentValue = 'present-value';
    private readonly _iPriority = 'point-priority';
    private readonly _iPriorityArrayLot = 'inputPriorityArray';
    private readonly _iPriorityArrayJson = 'priority-array';
    private readonly _sInputGroup = 'inputGroup';
    private readonly _sInputMethod = 'inputMethod';
    private readonly _sDecimal = 'decimals';
    private readonly _oOut = 'out';
    private readonly _oPriority = 'priority';
    private readonly _oPriorityArray = 'priority-array';

    abstract flowHandler(): DataPointFlowHandler<T>;

    onAdded(): void {
      if (this.side !== Side.server) {
        return;
      }
      let current = this.flowHandler().initializePointBySettingObject();
      this.flowHandler().notifyOutput(this.flowHandler().handleOnUpdate(current), this.pointObservers());
      this.updateTitle();
    }

    onAfterSettingsChange(oldSettings, oldName?): void {
      this.handleDynamicInput(oldSettings);
      if (this.side !== Side.server) {
        return;
      }
      let prev = this.flowHandler().initializePointBySettingObject(oldSettings);
      let current = this.flowHandler().initializePointBySettingInput();
      this.flowHandler().notifyOutput(this.flowHandler().handleOnUpdate(current, prev), this.pointObservers());
      this.updateTitle();
    }

    onInputUpdated(): void {
      if (this.side !== Side.server) {
        return;
      }
      let prev = this.flowHandler().initializePointBySettingObject();
      this.reEvaluateSettingByInput(this.inputs, this.settings);
      let current = this.flowHandler().initializePointBySettingInput();
      this.flowHandler().notifyOutput(this.flowHandler().handleOnUpdate(current, prev), this.pointObservers());
      this.updateTitle();
    }

    @ErrorHandler
    reEvaluateSettingByInput(inputs: { [p: number]: NodeInput }, settings: { [p: string]: NodeSetting }): void {
      if (isFunction(super['reEvaluateSettingByInput'])) {
        super['reEvaluateSettingByInput'](inputs, settings);
      }
      let inputMethod = InputMethod.enumValueOf(this.settings[this._sInputMethod].value);
      let pv: PointValue;
      if (inputMethod === InputMethod.VALUE_PRIORITY) {
        let presentValue = this.inputs[this.valueInputIdx()].updated
                           ? this.inputs[this.valueInputIdx()].data
                           : this.settings[this._iPresentValue].value;
        let priority = this.inputs[this.priorityInputIdx()].updated
                       ? this.inputs[this.priorityInputIdx()].data
                       : this.settings[this._iPriority].value;
        pv = PointValueCreator.init(presentValue, priority);
        this.settings[this._iPresentValue].value = pv.presentValue;
        this.settings[this._iPriority].value = pv.priority;
      }
      if (inputMethod === InputMethod.PRIORITY_ARRAY_LOT) {
        let startIdx = this.priorityArrayInputIdx();
        pv = PointValueCreator.by(Array.from(range(startIdx + 1, startIdx + 16)).map(i => this.inputs[i].data));
        this.settings[this._iPriorityArrayLot].value = pv.priorityArray;
      }
      if (inputMethod === InputMethod.PRIORITY_ARRAY_JSON) {
        pv = PointValueCreator.by(this.inputs[this.priorityArrayInputIdx()].data);
        this.settings[this._iPriorityArrayJson].value = pv.priorityArray;
      }
    }

    pointObservers(): Observer<PointOutput<T>>[] {
      return [new PointOutputObserver<T>(this, {
        output       : this.valueOutputIdx(),
        priority     : this.priorityOutputIdx(),
        priorityArray: this.priorityArrayOutputIdx(),
      })];
    }

    protected abstract modelSettingKey(): string;

    protected abstract valueInputIdx(): number;

    protected abstract valueOutputIdx(): number;

    protected abstract computeTitle(): string;

    protected priorityInputIdx(): number {
      return this.valueInputIdx() + 1;
    }

    protected priorityArrayInputIdx(): number {
      return this.priorityInputIdx() + 1;
    }

    protected priorityOutputIdx(): number {
      return this.valueOutputIdx() + 1;
    }

    protected priorityArrayOutputIdx(): number {
      return this.priorityOutputIdx() + 1;
    }

    protected presentValueType(): Type {
      return Type.NUMBER;
    }

    /**
     * Add required properties in {@link NodeSetting}, {@link NodeInput} and {@link NodeOutput}
     * @returns Setting configuration
     */
    protected mixinPointValueInputOutput(): NodeSettingConfig {
      this.addOutput(this._oOut, Type.NUMBER);
      this.addOutput(this._oPriority, Type.NUMBER);
      this.addOutput(this._oPriorityArray, Type.JSON);

      this.settings[this.modelSettingKey()] = NodeSetting.create({ description: '', value: null });
      this.settings[this._iPriorityArrayLot] = NodeSetting.create({ description: '', value: null });
      this.settings[this._sInputGroup] = NodeSetting.create({ description: 'Input Settings', value: '', type: SettingType.GROUP });
      this.settings[this._sDecimal] = NodeSetting.create({ description: 'Decimal Places (Limit 5)', value: 3, type: SettingType.NUMBER });
      this.settings[this._sInputMethod] = NodeSetting.create({
        description: 'Input Method',
        type       : SettingType.DROPDOWN,
        config     : { items: InputMethod.items() },
        value      : InputMethod.VALUE_PRIORITY.enumKey,
      });
      this.addInputWithSettings(this._iPresentValue, this.presentValueType(), null, 'Present Value');
      this.addInputWithSettings(this._iPriority, Type.NUMBER, 16, 'Point Priority');
      this.addInputWithSettings(this._iPriorityArrayJson, Type.JSON, null, 'Priority Array in JSON or Array');
      this.inputs[this.priorityArrayInputIdx()].setting.hidden = true;
      let cfg = {};
      for (let value of range(1, 16)) {
        this.settings[`in${value}`] = NodeSetting.create({ description: `in${value}`, type: SettingType.BOOLEAN, value: false });
        this.addInput(`[in${value}]`, Type.NUMBER, { exist: false, nullable: true, hidden: true } as InputSetting);
        cfg[`in${value}`] = () => InputMethod.PRIORITY_ARRAY_LOT.enumKey === this.settings[this._sInputMethod].value;
      }
      cfg[this.modelSettingKey()] = () => false;
      cfg[this._iPresentValue] = () => InputMethod.VALUE_PRIORITY.enumKey === this.settings[this._sInputMethod].value;
      cfg[this._iPriority] = () => InputMethod.VALUE_PRIORITY.enumKey === this.settings[this._sInputMethod].value;
      cfg[this._iPriorityArrayLot] = () => false;
      cfg[this._iPriorityArrayJson] = () => false;
      return { conditions: cfg };
    }

    protected updateTitle(): void {
      this.title = this.computeTitle();
      this.broadcastSettingsToClients();
    }

    protected createPointValue(st: { [name: string]: NodeSetting }): PointValue {
      let inputMethod = InputMethod.enumValueOf(st['inputMethod'].value);
      let pv: PointValue;
      let cur: PointValue = this.settings[this.modelSettingKey()]?.value?.pointValue;
      if (inputMethod === InputMethod.VALUE_PRIORITY) {
        pv = PointValueCreator.create(st[this._iPresentValue].value, st[this._iPriority].value, cur?.priorityArray);
      }
      if (inputMethod === InputMethod.PRIORITY_ARRAY_LOT) {
        pv = PointValueCreator.by(st[this._iPriorityArrayLot].value);
      }
      if (inputMethod === InputMethod.PRIORITY_ARRAY_JSON) {
        pv = PointValueCreator.by(st[this._iPriorityArrayJson].value);
      }
      return pv.merge(cur);
    }

    private handleDynamicInput(oldSettings: { [name: string]: NodeSetting }) {
      let oldInputMethod = InputMethod.enumValueOf(oldSettings ? oldSettings[this._sInputMethod]?.value : null);
      let newInputMethod = InputMethod.enumValueOf(this.settings[this._sInputMethod].value);
      if (oldInputMethod && oldInputMethod === newInputMethod && newInputMethod !== InputMethod.PRIORITY_ARRAY_LOT) {
        return;
      }
      if (newInputMethod === InputMethod.VALUE_PRIORITY) {
        this.hideInput(false, ...range(this.priorityArrayInputIdx(), this.priorityArrayInputIdx() + 16));
        this.showInput(false, this.valueInputIdx(), this.priorityInputIdx());
      }
      if (newInputMethod === InputMethod.PRIORITY_ARRAY_LOT) {
        let hidden = [this.valueInputIdx(), this.priorityInputIdx(), this.priorityArrayInputIdx()];
        let show = [];
        for (let idx of range(this.priorityArrayInputIdx() + 1, this.priorityArrayInputIdx() + 16)) {
          this.settings[`in${idx - this.priorityArrayInputIdx()}`].value ? show.push(idx) : hidden.push(idx);
        }
        this.hideInput(false, ...hidden);
        this.showInput(false, ...show);
      }
      if (newInputMethod === InputMethod.PRIORITY_ARRAY_JSON) {
        this.hideInput(
          false,
          this.valueInputIdx(),
          this.priorityInputIdx(),
          ...range(this.priorityArrayInputIdx() + 1, this.priorityArrayInputIdx() + 16),
        );
        this.showInput(false, this.priorityArrayInputIdx());
      }
      this.linkHandler.recomputeInputLinks();
    }
  }

  return PointNodeMixinBase;
}
