import { merge } from 'lodash';
import { Api } from '../../../backend/api';
import { Service } from '../../../backend/entry';
import { ModuleAPI, ServiceAPI } from '../../../backend/installer';
import { singleton as actor } from '../../../promise-actor';
import {
  MODULE_NODE_TYPE,
  MODULES_PARENT_CONTAINER_TYPE,
  SERVICE_NODE_TYPE,
  SERVICES_PARENT_CONTAINER_TYPE,
} from '../../constants';
import { Container } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { SettingType } from '../../node-io';
import ApiNode from './shared/api-node';
import Mixin from './shared/mixin';
import TopLevelApiContainerNode from './shared/top-level-api-container-node';

export class ModulesParentContainerNode extends TopLevelApiContainerNode<Service> {
  title: string = 'Modules';

  collectChildNodes(nodes: Node[]): ModuleNode[] {
    return <ModuleNode[]>nodes.filter(v => v instanceof ModuleNode);
  }

  protected initAPI(): Api<Service> {
    return new ModuleAPI();
  }

  protected fetchChildren() {
    return this.api.list();
  }

  protected childNodeType() {
    return [MODULE_NODE_TYPE];
  }
}

export class ServicesParentContainerNode extends TopLevelApiContainerNode<Service> {
  title: string = 'Services';

  collectChildNodes(nodes: Node[]): ServiceNode[] {
    return <ServiceNode[]>nodes.filter(v => v instanceof ServiceNode);
  }

  protected initAPI(): Api<Service> {
    return new ServiceAPI();
  }

  protected fetchChildren() {
    return this.api.list();
  }

  protected childNodeType() {
    return [SERVICE_NODE_TYPE];
  }
}

abstract class BaseServiceNode<A extends Api<Service>> extends ApiNode<Service, A> {
  properties: {
    entry: Service;
    is_protected: boolean;
  };
  required_settings = ['service_id', 'service_name', 'version'];
  settingFields = {
    service_id     : SettingType.STRING,
    service_name   : SettingType.STRING,
    version        : SettingType.STRING,
    service_type   : SettingType.STRING,
    published_by   : SettingType.STRING,
    state          : SettingType.STRING,
    deploy_id      : SettingType.STRING,
    deploy_location: SettingType.STRING,
    created_at     : SettingType.READONLY,
    modified_at    : SettingType.READONLY,
  };
  settingsHeaderComponent = {
    props   : ['self'],
    template:
      '<div>' +
      '  <v-alert v-if="self.properties.is_protected" type="warning">Waiting for required fields <br />' +
      `    <small>(${this.required_settings.join(', ')})</small>` +
      '  </v-alert>' +
      '</div>',
  };
  onAdded = Mixin.prototype.onAdded.bind(this);
  onRemoved = Mixin.prototype.onRemoved.bind(this);

  constructor() {
    super();
    this.title = this.namePrefix();
  }

  entryId = () => this.entry() && this.entry().service_id;

  syncWithBackend = () => {
  };

  onAfterSettingsChange() {
    if (this.side !== Side.server || !this.areSettingsValid()) return;
    if (!this.entry()) {
      actor.process(() =>
                      this.createEntry()
                          .then(entry => this.applyEntry(entry))
                          .then(() => this.displayMessage(`${this.namePrefix()} entry created.`))
                          .catch(e => this.displayError(e, 'Entry not created.')),
      );
    } else {
      // update entry
      Mixin.prototype.onAfterSettingsChange.call(this);
    }
  }

  applyEntry(entry: Service) {
    this.settingFields['service_id'] = SettingType.READONLY;
    Mixin.prototype.applyEntry.call(this, entry);
    this.properties.is_protected = !entry;
    this.broadcastPropertiesToClients();
  }

  onCreated() {
    if (this.entry()) {
      this.applyEntry(this.entry());
    } else {
      this.properties.is_protected = true; // await manual configuration
      merge(this.settings, this.createSettingForm(null));
    }
  }

  createEntry() {
    const entry = Object.keys(this.settingFields)
                        .map(key => ({ [key]: this.settings[key].value }))
                        .reduce(merge);
    return this.backend.create(this.settings['service_id'].value, <any>entry);
  }

  private areSettingsValid(): boolean {
    return this.required_settings.map(key => this.settings[key].value).reduce((a, b) => a && b);
  }
}

class ModuleNode extends BaseServiceNode<ModuleAPI> {
  namePrefix() {
    return 'Module';
  }

  protected initAPI(): ModuleAPI {
    return new ModuleAPI();
  }
}

class ServiceNode extends BaseServiceNode<ServiceAPI> {
  namePrefix() {
    return 'Service';
  }

  initAPI(): ServiceAPI {
    return new ServiceAPI();
  }
}

Container.registerNodeType(MODULES_PARENT_CONTAINER_TYPE, ModulesParentContainerNode, null, false);
Container.registerNodeType(SERVICES_PARENT_CONTAINER_TYPE, ServicesParentContainerNode, null, false);
Container.registerNodeType(MODULE_NODE_TYPE, ModuleNode, null, false);
Container.registerNodeType(SERVICE_NODE_TYPE, ServiceNode, null, false);
