import { PointModel, PointValue, PointValueCreator } from '../../../../../backend/models/point-model';
import { isJSON, isNull } from '../../../../../utils/helper';

export interface MqttPoint extends PointModel {
  mqttTopic: string;
}

export class MqttPointCreator {
  private constructor() {
  }

  static from = (json: object): MqttPoint => {
    if (isNull(json)) {
      return null;
    }
    // @ts-ignore
    let pv = json?.pointValue;
    // @ts-ignore
    return MqttPointCreator.by(json?.enabled, json?.mqttTopic,
                               PointValueCreator.create(pv?.presentValue, pv?.priority, pv?.priorityArray));
  };

  static by = (enabled: boolean, mqttTopic: string, pointValue: PointValue): MqttPoint => {
    return new DefaultMqttPoint(enabled, mqttTopic, pointValue);
  };
}

class DefaultMqttPoint implements MqttPoint {
  enabled: boolean;
  mqttTopic: string;
  pointValue: PointValue;

  constructor(enabled: boolean, mqttTopic: string, pointValue: PointValue) {
    this.enabled = enabled || false;
    this.mqttTopic = mqttTopic;
    this.pointValue = pointValue;
  }

  identifier(): string {
    return this.mqttTopic;
  }

  mightOnlyValueChanged(point: MqttPoint): boolean {
    return this.enabled === point?.enabled && this.mqttTopic === point?.mqttTopic;
  }

  equals(point: MqttPoint): boolean {
    return this.identifier() === point?.identifier();
  }
}

export interface MQTTMessage {
  topic: string;
  message: any;
}

export class MqttPointValue {
  value: number;
  priority: number;

  constructor(value: number, priority: number) {
    this.value = value;
    this.priority = priority;
  }

  static by(pv: PointValue): MqttPointValue {
    return new MqttPointValue(pv?.presentValue, pv?.priority ?? 16);
  }

  static parse(message: any): MqttPointValue {
    try {
      let payload = isJSON(message) ? message : JSON.parse(message);
      return new MqttPointValue(payload?.value, payload?.priority);
    } catch (e) {
      throw new Error('Invalid JSON when parsing MQTTMessage');
    }
  }

  to(): PointValue {
    return PointValueCreator.init(this.value, this.priority);
  }

  toString() {
    return JSON.stringify(this);
  }
}

//To support NubeIO request/response pattern. It should be removed in future
export class SillyMqttPoint extends DefaultMqttPoint implements MqttPoint {
  readonly isReqRes: boolean = false;

  private constructor(mqttPoint: MqttPoint, isReqRes: boolean) {
    super(mqttPoint.enabled, mqttPoint.mqttTopic, mqttPoint.pointValue);
    this.isReqRes = isReqRes;
  }

  static by = (mqttPoint: MqttPoint, isReqRes: boolean): SillyMqttPoint => {
    return isNull(mqttPoint) ? null : new SillyMqttPoint(mqttPoint, isReqRes);
  };

  mightOnlyValueChanged(point: MqttPoint): boolean {
    return super.mightOnlyValueChanged(point) && this.isReqRes === (<SillyMqttPoint>point)?.isReqRes;
  }

  subscribedTopic(): string {
    return this.isReqRes ? `${this.mqttTopic}/req` : this.mqttTopic;
  }

  publishedTopic(): string {
    return this.isReqRes ? `${this.mqttTopic}/res` : this.mqttTopic;
  }
}
