import { Container } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import HistoryConfig from '../../utils/points/history-config';
import PriorityConfig from '../../utils/points/priority-config';
import { NODE_INFO } from '../../node-info';

// TODO change history config to HistoryBase
class GenericNode extends Node {

  constructor() {
    super();
    this.title = NODE_INFO.point["generic-point"].title;
    this.description = NODE_INFO.point["generic-point"].description; 

    PriorityConfig.addPrioritySettings(this);
    HistoryConfig.addHistorySettings(this);
    this.setSettingsConfig({ groups: [], conditions: {} });
    PriorityConfig.addPrioritySettingsConfig(this);
    HistoryConfig.addHistorySettingsConfig(this, 0, false);
  }

  onCreated() {
    PriorityConfig.priorityOnCreated(this);
    HistoryConfig.historyOnCreated(this);
  }

  onAdded() {
    //this.size = this.computeSize();
    this.onAfterSettingsChange();
  }

  onInputUpdated() {
    PriorityConfig.doPriorityFunctions(this);
    if (this.side !== Side.server) return; // only execute on server
    HistoryConfig.doHistoryFunctions(this);
  }

  onAfterSettingsChange() {
    PriorityConfig.priorityFunctionsForAfterSettingsChange(this);
    HistoryConfig.historyFunctionsForAfterSettingsChange(this, this.settings['pointName'].value);
    PriorityConfig.doPriorityFunctions(this);
    if (this.side !== Side.server) return; // only execute on server
    HistoryConfig.doHistoryFunctions(this);
    /*
    super.onAfterSettingsChange();
    this.onInputUpdated();
    */
  }
}

Container.registerNodeType('point/generic-point', GenericNode);
