import axios from 'axios';
import * as _ from 'lodash';
import { Container } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import { bsaClientConfig } from './bsa-client-config';
import { NODE_INFO } from '../../node-info';

let moment = require('moment-timezone');

enum ALARM_TYPE {
  WARNING = 'WARNING',
  MINOR = 'MINOR',
  MAJOR = 'MAJOR',
  CRITICAL = 'CRITICAL',
}

const alarmConfig = {
  items: [
    { value: ALARM_TYPE.WARNING, text: ALARM_TYPE.WARNING },
    { value: ALARM_TYPE.MINOR, text: ALARM_TYPE.MINOR },
    { value: ALARM_TYPE.MAJOR, text: ALARM_TYPE.MAJOR },
    { value: ALARM_TYPE.CRITICAL, text: ALARM_TYPE.CRITICAL },
  ],
};

class BSACumulocityAlarmNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.cumulocity["cumulocity-alarm"].title;
    this.description =  NODE_INFO.cumulocity["cumulocity-alarm"].description;
    this.addInput('alarmInput', Type.BOOLEAN);
    this.addInputWithSettings('alarmText', Type.STRING, 'No Alarm Message Available', 'Enter Alarm Message');
    this.addInputWithSettings('alarmClass', Type.DROPDOWN, ALARM_TYPE.WARNING, 'Alarm Class', false, alarmConfig);

    this.addOutput('status', Type.BOOLEAN);
    this.addOutput('error', Type.STRING);

    this.settings['enable'] = NodeSetting.create({
      description: 'Alarm Enable',
      value: true,
      type: SettingType.BOOLEAN,
    });

    this.settings['CumulocityDeviceID'] = NodeSetting.create({
      description: 'Cumulocity Device ID (Number)',
      value: '',
      type: SettingType.STRING,
    });

    this.settings['CumulocityAlarmName'] = NodeSetting.create({
      description: 'Alarm Name',
      value: '',
      type: SettingType.STRING,
    });
  }

  onCreated() {
    if (_.isEmpty(this.properties['alarmRegistry'])) this.properties['alarmRegistry'] = {};
  }

  async onInputUpdated() {
    if (this.side !== Side.server) return;
    if (!this.settings['enable'].value) return;
    if (this.inputs[0].updated) await this.postAlarm(this.getInputData(0));
  }

  async onAfterSettingsChange() {
    await this.onInputUpdated();
  }

  async postAlarm(alarmInput) {
    let alarmName = this.settings['CumulocityAlarmName'].value || 'c8y_unknownAlarm';
    if (!alarmName.startsWith('c8y_')) alarmName = 'c8y_' + alarmName;

    let cfg: any = bsaClientConfig('alarm');
    if (alarmInput) {
      cfg = {
        ...cfg,
        method: 'POST',
        data: {
          source: {
            id: this.settings['CumulocityDeviceID'].value,
          },
          text: this.getInputData(1),
          time: moment().toISOString(),
          type: alarmName,
          status: 'ACTIVE',
          severity: this.getInputData(2),
        },
      };
      try {
        const response = await axios(cfg);
        this.setOutputData(0, true);
        this.setOutputData(1, false);
        this.properties['alarmRegistry'][alarmName] = response.data.id;
        this.debugInfo(`ALARM_REGISTRY ${JSON.stringify(this.properties['alarmRegistry'])}`);
      } catch (error) {
        this.debugErr(error);
        this.setOutputData(0, false);
        this.setOutputData(1, error);
      }
    } else if (!alarmInput) {
      const alarmID = this.properties['alarmRegistry'][alarmName];
      if (!alarmID) return;
      cfg = {
        ...cfg,
        method: 'PUT',
        url: `alarm/alarms/${alarmID}`,
        data: { status: 'CLEARED' },
      };
      try {
        await axios(cfg);
        this.setOutputData(0, false);
        this.setOutputData(1, false);
        delete this.properties['alarmRegistry'][alarmName];
        this.debugInfo(`Cleared alarm ${alarmName}`);
        this.debugInfo(`ALARM_REGISTRY ${JSON.stringify(this.properties['alarmRegistry'])}`);
      } catch (error) {
        this.debugErr(`ERROR ${error}`);
        this.setOutputData(0, false);
        this.setOutputData(1, error);
      }
    }
    this.persistProperties(false, true);
  }
}

Container.registerNodeType('cumulocity/cumulocity-alarm', BSACumulocityAlarmNode);
