import { NodeVM } from 'vm2';
import { isEmpty } from '../../../utils/helper';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType } from '../../node-io';

export abstract class BaseFunctionNode extends Node {

  protected constructor(title: string, description: string) {
    super();
    this.title = title;
    this.description = description;

    this.addInput('message');
    this.addOutput('output');
    this.addOutput('error');
  }

  onCreated() {
  }

  onAdded() {
  }

  onInputUpdated() {
    try {
      this.executeConfiguredScript(this.getScriptInput())
          .then(result => this.emitResult(result))
          .catch(e => this.emitError(e));
    } catch (e) {
      this.emitError(e);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }

  protected abstract getScriptInput();

  protected executeConfiguredScript(...variables): Promise<any> {
    const code = this.settings['script'].value;
    if (this.side !== Side.server || isEmpty(code)) return;
    const vm = new NodeVM({ timeout: 1000, sandbox: {} });
    let func = vm.run(`module.exports = function(message, ...args) {\n${code}\n};`);
    return Promise.resolve(func(...variables));
  }

  protected emitResult(result: any) {
    this.setOutputData(0, result);
    this.setOutputData(1, null); // reset error
  }

  protected emitError(error: Error) {
    this.setOutputData(0, null); // reset result
    this.debugErr(`Error when executing function node ${error.stack}`);
    this.setOutputData(1, error);
  }

  protected mixinScriptSetting(defaultScript: string) {
    this.settings['script'] = NodeSetting.create({ description: 'Function Code', value: defaultScript, type: SettingType.CODE_AREA });
  }
}
