import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathTanNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO["math-advanced"].tan.title;
    this.description = NODE_INFO["math-advanced"].tan.description;
    this.addInput('x', Type.NUMBER);
    this.addOutput('tan(x)', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    if (val != null) this.setOutputData(0, Math.tan(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math-advanced/tan', MathTanNode);
