import PlatformDataSource from '../../../database/datasource/PlatformDataSource';
export default class SystemUtils {
  static getDeviceID(self): Promise<string> {
    return new Promise((resolve, reject) => {
      if (self?.container?.db) {
        self.container.db.getNodeType('system/platform', (err, docs) => {
          if (!err) {
            if (docs?.length && docs[0]?.properties?.deviceID) {
              resolve(docs[0].properties.deviceID.trim());
            } else {
              reject('No deviceID');
            }
          } else {
            console.log(err);
            reject(err);
          }
        });
      } else {
        reject('No DB');
      }
    });
  }

  static getClientID(self): Promise<string> {
    return new Promise((resolve, reject) => {
      if (self?.container?.db) {
        self.container.db.getNodeType('system/platform', (err, docs) => {
          if (!err) {
            if (docs?.length && docs[0]?.settings?.['client-id']?.value) {
              resolve(docs[0].settings['client-id'].value.trim());
            } else {
              reject('No clientId');
            }
          } else {
            console.log(err);
            reject(err);
          }
        });
      } else {
        reject('No DB');
      }
    });
  }

  static getDeviceIdDb(): Promise<string> {
    return new Promise((resolve, reject) => {
      PlatformDataSource.getPlatform().then(e => {
        if(e){
          resolve(e.deviceID); //deviceID
        }
      }).catch(err => {
        reject('No deviceID');
      })
    });
  }

  static getClientIdDb(): Promise<string> {
    return new Promise((resolve, reject) => {
      PlatformDataSource.getPlatform().then(e => {
        if(e){
          resolve(e.clientId); //clientId
        }
      }).catch(err => {
        reject('No clientId');
      })
    });
  }

}
