import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';

class IntervalRampNode extends Node {
  countUp: boolean;
  lastInterval: number;
  lastReset: boolean;
  lastEnable: boolean;
  currentVal: number;
  timeoutFunc;

  constructor() {
    super();
    this.title = NODE_INFO.count["interval-ramp"].title;
    this.description = NODE_INFO.count["interval-ramp"].description;
    this.addInputWithSettings('enable', Type.BOOLEAN, 0, 'Enable', false);
    this.addInputWithSettings('min', Type.NUMBER, 0, 'min', false);
    this.addInputWithSettings('max', Type.NUMBER, 10, 'max', false);
    this.addInputWithSettings('step', Type.NUMBER, 1, 'Step Size', false);
    this.addInputWithSettings('interval', Type.NUMBER, 1, 'Count Interval', false);
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('output', Type.NUMBER);
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });

    this.settings['rampUpOnly'] = NodeSetting.create({
      description: 'Ramp Up Only',
      value: false,
      type: SettingType.BOOLEAN,
    });

  }

  onAdded() {
    this.countUp = true;
    this.lastInterval = 1000;
    this.lastReset = false;
    this.lastEnable = true;
    this.currentVal = 0;
    this.timeoutFunc = setInterval(() => {
      const min = this.getInputData(1);
      const max = this.getInputData(2);
      this.count(min, max);
      this.setOutputData(0, this.currentVal, true);
    }, this.lastInterval);
    this.inputs[4]['name'] = `[interval] (${this.settings['time'].value})`;
    this.onInputUpdated();
  }

  onInputUpdated() {
    const min = this.getInputData(1);
    const max = this.getInputData(2);

    this.currentVal = Number(this.currentVal);
    if (this.currentVal < min) this.currentVal = min;
    else if (this.currentVal > max) this.currentVal = max;

    const reset = Boolean(this.getInputData(5));
    if (reset && !this.lastReset) this.currentVal = min;
    this.countUp = true;
    this.lastReset = reset;

    let interval = this.getInputData(4);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    const enable = this.getInputData(0);
    if (enable && !this.lastEnable) {
      this.timeoutFunc = setInterval(() => {
        this.count(min, max);
        this.setOutputData(0, this.currentVal, true);
      }, interval);
    } else if (!enable && this.lastEnable) {
      clearInterval(this.timeoutFunc);
      this.setOutputData(0, null);
    } else if (enable && this.lastEnable && interval !== this.lastInterval) {
      clearInterval(this.timeoutFunc);
      this.timeoutFunc = setInterval(() => {
        this.count(min, max);
        this.setOutputData(0, this.currentVal, true);
      }, interval);
    }
    this.lastInterval = interval;
    this.lastEnable = enable;
  }

  count(min, max) {
    const step = this.getInputData(3);
    if (this.countUp && this.currentVal + step > max) {
      if (this.settings['rampUpOnly'].value) {
        this.countUp = true;
        this.currentVal = min - step;
      } else this.countUp = false;
    } else if (!this.countUp && this.currentVal - step < min) this.countUp = true;
    if (this.countUp) this.currentVal += step;
    else this.currentVal -= step;
  }

  onAfterSettingsChange() {
    this.inputs[4]['name'] = `[interval] (${this.settings['time'].value})`;
    this.onInputUpdated();
  }
}
Container.registerNodeType('count/interval-ramp', IntervalRampNode);
