import { PointOutput, PointValueCreator } from '../../../../backend/models/point-model';
import { isEmpty, isNull } from '../../../../utils/helper';
import { IdGenerator } from '../../../../utils/id-generator';
import {
  DefaultObservable,
  HasCentralizedListener,
  ICentralizedListener,
  IMessageConverter,
  MockCentralizedListener,
  Observer,
} from '../../../../utils/pattern';
import { Container } from '../../../container';
import { Node } from '../../../node';
import { NodeInput, NodeSetting, SettingType, Type } from '../../../node-io';
import { DependantConnectionNode, DependantConnectionNodeMixin } from '../connection-node-mixin';
import {
  DataPointFlowHandler,
  HasDataPointFlowHandler,
  HasPointOutputObservers,
  MockHasPointOutputObservers,
  PointNodeMixin,
} from '../model/point-node';
import { MqttPointCreator, MqttPointValue, SillyMqttPoint } from './core/mqtt-model';
import {
  MQTT_CLIENT_NODE,
  PUBLISH_MQTT_DATA,
  REGISTER_MQTT_POINT,
  UNREGISTER_MQTT_POINT,
  UPDATE_MQTT_POINT,
} from './mqtt-client-node';
import { MqttPointStoreItem } from './mqtt-client-node-store';
import { NODE_INFO } from '../../../node-info';

class MqttPointNode extends DependantConnectionNodeMixin(PointNodeMixin(Node))
  implements HasPointOutputObservers<SillyMqttPoint>, HasDataPointFlowHandler<SillyMqttPoint>,
    DependantConnectionNode, DataPointFlowHandler<SillyMqttPoint>, IMessageConverter, HasCentralizedListener {

  private readonly _iTopic = 'topic';
  private readonly _sReqRes = 'enabledReqRes';

  constructor() {
    super();
    this.title = NODE_INFO["protocols/mqtt"]["mqtt-point"].title;
    //TODO Should re-update
    this.description = NODE_INFO["protocols/mqtt"]["mqtt-point"].description;
    this.mixinEnableInputSetting();
    this.mixinConnectionStatusOutput();
    this.addInputWithSettings(this._iTopic, Type.STRING, '', 'MQTT Topic');
    this.settings[this._sReqRes] = NodeSetting.create({
      description: 'Request Response mode',
      type       : SettingType.BOOLEAN,
      value      : false,
    });
    this.setSettingsConfig(this.mixinPointValueInputOutput());
  }

  flowHandler(): DataPointFlowHandler<SillyMqttPoint> {
    return this;
  }

  listener(): ICentralizedListener {
    return MockCentralizedListener.cast(this.getParentNode());
  }

  pointObservers(): Observer<PointOutput<SillyMqttPoint>>[] {
    return [...super.pointObservers(),
            ...MockHasPointOutputObservers.cast(this.getParentNode()).pointObservers()];
  }

  notifyOutput(point: SillyMqttPoint, observers: Observer<PointOutput<SillyMqttPoint>>[]): void {
    if (isNull(point)) {
      return;
    }
    let cp = this.initializePointBySettingObject();
    point.pointValue = isNull(point?.pointValue) ? PointValueCreator.by() : point.pointValue.merge(cp?.pointValue);
    this.settings[this.modelSettingKey()].value = point;
    new DefaultObservable(observers).notify({ point: point, connStatus: { status: this.getConnectionStatus() } });
  }

  onRemoved() {
    if (this.listener() === MockCentralizedListener.LISTENER) {
      return;
    }
    let mqttPoint = this.initializePointBySettingObject();
    if (mqttPoint) {
      this.listener().listen({ action: UNREGISTER_MQTT_POINT, payload: this.createMqttPointStoreItem(mqttPoint) });
    }
  }

  reEvaluateSettingByInput(inputs: { [p: number]: NodeInput }, settings: { [p: string]: NodeSetting }) {
    settings[this._iTopic].value = inputs[this.topicInputIdx()].updated ? inputs[this.topicInputIdx()].data
                                                                        : settings[this._iTopic].value;
    super.reEvaluateSettingByInput(inputs, settings);
  }

  handleOnUpdate(current: SillyMqttPoint, prev?: SillyMqttPoint): SillyMqttPoint {
    if (!prev?.mqttTopic && !current?.mqttTopic) {
      return null;
    }
    if (isEmpty(current.mqttTopic)) {
      throw new Error('Must define MQTT topic');
    }
    let payload = this.createMqttPointStoreItem(current);
    if (isNull(prev) || this.getConnectionStatus().isError()) {
      this.listener().listen({ action: REGISTER_MQTT_POINT, payload });
    } else if (!current.mightOnlyValueChanged(prev)) {
      if (!current.equals(prev)) {
        current.pointValue = PointValueCreator.by();
      }
      payload.publisher.prev = this.createMqttPointStoreItem(prev);
      payload.subscriber.prev = this.createMqttPointStoreItem(prev);
      this.listener().listen({ action: UPDATE_MQTT_POINT, payload });
    }
    if (!current.enabled || (isNull(current?.pointValue?.presentValue) &&
                             (prev && !current.equals(prev) || isNull(prev?.pointValue?.presentValue)))) {
      return current;
    }
    if (current.isReqRes) {
      // Always publish highest data to mqtt broker
      return this.listener().listen({ action: PUBLISH_MQTT_DATA, payload: payload.toPublisher() });
    }
    let cov = current.pointValue.changedOfValue(prev?.pointValue);
    if (cov) {
      payload.publisher.data.pointValue = cov;
    }
    return this.listener().listen({ action: PUBLISH_MQTT_DATA, payload: payload.toPublisher() });
  }

  initializePointBySettingInput(settings?: { [name: string]: NodeSetting }): SillyMqttPoint {
    let st = (settings ?? this.settings) as { [name: string]: NodeSetting };
    return SillyMqttPoint.by(MqttPointCreator.by(this.isEnabled(), st[this._iTopic].value, this.createPointValue(st)),
                             this.settings[this._sReqRes].value);
  }

  initializePointBySettingObject(settings?: { [name: string]: NodeSetting }): SillyMqttPoint {
    let st = (settings ?? this.settings) as { [name: string]: NodeSetting };
    return SillyMqttPoint.by(MqttPointCreator.from(st[this.modelSettingKey()].value),
                             this.settings[this._sReqRes].value);
  }

  onConvertMessage(data: any): string {
    return MqttPointValue.by(data?.pointValue).toString();
  }

  onReceiveMessage(incomingMessage: any): void {
    let pointValue = MqttPointValue.parse(incomingMessage).to();
    let cp = this.initializePointBySettingObject();
    cp.pointValue = pointValue.merge(cp.pointValue);
    if (this.settings[this._sReqRes].value) {
      this.listener().listen({ action: PUBLISH_MQTT_DATA, payload: this.createMqttPointStoreItem(cp).toPublisher() });
    }
    this.notifyOutput((this.settings[this.modelSettingKey()].value = cp), this.pointObservers());
  }

  protected statusOutputIdx(): number {
    return 0;
  }

  protected modelSettingKey(): string {
    return `mqttp`;
  }

  protected valueInputIdx(): number {
    return 2;
  }

  protected valueOutputIdx(): number {
    return 2;
  }

  protected presentValueType(): Type {
    return Type.ANY;
  }

  protected computeTitle(): string {
    return `MQTT Point (Topic: ${this.settings[this._iTopic].value})`;
  }

  protected enableDescription(): string {
    return 'Enable MQTT Point';
  }

  protected topicInputIdx(): number {
    return 1;
  }

  private createMqttPointStoreItem(mqttPoint: SillyMqttPoint): MqttPointStoreItem {
    return new MqttPointStoreItem(mqttPoint.identifier(), IdGenerator.createNodeId(this.cid, this.id), mqttPoint?.enabled,
                                  {
                                    identifier: mqttPoint.publishedTopic(),
                                    data      : mqttPoint,
                                    callback  : (data: any) => this.onConvertMessage(data),
                                  }, {
                                    identifier: mqttPoint.subscribedTopic(),
                                    callback  : (msg: any) => this.onReceiveMessage(msg),
                                  });
  }
}

Container.registerNodeType('protocols/mqtt/mqtt-point', MqttPointNode, MQTT_CLIENT_NODE);
