import { Container } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

const moment = require('moment-timezone');

class AnyDataStoreNode extends Node {
  constructor() {
    super();
 
    this.title = NODE_INFO.history["data-store"].title;
    this.description = NODE_INFO.history["data-store"].description;
   
    this.addInput('input', Type.ANY);
    this.addInput('clear', Type.BOOLEAN);
    this.addOutput('output', Type.ANY);
    this.addOutput('storedCount', Type.NUMBER);

    this.settings['time-conversation-enable'] = NodeSetting.create({
      description: 'Timestamp in system timezone?',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['storage-limit'] = NodeSetting.create({
      description: 'Storage Limit (Max 50)',
      value: 25,
      type: SettingType.NUMBER,
    });
    this.settings['date-iso'] = NodeSetting.create({
      description: 'Typestamp Format',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: 'YYYY-MM-DD HH:mm:ss TZ (ISO8601)' },
          { value: 1, text: 'YYYY-MM-DD HH:mm:ss' },
          { value: 2, text: 'MM-DD-YYYY HH:mm:ss' },
          { value: 3, text: 'DD-MM-YYYY HH:mm:ss' },
          { value: 4, text: 'DAY, Month DD, YYYY HH:MM AM/PM' },
          { value: 5, text: 'Month DD, YYYY HH:MM AM/PM' },
          { value: 6, text: 'Unix timestamp in Milliseconds' },
        ],
      },
      value: 0,
    });
    this.settings['outputFormat'] = NodeSetting.create({
      description: 'Output Format',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'Array', text: 'Array' },
          { value: 'CSV', text: 'CSV' },
          { value: 'JSON', text: 'JSON' },
        ],
      },
      value: 'Array',
    });
    this.settings['pointName'] = NodeSetting.create({
      description: 'Point Name',
      value: '',
      type: SettingType.STRING,
    });
    this.settings['minuteRoundValue'] = NodeSetting.create({
      description: 'Round minutes up in increments of',
      value: 0,
      type: SettingType.NUMBER,
    });
    //this.settings['inputs'] = NodeSetting.create({ description: 'Tag Count', value: 0, type: SettingType.NUMBER });
    this.setSettingsConfig({
      conditions: {
        pointName: setting => {
          return setting['outputFormat'].value != 'Array';
        },
      },
    });
  }

  onCreated() {
    this.properties['historyLog'] = [];
  }

  onAdded() {
    this.setOutputData(0, this.properties['historyLog']);
    this.setOutputData(1, this.properties['historyLog'] ? this.properties['historyLog'].length : 0);
  }

  nearestFutureMinutes(interval, someMoment) {
    const roundedMinutes = Math.ceil(someMoment.minute() / interval) * interval;
    return someMoment
      .clone()
      .minute(roundedMinutes)
      .second(0);
  }

  formatOutputs() {
    // Format Output based on settings
    // System Timezone or UTC based on settings
    const timezone = this.settings['time-conversation-enable'].value
      ? Intl.DateTimeFormat().resolvedOptions().timeZone
      : 'Etc/UTC';
    let dateTz = null;
    const pointName = this.settings['pointName'].value;
    let timeFormatedArray = this.properties['historyLog'].map(entry => {
      return entry;
    });
    timeFormatedArray = timeFormatedArray.reverse().map(entry => {
      switch (this.settings['date-iso'].value) {
        case 0:
          dateTz = moment(entry.timestamp)
            .tz(timezone)
            .format();
          break;
        case 1:
          dateTz = moment(entry.timestamp)
            .tz(timezone)
            .format('YYYY-MM-DD HH:mm:ss');
          break;
        case 2:
          dateTz = moment(entry.timestamp)
            .tz(timezone)
            .format('MM-DD-YYYY HH:mm:ss');
          break;
        case 3:
          dateTz = moment(entry.timestamp)
            .tz(timezone)
            .format('DD-MM-YYYY HH:mm:ss');
          break;
        case 4:
          dateTz = moment(entry.timestamp)
            .tz(timezone)
            .format('LLLL');
          break;
        case 5:
          dateTz = moment(entry.timestamp)
            .tz(timezone)
            .format('LLL');
          break;
        case 6:
          dateTz = moment(entry.timestamp)
            .tz(timezone)
            .valueOf();
          break;
        default:
          dateTz = moment(entry.timestamp)
            .tz(timezone)
            .format();
      }
      return {
        payload: entry.payload,
        timestamp: dateTz,
      };
    });
    switch (this.settings['outputFormat'].value) {
      case 'Array':
        return timeFormatedArray;
      case 'CSV':
        let csvString = `name, timestamp, payload\n`;
        timeFormatedArray.forEach(log => {
          csvString += `${pointName}, ${log.timestamp}, ${log.payload} \n`;
        });
        return csvString;
      case 'JSON':
        return JSON.stringify({ [pointName]: timeFormatedArray });
    }
  }

  onInputUpdated() {
    if (this.side !== Side.server) return; // only execute on server
    if (this.getInputData(1) && this.inputs[1].updated) {
      this.properties['historyLog'] = []; // clear data if required
      this.setOutputData(0, this.properties['historyLog'].reverse());
      this.setOutputData(1, this.properties['historyLog'].length);
    }
    if (this.getInputData(0) == null || !this.inputs[0].updated) return;
    // cap the amount of data that can be stored
    const storageLimit = Utils.clamp(this.settings['storage-limit'].value, 0, 50);
    // round the time to the nearest min. For example 15min. 1:00, 1:15
    const minuteRound = Utils.clamp(this.settings['minuteRoundValue'].value, 0, 60);
    const now = minuteRound ? TimeUtils.nearestFutureMinutes(minuteRound, moment()) : moment();

    // object data
    let msg = {
      payload: this.getInputData(0),
      timestamp: now._d,
    };
    // build an object
    this.properties['historyLog'].push(msg);
    while (this.properties['historyLog'].length > storageLimit) {
      this.properties['historyLog'].shift();
    }

    this.persistProperties(true, true);

    // Format Output based on settings
    const newObj = this.formatOutputs() || [];
    this.setOutputData(0, newObj);
    this.setOutputData(1, this.properties['historyLog'].length);
  }

  onAfterSettingsChange() {
    this.outputs[0].name = `output (${this.settings['outputFormat'].value})`;
    this.broadcastOutputsToClients();
    const newObj = this.formatOutputs() || [];
    this.setOutputData(0, newObj);
    this.setOutputData(1, this.properties['historyLog'].length);
  }
}
Container.registerNodeType('history/data-store', AnyDataStoreNode);
