import { PointOutput } from '../../../../backend/models/point-model';
import { IMessageSubscriber, NodeStoreItem, Observer } from '../../../../utils/pattern';
import { Container } from '../../../container';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import { GlobalNodeRegistry } from '../../../node-registry';
import CryptoUtils from '../../../utils/crypto-utils';
import { ConnectionOutput, DependantConnectionNode } from '../connection-node-mixin';
import { CentralizedPointOutputObserver, HasPointOutputObservers } from '../model/point-node';
import { ProtocolDeviceNode } from '../ProtocolDeviceNode';
import { DefaultMqttClient, MqttClientWrapper } from './core/mqtt-device';
import { MQTTMessage, SillyMqttPoint } from './core/mqtt-model';
import { MqttPointStoreItem, MqttPublisherStore, MqttSubscriberStore } from './mqtt-client-node-store';

export const MQTT_CLIENT_NODE = 'protocols/mqtt/mqtt-network';
export const REGISTER_MQTT_PUBLISHER = 'REGISTER_MQTT_PUBLISHER';
export const UNREGISTER_MQTT_PUBLISHER = 'UNREGISTER_MQTT_PUBLISHER';
export const UPDATE_MQTT_PUBLISHER = 'UPDATE_MQTT_PUBLISHER';
export const PUBLISH_MQTT_DATA = 'PUBLISH_MQTT_DATA';
export const REGISTER_MQTT_SUBSCRIBER = 'REGISTER_MQTT_SUBSCRIBER';
export const UPDATE_MQTT_SUBSCRIBER = 'UPDATE_MQTT_SUBSCRIBER';
export const UNREGISTER_MQTT_SUBSCRIBER = 'UNREGISTER_MQTT_SUBSCRIBER';
export const REGISTER_MQTT_POINT = 'REGISTER_MQTT_POINT';
export const UPDATE_MQTT_POINT = 'UPDATE_MQTT_POINT';
export const UNREGISTER_MQTT_POINT = 'UNREGISTER_MQTT_POINT';

class MqttCentralizedPointOutputObserver extends CentralizedPointOutputObserver<SillyMqttPoint> {

  protected convert(data: PointOutput<SillyMqttPoint>): object {
    let output = {};
    output[data.point.identifier()] = {
      publisherTopic : data.point.publishedTopic(),
      subscriberTopic: data.point.subscribedTopic(),
      pointValue     : data.point.pointValue,
      status         : data.connStatus.status.label,
      error          : data.connStatus.error,
    };
    return output;
  }

}

/**
 * It resolves one mqtt client per host
 */
class MqttClientNode extends ProtocolDeviceNode implements HasPointOutputObservers<SillyMqttPoint>, IMessageSubscriber {

  private readonly _oTopicsSlot = 2;
  private publishers: MqttPublisherStore = new MqttPublisherStore();
  private subscribers: MqttSubscriberStore = new MqttSubscriberStore();
  private mqttClient: MqttClientWrapper = null;
  //TODO: should implement queue properly
  private retriedItems: { action: string; payload: any }[] = [];

  constructor() {
    super(`MQTT Network`, 'This node acts as a container for MQTT nodes. ' +
                          'All MQTT nodes should be added within the MQTT-Network container. ' +
                          'The MQTT broker details can be configured in settings.');
    this.mixinEnableInputSetting();
    this.addInputWithSettings('name', Type.STRING, 'nube-mqtt', 'MQTT Network');
    this.settings['server'] = NodeSetting.create({ description: 'Broker URL', value: 'localhost', type: SettingType.STRING });
    this.settings['port'] = NodeSetting.create({ description: 'Broker port', value: 1883, type: SettingType.NUMBER });
    this.settings['authentication'] = NodeSetting.create({ description: 'Use Authentication', value: false, type: SettingType.BOOLEAN });
    this.settings['username'] = NodeSetting.create({ description: 'User name', value: '', type: SettingType.STRING });
    this.settings['password'] = NodeSetting.create({ description: 'Password', value: '', type: SettingType.PASSWORD });

    this.setSettingsConfig({
                             groups    : [
                               { server: { weight: 3 }, port: { weight: 1 } },
                               { enable: {}, authentication: {} },
                               { username: {}, password: {} },
                             ],
                             conditions: {
                               username: setting => !!setting['authentication'].value,
                               password: setting => !!setting['authentication'].value,
                             },
                           });
    this.addOutput('topics', Type.JSON);
  }

  private static createMqttConnectionOptions(settings: { [name: string]: NodeSetting }) {
    const options: any = { host: settings['server'].value };

    const port = settings['port'].value;
    const username = settings['username'].value;
    const password = settings['password'].value;

    if (port != null) options.port = port;
    if (username != null && username != '') options.username = username;
    if (password != null && password != '') options.password = CryptoUtils.decrypt(password);
    return options;
  }

  pointObservers(): Observer<PointOutput<SillyMqttPoint>>[] {
    return [new MqttCentralizedPointOutputObserver(this, this._oTopicsSlot)];
  }

  listen({ action, payload }: { action: string; payload: NodeStoreItem }): any {
    this.debugInfo(`Handling ${action}:${payload.identifier}...`);
    switch (action) {
      case REGISTER_MQTT_POINT:
        return this.registerMqttPoint(payload);
      case UPDATE_MQTT_POINT:
        return this.updateMqttPoint(payload);
      case UNREGISTER_MQTT_POINT:
        return this.unregisterMqttPoint(payload);
      case REGISTER_MQTT_PUBLISHER:
        return this.publishers.register(payload);
      case UPDATE_MQTT_PUBLISHER:
        return this.publishers.update(payload, null, (p, force?: boolean) => this.afterUnregister(p, force));
      case UNREGISTER_MQTT_PUBLISHER:
        return this.publishers.unregister(payload, true, p => this.afterUnregister(p, true));
      case REGISTER_MQTT_SUBSCRIBER:
        return this.registerMqttSubscriber(payload);
      case UPDATE_MQTT_SUBSCRIBER:
        return this.updateMqttSubscriber(payload);
      case UNREGISTER_MQTT_SUBSCRIBER:
        return this.unregisterMqttSubscriber(payload);
      case PUBLISH_MQTT_DATA:
        return this.publishMqttData(payload);
      default:
        this.debugWarn('Request action doesn\'t match');
    }
  }

  connObserverNodes(): Observer<ConnectionOutput>[] {
    let nodeIds = [...new Set(this.publishers.listNodeIds().concat(this.subscribers.listNodeIds()))];
    return nodeIds.map(id => GlobalNodeRegistry.instance().get(id))
                  .filter(n => n)
                  .map(n => (<DependantConnectionNode><unknown>n).statusObserver());
  }

  isConnConnected(): boolean {
    return this.mqttClient?.isConnected();
  }

  onReceiveMessage(msg: MQTTMessage): void {
    if (this.side !== Side.server) return;
    let subscribersByTopic = this.subscribers.lookup(msg.topic);
    if (!subscribersByTopic) {
      this.debug(`Not found any subscriber on MQTT topic '${msg.topic}'`);
      return;
    }
    Object.entries(subscribersByTopic).forEach(([nodeId, val]) => {
      if (!val?.enabled) {
        return;
      }
      let node = GlobalNodeRegistry.instance().get(nodeId);
      if (!node) {
        this.debugWarn(`MQTT topic '${msg.topic}' is subscribed by Node ${nodeId} that not found`);
        return;
      }
      try {
        this.debugInfo(`Dispatch message on MQTT topic '${msg.topic}' to Node Id '${nodeId}'`);
        val.func(msg.message);
      } catch (e) {
        this.debugWarn(`Message on MQTT topic '${msg.topic}' cannot be dispatched due to ${e}`);
      }
    });
  }

  protected createThenStart() {
    const options = MqttClientNode.createMqttConnectionOptions(this.settings);
    options.clientId = `mqttjs_wires_cid_${this.container.id}_id${this.id}`;
    this.mqttClient = DefaultMqttClient.init(
      options,
      (client: MqttClientWrapper) => this.retryConnection(client),
      (client: MqttClientWrapper, msg?: string) => this.updateClientStatus(client, msg),
      (msg: MQTTMessage) => this.onReceiveMessage(msg),
    );
  }

  protected stop(): void {
    if (this.mqttClient) {
      this.mqttClient.stop();
      this.mqttClient = null;
    }
  }

  private updateClientStatus(client: MqttClientWrapper, errMsg?: string): void {
    this.notifyConnStatusOutput(this.getConnectionStatus(), errMsg);
  }

  private retryConnection(client: MqttClientWrapper): void {
    this.debugInfo('Retry connection after MQTT client connected...');
    //TODO dirty fix to avoid infinite loop
    let count = 0,
        max   = 500;
    while (this.retriedItems.length > 0 && count < max) {
      this.listen(this.retriedItems.shift());
      count++;
    }
    this.updateClientStatus(client);
  }

  private publishMqttData(payload: NodeStoreItem): any {
    if (this.side !== Side.server || !payload?.enabled) {
      return null;
    }
    if (!this.isConnConnected()) {
      payload.lenient = true;
      this.retriedItems.push({ action: PUBLISH_MQTT_DATA, payload: payload });
      return null;
    }
    let converter = this.publishers.lookup(payload?.identifier)?.func;
    this.mqttClient.publish(payload?.identifier, converter ? converter(payload?.data) : payload?.data?.toString());
    return payload?.data;
  }

  private registerMqttSubscriber(payload: NodeStoreItem) {
    if (!this.isConnConnected()) {
      payload.lenient = true;
      this.retriedItems.push({ action: REGISTER_MQTT_SUBSCRIBER, payload: payload });
      return;
    }
    this.subscribers.register(payload, p => this.mqttClient.subscribe(p?.identifier));
  }

  private unregisterMqttSubscriber(payload: NodeStoreItem) {
    if (!this.isConnConnected()) {
      payload.lenient = true;
      this.retriedItems.push({ action: UNREGISTER_MQTT_SUBSCRIBER, payload: payload });
      return;
    }
    this.subscribers.unregister(payload, true, p => {
      this.mqttClientDoUnsubscribe(p);
      this.afterUnregister(p, true);
    });
  }

  private updateMqttSubscriber(payload: NodeStoreItem) {
    if (!this.isConnConnected()) {
      payload.lenient = true;
      this.retriedItems.push({ action: UPDATE_MQTT_SUBSCRIBER, payload: payload });
      return;
    }
    this.subscribers.update(payload, p => this.mqttClient.subscribe(p?.identifier),
                            (p, force?: boolean) => {
                              this.mqttClientDoUnsubscribe(p);
                              this.afterUnregister(p, force);
                            });
  }

  private mqttClientDoUnsubscribe(payload: NodeStoreItem) {
    if (!this.subscribers.has(payload.identifier, (k, v) => v?.enabled)) {
      this.mqttClient.unsubscribe(payload?.identifier);
    }
  }

  private registerMqttPoint(payload: NodeStoreItem) {
    let item = payload as MqttPointStoreItem;
    let d = this.listen({ action: REGISTER_MQTT_PUBLISHER, payload: item.toPublisher() });
    this.listen({ action: REGISTER_MQTT_SUBSCRIBER, payload: item.toSubscriber() });
    return d;
  }

  private unregisterMqttPoint(payload: NodeStoreItem) {
    let item = payload as MqttPointStoreItem;
    let d = this.listen({ action: UNREGISTER_MQTT_PUBLISHER, payload: item.toPublisher() });
    this.listen({ action: UNREGISTER_MQTT_SUBSCRIBER, payload: item.toSubscriber() });
    this.afterUnregister(payload, true);
    return d;
  }

  private updateMqttPoint(payload: NodeStoreItem) {
    let item = payload as MqttPointStoreItem;
    try {
      return this.listen({ action: UPDATE_MQTT_PUBLISHER, payload: item.toPublisher() });
    } finally {
      this.listen({ action: UPDATE_MQTT_SUBSCRIBER, payload: item.toSubscriber() });
    }
  }

  private afterUnregister(payload: NodeStoreItem, force: boolean): void {
    if (force) {
      let output = this.outputs[this._oTopicsSlot].data;
      if (output) {
        delete output[payload.identifier];
      }
      this.setOutputData(this._oTopicsSlot, output);
    }
  }
}

Container.registerNodeType(MQTT_CLIENT_NODE, MqttClientNode);
