import { AxiosResponse, default as axios } from 'axios';
import { get } from 'lodash';
import config from '../config';
import { Entry } from './entry';

export abstract class Api<T extends Entry> {
  protected client = axios.create({ baseURL: config.backendSync.baseUrl });

  abstract list(objId?: string): Promise<T[]>;

  abstract get(objId: string): Promise<T[]>;

  abstract create(obj: T, parentId?: string): Promise<any>;

  abstract update(obj: T, objId?: string): Promise<any>;

  abstract delete(objId: string): Promise<any>;

  abstract factoryEntry(): T;

  protected fetchMany(url: string, respKey?: string): Promise<T[]> {
    return this.client.get(url).then(this.extractData(respKey)).then(this.assignAllTo(() => this.factoryEntry()));
  }

  protected fetchOne(url: string, respKey?: string) {
    return this.client.get(url).then(this.extractData(respKey)).then(p => Object.assign(this.factoryEntry(), p));
  }

  protected newEntry<T>(c: new () => T): T {
    return new c();
  }

  protected extractData<T>(path: string = null) {
    return (response: AxiosResponse<T>) => (path ? get(response.data, path) : response.data);
  }

  /**
   * Create a provided class instance using a factory method, for all given elements, and merge each element
   * into the class instance.
   *
   * Higher-order function
   */
  protected assignAllTo<T>(factory: () => T) {
    return (properties: any[]): T[] => properties.map(p => Object.assign(factory(), p));
  }
}
