import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class SystemBeepNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.system.beep.title;
    this.description = NODE_INFO.system.beep.description;
    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('error', Type.STRING);
  }

  onInputUpdated() {
    try {
      if (this.getInputData(0) == true) process.stderr.write('\x07');
      this.setOutputData(0, false);
    } catch (e) {
      this.setOutputData(0, true);
    }
  }
}
Container.registerNodeType('system/beep', SystemBeepNode);
