import { Container } from '../../container';
import { Type } from '../../node-io';
import HistoryBase from './HistoryBase';
import { NODE_INFO } from '../../node-info';

class HistoryNode extends HistoryBase {
  constructor() {
    super();
   
    this.title = NODE_INFO.history.history.title;
    this.description = NODE_INFO.history.history.description;
    
    this.addInput('input', Type.ANY);
    this.addHistorySettingsConfig();
  }
}
Container.registerNodeType('history/history', HistoryNode);
