import { db_logger as logger } from '../../utils/loggers';
import { AbstractDatabase } from '../database/BaseDatabase';
import HistorianDatabase from '../database/HistorianDatabase';
import { History } from '../models/historian';

export default class HistorianDataSource extends AbstractDatabase implements HistorianDatabase {
  addHistory(history: History, callback?: (err?: Error, doc?: History) => void) {
    this.db.insert(history, function (err, doc) {
      if (err) logger.error(err.message);
      if (callback) callback(err, doc);
    });
  }

  updateHistory(
    query: object,
    history: History,
    callback?: (err?: Error) => void,
    updatedCallback?: (updated?: number) => void,
  ) {
    this.db.update(query, history, {}, function (err, updated) {
      if (err) {
        logger.error(err.message);
      } else {
        updatedCallback(updated);
      }
      if (updated == 0) logger.error(`Can't update history [${name}]. Document not found.`);
      if (callback) callback(err);
    });
  }

  //remove
  removeHistory(
    query: object,
    callback?: (err?: Error) => void,
    removedCallback?: (removed?: number) => void,
  ) {
    this.db.remove(query, {}, function (err, removed) {
      if (err) {
        logger.error(err.message);
      } else {
        removedCallback(removed);
      }
      if (removed == 0) logger.debug('Cat\'t remove. History Document not found.');
      if (callback) callback(err);
    });
  }

  protected dbName(): string {
    return `history`;
  }
}

