import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class IntToBits extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.conversion["int-to-bits"].title;
    this.description = NODE_INFO.conversion["int-to-bits"].description;
    this.settings['start'] = NodeSetting.create({
      description: 'Start index if Array',
      type: SettingType.NUMBER,
      value: 0,
    });

    this.settings['bytes'] = NodeSetting.create({
      description: 'Bytes to read if Array',
      type: SettingType.NUMBER,
      value: 1,
    });

    this.addInput('input', Type.ANY);
  }

  init() {
    this.setNewOutputs();
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let input = this.getInputData(0);
    if (input === undefined || input === null) {
      return;
    }

    if (typeof input === 'number') {
      input = [input];
    }

    const START = this.settings['start'].value;
    const BYTES = this.settings['bytes'].value;

    if (!input || !input.length || input.length < BYTES) {
      return;
    }

    let outputIndex = 0;
    for (let i = 0; i < BYTES; i++) {
      for (let j = 0; j < 8; j++) {
        this.setOutputData(outputIndex++, (input[START + i] >> j) & 0x01);
      }
    }
  }

  onAfterSettingsChange() {
    this.setNewOutputs();
    this.onInputUpdated();
  }

  private setNewOutputs() {
    let outputsLen = this.getOutputsCount();
    for (let i = 0; i < outputsLen; i++) {
      this.removeOutput(i);
    }
    outputsLen = this.settings['bytes'].value * 8;
    for (let i = 0; i < outputsLen; i++) {
      this.addOutput(i.toString(), Type.NUMBER);
    }
    this.updateNodeOutput();
    this.onInputUpdated();
  }
}
Container.registerNodeType('conversion/int-to-bits', IntToBits);
