import { Container, ContainerRegistry } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType } from '../../node-io';

export class ConnectionLocalReceiverNode extends FlexibleNode {
  constructor() {
    super();
    this.dynamicOutputsExist = true;
    this.dynamicInputsExist = false;
    this.dynamicSettingsExist = false;
    this.dynamicDefaultOutputs = 1;
    this.dynamicMinOutputs = 1;
    this.title = NODE_INFO.connection["link-receiver"].title;
    this.description = NODE_INFO.connection["link-receiver"].description;
    this.settings['channel'] = NodeSetting.create({
      description: 'Channel Number',
      value: 1,
      type: SettingType.NUMBER,
    });
  }

  onCreated() {
    this.properties['channel'] = 1;
  }

  onAdded() {
    this.updateTitle();
    this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.updateTitle();
    if (this.properties['channel'] !== this.settings['channel'].value) this.resetOutputs();
    this.triggerTransmitterNodeUpdate();
  }

  triggerTransmitterNodeUpdate() {
    let transmitters = ContainerRegistry.instance().rootContainer().getNodesByType('connection/link-transmitter', true);
    transmitters.forEach(transmitter => {
      if (transmitter.settings['channel'].value == this.settings['channel'].value) {
        transmitter['onInputUpdated']();
      }
    });
  }

  resetOutputs() {
    for (let i in this.outputs) {
      this.setOutputData(+i, null);
    }
  }

  updateTitle() {
    this.title = 'Link Receiver [' + this.settings['channel'].value + ']';
  }
}
Container.registerNodeType('connection/link-receiver', ConnectionLocalReceiverNode);
