import { isNull } from '../../../../utils/helper';
import { BACNET_DEVICE, BACNET_POINT } from '../../../constants';
import { Container } from '../../../container';
import { NODE_INFO } from '../../../node-info';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import Utils from '../../../utils';
import HistoryBase from '../../history/HistoryBase';
import { SEND_PAYLOAD_TO_CHILD, SEND_POINT_VALUE } from './bacnet-constant';
import { BACnetNetwork } from './bacnet-network';
import BacnetUtils from './bacnet-utils';
import { DeviceObject } from './interface';

class BACnetPoint extends HistoryBase {
  networkNumber: number;
  points: Array<DeviceObject> = [];
  // poll count
  pollCount = 0;
  pollCountFail = 0;

  // node input nums
  inputInNum = 0;

  // node output nums
  outputOutNum = 0;
  jsonOutNum = 1;
  errorOutNum = 2;
  logsOutNum = 3;

  constructor() {
    super();
    this.title = NODE_INFO['protocols/bacnet']['bacnet-point'].title;
    this.description = NODE_INFO['protocols/bacnet']['bacnet-point'].description;
    this.addInput('input', Type.ANY);
    this.addOutput('output', Type.STRING);
    this.addOutput('output json', Type.STRING);
    this.addOutput('error', Type.STRING);
    this.addOutput('logs', Type.STRING);
    this.settings['pointEnable'] = NodeSetting.create({
      description: 'Point enable',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['pointWriteEnable'] = NodeSetting.create({
      description: 'Point write enable',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['manualPoint'] = NodeSetting.create({
      description: 'Add point manually',
      value: false,
      type: SettingType.BOOLEAN,
    });

    this.settings['pointsList'] = NodeSetting.create({
      description: 'Select a point',
      value: null,
      type: SettingType.DROPDOWN,
      config: {
        items: [],
      },
    });
    this.settings['objectType'] = NodeSetting.create({
      description: 'Output type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: 'analog-input' },
          { value: 1, text: 'analog-output' },
          { value: 2, text: 'analog-value' },
          { value: 3, text: 'binary-input' },
          { value: 4, text: 'binary-output' },
          { value: 5, text: 'binary-value' },
          { value: 13, text: 'multi-state-input' },
          { value: 14, text: 'multi-state-output' },
          { value: 19, text: 'multi-state-value' },
        ],
      },
      value: 0,
    });
    // The data-type of the value to be written.
    this.settings['objectTag'] = NodeSetting.create({ //The data-type of the value to be written.
      description: 'Data type value to be written',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: 'NULL(0)' },
          { value: 1, text: 'BOOLEAN(1)' },
          { value: 2, text: 'UNSIGNED_INTEGER(2)' },
          { value: 3, text: 'SIGNED_INTEGER(3)' },
          { value: 4, text: 'REAL(4)' },
          { value: 5, text: 'DOUBLE(5)' },
          { value: 6, text: 'OCTET_STRING(6)' },
          { value: 7, text: 'CHARACTER_STRING(7)' },
          { value: 8, text: 'BIT_STRING(8)' },
          { value: 9, text: 'ENUMERATED(9)' },
          { value: 10, text: 'DATE(10)' },
          { value: 11, text: 'TIME(11)' },
          { value: 12, text: 'OBJECTIDENTIFIER(12)' },
        ],
      },
      value: 1,
    });

    this.settings['objectInstance'] = NodeSetting.create({
      description: 'Point ID',
      value: 1,
      type: SettingType.NUMBER,
    });

    this.settings['priority'] = NodeSetting.create({
      description: 'Priority Number',
      value: 16,
      type: SettingType.NUMBER,
    });
    this.settings['logs'] = NodeSetting.create({
      description: 'Return full bacnet payload',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.setSettingsConfig({
      conditions: {
        pointsList: setting => {
          return !setting['manualPoint'].value;
        },
        objectType: setting => {
          return setting['manualPoint'].value;
        },
      },
    });
    this.addHistorySettingsConfig(0, false);
  }

  async onAdded() {
    super.onAdded();
    // this.name = `BACnet Point: cid_${this.container.id.toString()}_id${this.id.toString()}`; //set default node name
    if (this.side !== Side.server) return; // only execute on server
    await Utils.sleep(2000); // TODO: Since device should start after network > device, import may shows issue if we don't have this
    this.points = BacnetUtils.getPoints(this.getParentNode());
    this.setPointsListItems();
    this.getPresentValue();
    BacnetUtils.addPoint(this.getParentNode(), this);
  }

  async onInputUpdated() {
    // this.setOutputData(0, 1);
    await super.onInputUpdated();
    if (this.side !== Side.server) return; // only execute on server
    let objectType: number;
    let objectInstance: number;
    const value = this.getInputData(0);
    const priority = this.settings['priority'].value;
    const manualPoint = this.settings['manualPoint'].value;
    const selectPoint = this.settings['pointsList'].value;
    const objectTag = this.settings['objectTag'].value;
    let pointWriteEnable = this.settings['pointWriteEnable'].value;
    if (manualPoint) {
      objectType = this.settings['objectType'].value;
      objectInstance = this.settings['objectInstance'].value;
    }
    if (selectPoint) {
      objectType = selectPoint.type;
      objectInstance = selectPoint.instance;
    }
    if (isNull(value)) return;
    if (pointWriteEnable === false) return;
    // write point value
    BacnetUtils.writePresentValue(this.getParentNode(), [objectType, objectInstance, value, priority, objectTag]);
  }

  async onAfterSettingsChange() {
    await super.onAfterSettingsChange();
    this.getPresentValue();
    const networkNode: BACnetNetwork = BacnetUtils.getNetworkNode(this.getParentNode());
    if (networkNode.settings?.networkNumber?.value) {
      this.networkNumber = networkNode.settings?.networkNumber?.value;
    }
  }

  onRemoved() {
    super.onRemoved();
    BacnetUtils.removePoint(this.getParentNode(), this);
  }

  getPresentValue() {
    if (this.side !== Side.server) return; // only execute on serve
    let selectPoint = this.settings['pointsList'].value;
    const manualPoint = this.settings['manualPoint'].value;
    const objectType = this.settings['objectType'].value;
    const objectInstance = this.settings['objectInstance'].value;
    if (manualPoint) {
      selectPoint = { type: objectType, instance: objectInstance };
    }
    if (selectPoint) {
      BacnetUtils.getPresentValue(this.getParentNode(), selectPoint).then(msg => {
        this.setPointValue(msg);
      });
    }
  }

  setPointValue(msg) {
    const logs = this.settings['logs'].value;
    if (logs) {
      this.setOutputData(this.logsOutNum, true, true);
      this.debugInfo(`BACNET-NETWORK-POINT-NODE: msg from device ${JSON.stringify(msg)}`);
    }
    if (msg?.value?.values?.[0]?.value === undefined) {
      this.setOutputData(this.errorOutNum, true, true);
      this.debugErr(`BACNET-NETWORK-POINT-NODE: msg from device node is invalid ${JSON.stringify(msg)}`);
      return;
    }
    let pointValue = msg.value.values[0].value;
    let objectType: number;
    let objectInstance: number;
    let pointEnable = this.settings['pointEnable'].value;
    let pointWriteEnable = this.settings['pointWriteEnable'].value;
    const priority = this.settings['priority'].value;
    const manualPoint = this.settings['manualPoint'].value;
    const selectPoint = this.settings['pointsList'].value;
    const pointsList = this.settings['pointsList'].config.items;
    let bacnetPointName;

    if (manualPoint) {
      objectType = this.settings['objectType'].value;
      objectInstance = this.settings['objectInstance'].value;
    }
    if (selectPoint) {
      objectType = selectPoint.type;
      objectInstance = selectPoint.instance;
      // get bacnet point name
      if (pointsList) {
        let obj = pointsList;
        for (let key in obj) {
          if (
            obj.hasOwnProperty(key) &&
            obj[key].value.type === selectPoint.type &&
            obj[key].value.instance === selectPoint.instance
          ) {
            bacnetPointName = obj[key];
          }
        }
      }
    }

    if (msg.value) {
      this.pollCount++;
      const json = {
        name: this.name,
        pointValue: pointValue,
        enable: pointEnable,
        pointWriteEnable: pointWriteEnable,
        objectType: objectType,
        objectInstance: objectInstance,
        priority: priority,
        bacnetPointName: bacnetPointName,
        pollCount: this.pollCount,
        pollCountFail: this.pollCountFail,
      };
      this.setOutputData(this.outputOutNum, pointValue);
      this.setJsonOutput(json);
      this.setOutputData(this.errorOutNum, null, true);
    } else {
      this.setOutputData(this.outputOutNum, null, true);
      this.setJsonOutput(null);
      if (msg.error) {
        this.setOutputData(this.errorOutNum, 'Error on reading value');
        this.pollCountFail++;
      }
    }
  }

  setJsonOutput(json) {
    this.setOutputData(this.jsonOutNum, json);
    return json;
  }

  setPointsListItems() {
    this.settings['pointsList'].config.items = this.points.map(point => {
      return {
        value: { type: point.type, instance: point.objectInfo },
        text: point.name,
      };
    });
    this.persistConfiguration();
    this.broadcastSettingsToClients();
  }

  subscribe({ action, payload }) {
    switch (action) {
      case SEND_PAYLOAD_TO_CHILD:
        this.points = payload.points;
        this.setPointsListItems();
        break;
      case SEND_POINT_VALUE:
        this.setPointValue(payload);
        break;
      default:
        this.debugWarn("Request action doesn't match");
    }
  }
}
Container.registerNodeType(BACNET_POINT, BACnetPoint, BACNET_DEVICE);
