import { Database } from '../../database/database';
import { ContainerRegistry } from '../../nodes/container';
import { Node } from '../../nodes/node';
import { createNodeDbHelper, NodeDbHelper } from '../../nodes/node-db-helper';
import { NodeFactory } from '../../nodes/node-factory';
import {
  ContainerRegistryMetadata,
  IContainer,
  IContainerNode,
  isContainerNode,
  SerializedContainer,
  SerializedContainerNode,
  SerializedNode,
} from '../../nodes/node-interface';
import { SerializedNodeInput, SerializedNodeOutput } from '../../nodes/node-io';
import { Link } from '../../nodes/node-link';
import { Shape2D } from '../../nodes/node-presenter';
import { NodeRegistryHelper } from '../../nodes/node-registry';
import { NodeTypeRegistry } from '../../nodes/node-type-registry';
import { CONTAINER_NODE_TYPE } from '../../nodes/nodes/container/folder';
import { RegistryResult } from '../../nodes/registry';
import { SerializableHelper } from '../../nodes/serializable';
import { CloneResult, ExportResult, ImportResult, initETLResult, PackResult } from '../../shared/etl-result';
import { ContainerRegistryValidator, ListValidator } from '../../shared/validator';
import { arrayToObject, isEmpty, isNotNull, isNull, mapToObjectSameKey, unionToArray } from '../../utils/helper';
import { IdGenerator } from '../../utils/id-generator';
import { node_service_logger } from '../../utils/loggers';
import { NodeCRUDService } from './NodeCRUDService';

const logger = node_service_logger;

interface ExtensionContainer extends SerializedContainer {
  lastId: number;
}

/**
 * Represents for ETL service (Extract - Transform - Load data, a.k.a import/export)
 */
export class NodeETLService {

  private readonly dbHelper: NodeDbHelper;

  constructor(dbHelper?: NodeDbHelper) {
    this.dbHelper = dbHelper ?? createNodeDbHelper();
  }

  /**
   * Pack up nodes to new container under specific one
   * @param {number} parentId current parent container id
   * @param {number[]} ids node ids to pack up
   * @param atPos At position
   * @param {ContainerRegistryMetadata} metadata container registry
   * @return {Promise<PackResult>} pack result
   */
  packUpNodes(parentId: number, ids: number[], atPos: [number, number],
              metadata?: ContainerRegistryMetadata): Promise<PackResult> {
    logger.info(`Packing nodes [${ids}] in container ${parentId} into new container...`);
    const reg: ContainerRegistryMetadata = metadata ?? ContainerRegistry.instance();
    const v = ContainerRegistryValidator.mustExisted(parentId, reg);
    return v.then(p => ListValidator.notEmpty(ids, `No node to pack-up`)
                                    .then(nodeIds => this.doExport(parentId, nodeIds, reg)
                                                         .then(nodes => ({
                                                           folder: this.createFolder(p, reg, atPos), nodes: nodes,
                                                         }))
                                                         .then(r => this.doPackUp(p, r.folder, r.nodes, reg))));
  }

  /**
   * Clone nodes to other nodes under specific container
   * @param {number} parentId current parent container id
   * @param {number[]} ids node ids to clone
   * @param {[number, number]} atPos to move
   * @param {ContainerRegistryMetadata} metadata container registry
   * @return {Promise<CloneResult>} clone result
   */
  cloneNodes(parentId: number, ids: number[], atPos: [number, number],
             metadata?: ContainerRegistryMetadata): Promise<CloneResult> {
    logger.info(`Cloning nodes [${ids}] inside container id '${parentId}'...`);
    return ListValidator.notEmpty(ids, `No node to clone`)
                        .then(nodeIds => this.doExport(parentId, nodeIds, metadata))
                        .then(nodes => this.doImport(nodes, parentId, metadata, atPos))
                        .then(r => initETLResult<CloneResult>(CloneResult, parentId, ids.length, r));
  }

  /**
   * Export nodes and children nodes from specific container
   * @param {number} parentId
   * @param {number[]} ids
   * @param {ContainerRegistryMetadata} metadata
   * @return {Promise<SerializedNode[]>}
   */
  exportNodes(parentId: number, ids: number[], metadata?: ContainerRegistryMetadata): Promise<ExportResult> {
    return ListValidator.notEmpty(ids, `No node to export`)
                        .then(nodeIds => this.doExport(parentId, nodeIds, metadata))
                        .then(l => ListValidator.notEmpty<SerializedNode>(l, `Not found any [${ids}] to export`))
                        .then(nodes => new ExportResult(parentId, nodes));
  }

  /**
   * Import nodes into specific container
   * @param {number} parentId
   * @param {SerializedNode[]} nodes
   * @param {[number, number]} atPos
   * @param {ContainerRegistryMetadata} metadata?
   * @return {Promise<ImportResult>}
   */
  importNodes(parentId: number, nodes: SerializedNode[], atPos: [number, number],
              metadata?: ContainerRegistryMetadata): Promise<ImportResult> {
    return ListValidator.notEmpty(nodes, `No nodes to import`)
                        .then(n => this.doImport(n, parentId, metadata, atPos))
                        .then(r => initETLResult<ImportResult>(ImportResult, parentId, nodes.length, r));
  }

  private doExport(cId: number, ids: number[], metadata: ContainerRegistryMetadata) {
    function listNodesInContainer(dbHelper: NodeDbHelper, database: Database,
                                  nodes: SerializedNode[]): Promise<SerializedNode[]> {
      const list = nodes.filter(n => isContainerNode(n))
                        .map(n => <SerializedContainerNode>n)
                        .map(n => dbHelper.listByContainer(database, n.sub_container.id)
                                          .then(nodes => listNodesInContainer(dbHelper, database, nodes)));
      return Promise.all([Promise.resolve(nodes), ...list]).then(r => r.flatMap(nodes => nodes));
    }

    logger.info(`Exporting [${ids}] nodes in container id '${cId}'...`);
    const registry: ContainerRegistryMetadata = metadata ?? ContainerRegistry.instance();
    const v = ContainerRegistryValidator.mustExisted(cId, registry);
    return v.then(_ => registry.rootContainer()['db'])
            .then(database => this.dbHelper.listByIds(database,
                                                      unionToArray(ids.map(nId => IdGenerator.createNodeId(cId, nId))))
                                  .then(nodes => listNodesInContainer(this.dbHelper, database, nodes)));
  }

  private createFolder(parent: IContainer, registry: ContainerRegistryMetadata,
                       atPos: [number, number]): IContainerNode {
    logger.info(`Creating new container node into container id '${parent.id}'...`);
    const cNode = <IContainerNode><unknown>NodeFactory.create(
      <SerializedContainerNode>{
        id: IdGenerator.genNodeId(registry.get(parent.id).nodeRegistry().lastId()),
        cid: parent.id, type: CONTAINER_NODE_TYPE, sub_container: { id: -1 },
        pos: atPos,
      }, true, registry);
    cNode.container = parent;
    cNode.sub_container = registry.register(cNode.sub_container, true);
    logger.info(`Cached new container node id '${cNode.id}' with container id '${cNode.sub_container.id}'`);
    return cNode;
  }

  private doPackUp(parent: IContainer, folder: IContainerNode, nodes: SerializedNode[],
                   registry: ContainerRegistryMetadata): Promise<PackResult> {
    const cId = folder.sub_container.id;
    const total = nodes.length;
    logger.info(`Analyzing ${total} nodes to move into container id '${cId}'...`);
    return this.remapContainers(registry, cId, nodes)
               .then(containers => new NodeTree(nodes, containers))
               .then(tree => tree.compute([0, 0]))
               .then(tree => this.dbHelper.batchInsert(registry.rootContainer()['db'],
                                                       [SerializableHelper.serialize(folder), ...tree.flatten()]))
               .then(nodes => new NodeCRUDService().removeNodes(parent, nodes).then(rr => ({ nodes, rr })))
               .then(({ nodes, rr }) => NodeRegistryHelper.register(parent, nodes, registry, true)
                                                          .then(pr => ({ pr, rr })))
               .then(({ pr, rr }) => initETLResult<PackResult>(PackResult, parent.id, total, pr, { cId, rr }));
  }

  private doImport(nodes: SerializedNode[], parentId: number, metadata: ContainerRegistryMetadata,
                   atPos: [number, number]): Promise<RegistryResult<Node>> {
    logger.info(`Analyzing ${nodes.length} nodes to import into container id '${parentId}'...`);
    const registry: ContainerRegistryMetadata = metadata ?? ContainerRegistry.instance();
    const v = ContainerRegistryValidator.mustExisted(parentId, registry);
    return v.then(container => this.remapContainers(registry, parentId, nodes)
                                   .then(containers => new NodeTree(nodes, containers))
                                   .then(tree => tree.compute(atPos, parentId))
                                   .then(t => this.dbHelper.batchInsert(registry.rootContainer()['db'], t.flatten()))
                                   .then(n => NodeRegistryHelper.register(container, n, registry, true)));
  }

  private remapContainers(registry: ContainerRegistryMetadata, cId: number,
                          nodes: SerializedNode[]): Promise<{ [cid: string]: ExtensionContainer }> {
    const containers = arrayToObject(nodes.filter(n => isContainerNode(n))
                                          .map(n => <SerializedContainerNode>n)
                                          .map(n => n.sub_container),
                                     c => c.id,
                                     c => ({ id: IdGenerator.genContainerId(registry.lastId(), c.id), lastId: 0 }));
    const remainContainers = unionToArray(nodes.filter(n => isNull(containers[n.cid])).map(n => n.cid));
    if (remainContainers.length > 1) {
      throw new Error('The given nodes has invalid node that is under non-existed container');
    }
    const dest = arrayToObject(remainContainers, oldCid => oldCid,
                               _ => ({ id: cId, lastId: registry.get(cId).nodeRegistry().lastId() }));
    return Promise.resolve({ ...containers, ...dest });
  }

}

class NodeTree {
  private readonly nodes: SerializedNode[];
  private readonly remapContainers: { [cid: string]: ExtensionContainer };
  private tree: { [cId: string]: NodeBranch } = {};
  private outputs: SerializedNode[];

  constructor(nodes: SerializedNode[], remapContainers: { [p: string]: ExtensionContainer }) {
    this.nodes = nodes;
    this.remapContainers = remapContainers;
  }

  compute(atPos: [number, number], parentId?: number): NodeTree {
    return this.build().normalize(this.computeRelativePos(atPos, parentId));
  }

  /**
   * Returns flatten nodes to insert into database
   * @return {SerializedNode[]} list serialized node
   */
  flatten(): SerializedNode[] {
    return this.outputs ?? [];
  }

  private build() {
    this.nodes.sort((n1, n2) => n2.cid - n1.cid)
        .forEach(node => {
          const parent = this.tree[node.cid] = this.tree[node.cid] ?? new NodeBranch(this.remapContainers[node.cid]);
          let itself: NodeBranch = null;
          if (isContainerNode(node)) {
            const cId = (<SerializedContainerNode>node).sub_container.id;
            itself = this.tree[cId] ?? new NodeBranch(this.remapContainers[cId]);
            delete this.tree[cId];
          }
          let newId = itself?.parentId() ?? IdGenerator.genNodeId(parent.last(), node.id);
          parent.push(newId, node, itself);
        });
    return this;
  }

  private normalize(relativePos: [number, number]): NodeTree {
    this.outputs = Object.keys(this.tree)
                         .sort((b1, b2) => +b1 - +b2)
                         .map(cId => this.tree[cId] = this.tree[cId].normalize(relativePos))
                         .flatMap(branch => branch.flatten());
    return this;
  }

  private computeRelativePos(atPos: [number, number], parentId?: number): [number, number] {
    if (isNull(parentId) || isNull(this.tree[parentId])) {
      return atPos;
    }
    return Shape2D.relative(Shape2D.avgPos(this.tree[parentId].positions()), atPos);
  }
}

class NodeBranch {

  private readonly parent: ExtensionContainer;
  private nodes: { [nId: string]: { nnId: number, node: SerializedNode, branch?: NodeBranch } } = {};
  private outputs: SerializedNode[];

  constructor(parent: ExtensionContainer) {
    this.parent = parent;
  }

  parentId(): number {
    return this.parent.id;
  }

  last(): number {
    return this.parent.lastId;
  }

  push(newNodeId: number, node: SerializedNode, subNode?: NodeBranch): NodeBranch {
    this.nodes[node.id] = { nnId: newNodeId, node, branch: subNode };
    this.parent.lastId = Math.max(newNodeId, this.parent.lastId);
    return this;
  }

  normalize(relativePos: [number, number]): NodeBranch {
    this.outputs = Object.keys(this.nodes)
                         .sort((nId1, nId2) => +nId1 - +nId2)
                         .map(nId => this.nodes[nId] = this.normalizeNodeBranch(this.nodes[nId], relativePos))
                         .flatMap(branch => this.flattenBranch(branch));
    return this;
  }

  flatten(): SerializedNode[] {
    return this.outputs ?? [];
  }

  positions(): [number, number][] {
    return Object.values(this.nodes).map(b => b.node.pos);
  }

  private flattenBranch(branch: { nnId: number, node: SerializedNode, branch?: NodeBranch }): SerializedNode[] {
    if (isNull(branch.branch)) {
      return [branch.node];
    }
    const nodes = Object.values(branch.branch.nodes).flatMap(sub => this.flattenBranch(sub));
    return [branch.node, ...nodes];
  }

  private lookupNode(nodeId: string | number): SerializedNode {
    return this.nodes[nodeId]?.node;
  }

  private normalizeNodeBranch(branch: { nnId: number; node: SerializedNode; branch?: NodeBranch },
                              relativePos?: [number, number]): { nnId: number; node: SerializedNode; subNode?: NodeBranch } {
    if (isContainerNode(branch.node)) {
      const itself = <SerializedContainerNode>branch.node;
      itself.sub_container.id = branch.branch.parentId();
      branch.branch = branch.branch.normalizeBranch();
    }
    branch.node.id = branch.nnId;
    branch.node.cid = this.parent.id;
    branch.node.type = NodeTypeRegistry.instance().lookupNodeType(branch.node.type);
    branch.node.pos = Shape2D.move(relativePos, branch.node.pos, branch.node.size);
    branch.node.inputs = this.normalizeNodeInputs(branch.node.inputs, branch.branch);
    branch.node.outputs = this.normalizeNodeOutputs(branch.node.outputs, branch.branch);
    return branch;
  }

  private normalizeBranch(): NodeBranch {
    this.nodes = mapToObjectSameKey(this.nodes, (_, n) => this.normalizeNodeBranch(n));
    return this;
  }

  private normalizeNodeInputs(inputs: { [p: number]: SerializedNodeInput }, branch?: NodeBranch) {
    return mapToObjectSameKey(inputs, (_, input) => {
      input.link = this.normalizeLink(input.link);
      input.subNodeId = branch?.lookupNode(input.subNodeId)?.id;
      return input;
    });
  }

  private normalizeNodeOutputs(outputs: { [p: number]: SerializedNodeOutput }, branch?: NodeBranch) {
    return mapToObjectSameKey(outputs, (_, output) => {
      output.links = this.normalizeOutputLinks(output.links);
      output.subNodeId = branch?.lookupNode(output.subNodeId)?.id;
      return output;
    });
  }

  private normalizeOutputLinks(links: Link[]): Link[] {
    return isEmpty(links) ? [] : links.map(l => this.normalizeLink(l)).filter(l => isNotNull(l));
  }

  private normalizeLink(link: Link): Link {
    if (isNull(link)) {
      return null;
    }
    link.target_node_id = this.nodes[link.target_node_id]?.nnId;
    return isNull(link.target_node_id) ? null : link;
  }
}
