import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';

//String
class StringConstantNode extends Node {

  constructor() {
    super();
    this.title = NODE_INFO.point["string-constant"].title;
    this.description = NODE_INFO.point["string-constant"].description;
    this.settings['value'] = NodeSetting.create({ description: 'Value', value: '', type: SettingType.STRING });
    this.addOutput('output', Type.STRING);
  }

  onAdded() {
 
    this.onAfterSettingsChange();
  }

  onExecute(){
    // this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    const val = this.settings['value'].value;
    this.setOutputData(0, Utils.parseValue(val, Type.STRING));
  }
}
Container.registerNodeType('point/string-constant', StringConstantNode);
