import { Constructor, isFunction } from '../../utils/helper';
import { Node, NodeSettingConfig} from '../node';
import { NodeInput, NodeSetting, Type } from '../node-io';

/**
 * Represents decorator to evaluate {@link NodeSetting} by {@link NodeInput}
 */
export interface NodeInputEvaluation {
  reEvaluateSettingByInput(inputs: { [p: number]: NodeInput }, settings: { [p: string]: NodeSetting }): void;
}

export class NodeInputOutputDecoratorProxy implements NodeInputEvaluation {
  func: any;

  private constructor(func: any) {
    this.func = func;
  }

  // static execute(func: any): void {
  //   new NodeInputOutputDecoratorProxy(func).reEvaluateSettingByInput(this.inputs, this.settings);
  // }

  reEvaluateSettingByInput(inputs: { [p: number]: NodeInput }, settings: { [p: string]: NodeSetting }): void {
    if (this.func && isFunction(this.func)) {
      this.func(inputs, settings);
    }
  }
}

export interface HasEnabled {
  isEnabled(): boolean;
}

export function AbleEnableNode<TBase extends Constructor<Node>>(Base: TBase) {
  abstract class AbleEnableNodeBase extends Base implements NodeInputEvaluation, HasEnabled {
    mixinEnableInputSetting(): NodeSettingConfig {
      this.addInputWithSettings(this.enableSettingKey(), Type.BOOLEAN, false, this.enableDescription());
      return { conditions: {} };
    }

    reEvaluateSettingByInput(inputs: { [p: number]: NodeInput }, settings: { [p: string]: NodeSetting }): void {
      //TODO: need find a way to auto execute super mixin method
      // NodeInputOutputDecoratorProxy.execute(super['reEvaluateSettingByInput']);
      if (isFunction(super['reEvaluateSettingByInput'])) {
        super['reEvaluateSettingByInput'](inputs, settings);
      }
      this.settings[this.enableSettingKey()].value =
        this.inputs[this.enableInputIdx()].data ?? this.settings[this.enableSettingKey()].value;
    }

    protected abstract enableDescription(): string;

    protected enableInputIdx(): number {
      return 0;
    }

    protected enableSettingKey(): string {
      return 'enable';
    }

    isEnabled(): boolean {
      let parentIsEnabled = true;
      if (isFunction(super['isEnabled'])) {
        parentIsEnabled = super['isEnabled']();
      }
      return parentIsEnabled && this.settings[this.enableSettingKey()]?.value;
    }
  }

  return AbleEnableNodeBase;
}
