export enum MATH_FUNC_TYPE {
  NA = 0,
  ADD = 1,
  SUBTRACT = 2,
  MULTIPLY = 3,
  DIVIDE = 4,
  REMAINDER = 5,
  EXP = 6,
  BOOL_INVERT = 7,
  ONE_TO_TRUE = 8,
  TRUE_TO_ONE = 9,
  NUMBER_INVERT = 10,
}

export default class MathUtils {
  /**
   * Values will be converted into the range min-max
   * @param {number} value
   * @param {number} min
   * @param {number} max
   */
  static clamp(value: number, min: number, max: number): number {
    return Math.min(Math.max(value, min), max);
  }

  /**
   * It rounds to decimal place
   * @param {any} value
   * @param {number} decimals
   * @returns {number}
   */
  static decimals(value: any, decimals: number): number {
    if (isNaN(value)) return undefined;
    const out = value.toFixed(MathUtils.clamp(decimals, 0, 5));
    parseFloat(out);
    return out;
  }

  /**
   * It converts a 1 to a 0 or a 0 to a 1
   * @param {number} value
   */
  static numInvert(value: number): number {
    return value ^ 1;
  }

  /**
   * It convert a true to a 1
   * @param {number} value
   */
  static trueToOne(value: boolean): number {
    if (typeof value === 'boolean') {
      return value ? 1 : 0;
    }
    return undefined;
  }

  /**
   * It convert a 1 to a true
   * @param {number} value
   */
  static oneToTrue(value: number): boolean {
    if (typeof value === 'number') {
      return !!value;
    }
    return undefined;
  }

  /**
   * It inverts the boolean value
   * @param {boolean} value
   */
  static invert(value: boolean): boolean {
    if (typeof value === 'boolean') {
      return !value;
    }
    return undefined;
  }

  /**
   * Checks whether all values are numbers or not
   * @param values list of comma separated values
   * @returns {boolean}
   */
  static validateNumbers = (...values: any) => {
    return values.every(x => !isNaN(x));
  };

  static mathSwitch(mathFuncType: number, val: any, mathValue: number) {
    let out;
    switch (mathFuncType) {
      case MATH_FUNC_TYPE.NA:
        out = 0;
        break;
      case MATH_FUNC_TYPE.ADD:
        out = val + mathValue;
        break;
      case MATH_FUNC_TYPE.SUBTRACT:
        out = val - mathValue;
        break;
      case MATH_FUNC_TYPE.MULTIPLY:
        out = val * mathValue;
        break;
      case MATH_FUNC_TYPE.DIVIDE:
        out = val / mathValue;
        break;
      case MATH_FUNC_TYPE.REMAINDER:
        out = 1;
        break;
      case MATH_FUNC_TYPE.EXP:
        out = 1;
        break;
      case MATH_FUNC_TYPE.BOOL_INVERT:
        out = MathUtils.invert(val);
        break;
      case MATH_FUNC_TYPE.ONE_TO_TRUE:
        out = MathUtils.oneToTrue(val);
        break;
      case MATH_FUNC_TYPE.TRUE_TO_ONE:
        out = MathUtils.trueToOne(val);
        break;
      case MATH_FUNC_TYPE.NUMBER_INVERT:
        out = MathUtils.numInvert(val);
        break;
    }
    return out;
  }
}
