import { Api } from './api';
import { Device, Edge, Folder, Network, Point, PointData } from './entry';

export class EdgeAPI extends Api<Edge> {
  list(_objId?: string): Promise<Edge[]> {
    return this.fetchMany('edge', 'edges');
  }

  get(objId: string): Promise<Edge[]> {
    return this.fetchOne(`edge/${objId}`);
  }

  create(obj: Edge): Promise<any> {
    throw new Error('Method not implemented.');
  }

  update(obj: Edge, objId?: string): Promise<any> {
    return this.client.patch(`edge/${objId}`, obj);
  }

  delete(objId: string): Promise<any> {
    throw new Error('Method not implemented.');
  }

  factoryEntry(): Edge {
    return this.newEntry(Edge);
  }
}

export class NetworkAPI extends Api<Network> {
  list(objId?: string): Promise<Network[]> {
    return this.fetchMany(`edge/${objId}/network`, 'networks');
  }

  get(objId: string): Promise<Network[]> {
    return this.fetchOne(`network/${objId}`);
  }

  create(obj: Network, parentId: string): Promise<any> {
    return this.client.post(`edge/${parentId}/network`, obj).then(this.extractData('resource'));
  }

  update(obj: Network, objId?: string): Promise<any> {
    return this.client.patch(`network/${objId}`, obj);
  }

  delete(objId: string): Promise<any> {
    return this.client.delete(`network/${objId}?_force=true`);
  }

  factoryEntry(): Network {
    return this.newEntry(Network);
  }
}

export class DeviceAPI extends Api<Device> {
  factoryEntry(): Device {
    return this.newEntry(Device);
  }

  list(objId?: string): Promise<Device[]> {
    return this.client.get(`network/${objId}/device`)
               .then(this.extractData('devices'))
               .then(v => v.map(d => d.device))
               .then(this.assignAllTo(() => new Device()));
  }

  get(objId: string): Promise<Device[]> {
    return this.fetchOne(`device/${objId}`);
  }

  create(obj: Device, parentId?: string): Promise<any> {
    return this.client.post(`network/${parentId}/device`, obj).then(this.extractData('resource.device'));
  }

  update(obj: Device, objId?: string): Promise<any> {
    return this.client.patch(`device/${objId}`, obj);
  }

  delete(objId: string): Promise<any> {
    return this.client.delete(`device/${objId}?_force=true`);
  }
}

