export class PointAPI extends Api<Point> {
  factoryEntry(): Point {
    return this.newEntry(Point);
  }

  list(objId?: string): Promise<Point[]> {
    return this.fetchMany(`folder/${objId}/point`, 'points');
  }

  get(objId: string): Promise<Point[]> {
    return this.fetchOne(`point/${objId}`);
  }

  create(obj: Point, parentId?: string): Promise<any> {
    return this.client.post(`folder/${parentId}/point`, obj).then(this.extractData('resource.point'));
  }

  update(obj: Point, objId?: string): Promise<any> {
    return this.client.patch(`point/${objId}`, obj);
  }

  delete(objId: string): Promise<any> {
    return this.client.delete(`point/${objId}?_force=true`);
  }

  fetchPointData(pointId: string): Promise<PointData> {
    return this.client.get(`point/${pointId}/data`).then(this.extractData());
  }

  updatePointData(pointId: string, priority: number, value: number): Promise<any> {
    return this.client.put(`point/${pointId}/data`, { priority, value });
  }
}
