import { Container } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

const moment = require('moment');

export class ClockNode extends Node {
  lastInterval = 1000;
  timeoutFunc;

  constructor() {
    super();
    this.title = NODE_INFO.time.clock.title;
    this.description = NODE_INFO.time.clock.description;
    this.addOutput('timeString', Type.STRING);
    this.addOutput('hour', Type.NUMBER);
    this.addOutput('min', Type.NUMBER);
    this.addOutput('sec', Type.NUMBER);
    this.addInputWithSettings('interval', Type.NUMBER, 1, 'Interval');

    this.settings['time'] = NodeSetting.create({
      description: 'Time',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [{ time: {}, interval: { weight: 2 } }],
    });

    this.settings['advanced'] = NodeSetting.create({
      description: 'Show Advanced Options',
      value: false,
      type: SettingType.BOOLEAN,
    });

    this.properties['advancedOutputs'] = false;
  }

  init() {
    if (this.settings['advanced'].value) {
      this.addOutputs();
    }
  }

  onAdded() {
    this.inputs[0]['name'] = `[interval] (${this.settings['time'].value})`;
    clearInterval(this.timeoutFunc);
    let interval = this.getInputData(0);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    this.timeoutFunc = setInterval(() => {
      this.recalculate();
    }, interval);
    this.lastInterval = interval;
    this.recalculate();
  }

  onInputUpdated() {
    let interval = this.getInputData(0);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    if (interval != this.lastInterval) {
      this.lastInterval = interval;
      clearInterval(this.timeoutFunc);
      this.timeoutFunc = setInterval(() => {
        this.recalculate();
      }, interval);
    }
  }

  recalculate() {
    if (this.side !== Side.server) return;
    const advanced = this.settings['advanced'].value;
    const now = moment();
    this.setOutputData(0, now.format('HH:mm:ss'), true);
    this.setOutputData(1, now.hours(), true);
    this.setOutputData(2, now.minutes(), true);
    this.setOutputData(3, now.seconds(), true);
    if (advanced) {
      this.setOutputData(4, now.milliseconds(), true);
      this.setOutputData(5, now.toString(), true);
      this.setOutputData(6, now.utcOffset() / 60, true);
      this.setOutputData(7, now.valueOf(), true);
    }
  }

  onAfterSettingsChange() {
    const advanced = this.settings['advanced'].value;
    this.inputs[0]['name'] = `[interval] (${this.settings['time'].value})`;
    if (advanced && !this.properties['advancedOutputs']) {
      this.addOutputs();
      this.properties['advancedOutputs'] = true;
      this.persistProperties(false, true);
    } else if (!advanced && this.properties['advancedOutputs']) {
      this.removeOutput(4);
      this.removeOutput(5);
      this.removeOutput(6);
      this.removeOutput(7);
      this.properties['advancedOutputs'] = false;
      this.persistProperties(false, true);
    }
    this.updateNodeOutput();
    this.onInputUpdated();
  }

  private addOutputs() {
    this.addOutput('millis', Type.NUMBER);
    this.addOutput('longString', Type.STRING);
    this.addOutput('TZOffset', Type.NUMBER);
    this.addOutput('epoch', Type.NUMBER);
  }
}
Container.registerNodeType('time/clock', ClockNode);
