import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

// TODO: Check logic
class MinOnOffNode extends Node {
  lastValue: boolean;
  timeOn: number;
  timeOff: number;
  minOnEnabled: boolean;
  minOffEnabled: boolean;
  minOnTime: number;
  minOffTime: number;
  lastResetValue: boolean;

  constructor() {
    super();
    this.title = NODE_INFO.boolean["min-on-off"].title;
    this.description = NODE_INFO.boolean["min-on-off"].description;
    this.addInput('input', Type.BOOLEAN);
    this.addInputWithSettings('minOn', Type.NUMBER, 1, 'Minimum On Time');
    this.addInputWithSettings('minOff', Type.NUMBER, 1, 'Minimum Off Time');
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('minOn active', Type.BOOLEAN);
    this.addOutput('minOff active', Type.BOOLEAN);
    this.settings['timeMinOn'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.settings['timeMinOff'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [
        { minOn: { weight: 2 }, timeMinOn: {} },
        { minOff: { weight: 2 }, timeMinOff: {} },
      ],
    });

    this.timeOn = null;
    this.timeOff = null;
    this.minOnEnabled = false;
    this.minOffEnabled = false;
    this.lastResetValue = false;
  }

  onCreated() {
  }

  onAdded() {
    this.setOutputData(0, false);
    this.setOutputData(1, false, true);
    this.setOutputData(2, false, true);
    this.inputs[1]['name'] = `[minOn] (${this.settings['timeMinOn'].value})`;
    this.inputs[2]['name'] = `[minOff] (${this.settings['timeMinOff'].value})`;
    this.onInputUpdated();
  }

  onExecute() {
    let elapsed = null;
    if (this.minOnEnabled) {
      elapsed = Date.now() - this.timeOn;
      if (elapsed >= this.minOnTime) {
        this.minOnEnabled = false;
        this.onInputUpdated();
        this.setOutputData(1, false, true);
      }
    }
    if (this.minOffEnabled) {
      elapsed = Date.now() - this.timeOff;
      if (elapsed >= this.minOffTime) {
        this.minOffEnabled = false;
        this.onInputUpdated();
        this.setOutputData(2, false, true);
      }
    }
    this.setOutputData(1, this.outputs[1].data, true)
    this.setOutputData(2, this.outputs[2].data, true)
  }

  onInputUpdated() {
    let reset = this.getInputData(3);
    if (reset == null) reset = false;
    if (reset == true && reset != this.lastResetValue) {
      this.setOutputData(0, false);
      this.minOnEnabled = false;
      this.minOffEnabled = false;
      this.setOutputData(1, false, true);
      this.setOutputData(2, false), true;
    }
    this.lastResetValue = reset;

    let minOn = this.getInputData(1);
    minOn = TimeUtils.timeConvert(minOn, this.settings['timeMinOn'].value);
    this.minOnTime = minOn;

    let minOff = this.getInputData(2);
    minOff = TimeUtils.timeConvert(minOff, this.settings['timeMinOff'].value);
    this.minOffTime = minOff;

    let input = this.getInputData(0);
    if (!Utils.hasInput(input)) input = false;
    if (!this.minOnEnabled && !this.minOffEnabled) {
      this.setOutputData(0, input);
      if (input && input != this.lastValue) {
        this.timeOn = Date.now();
        this.minOnEnabled = true;
        this.setOutputData(1, false, true);
      } else if (!input && input != this.lastValue) {
        this.timeOff = Date.now();
        this.minOffEnabled = true;
        this.setOutputData(2, false), true;
      }
      this.lastValue = input;
    }
  }

  onAfterSettingsChange() {
    this.inputs[1]['name'] = `[minOn] (${this.settings['timeMinOn'].value})`;
    this.inputs[2]['name'] = `[minOff] (${this.settings['timeMinOff'].value})`;
    this.onInputUpdated();
  }
}
Container.registerNodeType('boolean/min-on-off', MinOnOffNode);
