import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

const CronJob = require('cron').CronJob;
// const parser = require('cron-parser');
//const cronTime = require('cron-time-generator');
const jsonata = require('jsonata');
const construe = require('cronstrue');

export class CronIntervalNode extends Node {
  job = null;
  jobCronExp = '';
  timeoutFunc;
  constructor() {
    super();
    this.title = NODE_INFO.time["cron-interval"].title;
    this.description = NODE_INFO.time["cron-interval"].description;
    this.addInputWithSettings('enable', Type.BOOLEAN, false, 'Enable', false);
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('info', Type.STRING);
    this.addOutput('cronExpression', Type.STRING);
    this.addOutput('cronDescription', Type.STRING);
    this.addOutput('nextExecution', Type.STRING);

    this.settings['timezone'] = NodeSetting.create({
      description: 'Enter the cron time zone',
      value: 'Australia/Sydney',
      type: SettingType.STRING,
    });
    this.settings['cron-type'] = NodeSetting.create({
      description: 'Cron Type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: 'Every Minutes (At every xth minute)' },
          { value: 1, text: 'Every Hours (At minute x past every xth hour.)' },
          { value: 2, text: 'Every Days (At x:xx every x days)' },
          { value: 8, text: 'Every Day At Time (x:xx)' },
          { value: 3, text: 'Every Month (Start of every month)' },
          { value: 4, text: 'Every Month On (On x Day at x:xx)' },
          { value: 5, text: 'Every Year (Start of every year)' },
          { value: 6, text: 'Every Year On (On x month x day at x:xx)' },
          { value: 7, text: 'Between Days (At x:xx between x and x day of the month)' },
        ],
      },
      value: 0,
    });
    this.settings['min'] = NodeSetting.create({
      description: 'Set minute',
      value: 1,
      type: SettingType.NUMBER,
    });
    this.settings['hour'] = NodeSetting.create({
      description: 'Set hour',
      value: 13,
      type: SettingType.NUMBER,
    });
    this.settings['day'] = NodeSetting.create({
      description: 'Set day',
      value: 1,
      type: SettingType.NUMBER,
    });
    this.settings['dayBetweenStart'] = NodeSetting.create({
      description: 'Day Range Start',
      value: 1,
      type: SettingType.NUMBER,
    });
    this.settings['dayBetweenEnd'] = NodeSetting.create({
      description: 'Day Range End',
      value: 15,
      type: SettingType.NUMBER,
    });
    this.settings['month'] = NodeSetting.create({
      description: 'Set month',
      value: 1,
      type: SettingType.NUMBER,
    });

    this.setSettingsConfig({
      groups: [{ dayBetweenStart: {}, dayBetweenEnd: {} }],
      conditions: {
        min: setting => {
          const cronSetting = setting['cron-type'].value;
          return (
            cronSetting == 0 ||
            cronSetting == 1 ||
            cronSetting == 2 ||
            cronSetting == 4 ||
            cronSetting == 6 ||
            cronSetting == 7 ||
            cronSetting == 8
          );
        },
        hour: setting => {
          const cronSetting = setting['cron-type'].value;
          return (
            cronSetting == 1 ||
            cronSetting == 2 ||
            cronSetting == 4 ||
            cronSetting == 6 ||
            cronSetting == 7 ||
            cronSetting == 8
          );
        },
        day: setting => {
          const cronSetting = setting['cron-type'].value;
          return cronSetting == 2 || cronSetting == 4 || cronSetting == 6 || cronSetting == 7;
        },
        dayBetweenStart: setting => {
          const cronSetting = setting['cron-type'].value;
          return cronSetting == 7;
        },
        dayBetweenEnd: setting => {
          const cronSetting = setting['cron-type'].value;
          return cronSetting == 7;
        },
        month: setting => {
          const cronSetting = setting['cron-type'].value;
          return cronSetting == 6;
        },
      },
    });
  }

  onAdded() {
    clearInterval(this.timeoutFunc);
    this.setOutputData(0, false);
    this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }

  onRemoved() {
    //remove the job on delete of the node
    if (this.job) this.job.stop();
  }

  jsonataQuery(nextJobs) {
    //use jsonata to filter the next scheduled time
    let expression = jsonata('_d');
    return expression.evaluate(nextJobs);
  }

  onInputUpdated() {
    const enable = this.getInputData(0);
    const minute = parseInt(this.settings['min'].value);
    const hour = parseInt(this.settings['hour'].value);
    const day = parseInt(this.settings['day'].value);
    const dayBetweenStart = parseInt(this.settings['dayBetweenStart'].value);
    const dayBetweenEnd = parseInt(this.settings['dayBetweenEnd'].value);
    const month = parseInt(this.settings['month'].value);
    const timezone = this.settings['timezone'].value;
    const cronType = this.settings['cron-type'].value;
    let cronExp = null;

    if (enable) {
      cronExp = 'invalid';
      //build the cronjob string
      if (cronType == 0 && minute >= 0 && minute < 60) {
        // 'Every Minutes (At every xth minute)'
        // cronExp = cronTime.every(minute).minutes();
        cronExp = '*/' + minute + ' * * * *';
      } else if (cronType == 1 && minute >= 0 && minute < 60 && hour >= 0 && hour < 24) {
        // 'Every Hours (At minute x past every xth hour.)'
        //cronExp = cronTime.every(hour).hours();
        cronExp = minute + ' */' + hour + ' * * *';
      } else if (cronType == 2 && minute >= 0 && minute < 60 && hour >= 0 && hour < 24 && day > 0 && day <= 31) {
        // 'Every Days (At x:xx every x days)'
        //cronExp = cronTime.every(day).days(hour, minute);
        cronExp = minute + ' ' + hour + ' */' + day + ' * *';
      } else if (cronType == 3) {
        // 'Every Month (Start of every month)'
        //cronExp = cronTime.everyMonth();
        cronExp = '0 0 1 * *';
      } else if (cronType == 4 && minute >= 0 && minute < 60 && hour >= 0 && hour < 24 && day > 0 && day <= 31) {
        // 'Every Month On (On x Day at x:xx)'
        //cronExp = cronTime.everyMonthOn(day, hour, minute);
        cronExp = minute + ' ' + hour + ' ' + day + ' * *';
      } else if (cronType == 5) {
        // 'Every Year (Start of every year)'
        //cronExp = cronTime.everyYear();
        cronExp = '0 0 1 1 *';
      } else if (
        cronType == 6 &&
        minute >= 0 &&
        minute < 60 &&
        hour >= 0 &&
        hour < 24 &&
        day > 0 &&
        day <= 31 &&
        month >= 0 &&
        month <= 12
      ) {
        // 'Every Year On (On x month x day at x:xx)'
        //cronExp = cronTime.everyYearIn(month, day, hour, minute);
        cronExp = minute + ' ' + hour + ' ' + day + ' ' + month + ' *';
      } else if (
        cronType == 7 &&
        minute >= 0 &&
        minute < 60 &&
        hour >= 0 &&
        hour < 24 &&
        dayBetweenStart < dayBetweenEnd &&
        dayBetweenStart >= 1 &&
        dayBetweenStart <= 30 &&
        dayBetweenEnd >= 2 &&
        dayBetweenEnd <= 31
      ) {
        //'Between Days (Between  x - x th day of the month)'
        //cronExp = cronTime.between(day, dayBetween).days();
        cronExp = minute + ' ' + hour + ' ' + dayBetweenStart + '-' + dayBetweenEnd + ' * *';
      } else if (cronType == 8 && minute >= 0 && minute < 60 && hour >= 0 && hour < 24) {
        // 'Every Day At Time (x:xx)'
        cronExp = minute + ' ' + hour + ' * * *';
      }
    }
    if (!enable || cronExp == null || cronExp === 'invalid') {
      if (cronExp === 'invalid') this.setOutputData(1, 'invalid configuration');
      else this.setOutputData(1, 'CRON job stopped');
      try {
        if (this.job) this.job.stop();
      } catch (err) {
        this.setOutputData(1, err);
      }
      this.job = null;
      this.jobCronExp = '';
      this.setOutputData(0, false, true);
      this.setOutputData(2, null, true);
      this.setOutputData(3, null, true);
      this.setOutputData(4, null, true);
      return;
    }

    if (this.job && cronExp !== this.jobCronExp) {
      this.setOutputData(1, 'CRON job stopped');
      this.job.stop();
      this.job = null;
      this.jobCronExp = '';
    }
    try {
      this.jobCronExp = cronExp;
      this.setOutputData(2, cronExp);
      this.setOutputData(3, construe.toString(cronExp));
      this.job = new CronJob(
        cronExp,
        () => {
          this.setOutputData(0, true);
          this.timeoutFunc = setTimeout(() => {
            this.setOutputData(0, false);
          }, 500);
          //get next job
          let nextJobs = this.job.nextDates(1);
          this.setOutputData(4, this.jsonataQuery(nextJobs));
        },
        null,
        null,
        timezone,
      );
      setTimeout(function() {}, 500);
      this.setOutputData(1, 'CRON job started');
      this.job.start();
      let nextJobs = this.job.nextDates(1);
      this.setOutputData(4, this.jsonataQuery(nextJobs));
    } catch (err) {
      this.setOutputData(1, err);
    }
  }
}
Container.registerNodeType('time/cron-interval', CronIntervalNode);
