import { Link } from './node-link';
import { Deserializable, Serializable, SerializableHelper } from './serializable';
import Validation from './validation/Validation';

export enum Type {
  STRING   = 'string',
  NUMBER   = 'number',
  BOOLEAN  = 'boolean',
  ANY      = 'any',
  JSON     = 'json',
  DROPDOWN = 'dropdown', // it is same as `ANY`, but when it has setting as pair it checks for validity
}

export interface SerializedNodeIO {
  name: string;
  type: Type;
  label?: string;
  locked?: boolean;
  pos?: number;
  round?: boolean;
  subNodeId?: number | string;
}

export interface SerializedNodeInput extends SerializedNodeIO {
  link?: Link;
  isOptional?: boolean;
  setting: InputSetting;
}

export interface SerializedNodeOutput extends SerializedNodeIO {
  links?: Array<Link>;
}

export interface NodeIO extends SerializedNodeIO {
  data?: any;
  updated?: boolean;
}

export interface SerializedNodeSetting {

  description: string;
  type?: SettingType;
  value?: any;
  config?: any;
}

export class NodeSetting implements SerializedNodeSetting, Serializable<SerializedNodeSetting>,
                                    Deserializable<SerializedNodeSetting> {

  description: string;
  type?: SettingType;
  value?: any;
  config?: any;
  validation?: Validation;
  toJSON?: Function; // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/JSON/stringify#toJSON_behavior

  constructor(description: string, type?: SettingType, value?: any, config?: any, validation?: Validation,
              toJSON?: Function) {
    this.description = description;
    this.type = type;
    this.value = value;
    this.config = config;
    this.validation = validation;
    this.toJSON = toJSON;
  }

  static create(object: {
    description: string, type?: SettingType, value?: any, config?: any, validation?: Validation, toJSON?: Function
  }): NodeSetting {
    return new NodeSetting(object.description, object.type, object.value, object.config, object.validation,
                           object.toJSON);
  };

  serialize(): SerializedNodeSetting {
    return {
      description: this.description,
      type: this.type,
      config: SerializableHelper.serialize<object>(this.config),
      value: SerializableHelper.serialize<object>(this.value),
    };
  }

  deserialize(serializedObj: SerializedNodeSetting): NodeSetting {
    return NodeSetting.create(serializedObj);
  }
}

abstract class AbstractNodeIO implements NodeIO, Serializable<SerializedNodeIO> {

  name: string;
  type: Type;
  label?: string;
  locked?: boolean;
  pos?: number;
  round?: boolean;
  subNodeId?: number | string;
  data?: any;
  updated?: boolean;

  protected constructor(name: string, type: Type, label?: string, locked?: boolean, pos?: number,
                        round?: boolean, data?: any, updated?: boolean, subNodeId?: number | string) {
    this.name = name;
    this.type = type;
    this.label = label;
    this.locked = locked;
    this.pos = pos;
    this.round = round;
    this.subNodeId = subNodeId;
    this.data = data;
    this.updated = updated;
  }

  serialize(): SerializedNodeIO {
    return {
      name: this.name,
      type: this.type,
      label: this.label,
      locked: this.locked,
      pos: this.pos,
      round: this.round,
      subNodeId: this.subNodeId,
    } as SerializedNodeIO;
  }

}

export class NodeOutput extends AbstractNodeIO
  implements NodeIO, SerializedNodeOutput, Serializable<SerializedNodeOutput>, Deserializable<SerializedNodeOutput> {

  links?: Link[];

  constructor(name: string, type: Type, links?: Array<Link>, label?: string, locked?: boolean, pos?: number,
              round?: boolean, data?: any, updated?: boolean, subNodeId?: number | string) {
    super(name, type, label, locked, pos, round, data, updated, subNodeId);
    this.links = links;
  }

  static create(object: {
    name: string, type: Type, links?: Array<Link>, label?: string, locked?: boolean,
    pos?: number, round?: boolean, data?: any, updated?: boolean, subNodeId?: number | string
  }) {
    return new NodeOutput(object.name, object.type, object.links, object.label, object.locked, object.pos, object.round,
                          object.data, object.updated, object.subNodeId);
  }

  serialize(): SerializedNodeOutput {
    return { ...super.serialize(), links: this.links?.map(l => SerializableHelper.serialize(l)) };
  }

  deserialize(serializedObj: SerializedNodeOutput): NodeOutput {
    return NodeOutput.create({ ...serializedObj });
  }

}

export interface InputSetting {
  exist: boolean; // bind setting exist or not
  nullable: boolean; // whether null value is acceptable or not
  hidden: boolean;
}

export class NodeInput extends AbstractNodeIO
  implements NodeIO, SerializedNodeInput, Serializable<SerializedNodeInput>, Deserializable<SerializedNodeInput> {

  link?: Link;
  isOptional?: boolean;
  setting: InputSetting;

  constructor(name: string, type: Type, setting: InputSetting, link?: Link, isOptional?: boolean, label?: string,
              locked?: boolean, pos?: number, round?: boolean, data?: any, updated?: boolean,
              subNodeId?: number | string) {
    super(name, type, label, locked, pos, round, data, updated, subNodeId);
    this.link = link;
    this.isOptional = isOptional;
    this.setting = setting;
  }

  static create(object: {
    name: string, type: Type, setting: InputSetting, link?: Link, isOptional?: boolean, label?: string,
    locked?: boolean, pos?: number, round?: boolean, data?: any, updated?: boolean, subNodeId?: number | string
  }) {
    return new NodeInput(object.name, object.type, object.setting, object.link, object.isOptional, object.label,
                         object.locked, object.pos, object.round, object.data, object.updated, object.subNodeId);
  }

  serialize(): SerializedNodeInput {
    return {
      ...super.serialize(),
      link: SerializableHelper.serialize(this.link),
      setting: SerializableHelper.serialize(this.setting),
      isOptional: this.isOptional,
    } as SerializedNodeInput;
  }

  deserialize(serializedObj: SerializedNodeInput): NodeInput {
    return NodeInput.create({ ...serializedObj });
  }
}

export interface IInputInfo {
  input: NodeInput;
  slot: number;
  link_pos: [number, number];
  locked: boolean;
}

export interface IOutputInfo {
  output: NodeOutput;
  slot: number;
  link_pos: [number, number];
  locked: boolean;
}

export const convertStringToType = (type: string): Type => {
  switch (type) {
    case Type.STRING:
      return Type.STRING;
    case Type.NUMBER:
      return Type.NUMBER;
    case Type.BOOLEAN:
      return Type.BOOLEAN;
    case Type.JSON:
      return Type.JSON;
  }
  return Type.ANY;
};

export enum SettingType {
  STRING      = 'string',
  NUMBER      = 'number',
  BOOLEAN     = 'boolean',
  READONLY    = 'readonly',
  DROPDOWN    = 'dropdown',
  TREE_SELECT = 'treeselect',
  CODE_AREA   = 'codearea',
  PASSWORD    = 'password',
  GROUP       = 'group',
}

export const convertSettingType = (type: SettingType): Type => {
  switch (type) {
    case SettingType.STRING:
      return Type.STRING;
    case SettingType.NUMBER:
      return Type.NUMBER;
    case SettingType.BOOLEAN:
      return Type.BOOLEAN;
  }
  return Type.ANY;
};

export const convertType = (type: Type): SettingType => {
  switch (type) {
    case Type.STRING:
      return SettingType.STRING;
    case Type.NUMBER:
      return SettingType.NUMBER;
    case Type.BOOLEAN:
      return SettingType.BOOLEAN;
    case Type.DROPDOWN:
      return SettingType.DROPDOWN;
  }
  return SettingType.STRING;
};
