import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class NumbersAverageNode extends Node {
  values = [];
  lastReset: boolean;
  avg: number;
  constructor() {
    super();
    this.title = NODE_INFO.streams["stream-average"].title;
    this.description =
      `## Description\n ` +
      NODE_INFO.streams["stream-average"].description + ` \n ` +
      ` ## Reset to null\n ` +
      `***Reset to null*** if set to option true and a true value on the node ***reset*** this will reset the node ***output*** to a value of null, If set to false it will keep the last node ***output*** value \n `
    this.addInput('value', Type.NUMBER);
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('output', Type.NUMBER);
    this.settings['null'] = NodeSetting.create({
      description: 'Reset to null',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.lastReset = false;
  }

  onInputUpdated() {
    const reset = this.getInputData(1);
    let val = this.getInputData(0);
    const resetToNull = this.settings['null'].value;
    if (reset && !this.lastReset) {
      if (resetToNull) {
        this.setOutputData(0, null);
        this.values = [];
        this.lastReset = reset;
      } else {
        this.values = [];
        this.setOutputData(0, this.avg);
        this.lastReset = reset;
      }
      return;
    } else if (!reset && this.lastReset) {
      this.lastReset = reset;
      return;
    }
    if (val == null) return;
    this.values.push(val);
    let sum = this.values.reduce(function (a, b) {
      return a + b;
    });
    let avg = sum / this.values.length;
    this.avg = avg
    this.setOutputData(0, this.avg);
  }
}
Container.registerNodeType('streams/stream-average', NumbersAverageNode);
