import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class MathFloorNode extends Node {

  constructor() {
    super();

    this.title = NODE_INFO["num-transform"].floor.title;
    this.description = NODE_INFO["num-transform"].floor.description; 

    this.addInput('input', Type.NUMBER);
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    this.emitTransformedInput(x => Math.floor(x));
  }
}
Container.registerNodeType('num-transform/floor', MathFloorNode);
