import * as moment from 'moment-timezone';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';
import UuidUtils from '../../utils/uuid-utils';

class WeeklyScheduleNode extends Node {
  daysArray = ['sunday', 'monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday'];

  constructor() {
    super();
    this.title = NODE_INFO.schedule["weekly-schedule"].title;
    this.description = NODE_INFO.schedule["weekly-schedule"].description;

    this.addOutput('output', Type.BOOLEAN);

    this.settings['enable'] = NodeSetting.create({
      description: 'Schedule Enable',
      value: true,
      type: SettingType.BOOLEAN,
    });

    this.settings['stringScheduleName'] = NodeSetting.create({
      //description: 'Schedule Name String',
      description: 'Schedule Name',
      value: '',
      type: SettingType.STRING,
    });

    this.settings['timezone'] = NodeSetting.create({
      description: 'TIMEZONE: Enter the timezone',
      value: moment.tz.guess(),
      type: SettingType.STRING,
    });

    this.settings['startHour'] = NodeSetting.create({
      description: 'Start Time',
      value: 7,
      type: SettingType.DROPDOWN,
      config: {
        items: this.makeHoursDropdown(),
      },
    });
    this.settings['startMin'] = NodeSetting.create({
      description: '',
      value: 0,
      type: SettingType.DROPDOWN,
      config: {
        items: this.makeMinsDropdown(),
      },
    });

    this.settings['endHour'] = NodeSetting.create({
      description: 'End Time',
      value: 19,
      type: SettingType.DROPDOWN,
      config: {
        items: this.makeHoursDropdown(),
      },
    });
    this.settings['endMin'] = NodeSetting.create({
      description: '',
      value: 0,
      type: SettingType.DROPDOWN,
      config: {
        items: this.makeMinsDropdown(),
      },
    });

    this.settings['sunday'] = NodeSetting.create({
      description: 'sunday',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['monday'] = NodeSetting.create({
      description: 'monday',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['tuesday'] = NodeSetting.create({
      description: 'tuesday',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['wednesday'] = NodeSetting.create({
      description: 'wednesday',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['thursday'] = NodeSetting.create({
      description: 'thursday',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['friday'] = NodeSetting.create({
      description: 'friday',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['saturday'] = NodeSetting.create({
      description: 'saturday',
      value: false,
      type: SettingType.BOOLEAN,
    });

    this.settings['payload'] = NodeSetting.create({
      description: 'Schedule Payload',
      value: 0,
      type: SettingType.NUMBER,
    });

    this.setSettingsConfig({
      groups: [
        { startHour: {}, startMin: {} },
        { endHour: {}, endMin: {} },
      ],
    });
  }

  onAdded() {
    this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    var scheduleOutput = { weekly: {}, events: {} };
    if (!this.settings['enable'].value) return scheduleOutput;
    const schedID = UuidUtils.createUUID();
    const tzOffset = moment.tz(this.settings['timezone'].value).utcOffset();
    const offsetHours = Math.floor(tzOffset / 60);
    const offsetMins = tzOffset % 60;
    var rolloverFlag = 0;
    var startHourUTC = this.settings['startHour'].value - offsetHours;
    if (startHourUTC < 0) {
      rolloverFlag = -1;
      startHourUTC = startHourUTC + 24;
    } else if (startHourUTC > 24) {
      rolloverFlag = 1;
      startHourUTC = startHourUTC - 24;
    }
    var startMinsUTC = this.settings['startMin'].value - offsetMins;
    if (startMinsUTC < 0) startMinsUTC = startMinsUTC + 60;
    else if (startMinsUTC >= 60) startMinsUTC = startMinsUTC - 60;
    var endHourUTC = this.settings['endHour'].value - offsetHours;
    if (endHourUTC < 0) {
      endHourUTC = endHourUTC + 24;
    } else if (endHourUTC > 24) {
      endHourUTC = endHourUTC - 24;
    }
    var endMinsUTC = this.settings['endMin'].value - offsetMins;
    if (endMinsUTC < 0) endMinsUTC = endMinsUTC + 60;
    else if (endMinsUTC >= 60) endMinsUTC = endMinsUTC - 60;

    const sundayIndex = this.settings['sunday'].value && rolloverFlag == -1 ? 7 : 0;
    const saturdayIndex = this.settings['saturday'].value && rolloverFlag == 1 ? -1 : 6;
    var daysOutput = [];
    this.settings['sunday'].value ? daysOutput.push(this.daysArray[sundayIndex + rolloverFlag]) : null;
    this.settings['monday'].value ? daysOutput.push(this.daysArray[1 + rolloverFlag]) : null;
    this.settings['tuesday'].value ? daysOutput.push(this.daysArray[2 + rolloverFlag]) : null;
    this.settings['wednesday'].value ? daysOutput.push(this.daysArray[3 + rolloverFlag]) : null;
    this.settings['thursday'].value ? daysOutput.push(this.daysArray[4 + rolloverFlag]) : null;
    this.settings['friday'].value ? daysOutput.push(this.daysArray[5 + rolloverFlag]) : null;
    this.settings['saturday'].value ? daysOutput.push(this.daysArray[saturdayIndex + rolloverFlag]) : null;

    scheduleOutput.weekly = {
      [schedID]: {
        colour: '#FFFFFF',
        name: this.settings['stringScheduleName'].value || 'unknownSchedule',
        start: `${String(startHourUTC).padStart(2, '0')}:${String(startMinsUTC).padStart(2, '0')}`,
        end: `${String(endHourUTC).padStart(2, '0')}:${String(endMinsUTC).padStart(2, '0')}`,
        days: daysOutput,
        value: this.settings['payload'].value,
        id: schedID,
      },
    };
    this.setOutputData(0, scheduleOutput);
  }

  makeHoursDropdown() {
    var hoursArray = [];
    for (var h = 0; h < 24; h++) {
      hoursArray.push({ value: h, text: String(h).padStart(2, '0') });
    }
    return hoursArray;
  }

  makeMinsDropdown() {
    var minsArray = [];
    for (var m = 0; m < 60; m++) {
      minsArray.push({ value: m, text: String(m).padStart(2, '0') });
    }
    return minsArray;
  }
}

Container.registerNodeType('schedule/weekly-schedule', WeeklyScheduleNode);
