import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class TriggeredRamp extends Node {
  countUp: boolean;
  lastTrigger: boolean;
  lastReset: boolean;
  currentVal: number;

  constructor() {
    super();
    this.title = NODE_INFO.count["triggered-ramp"].title;
    this.description = NODE_INFO.count["triggered-ramp"].description;
    this.addInput('trigger', Type.BOOLEAN);
    this.addInputWithSettings('min', Type.NUMBER, 0, 'min', false);
    this.addInputWithSettings('max', Type.NUMBER, 10, 'max', false);
    this.addInputWithSettings('step', Type.NUMBER, 1, 'Step Size', false);
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('output', Type.NUMBER);
    this.settings['countOnFalse'] = NodeSetting.create({
      description: 'Also count on true->false?',
      value: false,
      type: SettingType.BOOLEAN,
    });

    this.settings['rampUpOnly'] = NodeSetting.create({
      description: 'Ramp Up Only',
      value: false,
      type: SettingType.BOOLEAN,
    });
  }

  onAdded() {
    this.countUp = false;
    this.lastTrigger = false;
    this.lastReset = false;
    this.currentVal = 0;
    this.onInputUpdated();
  }

  onInputUpdated() {
    const min = this.getInputData(1);
    const max = this.getInputData(2);

    this.currentVal = Number(this.currentVal);
    if (this.currentVal < min) this.currentVal = min;
    else if (this.currentVal > max) this.currentVal = max;

    const reset = this.getInputData(4);
    if (reset && !this.lastReset) this.currentVal = min;
    this.lastReset = reset;

    const trigger = this.getInputData(0);
    if (trigger != this.lastTrigger) {
      if (this.settings['countOnFalse'].value || trigger) {
        const step = this.getInputData(3);
        if (this.countUp && this.currentVal + step > max) {
          if (this.settings['rampUpOnly'].value) {
            this.countUp = true;
            this.currentVal = min - step;
          } else this.countUp = false;
        } else if (!this.countUp && this.currentVal - step < min) this.countUp = true;
        if (this.countUp) this.currentVal += step;
        else this.currentVal -= step;
      }
      this.lastTrigger = trigger;
    }

    this.setOutputData(0, this.currentVal, true); //out
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('count/triggered-ramp', TriggeredRamp);
