import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class SchedulePrestartFixedNode extends Node {
  enabled;
  nextStart;

  constructor() {
    super();
    this.title = NODE_INFO.widget["schedule-prestart-fixed"].title;
    this.description = NODE_INFO.widget["schedule-prestart-fixed"].description;
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addInput('temp', Type.NUMBER);
    this.addInput('SP', Type.NUMBER);
    this.addInput('scheduleNextStart', Type.NUMBER);
    this.addInput('reset', Type.NUMBER);
    this.addOutput('output', Type.STRING);

    this.settings['condition1_range1'] = NodeSetting.create({
      description: 'temp > (SP + ?):',
      value: 1,
      type: SettingType.NUMBER,
    });
    this.settings['condition1_range2'] = NodeSetting.create({
      description: 'temp <= (SP + ?)',
      value: 2.5,
      type: SettingType.NUMBER,
    });
    this.settings['condition1_time'] = NodeSetting.create({
      description: 'Prestart Time (Mins)',
      value: 20,
      type: SettingType.NUMBER,
    });

    this.settings['condition2_range1'] = NodeSetting.create({
      description: 'temp > (SP + ?):',
      value: 2.5,
      type: SettingType.NUMBER,
    });
    this.settings['condition2_range2'] = NodeSetting.create({
      description: 'temp <= (SP + ?)',
      value: 5,
      type: SettingType.NUMBER,
    });
    this.settings['condition2_time'] = NodeSetting.create({
      description: 'Prestart Time (Mins)',
      value: 40,
      type: SettingType.NUMBER,
    });

    this.settings['condition3_range1'] = NodeSetting.create({
      description: 'temp > (SP + ?):',
      value: 5,
      type: SettingType.NUMBER,
    });
    this.settings['condition3_range2'] = NodeSetting.create({
      description: 'temp <= (SP + ?)',
      value: 100,
      type: SettingType.NUMBER,
    });
    this.settings['condition3_time'] = NodeSetting.create({
      description: 'Prestart Time (Mins)',
      value: 60,
      type: SettingType.NUMBER,
    });

    this.settings['condition4_range1'] = NodeSetting.create({
      description: 'temp > (SP + ?):',
      value: null,
      type: SettingType.NUMBER,
    });
    this.settings['condition4_range2'] = NodeSetting.create({
      description: 'temp <= (SP + ?)',
      value: null,
      type: SettingType.NUMBER,
    });
    this.settings['condition4_time'] = NodeSetting.create({
      description: 'Prestart Time (Mins)',
      value: null,
      type: SettingType.NUMBER,
    });

    this.settings['condition5_range1'] = NodeSetting.create({
      description: 'temp > (SP + ?):',
      value: null,
      type: SettingType.NUMBER,
    });
    this.settings['condition5_range2'] = NodeSetting.create({
      description: 'temp <= (SP + ?)',
      value: null,
      type: SettingType.NUMBER,
    });
    this.settings['condition5_time'] = NodeSetting.create({
      description: 'Prestart Time (Mins)',
      value: null,
      type: SettingType.NUMBER,
    });

    this.settings['condition6_range1'] = NodeSetting.create({
      description: 'temp < (SP + ?):',
      value: -1,
      type: SettingType.NUMBER,
    });
    this.settings['condition6_range2'] = NodeSetting.create({
      description: 'temp >= (SP + ?)',
      value: -2.5,
      type: SettingType.NUMBER,
    });
    this.settings['condition6_time'] = NodeSetting.create({
      description: 'Prestart Time (Mins)',
      value: 30,
      type: SettingType.NUMBER,
    });

    this.settings['condition7_range1'] = NodeSetting.create({
      description: 'temp < (SP + ?):',
      value: -2.5,
      type: SettingType.NUMBER,
    });
    this.settings['condition7_range2'] = NodeSetting.create({
      description: 'temp >= (SP + ?)',
      value: -5,
      type: SettingType.NUMBER,
    });
    this.settings['condition7_time'] = NodeSetting.create({
      description: 'Prestart Time (Mins)',
      value: 40,
      type: SettingType.NUMBER,
    });

    this.settings['condition8_range1'] = NodeSetting.create({
      description: 'temp < (SP + ?):',
      value: -5,
      type: SettingType.NUMBER,
    });
    this.settings['condition8_range2'] = NodeSetting.create({
      description: 'temp >= (SP + ?)',
      value: -100,
      type: SettingType.NUMBER,
    });
    this.settings['condition8_time'] = NodeSetting.create({
      description: 'Prestart Time (Mins)',
      value: 60,
      type: SettingType.NUMBER,
    });

    this.settings['condition9_range1'] = NodeSetting.create({
      description: 'temp < (SP + ?):',
      value: null,
      type: SettingType.NUMBER,
    });
    this.settings['condition9_range2'] = NodeSetting.create({
      description: 'temp >= (SP + ?)',
      value: null,
      type: SettingType.NUMBER,
    });
    this.settings['condition9_time'] = NodeSetting.create({
      description: 'Prestart Time (Mins)',
      value: null,
      type: SettingType.NUMBER,
    });

    this.settings['condition10_range1'] = NodeSetting.create({
      description: 'temp < (SP + ?):',
      value: null,
      type: SettingType.NUMBER,
    });
    this.settings['condition10_range2'] = NodeSetting.create({
      description: 'temp >= (SP + ?)',
      value: null,
      type: SettingType.NUMBER,
    });
    this.settings['condition10_time'] = NodeSetting.create({
      description: 'Prestart Time (Mins)',
      value: null,
      type: SettingType.NUMBER,
    });

    this.setSettingsConfig({
      groups: [
        { condition1_range1: {}, condition1_range2: {}, condition1_time: {} },
        { condition2_range1: {}, condition2_range2: {}, condition2_time: {} },
        { condition3_range1: {}, condition3_range2: {}, condition3_time: {} },
        { condition4_range1: {}, condition4_range2: {}, condition4_time: {} },
        { condition5_range1: {}, condition5_range2: {}, condition5_time: {} },
        { condition6_range1: {}, condition6_range2: {}, condition6_time: {} },
        { condition7_range1: {}, condition7_range2: {}, condition7_time: {} },
        { condition8_range1: {}, condition8_range2: {}, condition8_time: {} },
        { condition9_range1: {}, condition9_range2: {}, condition9_time: {} },
        { condition10_range1: {}, condition10_range2: {}, condition10_time: {} },
      ],
    });
  }

  onCreated() {
    this.enabled = false;
    this.EXECUTE_INTERVAL = 30000;
  }

  onAdded() {
    this.EXECUTE_INTERVAL = 30000;
    this.onInputUpdated();
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
    this.onExecute();
  }

  onExecute() {
    const now = new Date().valueOf();
    const enable = this.getInputData(0);
    if (!enable || this.nextStart == null || this.nextStart - now < 0) {
      this.setOutputData(0, false);
      this.enabled = false;
      return;
    }
    if (this.enabled) return;
    const temp = this.getInputData(1);
    const setpoint = this.getInputData(2);
    if (temp == null || setpoint == null) return;
    for (var i = 1; i <= 10; i++) {
      var fromRange = this.settings[`condition${i}_range1`].value;
      var toRange = this.settings[`condition${i}_range2`].value;
      var time = this.settings[`condition${i}_time`].value;
      if (fromRange == null || toRange == null || time == null) continue;
      time = TimeUtils.timeConvert(time, 'minutes');
      if (this.nextStart - now <= time) {
        if (i <= 5 && temp > setpoint + fromRange && temp <= setpoint + toRange) {
          this.enabled = true;
          this.setOutputData(0, true);
          return;
        }
        if (i > 5 && temp < setpoint + fromRange && temp >= setpoint + toRange) {
          this.enabled = true;
          this.setOutputData(0, true);
          return;
        }
      }
    }
  }

  onInputUpdated() {
    const enable = this.getInputData(0);
    if (!enable) {
      this.setOutputData(0, false);
      this.enabled = false;
      return;
    }
    const reset = this.getInputData(4);
    if (reset && this.inputs[4].updated) {
      this.enabled = false;
      this.setOutputData(0, false);
    }
    const nextStart = this.getInputData(3);
    if (nextStart == null || nextStart !== this.nextStart) {
      this.nextStart = nextStart;
      this.setOutputData(0, false);
      this.enabled = false;
    }
  }
}
Container.registerNodeType('widget/schedule-prestart-fixed', SchedulePrestartFixedNode);
