import { DispatchNode } from './DispatchNode';

export const UNKNOWN_POINT_NAME = 'Unknown';

export interface Point {
  msg: any;
  name: string;
  listeners: DispatchNode[];
  soft: boolean;
}

export interface Points {
  [point: string]: Point;
}

export interface Store {
  [protocol: string]: { points: Points };
}

export interface IPubSub {
  getStore(): Store;

  /**
   * Add point on a protocol whose output will be subscribed by listeners
   * @param {string} protocol - protocol for making things on different selection
   * @param {string} point - unique point id `${cid}/${id}`
   * @param {string} name - display name for point
   * @param {boolean} soft - true when it's real, false when it's unreal added from DispatchNode
   * soft is added for subscribing to the topic when topic is not added
   */
  addPoint(protocol: string, point: string, name: string, soft: boolean): void;

  /**
   * Remove added input point from it's protocol
   * @param {string} protocol - protocol for making things on different selection
   * @param point - unique point id `${cid}/${id}`
   */
  removePoint(protocol: string, point: string): void;

  /**
   * When `name` gets changed we need to notify it's changes to all subscriber
   * @param {string} protocol - protocol for making things on different selection
   * @param {string} point - unique point id `${cid}/${id}`
   * @param {string} name - display name for point
   */
  renamePoint(protocol: string, point: string, name: string): void;
  /**
   * Dispatch value for listeners
   * @param {string} protocol - protocol for making things on different selection
   * @param {string} point - unique point id `${cid}/${id}`
   * @param {any} value - value to send to it's listener
   */
  dispatch(protocol: string, point: string, value: any): void;

  /**
   * Add node for subscription on particular protocol and point
   * @param {string} protocol - protocol for making things on different selection
   * @param {string} point - unique point id `${cid}/${id}`
   * @param {string} name - display name for point
   * @param {DispatchNode} node - listener node for the registration
   */
  connect(protocol: string, point: string, name: string, node: DispatchNode): void;

  /**
   * Remove node from the subscription
   * @param {string} protocol - protocol for making things on different selection
   * @param {string} point - unique point id `${cid}/${id}`
   * @param {DispatchNode} node - listener node for the registration
   */
  disconnect(protocol: string, point: string, node: DispatchNode): void;
}
