import { isEmpty } from '../utils/helper';

export const TWEAK_POS: [number, number] = [50, 100];

export class Shape2D {
  /**
   * Calculate distance
   * @param pos1
   * @param pos2
   * @returns {number}
   */
  static distance(pos1: [number, number], pos2: [number, number]): number {
    const relative = Shape2D.relative(pos1, pos2);
    return Math.sqrt(Math.pow(relative[0], 2) + Math.pow(relative[1], 2));
  }

  static relative(pos1: [number, number], pos2: [number, number]): [number, number] {
    const p1 = Shape2D.guard(pos1);
    const p2 = Shape2D.guard(pos2);
    return [p2[0] - p1[0], p2[1] - p1[1]];
  }

  static bottomRight(size: [number, number], pos: [number, number]): [number, number] {
    const s = Shape2D.guard(size);
    const p = Shape2D.guard(pos);
    return [p[0] + s[0], p[1] + s[1]];
  }

  static move(relative: [number, number], origin: [number, number]): [number, number];
  static move(relative: [number, number], origin: [number, number], size: [number, number]): [number, number];
  static move(relative: [number, number], origin: [number, number], size?: [number, number]): [number, number] {
    const p1 = Shape2D.bottomRight(size, origin);
    const p2 = Shape2D.guard(relative);
    return [p1[0] + p2[0], p1[1] + p2[1]];
  }

  static avgPos(positions: [number, number][]): [number, number] {
    return positions.map(pos => Shape2D.guard(pos))
                    .reduce((avg, pos, _, { length }) => [avg[0] + pos[0] / length, avg[1] + pos[1] / length], [0, 0]);
  }

  static isInside(size: [number, number], pos: [number, number], eventPos: [number, number]): boolean {
    const bottomRightPos = this.bottomRight(size, pos);
    return eventPos[0] >= pos[0] && eventPos[0] <= bottomRightPos[0] &&
           eventPos[1] >= pos[1] && eventPos[1] <= bottomRightPos[1];
  }

  static guard(pos: [number, number], fallbackX: number = 0, fallbackY: number = 0): [number, number] {
    const x = fallbackX ?? 0;
    const y = fallbackY ?? 0;
    if (isEmpty(pos)) {
      return [x, y];
    }
    return [pos[0] ?? x, pos[1] ?? y];
  }

  /**
   * Compute is shape inside a rectangle
   * @param x
   * @param y
   * @param left
   * @param top
   * @param width
   * @param height
   * @returns {boolean}
   */
  static isInsideRectangle(x: number, y: number, left: number, top: number, width: number, height: number): boolean {
    return left < x && left + width > x && top < y && top + height > y;
  }

  /**
   * Compute is bounding overlap
   * @param a
   * @param b
   * @returns {boolean}
   */
  static overlapBounding([aLeft, aBottom, aRight, aTop]: [number, number, number, number],
                         [bLeft, bBottom, bRight, bTop]: [number, number, number, number]): boolean {
    return !(aLeft > bRight || aBottom > bTop || aRight < bLeft || aTop < bBottom);
  }

  /**
   * Solve quadratic equation
   * @param a
   * @param b
   * @param c
   * @param d
   * @returns {x1: number, x2: number, x3: number}
   */
  static quadraticEquationSolver(a: number, b: number, c: number, d: number): { x1: number, x2: number, x3: number } {
    const delta = b * b - 3 * a * c;
    const k = (9 * a * b * c - 2 * b * b * b - 27 * a * a * d) / (2 * Math.sqrt(Math.pow(Math.abs(delta), 3)));
    let result;
    if (delta > 0) {
      if (Math.abs(k) <= 1) {
        result = {
          x1: (2 * Math.sqrt(delta) * Math.cos(Math.acos(k) / 3) - b) / (3 * a),
          x2: (2 * Math.sqrt(delta) * Math.cos((Math.acos(k) / 3) - (2 * Math.PI) / 3) - b) / (3 * a),
          x3: (2 * Math.sqrt(delta) * Math.cos((Math.acos(k) / 3) + (2 * Math.PI) / 3) - b) / (3 * a),
        };
      }
      if (Math.abs(k) > 1) {
        const x = ((Math.sqrt(delta) * Math.abs(k)) / (3 * a * k)) *
                  (Math.cbrt(Math.abs(k) + Math.sqrt(k * k - 1)) + Math.cbrt(Math.abs(k) - Math.sqrt(k * k - 1))) - b /
                  (3 * a);
        result = { x1: x, x2: x, x3: x };
      }
    }
    if (delta === 0) {
      const x = (-b + Math.cbrt(b * b * b - 27 * a * a * d)) / (3 * a);
      result = { x1: x, x2: x, x3: x };
    }
    if (delta < 0) {
      const x = (Math.sqrt(Math.abs(delta)) / (3 * a)) *
                (Math.cbrt(k + Math.sqrt(k * k + 1)) + Math.cbrt(k - Math.sqrt(k * k + 1))) - b / (3 * a);
      result = { x1: x, x2: x, x3: x };
    }
    return result;
  }
}
