import { Node } from '../nodes/node';
import { INode, SerializedNode } from '../nodes/node-interface';
import { RegistryResult } from '../nodes/registry';
import { SerializableHelper } from '../nodes/serializable';
import { RegistryError } from '../utils/errors';
import { isEmpty } from '../utils/helper';

abstract class ETLResult extends RegistryResult<SerializedNode> {
  readonly containerId: number;
  readonly total: number;
  readonly message: string;

  protected constructor(containerId: number, total: number, objects: SerializedNode[],
                        errors: RegistryError<SerializedNode>[]) {
    super(objects, errors);
    this.containerId = containerId;
    this.total = total;
    this.message = this.genMessage();
  }

  static init<T extends ETLResult>(className: any, cId: number, total: number, result: RegistryResult<Node>,
                                   args?: {}): T {
    const nodes = result.objects.map(n => n.serialize());
    const errors = result.errors.map(e => new RegistryError(e.message, e.item?.serialize()));
    return new className(cId, total, nodes, errors, args);
  }

  toString(): string {
    return this.message;
  }

  protected errMsg(): string {
    return isEmpty(this.errors) ? '' : `Errors: [${this.errors}]`;
  }

  protected abstract genMessage(): string;
}

export class CreationResult {
  readonly node: SerializedNode;
  readonly message: string;
  readonly isDashboardNode: boolean;

  constructor(node: INode) {
    this.node = SerializableHelper.serialize(node);
    this.isDashboardNode = (<Node>node)?.isDashboardNode;
    this.message = `Created node ${(<Node>node)?.getReadableId()} into container ${node.cid}.`;
  }

}

export class RemoveResult extends ETLResult {

  protected genMessage(): string {
    return `Removed ${this.objects.length}/${this.total} nodes into container ${this.containerId}. ${this.errMsg()}`;
  }

}

export class ExportResult extends ETLResult {

  constructor(containerId: number, objects: SerializedNode[]) {
    super(containerId, 0, objects, null);
  }

  protected genMessage() {
    return `Exported ${this.objects.length} nodes and sub nodes into container ${this.containerId}.`;
  }
}

export class ImportResult extends ETLResult {

  protected genMessage() {
    return `Imported ${this.objects.length}/${this.total} nodes into container ${this.containerId}. ${this.errMsg()}`;
  }
}

export class CloneResult extends ETLResult {

  protected genMessage() {
    return `Cloned ${this.objects.length}/${this.total} nodes into container ${this.containerId}. ${this.errMsg()}`;
  }
}

export class PackResult extends ETLResult {

  readonly cleanupResult: RemoveResult;
  private readonly cid: number;

  constructor(containerId: number, total: number, objects: SerializedNode[], errors: RegistryError<SerializedNode>[],
              args: { cid: number, rr: RemoveResult }) {
    super(containerId, total, objects, errors);
    this.cid = args.cid;
    this.cleanupResult = args.rr;
  }

  protected genMessage() {
    return `Moved ${this.objects.length - 1}/${this.total} nodes ` +
           `from ${this.containerId} into container ${this.cid}. ${this.errMsg()}`;
  }
}

export const initETLResult = <T>(className: any, cId: number, total: number, result: RegistryResult<Node>,
                                 args?: {}): T => {
  const nodes = result.objects.map(n => SerializableHelper.serialize(n));
  const errors = result.errors.map(e => new RegistryError(e.message, SerializableHelper.serialize(e.item)));
  return new className(cId, total, nodes, errors, args);
};
