import { Node } from '../../node';
import { Type } from '../../node-io';
import { Container } from '../../container';
import { NODE_INFO } from '../../node-info';

export class BufferToHexString extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.conversion["buffer-to-hex-string"].title;
    this.description = NODE_INFO.conversion["buffer-to-hex-string"].description;

    this.addInput('input', Type.ANY);
    this.addOutput('output', Type.STRING);
  }

  onInputUpdated() {
    let buffer = this.getInputData(0);

    if (!buffer || !Buffer.isBuffer(buffer)) {
      return;
    }

    // console.log(buffer);
    let str = buffer.toString('hex').toUpperCase();
    // console.log(str);
    this.setOutputData(0, str);
  }
}
Container.registerNodeType('conversion/buffer-to-hex-string', BufferToHexString);
