import { exec } from 'child_process';
import * as fs from 'fs';
import { Container } from '../../../container';
import { Node } from '../../../node';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import CryptoUtils from '../../../utils/crypto-utils';
import { NODE_INFO } from '../../../node-info';

class MqttBroker extends Node {
  constructor() {
    super();
    this.title = NODE_INFO["protocols/mqtt-broker"]["mqtt-broker"].title;
    this.description = NODE_INFO["protocols/mqtt-broker"]["mqtt-broker"].description;

    this.addInput('ping broker', Type.STRING);
    this.addOutput('status', Type.STRING);
    this.addOutput('error', Type.STRING);
    this.addOutput('debug', Type.STRING);

    this.settings['enable'] = NodeSetting.create({
      description: 'Broker enable',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['authentication'] = NodeSetting.create({
      description: 'Use Authentication',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['username'] = NodeSetting.create({ description: 'User name', value: '', type: SettingType.STRING });
    this.settings['password'] = NodeSetting.create({ description: 'Password', value: '', type: SettingType.PASSWORD });
    //this.settings['password'] = NodeSetting.create({ description: 'Password', value: '', type: SettingType.STRING });

    this.setSettingsConfig({
      groups: [
        { enable: {}, authentication: {} },
        { username: {}, password: {} },
      ],
      conditions: {
        username: setting => {
          return !!setting['authentication'].value;
        },
        password: setting => {
          return !!setting['authentication'].value;
        },
      },
    });

    this.properties['useAuthentication'];
  }

  async onRemoved() {
    // super.onRemoved();
    await this.stopBroker()
      .then(e => {
        console.log(e);
      })
      .catch(err => {
        console.log(err);
      });
  }

  onInputUpdated() {
    if (this.side !== Side.server) return; // only execute on server
    const username = this.settings['username'].value;
    const password = this.settings['password'].value;
    const decryptPass = { username, password: CryptoUtils.decrypt(password) };
    console.log('password', decryptPass);

    this.statusBroker()
      .then(e => {
        this.setOutputData(0, e);
        this.setOutputData(1, false);
      })
      .catch(err => {
        this.setOutputData(0, false);
        this.setOutputData(1, true);
      });
  }

  onAfterSettingsChange() {
    const enable = this.settings['enable'].value;
    if (enable) {
      this.startBroker()
        .then(e => {
          this.onInputUpdated(); //call to get broker status (TODO not the best but to busy to care)
        })
        .catch(err => {
          this.setOutputData(0, false);
          this.setOutputData(1, true);
        });

      this.checkAuthentication();
    } else if (enable === false) {
      this.stopBroker()
        .then(e => {
          this.onInputUpdated(); //call to get broker status
        })
        .catch(err => {
          this.onInputUpdated(); //call to get broker status
        });
    }
  }

  stopBroker() {
    return new Promise((resolve, reject) => {
      let command = `sudo systemctl stop mosquitto`;
      exec(command, (err, stdout, stderr) => {
        if (err) {
          reject({ err, stderr });
        } else {
          resolve(stdout);
        }
      });
    });
  }

  statusBroker() {
    return new Promise((resolve, reject) => {
      let command = `ps -A | grep -i mosquitto | grep -v grep`;
      exec(command, (err, stdout, stderr) => {
        if (err) {
          reject(false);
        } else {
          resolve(true);
        }
      });
    });
  }

  startBroker() {
    return new Promise((resolve, reject) => {
      let command = `sudo systemctl start mosquitto`;
      exec(command, (err, stdout, stderr) => {
        if (err) {
          console.log(err);
          reject({ err, stderr });
        } else {
          resolve(stdout);
        }
      });
    });
  }

  checkAuthentication() {
    const path = '/etc/mosquitto/';
    const configFile = 'mosquitto.conf';
    const passwordFile = 'MQTTPasswords.txt';
    if (this.settings['authentication'].value && !this.properties['useAuthentication']) {
      this.properties['useAuthentication'] = this.settings['authentication'].value;
      const username = this.settings['username'].value;
      const password = this.settings['password'].value;
      if (!username || !password) {
        this.setOutputData(1, 'Invalid Credentials');
        this.debugErr('Invalid Credentials');
        return;
      }
      const command = 'sudo mosquitto_passwd -U ' + path + passwordFile;
      fs.writeFile(path + passwordFile, username + ':' + password, err => {
        if (err) {
          this.setOutputData(1, err);
          this.debugErr(err);
          return;
        }
        try {
          exec(command, (err, stdout, stderr) => {
            if (stdout) {
              this.setOutputData(1, stdout);
              this.debugErr(stdout);
              return;
            }
            if (err) {
              this.setOutputData(1, err);
              this.debugErr(err);
              return;
            }
            if (stderr) {
              this.setOutputData(1, stderr);
              this.debugErr(stderr);
              return;
            }
          });
          fs.readFile(path + configFile, (err, buff) => {
            if (err) {
              this.setOutputData(1, err);
              this.debugErr(err);
              return;
            }
            let text = buff.toString('utf8');
            this.setOutputData(2, text);
            const allowAnonymousIndex = text.indexOf('allow_anonymous ');
            if (allowAnonymousIndex >= 0) {
              const allowAnonymous = text.slice(allowAnonymousIndex, allowAnonymousIndex + 22);
              let allowAnonymousArray = allowAnonymous.split(' ');
              if (allowAnonymousArray[1] == 'true') allowAnonymousArray[1] = 'false';
              text.replace(
                allowAnonymous,
                allowAnonymousArray[0] + ' ' + allowAnonymousArray[1] + '       \n',
              );
            } else {
              text += '\nallow_anonymous true     \n';
            }
            const passwordRefIndex = text.indexOf('password_file ');
            if (passwordRefIndex >= 0) {
              const passwordRef = text.slice(passwordRefIndex, passwordRefIndex + 50);
              let passwordRefArray = passwordRef.split(' ');
              if (passwordRefArray[1] !== path + passwordFile) {
                passwordRefArray[1] = path + passwordFile;
                text = text.replace(
                  passwordRef,
                  passwordRefArray[0] + ' ' + passwordRefArray[1] + '       \n',
                );
              }
            } else {
              text += '\npassword_file /etc/mosquitto/MQTTPasswords.txt     \n';
            }
            fs.writeFile(path + configFile, text, err => {
              if (err) {
                this.setOutputData(1, err);
                this.debugErr(err);
                return;
              }
            });
            exec('sudo systemctl restart mosquitto', (err, stdout, stderr) => {
              if (stdout) {
                this.setOutputData(1, stdout);
                this.debugErr(stdout);
                return;
              }
              if (err) {
                this.setOutputData(1, err);
                this.debugErr(err);
                return;
              }
              if (stderr) {
                this.setOutputData(1, stderr);
                this.debugErr(stderr);
                return;
              }
            });
          });
        } catch (e) {
          this.setOutputData(1, e);
          this.debugErr(e);
          return;
        }
      });
    } else if (!this.settings['authentication'].value && this.properties['useAuthentication']) {
      this.properties['useAuthentication'] = this.settings['authentication'].value;
      fs.unlink(path + passwordFile, err => {
        if (err) {
          this.setOutputData(2, err);
          this.debugErr(err);
          return;
        }
      });
      fs.readFile(path + configFile, (err, buff) => {
        if (err) {
          this.setOutputData(1, err);
          this.debugErr(err);
          return;
        }
        let text = buff.toString('utf8');
        this.setOutputData(2, text);
        const allowAnonymousIndex = text.indexOf('allow_anonymous ');
        if (allowAnonymousIndex >= 0) {
          const allowAnonymous = text.slice(allowAnonymousIndex, allowAnonymousIndex + 22);
          let allowAnonymousArray = allowAnonymous.split(' ');
          if (allowAnonymousArray[1] == 'false') allowAnonymousArray[1] = 'true';
          text = text.replace(
            allowAnonymous,
            allowAnonymousArray[0] + ' ' + allowAnonymousArray[1] + '   ',
          );
        } else {
          text += '\nallow_anonymous false     \n';
        }
        fs.writeFile(path + configFile, text, err => {
          if (err) {
            this.setOutputData(1, err);
            this.debugErr(err);
            return;
          }
        });
        exec('sudo systemctl restart mosquitto', (err, stdout, stderr) => {
          if (stdout) {
            this.setOutputData(1, stdout);
            this.debugErr(stdout);
            return;
          }
          if (err) {
            this.setOutputData(1, err);
            this.debugErr(err);
            return;
          }
          if (stderr) {
            this.setOutputData(1, stderr);
            this.debugErr(stderr);
            return;
          }
        });
      });
    }
  }
}

Container.registerNodeType('protocols/mqtt-broker/mqtt-broker', MqttBroker);
