import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import jsonStringCompare from '../../utils/string-json-compare-utils';
import { NODE_INFO } from '../../node-info';

enum compareType {
  Equals = 'Equals',
  NotEquals = 'Not Equal',
  GreaterThanEqual = 'Greater Than Equal',
  GreaterThan = 'Greater Than',
  LessThanEqual = 'Less Than Equal',
  LessThan = 'Less Than',
  Includes = 'Includes',
  startsWith = 'Starts With',
  endsWith = 'Ends With'
}

class JsonFilterCompare extends Node {
  conversionFunction;
  constructor() {
    super();
    this.title = NODE_INFO.json["json-filter-compare"].title;
    this.description = NODE_INFO.json["json-filter-compare"].description; 
    this.addInput('input', Type.STRING);
    this.addInputWithSettings('comparison-to', Type.STRING, '', 'Comparison Value');
    this.addOutput('output', Type.STRING);
    this.addOutput('output-key', Type.STRING);
    this.addOutput('match', Type.BOOLEAN);
    this.settings['filter'] = NodeSetting.create({
      description: 'Example: msg.myKey',
      value: 'myKey',
      type: SettingType.STRING,
    });
    this.settings['function'] = NodeSetting.create({
      description: 'Comparison Type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: compareType.Equals, text: compareType.Equals },
          { value: compareType.NotEquals, text: compareType.NotEquals },
          { value: compareType.GreaterThanEqual, text: compareType.GreaterThanEqual },
          { value: compareType.GreaterThan, text: compareType.GreaterThan },
          { value: compareType.LessThanEqual, text: compareType.LessThanEqual },
          { value: compareType.LessThan, text: compareType.LessThan },
          { value: compareType.Includes, text: compareType.Includes },
          { value: compareType.startsWith, text: compareType.startsWith },
          { value: compareType.endsWith, text: compareType.endsWith },
        ],
      },
      value: compareType.Equals,
    });
    this.settings['isNumber'] = NodeSetting.create({
      description: 'Comparison value is a number',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['passNull'] = NodeSetting.create({
      description: 'Send null if not a match',
      value: false,
      type: SettingType.BOOLEAN,
    });
  }

  onAdded() {
  this.onAfterSettingsChange()
  }


  onInputUpdated() {
    const input = this.getInputData(0);
    if (input === null || input === undefined) {
      this.setOutputData(0, null);
      this.setOutputData(1, null);
      this.setOutputData(2, null);
      return;
    }
    try {
      const jsonCheck = JSON.parse(input);
      const filter = this.settings['filter'].value;
      let out = jsonStringCompare.findVal(jsonCheck, filter);
      const string2 = this.getInputData(1);
      if (jsonStringCompare.compare(this, out.value, string2)) {
        this.setOutputData(0, out.value);
        this.setOutputData(1, out.valueWithKey);
        this.setOutputData(2, true);
      } else if (this.settings['passNull'].value === true) {
        this.setOutputData(0, null);
        this.setOutputData(1, null);
        this.setOutputData(2, false);
      } else this.setOutputData(2, false);
    } catch (e) {
      this.setOutputData(0, null);
      this.setOutputData(1, null);
      this.setOutputData(2, true);
    }
  }
  onAfterSettingsChange() {
    this.onInputUpdated();
    const inputType = this.settings['function'].value
    const title = "Json Comparison"
    this.title = `${title} Compare (${inputType})`;
    this.broadcastTitleToClients();
  }

}
Container.registerNodeType('json/json-filter-compare', JsonFilterCompare);
