import axios from 'axios';
import { EDGE_28_NETWORK } from '../../../constants';
import { Container } from '../../../container';
import { ContainerNode } from '../../../container-node';
import { Node } from '../../../node';
import Icons from '../../../node-icons';
import { NODE_INFO } from '../../../node-info';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import Utils from '../../../utils';
import TimeUtils from '../../../utils/time-utils';
import { ADD_POINT, edgeApiVer, edgeIp, edgePort, REMOVE_POINT } from './edge-constant';
import Edge28Utils from './edge-utils';

class Edge28ApiNode extends ContainerNode {
  // point object
  pointNodes: Array<Node> = [];
  edgeReadDI_Store: {};
  edgeReadUI_Store: {};

  // node inputs
  inAlarmTrigger = 0;
  inHistoryTrigger = 1;
  inInput = 2;

  // node outputs
  outVal = 0;
  outError = 1;
  outMessageJson = 2;
  outAlarm = 3;

  lastInterval = 1000;
  runState: boolean = true;
  UI_timeoutFunc;
  DI_timeoutFunc;
  _ui = 'ui';
  _di = 'di';

  constructor() {
    super();
    this.title = NODE_INFO["protocols/nube"]["edge-28-network"].title;
    this.description = NODE_INFO["protocols/nube"]["edge-28-network"].description; 
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addInputWithSettings('interval', Type.NUMBER, 2, 'Polling Interval');
    this.addOutput('connected', Type.BOOLEAN);
    this.addOutput('error', Type.STRING);
    if (this.side !== Side.server) {
      this.iconImageUrl = Icons.frog;
    }
    this.headerColor = '#184840';
    this.settings['time'] = NodeSetting.create({
      description: 'Polling Interval Time Setting',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          // { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
  }

  async fetchPointValue(host: string, port: number, apiVer: string, pointType: string): Promise<any> {
    const url = `${Utils.buildUrl(host, port)}/api/${apiVer}/read/all/${pointType}`;
    const pointValue = await axios.get(url);
    return pointValue.data;
  }

  async onAdded() {
    super.onAdded();
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
    clearInterval(this.UI_timeoutFunc);
    clearInterval(this.DI_timeoutFunc);
    let enable = this.getInputData(0);
    if (!enable) {
      this.setOutputData(0, false); //connected
      this.runState = false;
      return;
    }
    let interval = this.getInputData(1);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    this.startInputPolling(interval);
    await this.onInputUpdated();
  }

  async onRemoved() {
    super.onRemoved();
    clearInterval(this.UI_timeoutFunc);
    clearInterval(this.DI_timeoutFunc);
  }

  startInputPolling(interval: number) {
    if (this.side !== Side.server) return;
    this.UI_timeoutFunc = setInterval(async () => {
      // get UIs
      await this.pollUIs();
      this.sendPayloadToPointNodesFunc();
    }, interval);
    this.DI_timeoutFunc = setInterval(async () => {
      // get DIs
      await this.pollDIs();
      this.sendPayloadToPointNodesFunc();
    }, interval + 5);
  }

  async pollUIs() {
    try {
      this.edgeReadUI_Store = await this.fetchPointValue(edgeIp, edgePort, edgeApiVer, this._ui);
      this.setOutputData(0, true); //connected
      this.setOutputData(1, false); //error
      // NodeColours.nodeColourChange(this, NodeState.ONLINE);
    } catch (err) {
      this.debugInfo(`ERROR: getting edge point type: ${this._ui} ${err}`);
      this.setOutputData(0, false); //connected
      this.setOutputData(1, true); //error
      // NodeColours.nodeColourChange(this, NodeState.OFFLINE);
    }
  }

  async pollDIs() {
    try {
      this.edgeReadDI_Store = await this.fetchPointValue(edgeIp, edgePort, edgeApiVer, this._di);
      this.setOutputData(0, true); //connected
      this.setOutputData(1, false); //error
      // NodeColours.nodeColourChange(this, NodeState.ONLINE);
    } catch (err) {
      this.debugInfo(`ERROR: getting edge point type: ${this._di} ${err}`);
      this.setOutputData(0, false); //connected
      this.setOutputData(1, true); //error
      // NodeColours.nodeColourChange(this, NodeState.OFFLINE);
    }
  }

  async pollInputsAndSend() {
    if (this.side !== Side.server) return;
    await this.pollUIs();
    await this.pollDIs();
    this.sendPayloadToPointNodesFunc();
  }

  sendPayloadToPointNodesFunc() {
    if (this.side !== Side.server) return;
    for (let pointNode of this.pointNodes) {
      Edge28Utils.sendPayloadToChild(pointNode, {
        pointsDI: this.edgeReadDI_Store,
        pointsUI: this.edgeReadUI_Store,
      });
    }
  }

  subscribe({ action, payload }) {
    switch (action) {
      case ADD_POINT:
        this.pointNodes.push(payload);
        break;
      case REMOVE_POINT:
        this.pointNodes = this.pointNodes.filter(pointNode => pointNode.id !== payload.id);
        break;
      default:
        this.debugWarn("Request action doesn't match");
    }
  }

  async onInputUpdated() {
    if (this.side !== Side.server) return;
    let enable = this.getInputData(0);
    if (!enable && this.runState) {
      clearInterval(this.UI_timeoutFunc);
      clearInterval(this.DI_timeoutFunc);
      this.setOutputData(0, false, true); //connected
      this.runState = false;
      return;
    } else if (!enable && !this.runState) {
      this.setOutputData(0, false, true); //connected
      this.runState = false;
      return;
    } else if (enable) {
      let interval = this.getInputData(1);
      interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
      if (this.runState) {
        if (interval != this.lastInterval) {
          this.lastInterval = interval;
          clearInterval(this.UI_timeoutFunc);
          clearInterval(this.DI_timeoutFunc);
          this.startInputPolling(interval);
        }
        return;
      } else if (!this.runState) {
        this.runState = true;
        this.startInputPolling(interval);
        return;
      }
      await this.pollInputsAndSend();
    }
  }

  async onAfterSettingsChange(oldSettings: any) {
    super.onAfterSettingsChange(oldSettings);
    if (this.side !== Side.server) return;
    await this.onInputUpdated();
  }
}

Container.registerNodeType(EDGE_28_NETWORK, Edge28ApiNode);
