import { ErrorCallbackHandler } from '../../../utils/decorators';
import { ConnectionStatus } from '../../../utils/enums';
import { IdGenerator } from '../../../utils/id-generator';
import { DefaultObservable, ICentralizedListener, NodeStoreItem, Observable, Observer } from '../../../utils/pattern';
import { ContainerNode } from '../../container-node';
import { Side } from '../../node-interface';
import { ConnectionOutput, ExternalConnectionNode, ExternalConnectionNodeMixin } from './connection-node-mixin';

export interface IProtocolDeviceNode extends ExternalConnectionNode, ICentralizedListener {

  statusObservable(observers: Observer<ConnectionOutput>[]): Observable<ConnectionOutput>;

}

export abstract class ProtocolDeviceNode extends ExternalConnectionNodeMixin(ContainerNode)
  implements IProtocolDeviceNode {
  private readonly _title: string;

  protected constructor(title, description) {
    super();
    this._title = title;
    this.description = description;
    this.mixinConnectionStatusOutput();
  }

  onCreated() {
    super.onCreated();
  }

  onAdded() {
    super.onAdded();
    this.startOrStop();
  }

  onAfterSettingsChange() {
    this.startOrStop();
    this.applyTitle();
  }

  onInputUpdated() {
    this.reEvaluateSettingByInput(this.inputs, this.settings);
    this.onAfterSettingsChange();
  }

  onRemoved() {
    this.stop();
    super.onRemoved();
  }

  statusObservable(observers: Observer<ConnectionOutput>[]): Observable<ConnectionOutput> {
    observers.push(this.statusObserver());
    return new DefaultObservable<ConnectionOutput>(observers);
  }

  abstract listen({ action, payload }: { action: string; payload: NodeStoreItem });

  abstract connObserverNodes(): Observer<ConnectionOutput>[];

  protected applyTitle() {
    super.applyTitle();
  }

  protected statusOutputIdx(): number {
    return 0;
  }

  protected enableDescription(): string {
    return `Enable ${this._title}`;
  }

  protected startOrStop(): void {
    if (this.side !== Side.server) return;
    this.stop(); // to restart device
    this.connectionStatus = ConnectionStatus.UNCONNECTED;
    if (this.isEnabled()) {
      this.kickoff(this);
    }
    this.notifyConnStatusOutput();
  }

  @ErrorCallbackHandler((err, node) => node.handleErrorInKickOff(err))
  protected kickoff(node: ProtocolDeviceNode) {
    node.createThenStart();
    this.notifyConnStatusOutput(ConnectionStatus.CONNECTED);
  }

  protected handleErrorInKickOff(err: Error) {
    this.notifyConnStatusOutput(ConnectionStatus.ERROR, err.message);
  }

  protected notifyConnStatusOutput(status?: ConnectionStatus, errMsg?: string) {
    this.connectionStatus = status ?? this.connectionStatus;
    let nodeId = IdGenerator.createNodeId(this.cid, this.id);
    this.statusObservable(this.connObserverNodes()).notify(new ConnectionOutput(this.getConnectionStatus(), errMsg),
                                                           nodeId);
  }

  protected abstract createThenStart(): void;

  protected abstract stop(): void;
}

