import { Node } from '../../node';
import { Container } from '../../container';
import * as _ from 'lodash';
import { Type } from '../../node-io';
import { isNull } from '../../../utils/helper';
import { NODE_INFO } from '../../node-info';

export class ArrayCombineNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.json["array-combine"].title;
    this.description = NODE_INFO.json["array-combine"].description; 
    this.addInput('input-1', Type.JSON);
    this.addInput('input-2', Type.JSON);
    this.addOutput('output-array', Type.JSON);
    this.addOutput('output-object', Type.JSON);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let out = {};
    const in1 = this.getInputData(0);
    const in2 = this.getInputData(1);
    if (isNull(in1)) return
    if (isNull(in2)) return
    try {
      out = [...in1, ...in2]
      this.setOutputData(0, out);
    } catch (err) {
      this.debugInfo(`ARRY-COMBINE NODE: try/catch concat, ${err}`);
    }
    try {
      const merged = Object.assign({}, in1, in2)  
      this.setOutputData(1, merged);
    } catch (err) {
      this.debugInfo(`ARRY-COMBINE NODE: try/catch assign, ${err}`);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }



}
Container.registerNodeType('json/array-combine', ArrayCombineNode);
