import { Enumify } from 'enumify';

export class ConnectionStatus extends Enumify {

  static CONNECTED = new ConnectionStatus('CONNECTED');
  static DISCONNECTED = new ConnectionStatus('DISCONNECTED');
  static ERROR = new ConnectionStatus('ERROR');
  static UNCONNECTED = new ConnectionStatus('UNCONNECTED');
  static _ = ConnectionStatus.closeEnum();

  readonly label: string;

  constructor(label: string) {
    super();
    this.label = label;
  }

  static lookupByLabel(label: string): ConnectionStatus {
    return ConnectionStatus.enumValues.map(e => <ConnectionStatus><unknown>e).find(value => value.label === label);
  }

  isConnected(): boolean {
    return this === ConnectionStatus.CONNECTED;
  }

  isError(): boolean {
    return this === ConnectionStatus.ERROR;
  }

  isDisconnected(): boolean {
    return this === ConnectionStatus.DISCONNECTED;
  }

  isUnconnected(): boolean {
    return this === ConnectionStatus.UNCONNECTED;
  }
}

export enum HistoryMode {
  COV,
  TRIGGERED,
}
