import * as convert from 'convert-units';
import { get } from 'lodash';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType } from '../../node-io';

class UnitConversionNode extends Node {
  constructor() {
    super();

    this.title = NODE_INFO.conversion["convert-units"].title;
    this.description = NODE_INFO.conversion["convert-units"].description;

    this.addInput('in');
    this.addOutput('converted');

    this.settings['from-to'] = NodeSetting.create({
      description: 'From / To unit',
      type: SettingType.TREE_SELECT,
      config: { items: [] },
    });
    this.ensureFromToConfig();
  }

  onAdded() {
    this.updateTitle();
    this.ensureFromToConfig();
  }

  // the from-to config is excluded from JSON serialization, we need to maintain it conditionally
  ensureFromToConfig() {
    // settings['from-to'].config is omitted from serialization / deserialization
    if (!get(this, "settings['from-to'].config.items.length", 0))
      this.settings['from-to'].config = { items: this.createTreeSelectItems() };
    // ensure config exclusion from JSON serialization
    this.settings['from-to'].toJSON = function() {
      const { config = null, ...withoutConfig } = { ...this };
      return withoutConfig;
    };
  }

  createTreeSelectItems() {
    const asItem = unit => ({ value: unit, text: unit });
    // higher-order function:
    const addAsChildren = (categoryName: string, units: { value: any; text: any }[]) => from => ({
      ...from,
      items: units.map(to => ({
        ...to,
        value: { type: categoryName, from: from.value, to: to.value },
      })),
    });
    return convert()
      .measures()
      .map(unitType => ({
        text: unitType,
        items: convert()
          .possibilities(unitType)
          .map(asItem),
      }))
      .map(category => ({
        ...category,
        items: category.items.map(addAsChildren(category.text, category.items)),
      }));
  }

  onInputUpdated() {
    if (!this.settings['from-to'].value) return;
    const { from, to } = this.settings['from-to'].value;
    const value = parseFloat(this.inputs[0].data);
    this.setOutputData(
      0,
      convert(value)
        .from(from)
        .to(to),
    );
  }

  onAfterSettingsChange() {
    this.ensureFromToConfig();
    this.onInputUpdated();
    this.updateTitle();
  }

  updateTitle() {
    if (!this.settings['from-to'].value) return;
    const { from, to } = this.settings['from-to'].value;
    this.title = 'Unit converter' + ' ' + '(' + from + '/' + to + ')';
  }
}
Container.registerNodeType('conversion/convert-units', UnitConversionNode);
