import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import { NODE_INFO } from '../../node-info';

class BitMaskSingle extends Node {
  constructor() {
    super();
  
    this.title = NODE_INFO.conversion["bit-mask-single"].title;
    this.description = NODE_INFO.conversion["bit-mask-single"].description;

    this.settings['bit'] = NodeSetting.create({
      description: 'bit index (0-7)',
      type: SettingType.NUMBER,
      value: 0,
    });

    this.settings['byte'] = NodeSetting.create({
      description: 'Byte to read if Array',
      type: SettingType.NUMBER,
      value: 0,
    });

    this.addInput('input', Type.ANY);
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let input = this.getInputData(0);
    if (input === undefined || input === null) {
      return;
    }

    const BIT = this.settings['bit'].value;
    const BYTE = this.settings['byte'].value;

    if (typeof input === 'object') {
      if (!input || !input.length || input.length < BYTE + 1) {
        return;
      }
      input = input[BYTE];
    }

    this.setOutputData(0, (input >> BIT) & 0x01);
  }

  onAfterSettingsChange() {
    this.settings['bit'].value = Utils.clamp(this.settings['bit'].value, 0, 7);
    this.onInputUpdated();
  }
}
Container.registerNodeType('conversion/bit-mask-single', BitMaskSingle);

class BitMaskMultiple extends Node {
  mask: number = 0;

  constructor() {
    super();
    this.title = 'Bit Mask Multiple';
    this.description = 'A node to mask certain bits of a byte';

    this.settings['bitStart'] = NodeSetting.create({
      description: 'Lowest bit to start (0-7)',
      type: SettingType.NUMBER,
      value: 0,
    });

    this.settings['maskLen'] = NodeSetting.create({
      description: 'Mask length (1-8)',
      type: SettingType.NUMBER,
      value: 1,
    });

    this.settings['byte'] = NodeSetting.create({
      description: 'Byte to read if Array',
      type: SettingType.NUMBER,
      value: 0,
    });

    this.addInput('input', Type.ANY);
    this.addOutput('output', Type.NUMBER);
    this.addOutput('error', Type.STRING);
  }

  init() {
    this.updateMask();
  }

  onAdded() {
    this.updateMask();
    this.onInputUpdated();
  }

  onInputUpdated() {
    let input = this.getInputData(0);
    if (input === undefined || input === null) {
      return;
    }

    const BIT_START = this.settings['bitStart'].value;
    const BYTE = this.settings['byte'].value;

    if (typeof input === 'object') {
      if (!input || !input.length || input.length < BYTE + 1) {
        return;
      }
      input = input[BYTE];
    }

    this.setOutputData(0, (input & this.mask) >> BIT_START);
  }

  onAfterSettingsChange() {
    this.settings['bitStart'].value = Utils.clamp(this.settings['bitStart'].value, 0, 7);
    this.settings['maskLen'].value = Utils.clamp(
      this.settings['maskLen'].value,
      1,
      8 - this.settings['bitStart'].value,
    );
    this.updateMask();
    this.onInputUpdated();
  }

  private updateMask() {
    this.mask = 0;
    for (let i = 0; i < this.settings['maskLen'].value; i++) {
      this.mask |= 1 << i;
    }
    this.mask <<= this.settings['bitStart'].value;
  }
}
Container.registerNodeType('conversion/bit-mask-multiple', BitMaskMultiple);
