import { Container } from '../../container';
import { BaseFunctionNode } from './base-function-node';
import { NODE_INFO } from '../../node-info';

export class FunctionNode extends BaseFunctionNode {

  constructor() {
    super(NODE_INFO["custom-function"].function.title, NODE_INFO["custom-function"].function.description);
    this.mixinScriptSetting('// message is the received value\nreturn message;');
  }

  protected getScriptInput() {
    return this.getInputData(0);
  }
}

Container.registerNodeType('custom-function/function', FunctionNode);
