import { PointOutput, PointValueCreator } from '../../../../backend/models/point-model';
import { isNull } from '../../../../utils/helper';
import { IdGenerator } from '../../../../utils/id-generator';
import {
  DefaultObservable,
  HasCentralizedListener,
  ICentralizedListener,
  MockCentralizedListener,
  NodeStoreItem,
  Observer,
} from '../../../../utils/pattern';
import { Container } from '../../../container';
import { Node } from '../../../node';
import { Side } from '../../../node-interface';
import { NodeInput, NodeSetting, SettingType, Type } from '../../../node-io';
import { DependantConnectionNode, DependantConnectionNodeMixin } from '../connection-node-mixin';
import {
  DataPointFlowHandler,
  HasDataPointFlowHandler,
  HasPointOutputObservers,
  MockHasPointOutputObservers,
  PointNodeMixin,
} from '../model/point-node';
import {
  BACNET_SERVER_NODE_TYPE,
  PUSH_BACNET_POINT_VALUE,
  REGISTER_BACNET_POINT,
  UNREGISTER_BACNET_POINT,
  UPDATE_BACNET_POINT,
} from './bacnet-server-node';
import { BacnetPoint, BacnetPointCreator, PointKind } from './core/bacnet-model';

class BACnetServerPointNode extends DependantConnectionNodeMixin(PointNodeMixin(Node))
  implements HasPointOutputObservers<BacnetPoint>, HasDataPointFlowHandler<BacnetPoint>, DataPointFlowHandler<BacnetPoint>,
    DependantConnectionNode, HasCentralizedListener {
  constructor() {
    super();
    this.title = 'BACnet Server Point';
    this.description =
      'Bacnet-server-point nodes should be added within a bacnet-server container node. ' +
      'BACnet point details should be set in settings. BACnet point settings include ID, Type, and Name.';
    this.mixinEnableInputSetting();
    this.mixinConnectionStatusOutput();
    this.addInputWithSettings('point-id', Type.NUMBER, 1, 'BACnet Object Id');
    this.settings['point-kind'] = NodeSetting.create({
      description: 'BACnet Object Type',
      type       : SettingType.DROPDOWN,
      config     : { items: PointKind.items() },
      value      : PointKind.ANALOG_INPUT.enumKey,
    });
    this.addInputWithSettings('point-name', Type.STRING, 'bacnet-point', 'BACnet Point Name');
    this.setSettingsConfig(this.mixinPointValueInputOutput());
  }

  flowHandler(): DataPointFlowHandler<BacnetPoint> {
    return this;
  }

  listener(): ICentralizedListener {
    return MockCentralizedListener.cast(this.getParentNode());
  }

  pointObservers(): Observer<PointOutput<BacnetPoint>>[] {
    return [...super.pointObservers(),
            ...MockHasPointOutputObservers.cast(this.getParentNode()).pointObservers()];
  }

  notifyOutput(point: BacnetPoint, observers: Observer<PointOutput<BacnetPoint>>[]): void {
    let cp = this.initializePointBySettingObject() ?? point;
    if (isNull(cp)) {
      return;
    }
    point.pointValue = isNull(point?.pointValue) ? PointValueCreator.by() : point?.pointValue.merge(cp.pointValue);
    this.settings[this.modelSettingKey()].value = point;
    new DefaultObservable(observers).notify({ point: point, connStatus: { status: this.getConnectionStatus() } });
  }

  onRemoved() {
    if (this.listener() === MockCentralizedListener.LISTENER) {
      return;
    }
    let event = {
      action : UNREGISTER_BACNET_POINT,
      payload: this.createPayload(this.initializePointBySettingObject()),
    };
    this.listener().listen(event);
  }

  handleOnUpdate(current: BacnetPoint, prev?: BacnetPoint): BacnetPoint {
    if (this.side !== Side.server || (!prev?.objectInstance && !current?.objectInstance)) {
      return null;
    }
    let payload = this.createPayload(current);
    if (!prev || this.getConnectionStatus().isError()) {
      this.listener().listen({ action: REGISTER_BACNET_POINT, payload });
    } else if (!current.mightOnlyValueChanged(prev)) {
      if (!current.equals(prev)) {
        current.pointValue = PointValueCreator.by();
      }
      payload.prev = this.createPayload(prev);
      this.listener().listen({ action: UPDATE_BACNET_POINT, payload });
    }
    if (!current.enabled || (isNull(current?.pointValue?.presentValue) &&
                             (prev && !current.equals(prev) || isNull(prev?.pointValue?.presentValue)))) {
      return current;
    }
    let cov = current.pointValue.changedOfValue(prev?.pointValue);
    if (cov) {
      payload.data.pointValue = cov;
    }
    current.pointValue = this.listener().listen({ action: PUSH_BACNET_POINT_VALUE, payload });
    return current;
  }

  initializePointBySettingObject(settings?: { [name: string]: NodeSetting }): BacnetPoint {
    let st = (settings ?? this.settings) as { [name: string]: NodeSetting };
    return BacnetPointCreator.from(st[this.modelSettingKey()].value);
  }

  initializePointBySettingInput(settings?: { [name: string]: NodeSetting }): BacnetPoint {
    let st = (settings ?? this.settings) as { [name: string]: NodeSetting };
    let kind = <PointKind>PointKind.enumValueOf(st['point-kind'].value);
    return BacnetPointCreator.by(this.isEnabled(), st['point-id'].value, kind, st['point-name'].value,
                                 this.createPointValue(st));
  }

  reEvaluateSettingByInput(inputs: { [p: number]: NodeInput }, settings: { [name: string]: NodeSetting }) {
    settings['point-id'].value = inputs[1].updated ? inputs[1].data : settings['point-id'].value;
    settings['point-name'].value = inputs[2].updated ? inputs[2].data : settings['point-name'].value;
    super.reEvaluateSettingByInput(inputs, settings);
  }

  onReceiveMessage(point: BacnetPoint): void {
    this.notifyOutput(point, this.pointObservers());
  }

  protected statusOutputIdx(): number {
    return 0;
  }

  protected enableDescription(): string {
    return 'Enable BACnet Point';
  }

  protected modelSettingKey(): string {
    return 'bp';
  }

  protected valueInputIdx(): number {
    return 3;
  }

  protected valueOutputIdx(): number {
    return this.errorOutputIdx() + 1;
  }

  protected computeTitle(): string {
    let kind = <PointKind>PointKind.enumValueOf(this.settings['point-kind'].value);
    return `BACnet Point (ObjectType: ${kind.label}, ObjectId: ${this.settings['point-id'].value})`;
  }

  private createPayload(point: BacnetPoint) {
    return {
      identifier: point?.identifier(),
      enabled   : point?.enabled,
      data      : point,
      nodeId    : IdGenerator.createNodeId(this.cid, this.id),
      callback  : (p: BacnetPoint) => this.onReceiveMessage(p),
    } as NodeStoreItem;
  }
}

Container.registerNodeType('protocols/bacnet-server/bacnet-server-point', BACnetServerPointNode,
                           BACNET_SERVER_NODE_TYPE);
