import * as express from 'express';
import HelpDataSource from '../../database/datasource/HelpDataSource';

let router = express.Router();

router.get('', async function(req, res) {
  try {
    const stringHtml = await HelpDataSource.getHelpComment();
    res.json(stringHtml);
  } catch (e) {
    res.status(500).send(e);
  }
});

router.put('', async function({ body }, res) {
  const { stringHtml } = body;
  try {
    await HelpDataSource.changeHelpComment(stringHtml);
    res.status(201).send("successful");
  } catch (e) {
    res.status(500).send(e);
  }
});

module.exports = router;

