import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';

//Bool Constant
class BoolConstantNode extends Node {

  constructor() {
    super();
    this.title = NODE_INFO.point["bool-constant"].title;
    this.description = NODE_INFO.point["bool-constant"].description;

    this.settings['value'] = NodeSetting.create({
      description: 'Payload',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: false, text: 'false' },
          { value: true, text: 'true' },
          { value: null, text: 'null' },
        ],
      },
      value: null,
    });

    this.addOutput('output', Type.BOOLEAN);
  }

  onAdded() {
 
    if (this.side != Side.server) return;
    let val = this.settings['value'].value;
    this.setOutputData(0, val);
  }

  onExecute(){
    // this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    let val = this.settings['value'].value;
    this.setOutputData(0, val);
  }
}
Container.registerNodeType('point/bool-constant', BoolConstantNode);
