import { isNotNull } from '../../../utils/helper';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';

class FiltersPreventNullNode extends Node {
  constructor() {
    super();

    this.title = NODE_INFO.filter["prevent-null"].title;
    this.description = NODE_INFO.filter["prevent-null"].description;

    this.addInput('input');
    this.addOutput('output');
    this.properties['value'];
  }

  onAdded(){
    this.setOutputData(0, this.properties['value']);
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);

    if (isNotNull(val) && val !== '') {
      this.setOutputData(0, val);
      this.properties['value'] = val;
      this.persistProperties(false, true);
    }
  }
}
Container.registerNodeType('filter/prevent-null', FiltersPreventNullNode);
