import * as fs from 'fs';
import config from '../../../config';
import PlatformDataSource from '../../../database/datasource/PlatformDataSource';
import { isJSON, isNull } from '../../../utils/helper';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import tagRefs from '../../utils/points/point-ref-utils';

enum TOPIC_OPTIONS {
  NA = 'na',
  UPPERCASE_SLASH = 'Upper case and /',
  LOWERCASE_SLASH = 'Lower case and /',
  UPPERCASE_UNDERSCORE = 'Upper case and _',
  LOWERCASE_UNDERSCORE = 'Lower case and _',
}

class PointTagNode extends Node {
  //in nums
  inputOneNum = 0;
  inputTwoNum = 1;
  inputThreeNum = 2;
  pointFeedbackNum = 3;
  inputJsonNum = 4;
  pointIdNumIn = 5;
  equipRefNumIn = 6;
  measurementNumIn = 7;
  manualTagsNum = 8;
  //out nums
  pointValNumOut = 0;
  nameNumOut = 1;
  topicNumOut = 2;
  jsonNumOut = 3;
  errorNumOut = 4;

  constructor() {
    super();
    this.title = NODE_INFO.history["point-tags"].title;
    this.description = NODE_INFO.history["point-tags"].description;
    this.settings['point_group'] = NodeSetting.create({
      description: 'Point Value',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['preventDuplicates'] = NodeSetting.create({
      description: 'Prevent Duplicates)',
      value: true,
      type: SettingType.BOOLEAN,
    });
    this.addInputWithSettings('in 1', Type.NUMBER, null, 'in 1', false);
    this.addInputWithSettings('in 2', Type.NUMBER, null, 'in 2', false);
    this.addInputWithSettings('in 3', Type.NUMBER, null, 'in 3', false);
    this.addInputWithSettings('point-feedback', Type.NUMBER, null, 'point-feedback', false);
    this.addInput('json-input', Type.STRING);
    this.settings['point_group'] = NodeSetting.create({
      description: 'Point/Equip Settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.addInputWithSettings('pointId', Type.STRING, 'point id', 'point id/name', false);
    this.addInputWithSettings('equipRef', Type.STRING, 'FCU123', 'Equipment Reference', false);
    this.addInputWithSettings('measurement', Type.STRING, 'points', 'InfluxDB table name', false);
    this.settings['topicOptions'] = NodeSetting.create({
      description: 'Enter manual (optional)',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.addOutput('output', Type.NUMBER);
    this.addOutput('point-name', Type.STRING);
    this.addOutput('point-topic', Type.STRING);
    this.addOutput('output-json', Type.JSON);
    this.addOutput('output-error', Type.ANY);
    this.settings['tags_group'] = NodeSetting.create({
      description: 'Auto generated  Tag Settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['equipType'] = NodeSetting.create({
      description: 'Equipment Type',
      type: SettingType.DROPDOWN,
      config: {
        items: tagRefs.tagCategory,
      },
    });
    this.settings['tags'] = NodeSetting.create({
      description: 'Point Type',
      value: null,
      type: SettingType.DROPDOWN,
    });
    this.settings['tags_values'] = NodeSetting.create({
      description: 'tags values',
      value: null,
      type: SettingType.READONLY,
    });

    this.settings['tags_group_manual'] = NodeSetting.create({
      description: 'Manual Tag Settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['manualSettings'] = NodeSetting.create({
      description: 'Enter manual (optional)',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['manualEquip'] = NodeSetting.create({
      description: 'User equip (optional)',
      value: 'CHILLER',
      type: SettingType.STRING,
    });
    this.settings['manualSensor'] = NodeSetting.create({
      description: 'User sensor (optional)',
      value: 'Supply Air Zone Sensor',
      type: SettingType.STRING,
    });
    this.addInputWithSettings('manualTags', Type.JSON, '["sensor", "ahu"]', 'manual Tags', false);
  }

  onAdded() {
    this.onAfterSettingsChange();
    const file = `${config.dataDir}/platform.db`;
    this.onWatchChange(file)
  }

  onWatchChange(file: string) {
    try {
      fs.watch(file, (eventType, filename) => {
        this.debugInfo(`PLATFORM NODE: file watch platform db file change, ${eventType} ${filename}`);
        this.onInputUpdated();
      });
    } catch (err) {
      this.debugInfo(`PLATFORM NODE ERROR: file watch platform db file error, ${err}`);
    }
  }
  async onInputUpdated() {
    if (this.side !== Side.server) return; // only execute on server
    let deviceId: string, clientId: string, clientName: string, siteId: string, siteName: string, siteAddress: string, state: string, city: string, zip: string, country: string, lat: string, lon: string
    await PlatformDataSource.getPlatform().then(e => {
      deviceId = e.deviceID
      clientId = e.clientId
      clientName = e.clientName
      siteId = e.siteId
      siteName = e.siteName
      siteAddress = e.siteAddress
      state = e.state
      city = e.city
      zip = e.zip
      country = e.country
      lat = e.lat
      lon = e.lon
    }).catch(err => {
      this.debugInfo(`PLATFORM NODE: try/catch  getPlatform info, ${err}`);
    })
    const preventDuplicates = this.settings['manualEquip'].value
    const pointFeedback = this.getInputData(this.pointFeedbackNum);
    const measurement = this.getInputData(this.measurementNumIn);
    let pointId = this.getInputData(this.pointIdNumIn);
    let jsonInput = this.getInputData(this.inputJsonNum);
    //small point priority
    let inputOne = this.getInputData(this.inputOneNum);
    let inputTwo = this.getInputData(this.inputTwoNum);
    let inputThree = this.getInputData(this.inputThreeNum);
    let pointValue: number;
    let pointValueIsJSON = false;
    // json input
    if (!isNull(jsonInput)) {
      let isJSON = true;
      try {
        jsonInput = JSON.parse(jsonInput);
        const msg = `PLATFORM NODE: try/catch  JSON.parse, ${JSON.stringify(jsonInput)}`
        this.debugInfo(msg);
        this.setOutputData(this.errorNumOut, pointValue, true);
      } catch {
        isJSON = false;
      }
      if (isJSON && jsonInput.hasOwnProperty('pointValue') && typeof jsonInput.pointValue === Type.NUMBER) {
        pointValue = jsonInput.pointValue
        pointValueIsJSON = true;
        this.debugInfo(`PLATFORM NODE: try/catch  jsonInput.hasOwnProperty('pointValue') in 3 ${inputThree}`);
        this.setOutputData(this.pointValNumOut, pointValue, preventDuplicates);
      }
      if (isJSON && jsonInput.hasOwnProperty('name') && typeof jsonInput.name === Type.STRING) {
        pointId = jsonInput.name
        this.debugInfo(`PLATFORM NODE: try/catch  jsonInput.hasOwnProperty('name') ${pointId}`);
      }
    }
    if (!isNull(inputOne)) {
      pointValue = inputOne;
      this.setOutputData(this.pointValNumOut, pointValue, preventDuplicates);
    } else if (!isNull(inputTwo)) {
      pointValue = inputTwo;
      this.setOutputData(this.pointValNumOut, pointValue, preventDuplicates);
    } else if (!isNull(inputThree) && pointValueIsJSON === false) {
      pointValue = inputThree;
      this.setOutputData(this.pointValNumOut, pointValue, preventDuplicates);
    }
    if(isNull(pointId)){
      const msg = `POINT-TAG NODE: pointId is null so return`
      this.debugInfo(msg);
      this.setOutputData(this.errorNumOut, pointValue, true);
      return
    }
    if(isNull(measurement)){
      const msg = `POINT-TAG NODE: measurement is null so return`
      this.debugInfo(msg);
      this.setOutputData(this.errorNumOut, pointValue, true);
      return
    }
    let pointArray = JSON.stringify({out: pointValue, in1: inputOne , in2: inputTwo,  in3: inputThree})
    const equipRef = this.getInputData(this.equipRefNumIn);
    let equipType = this.settings['equipType'].value;
    let topic = `${deviceId}/${equipRef}/${pointId}`.toLocaleLowerCase()
    this.setOutputData(this.nameNumOut, pointId, preventDuplicates);
    this.setOutputData(this.topicNumOut, topic, preventDuplicates);

    //manual settings
    const manualSettings = this.settings['manualSettings'].value
    const manualEquip = this.settings['manualEquip'].value
    const manualSensor = this.settings['manualSensor'].value
    const manualTags = this.getInputData(this.manualTagsNum);

    let tagSetting;
    if (!manualSettings) {
      tagSetting = tagRefs.tagType(equipType)
      if (isNull(tagSetting)) {
        return;
      }
      this.settings['tags'].config = {
        items: Object.keys(tagSetting),
      };
      this.broadcastSettingsToClients();
    }
    let pointType = this.settings['tags'].value
    let tags: any;
    if (manualSettings) {
      pointType = manualSensor;
      equipType = manualEquip;
      tags = manualTags;
    } else {
      for (var key in tagSetting) {
        if (key == pointType) {
          tags = tagSetting[key]
        }
      };
    }
    let fields = { value: pointValue }
    let pointArr = { topic: topic, pointType: pointType, equipRef: equipRef, pointArray:pointArray, pointFeedback: pointFeedback, equipType: equipType, deviceId: deviceId, pointId: pointId, clientId: clientId, clientName: clientName, siteId: siteId, siteName: siteName, siteAddress: siteAddress, city: city, zip: zip, state: state, country: country, lat: lat, lon: lon }
    if (tags) {
      if (!manualSettings) {
        this.settings['tags_values'].value = tags.tags
        this.broadcastSettingsToClients();
        tags = tags.tags
      }
      let pointTags = {}
      if (isJSON(tags)) {
        if (Object.keys(tags).length > 0) { //make sure array isnt empty
          tags.forEach(x => { pointTags[x] = x })
          const out = { ...pointArr, ...pointTags }
          const outOut = { tags: out, measurement: measurement, fields: fields, topic: topic }
          this.setOutputData(this.jsonNumOut, outOut, preventDuplicates);
        } else {
          const msg = `PLATFORM NODE: tag array is empty`
          this.debugInfo(msg);
          this.setOutputData(this.errorNumOut, pointValue, true);
        }
      }
    }
  }
  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('history/point-tags', PointTagNode);
