import { RegistryError, RegistryItemExistedError } from '../utils/errors';
import { isNotNull, isNull } from '../utils/helper';
import { registry_logger } from '../utils/loggers';

const logger = registry_logger;

export interface Registry<T> {

  register(obj: T, force?: boolean, key?: string): Promise<T> | T;

  list(): T[];

  size(): number;

  get(key: any): T;

  has(key: any): boolean;

  remove(key: any): T | Promise<T>;

  clear(): void;
}

export abstract class AbstractRegistry<T> implements Registry<T> {

  protected _registry: { [key: string]: T };

  protected constructor() {
    this._registry = {};
  }

  get(key: any): T {
    return this._registry[key];
  }

  has(key: any): boolean {
    return isNotNull(this.get(key));
  }

  list(): T[] {
    return Object.values(this._registry);
  }

  register(obj: T, force?: boolean, key?: string): Promise<T> | T {
    const o = this.optimizeBeforeRegister(this.validate(obj, force, key), force);
    return this.doRegister(key ?? this.createKey(o), o, force);
  }

  remove(key: any): T | Promise<T> {
    if (this.enableDebug()) {
      logger.debug(`Removing ${this.itemName()} by key='${key}'...`);
    }
    let obj = this.get(key);
    delete this._registry[key];
    return obj;
  }

  size(): number {
    return Object.keys(this._registry).length;
  }

  clear(): void {
    this._registry = {};
  }

  protected abstract createKey(obj: T): string | number;

  protected itemName(): string {
    return 'Item';
  }

  protected enableDebug(): boolean {
    return true;
  }

  protected itemNullable(): boolean {
    return false;
  }

  protected optimizeBeforeRegister(obj: T, force: boolean): T {
    return obj;
  }

  protected validate(obj: T, force: boolean, key: string): T {
    if (isNull(obj) && !this.itemNullable()) {
      throw new RegistryError(`Given ${this.itemName()} is null or undefined`);
    }
    return obj;
  }

  protected doRegister(k: string | number, o: T, force: boolean): Promise<T> | T {
    if (!force && this.has(k)) {
      throw new RegistryItemExistedError<T>(`${this.itemName()} with key ${k} is already existed`);
    }
    if (this.enableDebug()) {
      logger.debug(`Registering ${this.itemName()} by key='${k}' and force=${force}...`);
    }
    this._registry[k] = o;
    return o;
  }
}

export class RegistryResult<T> {
  objects: T[];
  errors: RegistryError<T>[];

  constructor(objects?: T[], errors?: RegistryError<T>[]) {
    this.objects = objects ?? [];
    this.errors = errors ?? [];
  }

}
