import config from '../../config';

const logger = require('logplease').create('cryto-utils', { color: 3 });

const CryptoJS = require('crypto-js');

export default class CryptoUtils {
  static encrypt = text => {
    if (text) {
      return CryptoJS.AES.encrypt(text, config.secretKey).toString();
    } else {
      logger.error('Your text to encrypt is empty');
    }
  };

  static decrypt = cipherText => {
    if (cipherText) {
      return CryptoJS.AES.decrypt(cipherText, config.secretKey).toString(CryptoJS.enc.Utf8);
    } else {
      logger.error('Your text to decrypt is empty');
    }
  };
}
