/**
 * <b>Serializable</b> is a marker interface for translating a data structure or object state into a format that can be
 * stored * or transmitted and reconstructed later.
 */
import { isFunction, isNull } from '../utils/helper';

export interface Serializable<T> {

  serialize(): T;

}

export interface IDeserializable<T, R> {

  deserialize(serializedObj: T): R;

}

export interface Deserializable<T> extends IDeserializable<T, Deserializable<T>> {
  /**
   * Deserialize from serialized object
   * @param {T} serializedObj serialized object
   * @return {Deserializable<T>} new instance object
   */
  deserialize(serializedObj: T): Deserializable<T>;
}

export class SerializableHelper {
  static serialize<T>(obj: Serializable<T> | any): T {
    if (isNull(obj)) {
      return null;
    }
    return isFunction(obj['serialize']) ? obj.serialize() : new SerializedObject(obj).serialize();
  }
}

class SerializedObject implements Serializable<object> {
  obj: any;

  constructor(obj: any) {
    this.obj = obj;
  }

  serialize(): object {
    return JSON.parse(JSON.stringify(this.obj));
  }
}
