import { isNull } from '../../../../utils/helper';
import { Container } from '../../../container';
import { Node } from '../../../node';
import { NODE_INFO } from '../../../node-info';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import SerialUtils from '../../../utils/serial-utils';

const SerialPort = require('serialport');

class SerialConnectorNode extends Node {
  baudRates = [110, 300, 600, 1200, 2400, 4800, 9600, 14400, 19200, 38400, 57600, 115200, 128000, 256000];
  isConnected = false;
  port = null;

  constructor() {
    super();
    this.title = NODE_INFO["protocols/misc"]["serial-connector"].title;
    this.description =
      `## Description\n ` +
      NODE_INFO["protocols/misc"]["serial-connector"].description + `\n ` +
      `   \n ` +
      `## Nube LoRa Raw Baud Rate\n ` +
      ` Default Baud Rate For Nube LoRa Raw is ***38400*** \n `
    this.addInput('writeValue', Type.STRING);
    this.addInput('enable', Type.BOOLEAN);
    this.addInput('list-ports', Type.BOOLEAN);
    // this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addOutput('output', Type.STRING);
    this.addOutput('error', Type.STRING);
    this.addOutput('status', Type.BOOLEAN);
    this.addOutput('last-message', Type.STRING);
    this.addOutput('active-ports', Type.BOOLEAN);
    this.settings['manualPort'] = NodeSetting.create({
      description: 'Enter port manually',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['port'] = NodeSetting.create({
      description: 'Serial Port',
      value: '/dev/ttyUSB2',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: '/dev/ttyUSB2', text: 'RS485-1 (/dev/ttyUSB2)' },
          { value: '/dev/ttyUSB3', text: 'RS485-2 (/dev/ttyUSB3)' },
          { value: '/dev/ttyUSB1', text: 'Xbee-1 (/dev/ttyUSB1)' },
          { value: '/dev/ttyUSB0', text: 'Xbee-2 (/dev/ttyUSB0)' },
          { value: '/dev/ttyAMA0', text: 'Xbee-3 (/dev/ttyAMA0)' },
          { value: '/dev/ttyUSB4', text: 'USB Port (/dev/ttyUSB4)' },
          { value: '/dev/ttyS1', text: 'Edge28 Expansion (/dev/ttyS1)' },
        ],
      },
    });
    this.settings['manualPortNum'] = NodeSetting.create({
      description: 'Enter port number',
      value: '/dev/ttyACM0',
      type: SettingType.STRING,
    });
    this.settings['baudRate'] = NodeSetting.create({
      description: 'Baud rate',
      value: this.baudRates[6],
      type: SettingType.DROPDOWN,
      config: {
        items: this.baudRates.map(baudRate => {
          return { value: baudRate, text: baudRate };
        }),
      },
    });
    this.setOutputData(0, null);
    this.setOutputData(1, false);
    this.setOutputData(2, null);

    this.setSettingsConfig({
      conditions: {
        manualPortNum: setting => {
          return setting['manualPort'].value;
        },
        port: setting => {
          return !setting['manualPort'].value;
        },
      },
    });

  }

  onInputUpdated() {
    if (!this.isConnected) {
      this.connectToSerialPort();
    } else if (this.isConnected && !this.getInputData(1)) {
      this.isConnected = false;
      this.clearOutputs();
      this.closePort();
      return;
    }

    this.writePortData(this.getInputData(0));

    if (this.getInputData(2) === true) {
      this.listPorts();
    }
  }

  onAfterSettingsChange() {
    this.connectToSerialPort();
  }

  onRemoved() {
    this.closePort();
  }

  connectToSerialPort() {
    if (this.side !== Side.server) return;

    this.clearOutputs();
    this.closePort();

    if (!this.getInputData(1)) return;
    let portName = this.settings['port'].value;
    const manualPort = this.settings['manualPort'].value;
    const manualPortNum = this.settings['manualPortNum'].value;

    if (manualPort) {
      if (manualPort === isNull) {
        portName = portName
      } else portName = manualPortNum
    }

    let baudRate = this.settings['baudRate'].value;

    if (!portName) {
      this.debugErr('Port name is not defined!');
      this.setOutputData(1, 'Port name is not defined!');
      return;
    }

    this.port = new SerialPort(portName, { baudRate: baudRate, autoOpen: false });

    this.port.on('open', () => {
      this.isConnected = true;
      this.setOutputData(2, this.isConnected);
      this.debugInfo('Port connected');
    });

    this.port.on('error', err => {
      this.port = null; // this.port contains a serial object but it can't close since it never been opened
      this.isConnected = false;
      this.setOutputData(1, err.message);
      this.setOutputData(2, this.isConnected);
      this.debugErr(err.message);
    });

    this.port.on('close', () => {
      this.debugInfo(this.isConnected ? 'Port closed. Gateway disconnected.' : 'Port closed');
      this.isConnected = false;
      this.setOutputData(2, this.isConnected);
    });

    this.port.on('disconnect', () => {
      this.isConnected = false;
      this.setOutputData(2, this.isConnected);
      this.debugInfo('Disconnected');
    });

    const Readline = SerialPort.parsers.Readline;
    const parser = this.port.pipe(new Readline({ delimiter: '\n' }));
    parser.on('data', data => this.readPortData(data));

    this.debugInfo('Connecting to ' + portName + ' at ' + baudRate + '...');
    this.port.open();
  }

  private clearOutputs() {
    this.setOutputData(0, null);
    this.setOutputData(1, null);
    this.setOutputData(2, false);
  }

  private listPorts() {
    SerialUtils.listPorts()
      .then(e => {
        this.setOutputData(4, e);
      })
      .catch(err => {
        this.setOutputData(4, err);
      });
  }

  private closePort() {
    if (this.port) {
      this.port.close();
    }
    this.port = null;
  }

  private readPortData(data) {
    this.setOutputData(0, data);
    this.setOutputData(3, this.formattedExecutionTime());
  }

  private writePortData(message) {
    if (message) {
      //TODO: temporary fix
      //  any byte over 128 gets message up (nodejs serialPort must use signed bytes for strings for some dumb reason...)
      //  best solution is to be able to send an array to this (typeof [1,2,3] === 'object')
      //  couldn't get it working for now so sending to this node as a JSON string for now
      try {
        message = Buffer.from(JSON.parse(message));
      } catch (e) { }

      try {
        this.port.write(message);
      } catch (e) {
        this.setOutputData(1, e.message);
      }
    }
  }
}

Container.registerNodeType('protocols/misc/serial-connector', SerialConnectorNode);
