import { get, merge } from 'lodash';
import { Api } from '../../../backend/api';
import { DeviceAPI, EdgeAPI, FolderAPI, NetworkAPI, PointAPI } from '../../../backend/datapoint';
import { Device, Edge, Folder, Network, Point } from '../../../backend/entry';
import config from '../../../config';
import { singleton as actor } from '../../../promise-actor';
import { ERROR } from '../../../shared/socket-events';
import {
  DEVICE_CONTAINER_TYPE,
  EDGE_CONTAINER_TYPE,
  EDGES_PARENT_CONTAINER_TYPE,
  GROUP_CONTAINER_TYPE,
  NETWORK_CONTAINER_TYPE,
  POINT_NODE_TYPE,
} from '../../constants';
import { Container } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import ApiContainerNode from './shared/api-container-node';
import EntryNode from './shared/entry-node';
import Mixin from './shared/mixin';
import TopLevelApiContainerNode from './shared/top-level-api-container-node';

const doNothing = () => {
};

export class EdgesParentContainerNode extends TopLevelApiContainerNode<Edge> {
  title: string = 'Edges';
  extra_node_types = []; // not possible to create edges via API

  collectChildNodes(nodes: Node[]): Node[] {
    return nodes.filter(n => n instanceof EdgeContainerNode);
  }

  protected initAPI(): Api<Edge> {
    return new EdgeAPI();
  }

  protected fetchChildren(): Promise<Edge[]> {
    return this.api.list();
  }

  protected childNodeType(): string[] {
    return [EDGE_CONTAINER_TYPE];
  }
}

export class EdgeContainerNode extends ApiContainerNode<Edge> {
  title: string = 'Edge Container';
  settingFields = {
    code            : SettingType.STRING,
    customer_code   : SettingType.STRING,
    side_code       : SettingType.STRING,
    model           : SettingType.STRING,
    firmware_version: SettingType.STRING,
    data_version    : SettingType.STRING,
  };

  onAdded() {
    this.ignore_remove = true;
    this.removable = false;
    this.movable = false;
    super.onAdded();
  }

  createEntry() {
    return Promise.reject('ERROR: Edge creation not currently supported.');
  }

  deleteEntry() {
    return Promise.reject('ERROR: Edge deletion not currently supported.');
  }

  collectChildNodes(nodes: Node[]): Node[] {
    return nodes.filter(v => v instanceof NetworkContainerNode);
  }

  namePrefix() {
    return 'Edge';
  }

  protected initAPI(): Api<Edge> {
    return new EdgeAPI();
  }

  protected fetchChildren() {
    return new NetworkAPI().list(this.entryId());
  }

  protected childNodeType(): string[] {
    return [NETWORK_CONTAINER_TYPE];
  }
}

export class NetworkContainerNode extends ApiContainerNode<Network> {
  title: string = 'Network Container';
  settingFields = {
    state   : SettingType.READONLY,
    protocol: SettingType.READONLY,
    code    : SettingType.STRING,
  };
  private protocols = ['GPIO', 'BACNET', 'MODBUS', 'UNKNOWN'].map(point => ({
    value: point,
    text : point,
  }));

  createSettingForm = (entry: Network) => {
    return merge(Mixin.prototype.createSettingForm.call(this, entry), {
      protocol: {
        value : this.protocols[this.protocols.length - 1].value,
        type  : SettingType.DROPDOWN,
        config: {
          items: this.protocols,
        },
      },
    });
  };

  collectChildNodes(nodes: Node[]): Node[] {
    return nodes.filter(v => v instanceof DeviceContainerNode);
  }

  namePrefix() {
    return 'Network';
  }

  createEntry() {
    const edge_node = <EdgeContainerNode>this.container.container_node;
    if (!(edge_node instanceof EdgeContainerNode)) {
      const errorMessage =
              'Error: Trying to create network, but network node does not exist inside a valid Edge container...';
      console.log(errorMessage);
      return Promise.reject(errorMessage);
    }
    return this.findFreeIdentifier().then(code => this.api.create(<any>{ code }, edge_node.entryId()));
  }

  protected initAPI(): Api<Network> {
    return new NetworkAPI();
  }

  protected fetchChildren(): Promise<any[]> {
    return new DeviceAPI().list(this.entryId());
  }

  protected childNodeType(): string[] {
    return [DEVICE_CONTAINER_TYPE];
  }
}

export class DeviceContainerNode extends ApiContainerNode<Device> {
  title: string = 'Device Container';
  settingFields = {
    state       : SettingType.READONLY,
    code        : SettingType.STRING,
    manufacturer: SettingType.STRING,
    protocol    : SettingType.STRING,
    type        : SettingType.STRING,
  };

  collectChildNodes(nodes: Node[]): Node[] {
    return nodes.filter(v => v instanceof GroupContainerNode || v instanceof PointNode);
  }

  namePrefix() {
    return 'Device';
  }

  createEntry() {
    const network_node = <NetworkContainerNode>this.container.container_node;
    const edge_node = <EdgeContainerNode>network_node.container.container_node;
    if (!(network_node instanceof NetworkContainerNode)) {
      const errorMessage =
              'Error: Trying to create device, but device node does not exist inside a valid Network container...';
      return Promise.reject(errorMessage);
    }
    return this.findFreeIdentifier().then(code => this.api.create(<any>{
                                                                    edge_id: edge_node.entryId(),
                                                                    device : {
                                                                      type: 'EQUIPMENT',
                                                                      code,
                                                                    },
                                                                  },
                                                                  network_node.entryId()));
  }

  protected initAPI(): Api<Device> {
    return new DeviceAPI();
  }

  protected fetchChildren(): Promise<Folder[]> {
    return new FolderAPI().list(this.entryId());
  }

  protected childNodeType(): string[] {
    return [GROUP_CONTAINER_TYPE, POINT_NODE_TYPE];
  }
}

export class GroupContainerNode extends ApiContainerNode<Folder> {
  title: string = 'Group Container';
  settingFields = {
    name: SettingType.STRING,
  };

  collectChildNodes(nodes: Node[]): Node[] {
    return nodes.filter(v => v instanceof PointNode);
  }

  namePrefix() {
    return 'Group';
  }

  createEntry() {
    const device_node = <DeviceContainerNode>this.container.container_node;
    const network_node = <NetworkContainerNode>device_node.container.container_node;
    const edge_node = <EdgeContainerNode>network_node.container.container_node;
    if (
      !(device_node instanceof DeviceContainerNode) ||
      !(network_node instanceof NetworkContainerNode) ||
      !(edge_node instanceof EdgeContainerNode)
    ) {
      const errorMessage =
              'Error: Trying to create group, but group node does not exist inside a valid Device container hierarchy...';
      return Promise.reject(errorMessage);
    }
    return this.findFreeIdentifier().then(name => this.api.create(<any>{ folder: { name } }, device_node.entryId()));
  }

  protected initAPI(): Api<Folder> {
    return new FolderAPI();
  }

  protected fetchChildren(): Promise<any[]> {
    return new PointAPI().list(this.entryId());
  }

  protected childNodeType() {
    return [POINT_NODE_TYPE];
  }
}

export class PointNode extends Node implements EntryNode<Point> {
  title: string;
  backend: Api<Point>;
  properties: {
    entry: Point;
    is_protected: boolean;
  };
  clonable = false;
  movable = false;
  settingFields = {
    edge        : SettingType.READONLY,
    network     : SettingType.READONLY,
    code        : SettingType.STRING,
    enabled     : SettingType.BOOLEAN,
    kind        : SettingType.STRING,
    measure_unit: SettingType.STRING,
    offset      : SettingType.NUMBER,
    precision   : SettingType.NUMBER,
    protocol    : SettingType.STRING,
    type        : SettingType.STRING,
  };

  entry = Mixin.prototype.entry.bind(this);
  entryId = Mixin.prototype.entryId.bind(this);
  onAfterSettingsChange = Mixin.prototype.onAfterSettingsChange.bind(this);
  applyEntry = Mixin.prototype.applyEntry.bind(this);
  deactivate = Mixin.prototype.deactivate.bind(this);
  onCreated = Mixin.prototype.onCreated.bind(this);
  makeIdentifier = Mixin.prototype.makeIdentifier.bind(this);
  findFreeIdentifier = Mixin.prototype.findFreeIdentifier.bind(this);
  getTakenIdentifiers = Mixin.prototype.getTakenIdentifiers.bind(this);
  createSettingForm = Mixin.prototype.createSettingForm.bind(this);

  private interval: number;

  constructor() {
    super();
    this.title = 'Point container';
    this.description = 'placeholder'; // FIXME
    this.backend = new PointAPI();
    this.settings['name'] = NodeSetting.create({
      description: 'Point ID',
      value      : this.title,
      type       : SettingType.READONLY,
    });
    this.settings['priority'] = NodeSetting.create({
      description: 'Priority',
      value      : 1,
      type       : SettingType.NUMBER,
    });
    this.addOutput('value', Type.NUMBER);
    this.addInput('in', Type.NUMBER);
  }

  entryFactory() {
    return new Point();
  }

  onAdded() {
    Mixin.prototype.onAdded.call(this);
    this.syncWithBackend();
    this.interval = <any>(
      setInterval(() => this.syncWithBackend(), config.backendSync.pointRefreshIntervalSeconds * 1000)
    );
  }

  onRemoved() {
    return Mixin.prototype.onRemoved.call(this);
    // TODO: hmmm, wtf?
    // if (this.interval) clearInterval(this.interval);
  }

  namePrefix() {
    return 'Point';
  }

  syncWithBackend() {
    // refactor into mixin and reuse
    if (this.side !== Side.server) return;
    actor.process(() => {
      if (!this.entry()) return;
      return (this.backend as PointAPI)
        .fetchPointData(this.entryId())
        .then(data => {
          // we check again if the node is still active - this may have changed since the request was sent
          if (!this.entry()) return; // TODO: Replace with isActive()
          this.setOutputData(0, get(data, 'value'));
        })
        .catch(doNothing); // error response presumably means no value is set yet
    });
  }

  onInputUpdated() {
    let value = parseFloat(this.getInputData(0));
    if (isNaN(value) || value === null) {
      console.log('WARNING: Received non-numeric value on point inlet.');
      return;
    }
    this.setOutputData(0, value);
    const priority = Utils.clamp(this.settings['priority'].value, 1, 17);
    (this.backend as PointAPI)
      .updatePointData(this.entryId(), priority, value)
      .catch(e => this.container.server_editor_socket.emit(ERROR, `Could not patch point value. ${e.message}`));
  }

  createEntry() {
    const parentNode = this.container.container_node;
    const device_node = parentNode.container.container_node;
    const network_node = device_node.container.container_node;
    const edge_node = network_node.container.container_node;
    if (!(parentNode instanceof GroupContainerNode) || !(parentNode instanceof DeviceContainerNode)) {
      const errorMessage =
              'Error: Trying to create point, but point node does not exist inside a valid Group container hierarchy...';
      console.log(errorMessage);
      return Promise.reject(errorMessage);
    }
    return this.findFreeIdentifier().then(code => this.backend.create(
      <any>{
        device_id: (device_node as DeviceContainerNode).entryId(),
        point    : {
          network: (network_node as NetworkContainerNode).entryId(),
          edge   : (edge_node as EdgeContainerNode).entryId(),
          code,
          unit   : { type: 'number' },
          enabled: false,
        },
      },
      parentNode.entryId(),
                                          ),
    );
  }

  updateEntry(body: Point) {
    return this.backend.update(body, this.entryId());
  }

  deleteEntry() {
    return this.backend.delete(this.entryId());
  }
}

Container.registerNodeType(EDGES_PARENT_CONTAINER_TYPE, EdgesParentContainerNode, null, false);
Container.registerNodeType(EDGE_CONTAINER_TYPE, EdgeContainerNode, null, false);
Container.registerNodeType(NETWORK_CONTAINER_TYPE, NetworkContainerNode, null, false);
Container.registerNodeType(DEVICE_CONTAINER_TYPE, DeviceContainerNode, null, false);
Container.registerNodeType(GROUP_CONTAINER_TYPE, GroupContainerNode, null, false);
Container.registerNodeType(POINT_NODE_TYPE, PointNode, null, false);
