export enum BROADCAST {
  UPDATE_SETTINGS,
  UPDATE_OUTPUTS,
  UPDATE_TITLE,
  UPDATE_VALUE,
  UPDATE_STATE,
  UPDATE_NAME,
  UPDATE_PROPERTIES,
}

export enum NodeState {
  NORMAL   = 'normal', // the node default colour
  INFO     = 'info',
  WARNING  = 'warning',
  ERROR    = 'error', // when user added something that they shouldn't have
  DISABLED = 'disabled', // when a network is disabled
  ONLINE   = 'online', // when a device is online
  OFFLINE  = 'offline', // when a device is offline
}

