import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class FiltersOnlyGreaterNode extends Node {
  constructor() {
    super();

    this.title = NODE_INFO.filter["only-greater"].title;
    this.description = NODE_INFO.filter["only-greater"].description;
    this.addInput('input', Type.NUMBER);
    this.addInputWithSettings('threshold', Type.NUMBER, 0, 'threshold', false);
    this.addOutput('output', Type.NUMBER);
    this.properties['value'];
  }

  onAdded(){
    this.setOutputData(0, this.properties['value'] || null);
    this.onInputUpdated();
  }

  onAfterSettingsChange(){
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    let threshold = this.getInputData(1);
    if (val == null || threshold == null) {
      this.setOutputData(0, null);
    } else if (val > threshold) {
      this.setOutputData(0, val);
      this.properties['value'] = val;
      this.persistProperties(false, true);
    }
  }
}
Container.registerNodeType('filter/only-greater', FiltersOnlyGreaterNode);
