import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class ScheduleAfterhoursOffWidgetNode extends Node {
  currentMode: string = 'SCHEDULE';
  remainingRuntime: number = 0;
  AHRuntime: number;
  timeoutFunc;
  updateInterval: number = 5000;

  constructor() {
    super();
    this.title = NODE_INFO.widget["schedule-afterhours-off"].title;
    this.description = NODE_INFO.widget["schedule-afterhours-off"].description;
    this.addInput('modeInput', Type.STRING);
    this.addInput('scheduleInput', Type.BOOLEAN);
    this.addInput('scheduleNextStop', Type.NUMBER);
    this.addInputWithSettings('AH duration (mins)', Type.NUMBER, 120, 'Afterhours Duration (minutes)');
    this.addOutput('modeOutput', Type.STRING);
    this.addOutput('enableOutput', Type.BOOLEAN);
    this.addOutput('remainingRuntime (ms)', Type.NUMBER);
    this.AHRuntime = TimeUtils.timeConvert(120, 'minutes');
  }

  onCreated() {
    this.currentMode = 'OFF';
    this.onInputUpdated();
  }

  onAdded() {
    this.currentMode = 'OFF';
    this.onInputUpdated();
  }

  onInputUpdated() {
    const modeInput = this.getInputData(0);
    if (modeInput == null) {
      this.currentMode = null;
      this.setOutputData(0, '');
      this.setOutputData(1, null);
      if (this.timeoutFunc) clearInterval(this.timeoutFunc);
      this.remainingRuntime = 0;
      this.setOutputData(2, null);
      return;
    } else if (modeInput == 'OFF') {
      this.currentMode = 'OFF';
      this.setOutputData(0, 'OFF');
      this.setOutputData(1, false);
      if (this.timeoutFunc) clearInterval(this.timeoutFunc);
      this.remainingRuntime = 0;
      this.setOutputData(2, 0);
      return;
    } else if (modeInput == 'SCHEDULE' && this.currentMode !== modeInput) {
      this.currentMode = 'SCHEDULE';
      this.setOutputData(0, 'SCHEDULE');
      if (this.getInputData(1)) this.startSchedule();
      else {
        if (this.timeoutFunc) clearInterval(this.timeoutFunc);
        this.setOutputData(1, false);
        this.setOutputData(2, 0);
      }
    } else if (modeInput == 'AFTERHOURS' && this.currentMode !== modeInput) {
      this.startAfterhours();
    }
    if (this.currentMode === 'SCHEDULE') {
      const schedInput = this.getInputData(1);
      if(!schedInput) this.setOutputData(2, 0);
      if (this.inputs[1].updated) {
        if (schedInput) {
          this.setOutputData(1, true);
          this.startSchedule();
        } else {
          this.setOutputData(1, false);
          if (this.timeoutFunc) clearInterval(this.timeoutFunc);
          this.setOutputData(2, 0);
        }
      }
      if (this.getInputData(2) == null) this.setOutputData(2, null);
      else if (this.inputs[2].updated) {
        const nextStop = this.getInputData(2);
        if (nextStop) this.setOutputData(2, nextStop - Date.now());
      }
    } else if (
      TimeUtils.timeConvert(this.getInputData(3), 'minutes') !== this.AHRuntime &&
      this.currentMode === 'AFTERHOURS'
    ) {
      this.startAfterhours();
    }
  }

  startSchedule() {
    if (this.timeoutFunc) clearInterval(this.timeoutFunc);
    const nextStop = this.getInputData(2);
    if (nextStop) {
      this.remainingRuntime = nextStop - Date.now();
      this.setOutputData(2, this.remainingRuntime);
      this.timeoutFunc = setInterval(() => {
        this.remainingRuntime -= this.updateInterval;
        this.setOutputData(2, this.remainingRuntime);
      }, this.updateInterval);
    } else this.setOutputData(2, null);
    this.setOutputData(1, true);
  }

  startAfterhours() {
    this.currentMode = 'AFTERHOURS';
    this.setOutputData(0, 'AFTERHOURS');
    this.setOutputData(1, true);
    clearInterval(this.timeoutFunc);
    const AHDuration = TimeUtils.timeConvert(this.getInputData(3) || 120, 'minutes');
    this.AHRuntime = AHDuration;
    this.remainingRuntime = AHDuration;
    this.setOutputData(2, AHDuration);
    this.timeoutFunc = setInterval(() => {
      this.remainingRuntime -= this.updateInterval;
      if (this.remainingRuntime <= 0) {
        this.setOutputData(1, false);
        this.setOutputData(2, 0);
        this.currentMode = 'SCHEDULE';
        this.setOutputData(0, 'SCHEDULE');
        clearInterval(this.timeoutFunc);
        if (this.getInputData(1)) this.startSchedule();
        return;
      }
      this.setOutputData(2, this.remainingRuntime);
    }, this.updateInterval);
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('widget/schedule-afterhours-off', ScheduleAfterhoursOffWidgetNode);
