import axios from 'axios';
import config from '../../../config';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils, { TIME_TYPE } from '../../utils/time-utils';
import SystemUtils from '../system/system-utils';

const btoa = require('btoa');

class GetSchedulesNode extends Node {
  enable;

  constructor() {
    super();
    this.title = NODE_INFO.nube["get-schedules"].title;
    this.description = NODE_INFO.nube["get-schedules"].description;
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addOutput('data', Type.JSON);
    this.addOutput('error', Type.BOOLEAN);

    this.settings['interval'] = NodeSetting.create({
      description: 'Update Interval (min 10 seconds)',
      type: SettingType.NUMBER,
      value: 60,
    });
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: TIME_TYPE.SECONDS, text: 'Seconds' },
          { value: TIME_TYPE.MINUTES, text: 'Minutes' },
          { value: TIME_TYPE.HOURS, text: 'Hours' },
        ],
      },
      value: TIME_TYPE.SECONDS,
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
  }

  async onAdded() {
    if (this.properties['scheduleData']) this.setOutputData(0, this.properties['scheduleData']);
    await this.onAfterSettingsChange();
  }

  async onAfterSettingsChange() {
    this.setExecuteInterval();
    await this.onInputUpdated();
  }

  async onInputUpdated() {
    await this.getDittoSchedules();
  }

  async onExecute() {
    await this.getDittoSchedules();
  }
 
  async getDittoSchedules() {
    if (this.side !== Side.server || !this.getInputData(0)) return;
    try {
      const deviceIdFromPlat = await SystemUtils.getDeviceIdDb();
      const { username, password } = config.ditto;
      const dittoUrl =  config.ditto.baseURL
      const response = await axios({
        method: 'get',
        url:
        dittoUrl + deviceIdFromPlat + '/features/schedules/properties',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Basic ${btoa(`${username}:${password}`)}`,
        },
      });
      this.setOutputData(0, response.data, false);
      this.setOutputData(1, false);
      this.properties['scheduleData'] = response.data;
      this.persistProperties(false, true);
    } catch (error) {
      this.setOutputData(0, null);
      this.setOutputData(1, error);
      this.debugErr(error);
      return;
    }
  }

  setExecuteInterval() {
    let interval = this.settings['interval'].value;
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    interval = Math.max(interval, 10000);
    this.EXECUTE_INTERVAL = interval;
  }
}
Container.registerNodeType('nube/get-schedules', GetSchedulesNode);
