import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class TextCharAtIndexNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.string["char-at-index"].title;
    this.description = NODE_INFO.string["char-at-index"].description;
    this.addInput('input', Type.STRING);
    this.addInputWithSettings('index', Type.NUMBER, 0, 'Index', false);
    this.addOutput('output', Type.STRING);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    const text = this.getInputData(0);
    const index = this.getInputData(1);
    if (text != null && index != null) {
      if (index === -1) this.setOutputData(0, text.slice(-1));
      else this.setOutputData(0, text.slice(index, index + 1));
    } else this.setOutputData(0, null);
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('string/char-at-index', TextCharAtIndexNode);
