import { isNull } from '../../../../utils/helper';
import { MODBUS_RTU_DEVICE, MODBUS_RTU_POINT } from '../../../constants';
import { Container } from '../../../container';
import { Node, NodeState } from '../../../node';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import MathUtils, { MATH_FUNC_TYPE } from '../../../utils/MathUtils';
import BACnetEnumsUnits from '../../../utils/points/BACnet-enums-units';

export class ModbusPointNode extends Node {
  dynamicInputsExist: boolean = false;
  dynamicSettingsExist: boolean = false;
  dynamicInputStartPosition: number = 0;
  inputCount: number;
  dynamicMinInputs: number = 0;
  nullableInputs: boolean = true;
  dynamicOutputsExist: boolean = false;
  dynamicInputsStartName: string = 'in';
  dynamicOutputsStartName: string = 'out';
  dynamicInputsType: Type = Type.NUMBER;
  previousPointType;

  lastInputState: any;

  // node inputs
  inAlarmTrigger = 0;
  inHistoryTrigger = 1;
  inInput = 2;

  // poll count
  pollCount = 0;
  pollCountFail = 0;

  // node outputs
  outVal = 0;
  outError = 1;
  outModbusMessage = 2;
  outMessageJson = 3;
  outArray = 4;
  constructor() {
    super();
    this.title = 'Modbus Point';
    this.description =
      `## Description\n ` +
      ` This node is used as a modbus device point.\n ` +
      ` The points for the device will be added inside the device node (right click and **open** on the network node to add the device and points) \n ` +
      `   \n ` +
      `## Point Address\n ` +
      `   \n ` +
      `### Coils\n ` +
      `   \n ` +
      ` Valid Coil range is between 0 and 10001. This will allow any modbus client to read and write to those coli ranges\n ` +
      `   \n ` +
      `### Holding Register\n ` +
      `   \n ` +
      ` Valid Holding Register range is between 10001 and 19999. This will allow any modbus client to read and write to those coli ranges\n ` +
      `   \n ` +
      `## Enable\n ` +
      `   \n ` +
      ` This will enable/disable the point from polling \n ` +
      `   \n `;

    this.addInput('[name]', Type.STRING);
    this.addOutput('output', Type.NUMBER);
    this.addOutput('error', Type.STRING);
    this.addOutput('output-msg', Type.STRING);
    this.addOutput('output-json', Type.STRING);
    this.addOutput('output-modbus-array', Type.STRING);
    this.properties['pointVal'] = null;
    this.properties['pointAddress'] = 0;
    /**
      Default point Settings (Will be in point class)
   */
    this.settings['pointEnable'] = NodeSetting.create({
      description: 'Point enable',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['value_group'] = NodeSetting.create({
      description: 'Point settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.addInputWithSettings('input', Type.NUMBER, 0, 'point write value', false);
    this.settings['valOffset'] = NodeSetting.create({
      description: 'Out value offset',
      value: 1000,
      type: SettingType.NUMBER,
    });
    this.settings['valPrecision'] = NodeSetting.create({
      description: 'Out value precision',
      value: 0,
      type: SettingType.NUMBER,
    });
    this.settings['units_group'] = NodeSetting.create({
      description: 'Units (Save to get units)',
      type: SettingType.GROUP,
    });
    this.settings['unitsType'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: BACnetEnumsUnits.unitCategory,
      },
    });
    this.settings['units'] = NodeSetting.create({
      description: 'Units',
      value: BACnetEnumsUnits.COMMON_METRIC.NO_UNITS,
      type: SettingType.DROPDOWN,
    });

    /**
      Point Math settings
   */
    this.settings['math_group'] = NodeSetting.create({
      description: 'Math settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['mathFunc'] = NodeSetting.create({
      description: 'Math on output value (optional)',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: MATH_FUNC_TYPE.NA, text: 'na' },
          { value: MATH_FUNC_TYPE.ADD, text: 'add' },
          { value: MATH_FUNC_TYPE.SUBTRACT, text: 'subtract' },
          { value: MATH_FUNC_TYPE.MULTIPLY, text: 'multiply' },
          { value: MATH_FUNC_TYPE.DIVIDE, text: 'divide' },
          { value: MATH_FUNC_TYPE.BOOL_INVERT, text: 'bool invert' },
          { value: MATH_FUNC_TYPE.ONE_TO_TRUE, text: 'convert 1/0 to true/false' },
          { value: MATH_FUNC_TYPE.TRUE_TO_ONE, text: 'convert true/false to 1/0' },
        ],
      },
      value: MATH_FUNC_TYPE.NA,
    });
    this.settings['mathValue'] = NodeSetting.create({
      description: 'Enter a value',
      value: 1,
      type: SettingType.NUMBER,
    });
    /**
     Modbus (Just for modbus)
     */
    this.settings['modbus_group'] = NodeSetting.create({
      description: 'Modbus point settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['pointType'] = NodeSetting.create({
      description: 'Select a point type',
      value: 1,
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 1, text: `Read Coil's` },
          // { value: 21, text: `Read DO` },
          { value: 2, text: `readDiscreteInput's` },
          // { value: 22, text: `Read DI` },
          { value: 3, text: `readHoldingRegister's` },
          // { value: 23, text: `Read AO` },
          { value: 4, text: `readInputRegister's` },
          // { value: 24, text: `Read AI` },
          { value: 5, text: `writeCoil` },
          // { value: 25, text: `Write DO` },
          { value: 6, text: `writeRegister` },
          // { value: 26, text: `Write AO` },
          // { value: 15, text: `writeCoil's` },
          { value: 16, text: `writeRegister's` },
        ],
      },
    });
    this.addInputWithSettings('pointAddress', Type.NUMBER, 1, 'Register address', false);
    this.settings['pointTimeout'] = NodeSetting.create({
      description: 'Point Timeout (MS)',
      value: 1000,
      type: SettingType.NUMBER,
    });
    this.settings['offset'] = NodeSetting.create({
      description: 'Register offset',
      value: 1,
      type: SettingType.NUMBER,
    });
    this.settings['dataType'] = NodeSetting.create({
      description: 'Set data type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: 'na' }, // use reg length from setting
          { value: 1, text: 'INT-16' }, // 1 reg
          { value: 2, text: `UINT-16` }, // 1 reg
          { value: 3, text: 'INT-32' }, // 2 reg
          { value: 4, text: 'UINT-32' }, // 2 reg
          { value: 5, text: 'FLOAT-32' }, // 2 reg // for support of nube hardware BEB_LEW as a float
          // { value: 6, text: 'INT64' },
          // { value: 7, text: 'UINT64' },
          { value: 8, text: 'DOUBLE-64' }, // 4 reg
        ],
      },
      value: 0,
    });
    this.settings['dataEndian'] = NodeSetting.create({
      description: 'Set byte order',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: 'LE-Byte BE-Word' },
          { value: 1, text: 'LE-Byte LE-Word' },
          { value: 2, text: 'BE-Byte LE-Word' },
          { value: 3, text: 'BE-Byte BE-Word' },
        ],
      },
      value: 0,
    });
    this.setSettingsConfig({
      groups: [
        { pointAddress: {}, offset: {}, pointTimeout: {} },
        { dataType: {}, dataEndian: {} },
        { pointLimitEnable: {}, limitLow: {}, limitHigh: {} },
        { mathFunc: {}, mathValue: {} },
        { valOffset: {}, valPrecision: {} },
      ],
      conditions: {
        offset: setting => {
          const val = setting['pointType'].value;
          return ![5, 6].includes(val);
        },
        dataType: setting => {
          const val = setting['pointType'].value;
          return ![1, 2, 5, 15].includes(val);
        },
        dataEndian: setting => {
          const val = setting['pointType'].value;
          return ![1, 2, 5, 15].includes(val);
        },
      },
    });
  }

  onCreated() {
    this.updateTitle();
    this.nodeColour()
  }

  onAdded() {
    this.nodeColour()
    if (this.side !== Side.server) return;
    this.onInputUpdated();
    this.updateTitle();
  }

  onRemoved() { }

  updateTitle() {
    let pointAddress = this.getInputData(1);
    this.title = `Modbus Point (FC: ${this.settings['pointType'].value}, AD: ${pointAddress})`;
    this.broadcastSettingsToClients();
  }

  onInputUpdated() {
    // priority array
    let pointType = this.settings['pointType'].value;
    if ([5, 6, 15, 16, 25, 26].includes(pointType)) {
      const input = this.getInputData(1);
      if (isNull(input)) return;
      this.setOutputData(this.outModbusMessage, `write val ${input}`);
      // lastInputState is to stop a new value that is input into the node but the value hasn't updated
      if (input !== this.lastInputState) {
        this.properties['pointVal'] = input;
        // Persist the pointVal. (This is for outputs nodes only like AO, or BO)
        this.persistProperties(false, true);
        this.lastInputState = input;
      } else if (typeof this.lastInputState === 'undefined') {
        this.lastInputState = input;
      }
    }

    //update point pointAddress
    if (this.inputs[2].updated) {
      const pointAddress = this.getInputData(2);
      if (!isNull(pointAddress)) {
        this.properties['pointAddress'] = pointAddress;
        this.persistProperties(false, true);
      } else {
        this.properties['pointAddress'] = 0;
        this.persistProperties(false, true);
      }
    }

    //update node name
    if (this.inputs[0].updated) {
      let nodeName = this.getInputData(0);
      if (!isNull(nodeName)) {
        this.name = nodeName;
        this.broadcastNameToClients();
      }
    }
  }

  onAfterSettingsChange() {
    const pointAddress = this.getInputData(2);
    if (!isNull(pointAddress)) {
      this.properties['pointAddress'] = pointAddress;
      this.persistProperties(false, true);
    } else {
      this.properties['pointAddress'] = 0;
      this.persistProperties(false, true);
    }
    this.onInputUpdated();
    this.updateTitle();
    this.nodeColour()
  }

  sendJson(pointValue) {
    return {
      name: this.name,
      pointValue: pointValue,
      enable: this.settings['pointEnable'].value,
      address: this.settings['pointAddress'].value,
      offset: this.settings['offset'].value,
      pointType: this.settings['pointType'].value,
      pollCount: this.pollCount,
      pollCountFail: this.pollCountFail,
    };
  }
  /**
   * Gets a the modbus values from the network to update the point node values
   */
  subscribe(payload) {
    let pointType = this.settings['pointType'].value;
    this.setOutputData(this.outArray, payload.res.data);
    this.setOutputData(this.outError, false, true);
    this.nodeColour(false)
    if ([5, 6, 15, 16, 25, 26].includes(pointType)) {
      if (payload.payload === 'writeOk') {
        this.setOutputData(this.outVal, this.properties['pointVal']);
        this.setOutputData(this.outMessageJson, this.sendJson(this.properties['pointVal']));
        this.pollCount++;
      } else {
        this.setOutputData(this.outModbusMessage, `write fail`, true);
        this.setOutputData(this.outMessageJson, this.sendJson(null));
        this.pollCountFail++;
      }
    } else {
      const mathFunc = this.settings['mathFunc'].value;
      const mathValue = this.settings['mathValue'].value;
      let out: any;
      if (mathFunc !== 0) {
        if (MathUtils.validateNumbers(payload.payload, mathValue)) {
          out = MathUtils.mathSwitch(mathFunc, payload.payload, mathValue);
          this.setOutputData(this.outVal, out);
          this.setOutputData(this.outMessageJson, this.sendJson(out));
          this.setOutputData(this.outModbusMessage, false, true);
          this.pollCount++;
        }
      } else {
        if (typeof payload.payload === 'number') {
          this.setOutputData(this.outVal, payload.payload);
          this.setOutputData(this.outMessageJson, this.sendJson(payload.payload));
          this.setOutputData(this.outModbusMessage, false, true);
          this.pollCount++;
        } else if (typeof payload.payload === 'boolean') {
          this.setOutputData(this.outVal, payload.payload ? 1 : 0); // convert bool to int
          this.setOutputData(this.outMessageJson, this.sendJson(payload.payload ? 1 : 0)); // convert bool to int
          this.setOutputData(this.outModbusMessage, false, true);
          this.pollCount++;
        }
      }
    }
  }
  // this is called from the modbus network node
  subscribeError(e: any) {
    this.setOutputData(this.outError, true, true);
    this.setOutputData(this.outModbusMessage, e, true);
    this.pollCountFail++;
    this.nodeColour(true)
  }

  nodeColour(err?: boolean) {
    const pointEnable = this.settings['pointEnable'].value
    if (pointEnable === false) {
      this.setNodeState(NodeState.DISABLED);
    } else if (pointEnable === true && !err){
      this.setNodeState(NodeState.NORMAL)
    } else if (err === true){
      this.setNodeState(NodeState.ERROR)
    }
  }

}

Container.registerNodeType(MODBUS_RTU_POINT, ModbusPointNode, MODBUS_RTU_DEVICE);
