import { exec } from 'child_process';
const os = require('os');
const si = require('systeminformation');
const pkg = require('../../../package.json');

export default class OsUtils {
  /**
   * Runs a system command
   * @param command {string} system command. Example 'sudo reboot'
   * @param options {string} system command options
   * @returns {Promise}
   *
   */

  static execute(command, options) {
    return new Promise((resolve, reject) => {
      exec(command, options, (err, stdout, stderr) => {
        if (err) {
          reject(err);
          return;
        } else {
          resolve({
            stdout: stdout,
            stderr: stderr,
          });
        }
      });
    });
  }

  /**
   * Returns hostname
   */
  static hostName() {
    return new Promise<any>((resolve, reject) => {
      try {
        resolve(os.hostname());
      } catch (e) {
        reject('Unknown');
      }
    });
  }

  /**
   * Returns Current Wires Version
   */
  static getCurrentWiresVersion() {
    return new Promise<any>((resolve, reject) => {
      try {
        resolve(pkg.version);
      } catch (e) {
        reject('Unknown');
      }
    });
  }

  /**
   * Returns host cpu temp
   * main: main temperature (avg)
   * cores: array of temperatures
   * max: max temperature
   */
  static cpuTemperature() {
    return new Promise<any>((resolve, reject) => {
      try {
        resolve(si.cpuTemperature());
      } catch (e) {
        reject('Unknown');
      }
    });
  }

  /**
    Returns File System and File System Stats
    fs: name of file system
    type: type of file system
    size: sizes in bytes
    used: used in bytes
    use: used in %
    mount: mount point
   */
  static fsSize() {
    return new Promise<any>((resolve, reject) => {
      try {
        resolve(si.fsSize());
      } catch (e) {
        reject('Unknown');
      }
    });
  }

  /**
    Returns host system info
   */
  static systemInfo() {
    return new Promise<any>((resolve, reject) => {
      try {
        // define all values, you want to get back
        const valueObject = {
          cpu: '*',
          osInfo: 'platform, release',
          system: 'model, manufacturer',
        };
        resolve(si.get(valueObject));
      } catch (e) {
        reject('Unknown');
      }
    });
  }


  /**
  Check if host is a BBBB
 */
  static systemModelBBB() {
    return new Promise<any>((resolve, reject) => {
      let host = "GENERIC AM33XX (FLATTENED DEVICE TREE)"
      OsUtils.systemInfo()
        .then(e => {
          if (e.system.model === host) {
            resolve(true)
          } else resolve(false)
        }).catch(err => {
          reject(false)
        })
    });
  }

  /**
  Check if host is a Rubix Compute
  */
  static systemModelRubixCompute() {
    return new Promise<any>((resolve, reject) => {
      let host = "BCM2835"
      OsUtils.systemInfo()
        .then(e => {
          if (e.system.model === host) {
            resolve(true)
          } else resolve(false)
        }).catch(err => {
          reject(false)
        })
    });
  }

  /**
    Returns host time
    current: local (server) time
    uptime: uptime in number of seconds
    timezone: e.g. GMT+0200
    timezoneName: e.g. CEST
   */
  static systemTime() {
    return new Promise<any>((resolve, reject) => {
      try {
        resolve(si.time());
      } catch (e) {
        reject('Unknown');
      }
    });
  }

  /**
    Returns host memory
    total: total memory in bytes
    free: not used in bytes
    used: used (incl. buffers/cache)
    active: used actively (excl. buffers/cache)
    available: potentially available (total - active)
    buffers: used by buffers
    cached: used by cache
    slab: used by slab
    buffcache: used by buffers+cache
    swaptotal: swap total
    swapused: swap used
    swapfree: swap free
   */
  static systemMem() {
    return new Promise<any>((resolve, reject) => {
      try {
        resolve(si.mem());
      } catch (e) {
        reject('Unknown');
      }
    });
  }


  static systemAll() {
    return new Promise<any>(async (resolve, reject) => {
      let all = []
      await OsUtils.systemInfo().then(e => {
        console.log(' systemInfo', e)
        all.push({ systemInfo: e })
      }).catch(err => {
        console.log('ERROR systemInfo', err)
      })
      await OsUtils.systemTime().then(e => {
        console.log(' systemTime', e)
        all.push({ systemTime: e })
      }).catch(err => {
        console.log('ERROR systemTime', err)
      })
      await OsUtils.fsSize().then(e => {
        console.log(' fsSize', e)
        all.push({ fsSize: e })
      }).catch(err => {
        console.log('ERROR fsSize', err)
      })
      await OsUtils.cpuTemperature().then(e => {
        console.log(' cpuTemperature', e)
        all.push({ cpuTemperature: e })
      }).catch(err => {
        console.log('ERROR cpuTemperature', err)
      })
      await OsUtils.hostName().then(e => {
        console.log(' hostName', e)
        all.push({ hostName: e })
      }).catch(err => {
        console.log('ERROR hostName', err)
      })
      await OsUtils.getCurrentWiresVersion().then(e => {
        console.log(' getCurrentWiresVersion', e)
        all.push({ getCurrentWiresVersion: e })
      }).catch(err => {
        console.log('ERROR getCurrentWiresVersion', err)
      })
      resolve(all)

    });
  }


}
