import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';
import Statistics from './statistics';
import { NODE_INFO } from '../../node-info';

class MedianNode extends Statistics {
  constructor() {
    super();
    this.title = NODE_INFO.statistics.median.title;
    this.description = NODE_INFO.statistics.median.description;
    this.addOutput('median', Type.NUMBER);
  }

  onInputUpdated() {
    const data = this.getDefinedInputsOrSettingsValues();
    const median = Utils.stat.median(data);
    this.setOutputData(0, median);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('statistics/median', MedianNode);
