import { isFunction } from './helper';
import { reliability_logger } from './loggers';

const logger = reliability_logger;

//TODO: should be notify in frontend instead of using `alert`
function logError(error, ...args: any[]) {
  logger.error(error);
  if (typeof alert !== 'undefined') {
    alert(error.toString());
  }
}

export const ErrorHandler = (target: any, propertyKey: string, descriptor: any) => {
  const fn = descriptor.value!;
  descriptor.value = function DescriptorValue(...args: any[]) {
    try {
      return fn.apply(this, args);
    } catch (error) {
      logError(error, args);
    }
  };
  return descriptor;
};

export const ErrorCallbackHandler = (errHandler: Function) => {
  return (target: any, propertyKey: string, descriptor: any) => {
    const fn = descriptor.value!;
    descriptor.value = function DescriptorValue(...args: any[]) {
      try {
        return fn.apply(this, args);
      } catch (error) {
        if (isFunction(errHandler)) {
          errHandler(error, args);
        } else {
          logError(error, args);
        }
      }
    };
    return descriptor;
  };
};

export const AsyncErrorHandler = {};
