import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class DelayMeterNode extends Node {
  lastTime;
  lastReset: boolean;

  constructor() {
    super();
    this.title = NODE_INFO.streams["stream-delay-meter"].title;
    this.description = NODE_INFO.streams["stream-delay-meter"].description;
    this.addInput('input');
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('ms', Type.NUMBER);
    this.lastReset = false;
  }

  onInputUpdated() {
    const reset = this.getInputData(1);
    if (reset && !this.lastReset) {
      this.lastTime = null;
      this.setOutputData(0, null);
      this.lastReset = reset;
      return;
    } else if (!reset && this.lastReset) {
      this.lastReset = reset;
      return;
    }

    if (this.inputs[0].updated && this.inputs[0].data != null) {
      if (this.lastTime != null) {
        let delay = Date.now() - this.lastTime;
        this.setOutputData(0, delay);
      }

      this.lastTime = Date.now();
    }
  }
}
Container.registerNodeType('streams/stream-delay-meter', DelayMeterNode);
