import FileUtils from './file-utils';

export default class UuidUtils {
  /**
   * Generates a UUID 28 digit
   * @returns {string}
   */
  static createUUID = (a: string = ''): string =>
    a
      ? ((Number(a) ^ (Math.random() * 16)) >> (Number(a) / 4)).toString(16)
      : `${1e7}-${1e3}-${4e3}-${8e3}-${1e11}`.replace(/[018]/g, UuidUtils.createUUID);
  /**
   * Generates a UUID 8 digit
   * @returns {string}
   */
  static create8DigId = (a: string = ''): string =>
    a
      ? ((Number(a) ^ (Math.random() * 16)) >> (Number(a) / 4)).toString(16)
      : `${1e7}`.replace(/[018]/g, UuidUtils.create8DigId);

  /**
   * Generates a UUID 12 digit
   * @returns {string}
   */
  static create12DigId = (a: string = ''): string =>
    a
      ? ((Number(a) ^ (Math.random() * 16)) >> (Number(a) / 4)).toString(16)
      : `${1e7}-${4e3}`.replace(/[018]/g, UuidUtils.create12DigId);

  static isUUID(data: string): boolean {
    const dataArray: string[] = data
      .toString()
      .trim()
      .split('-');
    return dataArray.length >= 1 && dataArray[0].length === 8;
  }

  static async isUUIDFile(filePath): Promise<boolean> {
    try {
      let fileContent = await FileUtils.readFile(filePath);
      if (fileContent) {
        return UuidUtils.isUUID(fileContent);
      } else return false;
    } catch (e) {
      return false;
    }
  }

  static async makeUUIDFile(dirPath, fileName): Promise<void> {
    try {
      await FileUtils.createDirectory(dirPath);
      await FileUtils.writeFile(`${dirPath}/${fileName}`, UuidUtils.create8DigId());
    } catch (e) {
      throw e;
    }
  }
}
