import { Colors, create } from 'logplease';
import app from '../app';
import { ContainerRegistry } from '../nodes/container';
import { Node } from '../nodes/node';
import { Side } from '../nodes/node-interface';
import NodeUtils from '../nodes/node-utils';
import Namespace = SocketIO.Namespace;

const log = create('server', { color: Colors.Yellow });
const SLOTS_VALUES_INTERVAL = 200;

export class EditorServerSocket {
  io_root: SocketIO.Server;
  io: Namespace;

  constructor(io_root: SocketIO.Server) {
    this.io_root = io_root;
    const io = io_root.of('/editor');
    this.io = io;

    io.on('connection', function(socket) {
      log.debug('New client connected to editor');

      socket.on('disconnect', () => {
        log.debug('Client disconnected from editor');
      });

      // join client to container room
      socket.on('room', function(room) {
        if ((<any>socket).room != null) {
          socket.leave((<any>socket).room, function() {
            // it is necessary to waiting for leave room before join only if it's the same room (same id)
            (<any>socket).room = room;
            socket.join(room, () => {
              log.debug('Switch editor room to [' + room + ']');
            });
          });
        } else {
          (<any>socket).room = room;
          socket.join(room, () => {
            log.debug('Join to editor room [' + room + ']');
          });
        }
      });

      socket.on('nodeMessageToServerSide', function(n) {
        let node = EditorServerSocket.checkContainerNodeValidity(n, Side.server);
        node && node['onGetMessageToServerSide'](n.message);
      });

      //redirect message
      socket.on('nodeMessageToEditorSide', function(n) {
        let node = EditorServerSocket.checkContainerNodeValidity(n, Side.editor);
        node && app.server.editorSocket.io.in(n.cid).emit('nodeMessageToEditorSide', n);
      });

      //redirect message
      socket.on('nodeMessageToDashboardSide', function(n) {
        let node = EditorServerSocket.checkContainerNodeValidity(n, Side.dashboard);
        node && app.server.dashboardSocket.io.in(n.cid).emit('nodeMessageToDashboardSide', n);
      });
    });

    setInterval(() => {
      this.sendSlotsValues(io);
    }, SLOTS_VALUES_INTERVAL);
  }

  sendSlotsValues(io) {
    const connectedSocket = io.connected;
    for (let key of Object.keys(connectedSocket)) {
      const room: number = +Object.keys(io.connected[key].rooms).filter(x => x !== key)[0];
      let slotsValues = NodeUtils.getNodesIOValues(room);
      io.to(room).emit('nodes-io-values', slotsValues);
    }
  }

  /**
   * @param {Node} n
   * @param {Side} side
   * @returns {Node} Node if Container and Node is valid otherwise null
   */
  static checkContainerNodeValidity(n: Node, side: Side): Node {
    let cont = ContainerRegistry.instance().get(n.cid);
    if (!cont) {
      log.error(
        `Can't send node message to ${side}-side. Container id ["${n.cid}'] does not exist`,
      );
      return null;
    }

    let node = cont.getSubNode(n.id);
    if (!node) {
      log.error(
        `Can't send node message to ${side}-side. Node id ["${n.cid}'/'${n.id}'] does not exist`,
      );
      return null;
    }
    return node;
  }
}
