import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class SetResetNode extends Node {
  out = null;


  constructor() {
    super();

    this.title = NODE_INFO.latch["set-reset"].title;
    this.description =NODE_INFO.latch["set-reset"].description;

    this.addInput('set', Type.BOOLEAN);
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
    this.settings['lock'] = NodeSetting.create({
      description: 'Reset when set=true',
      value: true,
      type: SettingType.BOOLEAN,
    });
  }

  onAdded() {
 
    this.setOutputData(0, false);
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let set = this.inputs[0].data;
    let reset = this.inputs[1].data;
    let lock = this.settings['lock'].value;
    let out = this.out;
    if (set && !reset) {
      this.setOutputData(0, true);
      this.out = true;
    } else if (lock && reset && out && set) {
      //when lock = true
      this.setOutputData(0, false);
      this.out = false;
    } else if (!set && out && reset) {
      //when lock = false
      this.setOutputData(0, false);
      this.out = false;
    }
  }
}
Container.registerNodeType('latch/set-reset', SetResetNode);
