var crypto = require('crypto');
const aesCmac = require('node-aes-cmac').aesCmac;

const IV_DEFAULT = Buffer.alloc(16, 0);

export enum LORA_RAW_ERROR {
  LRERR_NOERR,
  LRERR_INVALID_KEY,
  LRERR_INVALID_IV,
  LRERR_INVALID_PAYLOAD,
  LRERR_INVALID_CMAC,
};

/* Encrypts a payload for the Nube iO LoRaRaw protocol
*   AES128 with AES-CMAC
*   Nonce must be handled externally
*   
*   Address - Buffer (4 bytes)
*   payload - Buffer (any size)
*   key     - Buffer (16 bytes)
*   iv     - Buffer (16 bytes). All 0 as DEFAULT
*
* Returns array[2]
*   - Buffer (mod16 bytes) on success or null on error
*   - LORA_RAW_ERROR code
*/
export function loraRawEncryptBuffer(address, payload, key, iv = IV_DEFAULT) {
  if (key.length !== 16) {
    return [null, LORA_RAW_ERROR.LRERR_INVALID_KEY];
  }
  if (iv.length !== 16) {
    return [null, LORA_RAW_ERROR.LRERR_INVALID_IV];
  }

  var cipher = crypto.createCipheriv('aes-128-cbc', key, iv)
    .setAutoPadding(false);

  if (payload.length % 16) {
    payload = Buffer.concat([payload, Buffer.alloc(16 - (payload.length % 16), 0)]);
  }
  payload = cipher.update(payload);
  payload = Buffer.concat([payload, cipher.final()]);

  var cmacBlock = Buffer.concat([address, payload.slice(0, 12)], 16);
  cmacBlock = Buffer.concat([cmacBlock, payload], 16 + payload.length);
  var cmac = aesCmac(key, cmacBlock, { returnAsBuffer: true });

  payload = Buffer.concat([payload, cmac.slice(0, 4)], payload.length + 4)
  return [payload, LORA_RAW_ERROR.LRERR_NOERR];
}

/* Decrypts payload for Nube iO LoRaRaw protocol
*   AES128 with AES-CMAC
*   
*   Address - Buffer (4 bytes)
*   payload - Buffer (mod16 bytes + 4 bytes for CMAC)
*   key     - Buffer (16 bytes)
*   iv     - Buffer (16 bytes). All 0 as DEFAULT
*
* Returns array[2]
*   - Buffer (mod16 bytes) on success or null on error
*   - LORA_RAW_ERROR code
*/
export function loraRawDecryptBuffer(address, payload, key, iv = IV_DEFAULT) {
  if (key.length !== 16) {
    return [null, LORA_RAW_ERROR.LRERR_INVALID_KEY];
  }
  if (iv.length !== 16) {
    return [null, LORA_RAW_ERROR.LRERR_INVALID_IV];
  }

  if (payload.length % 16 !== 4) {
    return [null, LORA_RAW_ERROR.LRERR_INVALID_PAYLOAD];
  }

  var cmacBlock = Buffer.concat([address, payload], 16);
  cmacBlock = Buffer.concat([cmacBlock, payload], 16 + payload.length);
  var cmac = aesCmac(key, cmacBlock, { returnAsBuffer: true }).slice(0, 4);
  if (!cmac.compare(payload, payload.length - 4)) {
    return [null, LORA_RAW_ERROR.LRERR_INVALID_CMAC];
  }

  var cipher = crypto.createDecipheriv('aes-128-cbc', key, iv)
    .setAutoPadding(false);

  payload = cipher.update(payload.slice(0, payload.length - 4));
  payload = Buffer.concat([payload, cipher.final()]);

  return [payload, LORA_RAW_ERROR.LRERR_NOERR];
}
