import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class StringPriorityNode extends FlexibleNode {

  constructor() {
    super();
    this.dynamicInputsType = Type.STRING;
    this.title = NODE_INFO.point["string-writable"].title;
    this.description = NODE_INFO.point["string-writable"].description; 
   
    this.addOutput('out', Type.STRING);
  }

  onAdded() {
    this.size = this.computeSize();
    this.onInputUpdated();
  }

  onInputUpdated() {
    for (let i = this.dynamicInputStartPosition; i < this.getInputsCount(); i++) {
      let inputValue = this.getInputData(i);
      if (inputValue) {
        this.setOutputData(0, inputValue);
        return;
      }
    }
    this.setOutputData(0, '');
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('point/string-writable', StringPriorityNode);
