import * as NeDBDataStore from 'nedb';
import { db_logger as logger } from '../../utils/loggers';

export default interface BaseDatabase {
  asyncLoadDatabase(dataPath: string, cleanUpInterval?: any): Promise<any>;
}

export abstract class AbstractDatabase implements BaseDatabase {

  protected db: NeDBDataStore;

  asyncLoadDatabase(dataPath: string, cleanUpInterval?: any): Promise<any> {
    const dbPath = `${dataPath}/${this.dbName()}.db`;
    logger.info(`Loading database '${this.dbName()}' from '${dbPath}'...`);
    this.db = new NeDBDataStore({ filename: `${dbPath}` });
    this.db.persistence.setAutocompactionInterval(cleanUpInterval);
    return this.load();
  };

  protected abstract dbName(): string;

  protected load(): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      this.db.loadDatabase(err => {
        if (err) {
          logger.error(`Failed when loading database ${this.dbName()}: ${err.stack}`);
          reject(err);
        }
        return resolve();
      });
    });
  };
}
