import * as _ from 'lodash';
import { ErrorHandler } from '../../../utils/decorators';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';

class JsonFilterMultiple extends Node {
  outputsCount = 0;
  filterKeys: Array<string> = [];

  inInput: number = 0;

  outError: number = 0;
  outJsonOutput: number = 1;

  constructor() {
    super();
    this.title = NODE_INFO.json['json-filter-multiple'].title;
    this.description = NODE_INFO.json['json-filter-multiple'].description;
    this.addInput('input', Type.JSON);
    this.addOutput('error', Type.STRING);
    this.addOutput('json-output', Type.JSON);
    this.settings['outputs_count'] = NodeSetting.create({
      description: 'Number of Outputs',
      value: 1,
      type: SettingType.NUMBER,
    });

    this.setSettingsConfig({
      groups: [{ outputs_count: {} }],
    });
  }

  init() {
    this.changeOutputsAndSettings();
  }

  onCreated() {
    this.renameInputsOutputs();
  }

  onAdded() {
    this.outputsCount = this.settings['outputs_count'].value;
    this.onAfterSettingsChange();
  }

  onInputUpdated() {
    const input = this.getInputData(this.inInput);
    if (input == null) {
      this.setOutputData(this.outError, null);
      return;
    }
    try {
      const outputJson: any = {};
      for (let i = 0; i < this.filterKeys.length; i++) {
        let out = _.get(input, this.filterKeys[i]);
        if (out !== undefined) {
          outputJson[this.filterKeys[i]] = out;
          this.setOutputData(i + this.dynamicOutputStartPosition(), out);
        }
      }
      this.setOutputData(this.outJsonOutput, outputJson);
      this.setOutputData(this.outError, null);
    } catch (e) {
      this.setOutputData(this.outError, e.toString());
    }
  }

  onAfterSettingsChange() {
    this.changeOutputsAndSettings();
    this.renameInputsOutputs();
    // update db
    if (this.container.db)
      this.container.db.updateNode(this.id, this.container.id, {
        $set: { outputs: this.outputs, settings: this.settings },
      });
    this.onInputUpdated();
  }

  @ErrorHandler
  changeOutputsAndSettings() {
    let outputs = this.settings['outputs_count'].value;
    outputs = Utils.clamp(outputs, 1, 10);
    this.settings['outputs_count'].value = outputs;
    // change outputs count
    if (this.outputsCount != outputs) this.changeMyOutputsCount(outputs);
  }

  @ErrorHandler
  changeMyOutputsCount(targetOutputCount) {
    const diff = targetOutputCount - this.outputsCount;
    if (diff == 0) return;
    // add inputs, outputs
    this.changeOutputsCount(targetOutputCount + this.dynamicOutputStartPosition(), Type.STRING);

    // change settings for outputs
    if (diff > 0) {
      for (
        let i = this.outputsCount + this.dynamicOutputStartPosition();
        i < targetOutputCount + this.dynamicOutputStartPosition();
        i++
      ) {
        this.settings['output' + this.getOutputNumber(i)] = NodeSetting.create({
          description: 'Output ' + this.getOutputNumber(i),
          value: '',
          type: SettingType.STRING,
        });
      }
    } else if (diff < 0) {
      for (
        let i = this.outputsCount + this.dynamicOutputStartPosition() - 1;
        i >= targetOutputCount + this.dynamicOutputStartPosition();
        i--
      ) {
        delete this.settings['output' + this.getOutputNumber(i)];
      }
    }
    this.outputsCount = targetOutputCount;
  }

  @ErrorHandler
  renameInputsOutputs() {
    this.filterKeys = [];
    for (let i = this.dynamicOutputStartPosition(); i < this.outputsCount + this.dynamicOutputStartPosition(); i++) {
      this.filterKeys.push(this.settings['output' + this.getOutputNumber(i)].value);
      const output = this.settings['output' + this.getOutputNumber(i)].value || '';
      this.outputs[i].name = this.getOutputNumber(i) + ' | ' + output;
      this.setOutputData(i, '');
    }
  }

  private getOutputNumber(i: number): number {
    return i - this.dynamicOutputStartPosition() + 1;
  }

  private dynamicOutputStartPosition() {
    return 2;
  }
}
Container.registerNodeType('json/json-filter-multiple', JsonFilterMultiple);
