import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class RankNode extends FlexibleNode {
  constructor() {
    super();
    this.dynamicOutputsExist = true;
    this.dynamicSettingsExist = false;
    this.dynamicInputsType = Type.NUMBER;
    super.dynamicOutputsType = Type.NUMBER;
    this.dynamicDefaultInputs = 5;
    this.dynamicDefaultOutputs = 3;
    this.title = NODE_INFO.statistics.rank.title;
    this.description = NODE_INFO.statistics.rank.description;
    this.settings['rank'] = NodeSetting.create({
      description: 'Highest or Lowest',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'top', text: 'Rank Highest' },
          { value: 'bottom', text: 'Rank Lowest' },
        ],
      },
      value: 'top',
    });
  }

  onAdded() {
    this.updateTitle();
    this.onInputUpdated();
  }

  onInputUpdated() {
    let data = this.getDefinedInputValues();
    data = data.sort((a, b) => a - b);
    const outputCount = this.getOutputsCount();
    if (this.settings['rank'].value === 'bottom') {
      for (let i = 0; i < outputCount; i++) {
        this.setOutputData(i, data[i]);
      }
    } else {
      for (let i = 0; i < outputCount; i++) {
        this.setOutputData(i, data[data.length - (i + 1)]);
      }
    }
  }

  onAfterSettingsChange() {
    if (Number(this.settings['outputs'].value) > Number(this.settings['inputs'].value)) {
      this.settings['outputs'].value = Number(this.settings['inputs'].value);
    }
    super.onAfterSettingsChange();
    this.onInputUpdated();
    this.updateTitle();
  }

  private updateTitle() {
    if (this.settings['rank'].value === 'top') this.title = 'Rank (Highest)';
    else if (this.settings['rank'].value === 'bottom') this.title = 'Rank (Lowest)';
  }
}
Container.registerNodeType('statistics/rank', RankNode);
