import * as mqtt from 'mqtt';
import config from '../../../../config';
import { isNull } from '../../../../utils/helper';
import { Container } from '../../../container';
import { Node } from '../../../node';
import { NODE_INFO } from '../../../node-info';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import CryptoUtils from '../../../utils/crypto-utils';
import { MQTTMessage } from './core/mqtt-model';

const matchMQTT = require('mqtt-match');

class MqttClientSingleNode extends Node {
  client: any;
  enableDebug: boolean;
  //node inputs
  inName = 0;
  inEnable = 1;
  inData = 2;
  inTopic = 3;

  constructor() {
    super();
    this.title = NODE_INFO["protocols/mqtt"]["client-single"].title;
    this.description =
      `## Description\n ` +
      NODE_INFO["protocols/mqtt"]["client-single"].description + '\n' +
      `   \n ` +
      `### Enable\n ` +
      `   \n ` +
      ` This will enable/disable the device from broker \n ` +
      `   \n ` +
      `### Use env settings\n ` +
      `   \n ` +
      ` If set to true this will allow you to use the MQTT broker details set from the .env configuration file in the device\n ` +
      ` If set to false this will allow you to set the MQTT broker details manually\n ` +
      `   \n ` +
      `### Node input\n ` +
      `   \n ` +
      ` The input named "input" can accept a json input or an mqtt payload to send when the topic is set in the node setting called "[topic]".\n ` +
      ` The input named "input" can accept a json payload input with the name, topic, and mqtt payload. See example below.\n ` +
      ` { "payload": 22, "topic": "my topic", "name": "my node node"}\n ` +
      `   \n `;
    this.addInput('[name]', Type.STRING);
    this.addInputWithSettings('enable', Type.BOOLEAN, false, 'enable broker');
    this.addInputWithSettings('input', Type.STRING, '', 'JSON input or input value');
    this.addInputWithSettings('topic', Type.STRING, '', 'enter topic if not used from input');
    this.addOutput('connected', Type.BOOLEAN);
    this.addOutput('output', Type.ANY);
    this.addOutput('output-json', Type.JSON);
    this.settings['retain'] = NodeSetting.create({ description: 'Retain message on publish', value: true, type: SettingType.BOOLEAN });
    this.settings['payloadJson'] = NodeSetting.create({ description: 'Send payload ', value: false, type: SettingType.BOOLEAN });
    this.settings['enableDebug'] = NodeSetting.create({ description: 'Log debug messages (view logs in platform)', value: false, type: SettingType.BOOLEAN });
    this.settings['qos'] = NodeSetting.create({
      description: 'MQTT Quality Of Service (QoS)',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: -1, text: 'na' },
          { value: 0, text: 'QoS 0' },
          { value: 1, text: 'QoS 1' },
          { value: 2, text: 'QoS 2' },
        ],
      },
      value: 0,
    });
    this.settings['broker_group'] = NodeSetting.create({
      description: 'Broker settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['useEnv'] = NodeSetting.create({ description: 'Dont use env settings', value: false, type: SettingType.BOOLEAN });
    this.settings['server'] = NodeSetting.create({ description: 'Broker URL', value: '0.0.0.0', type: SettingType.STRING });
    this.settings['port'] = NodeSetting.create({ description: 'Broker port', value: '1883', type: SettingType.STRING });
    this.settings['authentication'] = NodeSetting.create({
      description: 'Use Authentication',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['username'] = NodeSetting.create({ description: 'User name', value: '', type: SettingType.STRING });
    this.settings['password'] = NodeSetting.create({ description: 'Password', value: '', type: SettingType.PASSWORD });
    this.setSettingsConfig({
      groups: [
        { server: { weight: 3 }, port: { weight: 1 } },
        { username: {}, password: {} },
      ],
      conditions: {
        server: setting => {
          return !!setting['useEnv'].value;
        },
        port: setting => {
          return !!setting['useEnv'].value;
        },
        authentication: setting => {
          return !!setting['useEnv'].value;
        },
        username: setting => {
          return !!setting['authentication'].value;
        },
        password: setting => {
          return !!setting['authentication'].value;
        },
      },
    });
  }

  onAdded() {
    this.EXECUTE_INTERVAL = 200;
    if (this.side == Side.server) {
      this.setOutputData(0, false);
      if (this.getInputData(this.inEnable) === true) {
        this.connectToBroker();
        this.enableDebug = this.settings['enableDebug'].value;
        if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE: connect to Broker`);
      }
    }
  }

  connectToBroker() {
    let { host, port, username, password } = config.mqtt;
    let options: any = {};
    const qos = this.settings['qos'].value;
    if (qos !== -1) {
      options.qos = qos;
    }
    if (this.settings['useEnv'].value) {
      //dont use env
      if (this.settings['server'].value != null && this.settings['server'].value != '')
        options.host = this.settings['server'].value;
      if (this.settings['port'].value != null && this.settings['port'].value != '')
        options.port = this.settings['port'].value;
      if (this.settings['username'].value != null && this.settings['username'].value != '')
        options.username = this.settings['username'].value;
      if (this.settings['password'].value != null && this.settings['password'].value != '')
        options.password = CryptoUtils.decrypt(this.settings['password'].value);
    } else {
      //use use env
      if (isNull(host)) return;
      if (isNull(port)) return;
      options.host = host;
      options.port = port;
      if (this.settings['authentication'].value === true) {
        if (!isNull(options.username)) {
          options.username = username;
        }
        if (!isNull(options.password)) {
          options.password = CryptoUtils.decrypt(password);
        }
      }
    }
    this.disconnectFromBroker();
    this.client = mqtt.connect(options);
    this.client.on('connect', () => {
      if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE: this.client = mqtt.connect options: ${JSON.stringify(options)}`);
      this.setOutputData(0, true);
      let data = this.getInputData(this.inData);
      let topic = this.getInputData(this.inTopic);
      if (data || topic) {
        let isJSON = true;
        try {
          data = JSON.parse(data);
        } catch {
          if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE: input isn't JSON`);
          isJSON = false;
        }
        if (isJSON && data.hasOwnProperty('topic') && typeof data.topic === Type.STRING) {
          if (data.topic != '') {
            this.client.subscribe(data.topic);
            if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE:  subscribe on json topic ${data.topic}`);
          }
        } else {
          // if no json topic use input 2 topic
          if (data.topic != '') {
            if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE:  subscribe on node input topic ${topic}`);
            this.client.subscribe(topic);
          }
        }
      }
    });
    this.client.on('close', () => {
      this.setOutputData(0, false, true);
    });

    this.client.on('error', error => {
      this.debugWarn(error);
    });
    this.client.on('message', (topic, message) => {
      let obj: MQTTMessage = { topic: topic, message: message.toString() };
      if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE:  this.client.on('message' ${JSON.stringify(obj)}`);
      this.processMessage(obj);
    });
  }

  disconnectFromBroker() {
    if (this.client) this.client.end();
  }

  onInputUpdated() {
    //connect/disconnect
    if (this.inputs[this.inEnable].updated) {
      if (this.getInputData(this.inEnable) === true) {
        this.connectToBroker();
        if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE: connect to Broker`);
      } else if (this.getInputData(this.inEnable) === false) {
        this.disconnectFromBroker();
        if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE: disconnect From Broker`);
      } else if (isNull(this.getInputData(this.inEnable))) {
        this.onAfterSettingsChange();
        if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE: disconnect enable input from node`);
      }
    }

    //update node name
    if (this.inputs[this.inName].updated) {
      let nodeName = this.getInputData(this.inName);
      if (!isNull(nodeName)) {
        this.name = nodeName;
        this.broadcastNameToClients();
      }
    }

    //update topic
    if (this.getInputData(this.inEnable) && this.inputs[this.inTopic].updated) {
      this.connectToBroker();
      if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE: node input topic updated`);
    }

    //publish message
    if (this.getInputData(this.inEnable) && this.client && this.client.connected) {
      let data = this.getInputData(this.inData);
      if (data && this.inputs[this.inData].updated) {
        let isJSON = true;
        try {
          data = JSON.parse(data);
        } catch {
          if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE: input isn't JSON`);
          isJSON = false;
        }
        let topic: string;
        let payload: string = '';
        if (
          isJSON &&
          data.hasOwnProperty('topic') &&
          typeof data.topic === Type.STRING &&
          data.hasOwnProperty('payload') || data.hasOwnProperty('fields')
        ) {
          if (data.hasOwnProperty('payload')) {
            payload = JSON.stringify(data.payload);
          } else if (data.hasOwnProperty('fields')) {
            payload = JSON.stringify(data.fields.value); //influx db uses fields
          }
          if (data.topic == '') {
            return;
          }
          this.publish(data.topic, payload)
        } else {
          topic = this.getInputData(this.inTopic);
          if (isJSON) payload = JSON.stringify(data);
          else payload = String(data);
          if (topic == '') {
            return;
          }
          this.publish(topic, payload)
        }
      }
    }
  }

  publish(topic, payload) {
    const retain = this.settings['retain'].value
    const payloadJson = this.settings['payloadJson'].value
    if (payloadJson) {
      let out = JSON.stringify({ value: payload })
      this.client.publish(topic, '' + out, { retain: retain });
      if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE: publish on input topic from json ${topic}, '' + ${out}`);
    } else {
      this.client.publish(topic, '' + payload, { retain: retain });
      if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE: publish on input topic from node input ${topic}, '' + ${payload}`);
    }
  }

  onAfterSettingsChange() {
    //update db
    if (this.container.db)
      this.container.db.updateNode(this.id, this.container.id, {
        $set: { inputs: this.inputs, outputs: this.outputs, settings: this.settings },
      });

    if (this.side == Side.server) {
      this.disconnectFromBroker();
      if (this.getInputData(this.inEnable)) {
        this.connectToBroker();
        if (this.enableDebug) this.debugInfo(`MQTT-CLIENT-SINGLE NODE: connect to Broker from settings`);
      }
    }
  }

  onRemoved() {
    this.disconnectFromBroker();
  }

  processMessage(mqttMessage: MQTTMessage) {
    // look into using mqtt-match library. (https://www.npmjs.com/package/mqtt-match)
    let topic = this.getInputData(this.inTopic);
    if (matchMQTT(topic, mqttMessage.topic)) {
      this.setOutputData(1, mqttMessage.message);
      this.setOutputData(2, mqttMessage);
    }
  }
}
Container.registerNodeType('protocols/mqtt/client-single', MqttClientSingleNode);
