import { isEmpty, isFunction } from './helper';

export interface NodeStoreItem {
  identifier: string;
  nodeId: string;
  enabled: boolean;
  callback?: Function;
  prev?: NodeStoreItem;
  data?: any;
  lenient?: boolean;
}

export interface ICentralizedListener {

  listen({ action, payload }: { action: string; payload: NodeStoreItem }): any;

}

/**
 * To guard some error import without parent
 */
export class MockCentralizedListener implements ICentralizedListener {

  static readonly LISTENER = new MockCentralizedListener();

  static cast(object: any) {
    if (isEmpty(object)){
      return MockCentralizedListener.LISTENER;
    }
    if (isFunction(object['listen'])){
      return object;
    }
    return MockCentralizedListener.LISTENER;
  }

  listen({ action, payload }: { action: string; payload: NodeStoreItem }): any {
    throw new Error(`Not found handler or parent to process action ${action}`);
  }

  private MockCentralizedListener() {
  }
}

export interface HasCentralizedListener {

  listener(): ICentralizedListener;

}

/**
 * Represents for observer to listen change from {@link Observable}
 */
export interface Observer<D extends EventData> {

  /**
   * Do update from receive information
   * @param {EventData} data Event data
   * @param {string} eventSourceId? event source
   */
  update(data: D, eventSourceId?: string): void;

}

export interface Observable<D extends EventData> {

  /**
   * Defines list of observer
   * @return {Observer<D>[]} observers
   * @see Observer
   */
  observers(): Observer<D>[];

  /**
   * Notify to observers the changed of data
   * @param {D} data Event data
   * @param {string} eventSourceId
   * @see observers
   */
  notify(data: D, eventSourceId?: string): void;

}

export interface EventData {

}

export class DefaultObservable<D extends EventData> implements Observable<D> {

  list: Observer<D>[];

  constructor(list: Observer<D>[]) {
    this.list = list;
  }

  observers(): Observer<D>[] {
    return this.list;
  }

  notify(data: D, eventSourceId?: string): void {
    this.observers()?.forEach(observer => observer.update(data, eventSourceId));
  }
}

export interface IMessageConverter {
  /**
   * Convert outcome data before using
   * @param outcomeData
   * @returns {string} message in string
   */
  onConvertMessage(outcomeData: any): string;
}

export interface IMessageSubscriber {
  /**
   * Handles incoming message from external resource
   * @param incomingMessage
   */
  onReceiveMessage(incomingMessage: any): void;
}
