import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class BoolPriorityNode extends FlexibleNode {

  constructor() {
    super();
    this.dynamicInputsType = Type.BOOLEAN;
    this.title = NODE_INFO.point["bool-writable"].title;
    this.description = NODE_INFO.point["bool-writable"].description; 
    this.addOutput('out', Type.BOOLEAN);
  }

  onAdded() {
    this.size = this.computeSize();
    this.onInputUpdated();
  }

  onInputUpdated() {
    const inputs = this.getInputsCount();
    for (let i = this.dynamicInputStartPosition; i < inputs; i++) {
      let inputValue = this.getInputData(i);
      if (inputValue != null) {
        this.setOutputData(0, inputValue);
        return;
      }
    }
    this.setOutputData(0, null);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('point/bool-writable', BoolPriorityNode);
