import { Enumify } from 'enumify';
import { PointModel, PointValue, PointValueCreator } from '../../../../../backend/models/point-model';
import { isNull } from '../../../../../utils/helper';

export class PointKind extends Enumify {
  static ANALOG_INPUT = new PointKind(0, 'analog-input');
  static ANALOG_OUTPUT = new PointKind(1, 'analog-output');
  static ANALOG_VALUE = new PointKind(2, 'analog-value');
  static BINARY_INPUT = new PointKind(3, 'binary-input');
  static BINARY_OUTPUT = new PointKind(4, 'binary-output');
  static BINARY_VALUE = new PointKind(5, 'binary-value');
  static MULTI_STATE_INPUT = new PointKind(13, 'multi-state-input');
  static MULTI_STATE_OUTPUT = new PointKind(14, 'multi-state-output');
  static MULTI_STATE_VALUE = new PointKind(19, 'multi-state-value');

  static _ = PointKind.closeEnum();

  readonly value: number;
  readonly label: string;

  constructor(value: number, label: string) {
    super();
    this.value = value;
    this.label = label;
  }

  static items(): { text: string; value: any }[] {
    return PointKind.enumKeys.map(k => {
      return { value: k, text: (<PointKind>PointKind.enumValueOf(k)).label };
    });
  }

  static lookupByValue(objectType: number): PointKind {
    return PointKind.enumValues.map(pk => <PointKind>pk).find(pk => pk.value === objectType);
  }

  static lookupByLabel(objectType: string): PointKind {
    return PointKind.enumValues.map(pk => <PointKind>pk).find(pk => pk.label === objectType);
  }
}

export interface BacnetPoint extends PointModel {
  objectInstance: number;

  objectType: PointKind;

  objectName: string;
}

export class BacnetPointCreator {
  private constructor() {
  }

  static from = (json: object): BacnetPoint => {
    if (isNull(json)) {
      return null;
    }
    // @ts-ignore
    let pv = json?.pointValue;
    // @ts-ignore
    return BacnetPointCreator.create(json?.enabled, json?.objectInstance, json?.objectType, json?.objectName,
                                     pv?.presentValue, pv?.priority, pv?.priorityArray);
  };

  static create = (enabled: boolean, objectInstance: number, objectType: PointKind, objectName: string,
                   presentValue?: number, priority?: number, priorityArray?: any): BacnetPoint => {
    return BacnetPointCreator.by(enabled, objectInstance, objectType, objectName,
                                 PointValueCreator.create(presentValue, priority, priorityArray));
  };

  static by = (enabled: boolean, objectInstance: number, objectType: PointKind, objectName: string,
               pointValue: PointValue): BacnetPoint => {
    return new DefaultBacnetPoint(enabled, objectInstance, objectType, objectName, pointValue);
  };
}

class DefaultBacnetPoint implements BacnetPoint {
  enabled: boolean;
  objectInstance: number;
  objectType: PointKind;
  objectName: string;
  pointValue: PointValue;

  constructor(enabled: boolean, objectInstance: number, objectType: PointKind, objectName: string,
              pointValue: PointValue) {
    this.enabled = enabled || false;
    this.objectInstance = objectInstance;
    this.objectType = objectType;
    this.objectName = objectName;
    this.pointValue = pointValue;
  }

  identifier(): string {
    return `${this.objectType.enumKey}:${this.objectInstance}`;
  }

  mightOnlyValueChanged(bp: BacnetPoint): boolean {
    return (this.enabled === bp?.enabled && this.objectInstance === bp?.objectInstance &&
            this.objectType === bp?.objectType && this.objectName === bp?.objectName);
  }

  equals(bp: BacnetPoint): boolean {
    return this.identifier() === bp?.identifier();
  }
}
