import { Node} from './node';
import { convertType, InputSetting, NodeInput, NodeOutput, NodeSetting, SettingType, Type } from './node-io';
import Utils from './utils';

export class FlexibleNode extends Node {
  nullableInputs: boolean = true;
  dynamicInputsExist: boolean = true;
  dynamicOutputsExist: boolean = false;
  dynamicSettingsExist: boolean = true;
  /**
   * You can override this field if you have different setting name than `inputs` for reading inputs counts
   * @returns {string} field name
   */
  dynamicInputsField: string = 'inputs';
  /**
   * You can override this field if you have different setting name than `outputs` for reading inputs counts
   * @returns {string} field name
   */
  dynamicOutputsField: string = 'outputs';
  /**
   * For name convention on output
   * Example:
   * 0 for in1, in2, ...
   * 1 for select, in1, in2, ...
   * 2 for select, enable, in1, in2, ...
   */
  dynamicInputStartPosition: number = 0;
  /**
   * Dynamic input start name
   * Example (for in):
   * `[in 1]`, `[in 2]` will be inputs name
   * `in1`, `in2` will be settings keys and `in 1` and `in 2` will be settings description
   */
  dynamicInputsStartName: string = 'in';
  dynamicOutputsStartName: string = 'out';
  dynamicInputsType: Type = Type.ANY;
  dynamicOutputsType: Type = Type.ANY;
  dynamicMinInputs: number = 2;
  dynamicMaxInputs: number = 100;
  dynamicMinOutputs: number = 2;
  dynamicMaxOutputs: number = 100;
  dynamicDefaultInputs: number = 2;
  dynamicDefaultOutputs: number = 2;

  constructor() {
    super();
  }

  setup() {
    if (this.dynamicInputsExist) {
      this.settings['inputs'] = NodeSetting.create({
        description: 'Inputs count',
        value: this.dynamicDefaultInputs,
        type: SettingType.NUMBER,
      });
    }
    if (this.dynamicOutputsExist) {
      this.settings['outputs'] = NodeSetting.create({
        description: 'Outputs count',
        value: this.dynamicDefaultOutputs,
        type: SettingType.NUMBER,
      });
    }
  }

  /**
   * It invokes right after finishing the constructor call
   */
  init() {
    if (this.dynamicInputsExist) {
      this.changeDynamicInputsAndSettings(false);
    }
    if (this.dynamicOutputsExist) {
      this.addDynamicOutputsOnRange(false);
    }
  }

  onAfterSettingsChange() {
    if (this.dynamicInputsExist) {
      this.changeDynamicInputsAndSettings();
    }
    if (this.dynamicOutputsExist) {
      this.addDynamicOutputsOnRange();
    }
  }

  /**
   * Adds inputs dynamically as per the settings['inputs'] value
   */
  addDynamicInputsOnRange = (save: boolean = true): void => {
    const count = this.dynamicInputsCount();
    const targetCount = Utils.clamp(count, this.dynamicMinInputs, this.dynamicMaxInputs);
    const currentCount = this.getInputsCount() - this.dynamicInputStartPosition;
    const addedInputsCount = targetCount - currentCount;

    if (addedInputsCount > 0) {
      // add
      for (let i = 0; i < addedInputsCount; i++) this.addInputWithName();
    } else if (addedInputsCount < 0) {
      // remove
      const inputCount = this.getInputsCount();
      for (let i = inputCount; i > inputCount + addedInputsCount; i--) {
        this.removeInput(i - 1);
      }
    }
    save && this.updateNodeInput();
  };

  /**
   * Adds outputs dynamically as per the settings['outputs'] value
   */
  addDynamicOutputsOnRange = (save: boolean = true): void => {
    const count = this.dynamicOutputsCount();
    const targetCount = Utils.clamp(count, this.dynamicMinOutputs, this.dynamicMaxOutputs);
    const currentCount = this.getOutputsCount();
    const addedOutputsCount = targetCount - currentCount;

    if (addedOutputsCount > 0) {
      // add
      for (let i = 0; i < addedOutputsCount; i++) this.addOutputWithName();
    } else if (addedOutputsCount < 0) {
      // remove
      const inputCount = this.getOutputsCount();
      for (let i = inputCount; i > inputCount + addedOutputsCount; i--) {
        this.removeOutput(i - 1);
      }
    }
    save && this.updateNodeOutput();
  };

  settingWiseDynamicInputsAndSettingsUpdate = (save: boolean = true) => {
    const startingCount = this.getInputsCount();
    this.addDynamicInputsOnRange(save);
    const targetCount = this.getInputsCount();
    this.addDynamicSettings(startingCount, targetCount);
  };

  settingWiseDynamicInputsAndSettingsUpdateForBoolean = (save: boolean = true) => {
    this.dynamicInputsType = Type.BOOLEAN; // if not assigned
    const startingCount = this.getInputsCount();
    this.addDynamicInputsOnRange(save);
    const targetCount = this.getInputsCount();
    const name = this.dynamicInputsStartName;
    for (let i = startingCount; i < targetCount; i++) {
      if (!this.settings[`${name} ${this.dynamicIndex(i)}`]) {
        this.settings[`${name} ${this.dynamicIndex(i)}`] = NodeSetting.create({
          description: `${name} ${this.dynamicIndex(i)}`,
          type: SettingType.DROPDOWN,
          config: {
            items: [
              { value: false, text: 'false' },
              { value: true, text: 'true' },
              { value: null, text: 'null' },
            ],
          },
          value: null,
        });
      }
    }
    for (let i = targetCount; i < startingCount; i++) {
      delete this.settings[`${name} ${this.dynamicIndex(i)}`];
    }
  };

  private changeDynamicInputsAndSettings(save: boolean = true) {
    if (this.dynamicSettingsExist) {
      if (this.dynamicInputsType === Type.BOOLEAN) {
        this.settingWiseDynamicInputsAndSettingsUpdateForBoolean(save);
      } else {
        this.settingWiseDynamicInputsAndSettingsUpdate(save);
      }
    } else {
      this.addDynamicInputsOnRange(save);
    }
  }

  /**
   * To read total number of inputs for dynamic input update
   * @returns {number} total dynamic input count
   */
  public dynamicInputsCount = (): number => this.settings[this.dynamicInputsField].value;

  public dynamicOutputsCount = (): number => this.settings[this.dynamicOutputsField].value;

  private getInputSetting() {
    return {
      exist: this.dynamicSettingsExist,
      nullable: this.nullableInputs,
      hidden: false,
    };
  }

  private addInputWithName = (): void => {
    let id = this.getFreeInputId();
    let setting: InputSetting = this.getInputSetting();
    let name = `${this.dynamicInputsStartName} ${id + 1 - this.dynamicInputStartPosition}`;
    if (this.dynamicSettingsExist) {
      name = `[${name}]`;
    }
    let input: NodeInput = NodeInput.create({ name, type: this.dynamicInputsType, setting });
    if (!this.inputs) this.inputs = {};
    this.inputs[id] = input;
    if (this['onInputAdded']) this['onInputAdded'](input);
  };

  private addOutputWithName = (): void => {
    let id = this.getFreeOutputId();
    let name = `${this.dynamicOutputsStartName} ${id + 1}`;
    let output: NodeOutput = NodeOutput.create({ name: name, type: this.dynamicOutputsType });
    if (!this.outputs) this.outputs = {};
    this.outputs[id] = output;
    if (this['onOutputAdded']) this['onOutputAdded'](output);
  };

  /**
   * Adds settings text fields dynamically as per starting count
   */
  private addDynamicSettings = (startingCount, targetCount) => {
    // We are treating '' as null, if we store null value on string type it will create issue on everywhere.
    // For example: null + 'ability' = 'ability'
    const value = SettingType.STRING ? '' : null;
    const name: string = this.dynamicInputsStartName;
    const type: SettingType = convertType(this.dynamicInputsType);
    for (let i = startingCount; i < targetCount; i++) {
      if (!this.settings[`${name} ${this.dynamicIndex(i)}`]) {
        this.settings[`${name} ${this.dynamicIndex(i)}`] = NodeSetting.create({
          description: `${name} ${this.dynamicIndex(i)}`,
          value,
          type: type,
        });
      }
    }
    for (let i = targetCount; i < startingCount; i++) {
      delete this.settings[`${name} ${this.dynamicIndex(i)}`];
    }
  };

  private dynamicIndex = (id): number => id + 1 - this.dynamicInputStartPosition;
}
