import * as express from 'express';
import app from '../../app';
import WireNameDataSource from '../../database/datasource/WireNameDataSource';

let router = express.Router();

router.get('', async function(req, res) {
  try {
    const wire = await WireNameDataSource.getWireName();
    res.json(wire);
  } catch (e) {
    res.status(500).send(e);
  }
});

router.put('', async function({ body }, res) {
  const { name } = body;
  try {
    await WireNameDataSource.changeWireName(name);
    app.server.wireNameSocket.io.emit('wire-name-update', name);
    res.status(201).send(body);
  } catch (e) {
    res.status(500).send(e);
  }
});

module.exports = router;
