import { ModbusRTU as RTUModbus } from 'modbus-serial/ModbusRTU';
import { IdGenerator } from '../../../../utils/id-generator';
import { MODBUS_RTU_DEVICE, MODBUS_RTU_NETWORK, MODBUS_RTU_POINT } from '../../../constants';
import { Container } from '../../../container';
import { ContainerNode } from '../../../container-node';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import { GlobalNodeRegistry } from '../../../node-registry';
import Utils from '../../../utils';
import ModbusMethods from './modbus-fc/modbus-point-methods';
import ModbusUtils from './modbus-functions';

const ModbusRTU = require('modbus-serial');

enum Poll {
  DO_NOT_INIT = 0,
  FIRST_START = 1,
  STOP = 2,
}

enum Transport {
  TCP = 'tcp',
  RTU = 'rtu',
}

class ModbusSerialNetworkNode extends ContainerNode {
  rs485DeviceName: string = 'ttyUSB1';
  isPortOk: boolean = false;
  isPortInUse: boolean = false;
  serialPortStatus: boolean = false;
  checkPort: any;

  // settings
  networkEnable: boolean;
  port: string;
  baudRate: string;
  parity: number;
  dataBits: number;
  stopBits: number;
  devicePolling: number;
  registerDelay: number;
  transport: string;

  // pooling running
  pollEnableSetting: boolean = false;
  poll: Poll;

  // modbus client
  client = null;
  tcpClients: { [key: string]: RTUModbus } = {};

  inNetworkEnable = 0;

  // node outputs
  outStatus = 0;
  outError = 1;
  outPortStatus = 2;
  outPortMsg = 3;
  outMessageJson = 4;

  constructor() {
    super();
    this.title = 'Modbus 485 Network';
    this.description =
      `## Description\n ` +
      ` This node is used as a modbus TCP or RTU(rs485) modbus network.\n ` +
      ` The device and points for the device will be added inside this node (right click and **open** on the network node to add the device and points) \n ` +
      `   \n ` +
      `### Enable\n ` +
      `   \n ` +
      ` This will enable/disable the network from polling \n ` +
      `   \n `;
    this.addInputWithSettings('enable', Type.BOOLEAN, false, 'Network Enable', false);
    this.addOutput('status', Type.BOOLEAN);
    this.addOutput('error', Type.STRING);
    this.addOutput('port status', Type.STRING);
    this.addOutput('port messages', Type.STRING);
    this.addOutput('message', Type.STRING);
    this.settings['devicePolling'] = NodeSetting.create({
      description: 'Polling time setting in ms',
      value: 2000, // TODO Add min limit
      type: SettingType.NUMBER,
    });
    this.settings['registerDelay'] = NodeSetting.create({
      description: 'Polling delay between each register in ms',
      value: 30, // TODO Add min limit
      type: SettingType.NUMBER,
    });
    this.settings['transport'] = NodeSetting.create({
      description: 'TCP or RTU/RS485',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: Transport.RTU, text: 'Modbus RTU over Serial 485' },
          { value: Transport.TCP, text: 'Modbus TCP/IP' },
        ],
      },
      value: Transport.RTU,
    });

    this.settings['manualPort'] = NodeSetting.create({
      description: 'Enter port manually',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['port'] = NodeSetting.create({
      description: 'port',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: '/dev/ttyUSB0', text: 'ttyUSB0' },
          { value: '/dev/ttyUSB1', text: 'ttyUSB1' },
          { value: '/dev/ttyUSB2', text: 'RS485-1 (/dev/ttyUSB2)' },
          { value: '/dev/ttyUSB3', text: 'RS485-2 (/dev/ttyUSB3)' },
          { value: '/dev/ttyUSB4', text: 'USB Port (/dev/ttyUSB4)' },
        ],
      },
      value: '/dev/ttyUSB0',
    });
    this.settings['manualPortNum'] = NodeSetting.create({
      description: 'Enter port number',
      value: '/dev/ttyAMA0',
      type: SettingType.STRING,
    });
    this.settings['baudRate'] = NodeSetting.create({
      description: 'Baud Rate',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 115200, text: '115200' },
          { value: 57600, text: '57600' },
          { value: 38400, text: '38400' },
          { value: 19200, text: '19200' },
          { value: 9600, text: '9600' },
        ],
      },
      value: 9600,
    });
    this.settings['parity'] = NodeSetting.create({
      description: 'Parity',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'none', text: 'none' },
          { value: 'even', text: 'even' },
          { value: 'mark', text: 'mark' },
          { value: 'odd', text: 'odd' },
          { value: 'space', text: 'space' },
        ],
      },
      value: 'none',
    });
    this.settings['dataBits'] = NodeSetting.create({
      description: 'Data Bits',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 8, text: '8' },
          { value: 7, text: '7' },
          { value: 6, text: '6' },
          { value: 5, text: '5' },
        ],
      },
      value: 8,
    });
    this.settings['stopBits'] = NodeSetting.create({
      description: 'Stop Bits',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 2, text: '2' },
          { value: 1.5, text: '1.5' },
          { value: 1, text: '1' },
        ],
      },
      value: 1,
    });
    this.setSettingsConfig({
      groups: [
        { port: {}, manualPortNum: {} },
        { parity: {}, dataBits: {}, stopBits: {} },
        { devicePolling: {}, registerDelay: {} },
      ],
      conditions: {
        port: setting => {
          return setting['transport'].value === Transport.RTU;
        },
        baudRate: setting => {
          return setting['transport'].value === Transport.RTU;
        },
        parity: setting => {
          return setting['transport'].value === Transport.RTU;
        },
        dataBits: setting => {
          return setting['transport'].value === Transport.RTU;
        },
        stopBits: setting => {
          return setting['transport'].value === Transport.RTU;
        },
        manualPort: setting => {
          return setting['transport'].value === Transport.RTU;
        },
        manualPortNum: setting => {
          return setting['transport'].value === Transport.RTU;
        },
      },
    });
  }

  static async getDevicesPoints(sub_container: any, container_db: any) {
    let devicesPoints = [];
    let devices: any = await ModbusUtils.getDevice(MODBUS_RTU_DEVICE, sub_container, container_db);
    for (let device of devices) {
      let points = await ModbusUtils.getPoints(MODBUS_RTU_POINT, device, container_db);
      devicesPoints.push(points);
    }
    return devicesPoints;
  }

  async onAdded() {
    super.onAdded();
    let nodeDetailsMessage = {
      nodeId: this.id,
      cid: this.container.id,
      sub_id: this.sub_container.id,
    };
    this.setOutputData(this.outMessageJson, nodeDetailsMessage);
    await this.setupForPolling();
  }

  onCreated() {
    super.onCreated();
    if (this.side !== Side.server) return;
  }

  async onRemoved() {
    super.onRemoved();
    await this.polling(Poll.STOP);
    if (this.client) {
      this.client.close(() => {});
    }
    Object.keys(this.tcpClients).forEach(key => {
      if (this.tcpClients[key]) this.tcpClients[key].close(() => {});
    });
  }

  onInputUpdated() {
    setTimeout(async () => {
      await this.setupForPolling();
    });
  }

  onAfterSettingsChange(oldSettings: any) {
    super.onAfterSettingsChange(oldSettings);
    setTimeout(async () => {
      await this.setupForPolling();
    });
  }

  async polling(poll: Poll) {
    this.poll = poll;
    if (this.side !== Side.server) return; // only execute on server
    if (this.poll === Poll.STOP) {
      // close MB client
      this.setOutputData(this.outPortMsg, 'INFO: closed port connection');
      this.setOutputData(this.outStatus, false, true);
      if (this.client) this.client.close(() => {});
      return;
    }
    if (this.transport === Transport.RTU) {
      if (this.client) this.client.close(() => {});
      this.client = new ModbusRTU();
      this.client.setTimeout(500);
      setTimeout(() => {
        this.client
          .connectRTUBuffered(this.port, {
            baudRate: this.baudRate,
            parity: this.parity,
            dataBits: this.dataBits,
            stopBits: this.stopBits,
          })
          .catch(() => {
            this.setOutputData(this.outStatus, false, true);
            this.setOutputData(this.outError, true, true);
            this.setOutputData(this.outPortMsg, `ERROR! port: ${this.port.substring(5)}`);
          });
        this.setOutputData(this.outStatus, true, true);
        this.setOutputData(this.outPortMsg, `INFO: polling RS485 `);
      }, 500);
    } else {
      this.setOutputData(this.outPortMsg, `INFO: polling TCP `);
    }
    if (this.poll === Poll.FIRST_START) {
      await this.pollDevicesPoints();
    }
  }

  async pollDevicesPoints() {
    if (this.poll === Poll.STOP) return;
    const devices = await ModbusSerialNetworkNode.getDevicesPoints(this.sub_container.id, this.container.db);
    let errorOnModbusDevicesExec = false;
    for (let device of devices) {
      let { deviceCID, deviceID, deviceAddress, ipAddress, ipPort, deviceAddressOffset } = device.deviceObj;
      const deviceNode = GlobalNodeRegistry.instance().get(IdGenerator.createNodeId(deviceCID, deviceID));
      if (!deviceNode) return;
      let points = device.points;

      let client = this.client;
      let existingTcpClients: string[] = [];
      if (this.transport === Transport.TCP) {
        const key = this.getTcpKey(ipAddress, ipPort);
        existingTcpClients.push(key);
        if (!this.tcpClients[key]) {
          this.debugInfo(`Creating tcpClient with IP: ${ipAddress} and Port: ${ipPort}`);
          this.tcpClients[key] = new ModbusRTU();
        }
        if (!this.tcpClients[key].isOpen) {
          this.debugInfo(`Connecting tcpClient with IP: ${ipAddress} and Port: ${ipPort}`);
          try {
            await this.tcpClients[key].connectTCP(ipAddress, { port: ipPort });
            this.tcpClients[key].setTimeout(5000);
          } catch (e) {
            errorOnModbusDevicesExec = true;
            ModbusUtils.sendDeviceError(deviceNode, e);
            this.setOutputData(this.outError, e);
            continue;
          }
        }
        client = this.tcpClients[key];
      }

      // Close if tcpClient doesn't exist
      Object.keys(this.tcpClients).forEach(key => {
        if (!existingTcpClients.includes(key)) {
          this.tcpClients[key].close(() => {});
        }
      });

      let errorOnModbusPointsExec = false;
      for (let point of points) {
        const { pntAddr, pntType, pntOffset, pntVal, pntCid, pntId, pntDataType, pntDataEndian } = point;
        await Utils.sleep(this.registerDelay || 50);
        const pointNode = GlobalNodeRegistry.instance().get(IdGenerator.createNodeId(pntCid, pntId));
        if (!pointNode) return;
        // TODO for point write don't return if input properties.pointVal = null
        // if ((pntType === 5 || pntType === 6) && typeof pntVal === 'number') {
        try {
          const response: any = await ModbusMethods.modbusMethods(
            // TODO add return interface
            client,
            deviceAddress,
            pntType,
            pntAddr,
            pntOffset,
            pntVal,
            pntDataType,
            pntDataEndian,
            deviceAddressOffset,
          );
          ModbusUtils.sendPointMessage(pointNode, response);
          ModbusUtils.sendDeviceMessage(deviceNode, response);
        } catch (e) {
          errorOnModbusDevicesExec = true;
          this.setOutputData(this.outError, e);
          ModbusUtils.sendDeviceError(deviceNode, e);
          ModbusUtils.sendPointError(pointNode, e);
        }
      }
      if (!errorOnModbusDevicesExec) ModbusUtils.sendDeviceError(deviceNode, null);
      errorOnModbusDevicesExec = errorOnModbusDevicesExec || errorOnModbusPointsExec;
    }
    if (errorOnModbusDevicesExec) {
      this.setOutputData(this.outStatus, false, true);
      this.setOutputData(this.outError, true, true);
    } else {
      this.setOutputData(this.outStatus, true, true);
      this.setOutputData(this.outError, null, true);
    }

    await Utils.sleep(this.devicePolling || 2000);
    await this.pollDevicesPoints(); // start loop again
  }

  private async setupForPolling() {
    if (this.side !== Side.server) return;
    this.networkEnable = this.getInputData(this.inNetworkEnable);
    if (this.settings['manualPort'].value) {
      this.port = this.settings['manualPortNum'].value;
    } else {
      this.port = this.settings['port'].value;
    }
    this.baudRate = this.settings['baudRate'].value;
    this.parity = this.settings['parity'].value;
    this.dataBits = this.settings['dataBits'].value;
    this.stopBits = this.settings['stopBits'].value;
    this.devicePolling = this.settings['devicePolling'].value;
    this.registerDelay = this.settings['registerDelay'].value;
    this.transport = this.settings['transport'].value;

    if (this.networkEnable) {
      if (!this.pollEnableSetting) {
        // start devicePolling
        this.pollEnableSetting = true;
        await this.polling(Poll.FIRST_START);
      } else {
        // continue devicePolling
        await this.polling(Poll.DO_NOT_INIT);
      }
    } else {
      this.pollEnableSetting = false;
      await this.polling(Poll.STOP);
    }
  }

  private getTcpKey(address: string, port: number): string {
    return `${address}:${port}`;
  }
}
Container.registerNodeType(MODBUS_RTU_NETWORK, ModbusSerialNetworkNode);
