import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class TickerNode extends Node {
  lastInterval = 1000;
  state: boolean = false;
  runState: boolean = true;
  timeoutFunc;


  constructor() {
    super();
    this.title = NODE_INFO.boolean.ticker.title;
    this.description = NODE_INFO.boolean.ticker.description;

    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addInputWithSettings('interval', Type.NUMBER, 1, 'Interval');
    this.addOutput('output', Type.BOOLEAN);

    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });

    this.settings['false'] = NodeSetting.create({
      description: 'Generate False',
      value: true,
      type: SettingType.BOOLEAN,
    });
  }

  onAdded() {
    //this.title = `Ticker (${this.settings['time'].value})`;
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
    clearInterval(this.timeoutFunc);
    this.setOutputData(0, false);
    let enable = this.getInputData(0);
    if (!enable) {
      this.setOutputData(0, false);
      this.runState = false;
      return;
    }
    let interval = this.getInputData(1);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    this.timeoutFunc = setInterval(() => {
      this.state = !this.state;
      if (!this.state && this.settings['false'].value) {
        this.setOutputData(0, false);
        return;
      }
      if (this.state) this.setOutputData(0, true);
    }, interval / 2);
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let enable = this.getInputData(0);

    if (!enable && this.runState) {
      clearInterval(this.timeoutFunc);
      this.setOutputData(0, false);
      this.runState = false;
      this.state = false;
      return;
    } else if (!enable && !this.runState) {
      this.setOutputData(0, false);
      this.runState = false;
      this.state = false;
      return;
    } else if (enable) {
      let interval = this.getInputData(1);
      interval = TimeUtils.timeConvert(interval, this.settings['time'].value);

      if (this.runState) {
        if (interval != this.lastInterval) {
          this.lastInterval = interval;
          clearInterval(this.timeoutFunc);
          this.timeoutFunc = setInterval(() => {
            this.state = !this.state;
            if (!this.state && this.settings['false'].value) {
              this.setOutputData(0, false);
              return;
            }
            if (this.state) this.setOutputData(0, true);
          }, interval / 2);
        }
        return;
      } else if (!this.runState) {
        this.runState = true;
        this.state = true;
        this.setOutputData(0, true);
        this.timeoutFunc = setInterval(() => {
          this.state = !this.state;
          if (!this.state && this.settings['false'].value) {
            this.setOutputData(0, false);
            return;
          }
          if (this.state) this.setOutputData(0, true);
        }, interval / 2);
      }
    }
  }

  onAfterSettingsChange() {
    //this.title = `Ticker (${this.settings['time'].value})`;
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
    this.onInputUpdated();
  }
}
Container.registerNodeType('boolean/ticker', TickerNode);
