import * as express from 'express';
import { ContainerRegistry } from '../../nodes/container';

let router = express.Router();

/**
 * Get container
 */
router.get('/c/:cid', function(req, res) {
  let container = ContainerRegistry.instance().get(req.params.cid);
  if (!container)
    return res.status(404).send(`Can't get container. Container id [${req.params.cid}] not found.`);
  //TODO: fix it
  // let s = container.serialize(true);
  let s = container.serialize();
  res.json(s);
});

module.exports = router;
