import { isNull, isString } from '../../../utils/helper';
import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import Utils from '../../utils';
import tagRefs from '../../utils/points/point-ref-utils';
import { NODE_INFO } from '../../node-info';

class StringCaseAcronymsNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.string["string-acronyms"].title;
    this.description = NODE_INFO.string["string-acronyms"].description;
    this.addInput('input', Type.STRING);
    this.addOutput('output', Type.STRING);
    // this.setOutputData(0, null);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    const input = this.getInputData(0);
    if (!Utils.hasInput(input)) {
      this.setOutputData(0, null, true);
      return;
    }
    if (isNull(input)) return
    let result:string;
    let obj = tagRefs.acronyms
    if (isString(input)) {
      for (let key in obj) {
        if (obj[key].toLowerCase() === input.toLowerCase()) {
          result = obj[key].toUpperCase()
        }
      };
      this.setOutputData(0, result, true);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }


}
Container.registerNodeType('string/string-acronyms', StringCaseAcronymsNode);
