export class ContainerRegistry extends DefaultContainerRegistry<Container> {

  private static _instance: ContainerRegistry;

  private constructor() {
    super();
  }

  public static instance(): ContainerRegistry {
    if (!ContainerRegistry._instance) {
      ContainerRegistry._instance = new ContainerRegistry();
    }

    return ContainerRegistry._instance;
  }

  increaseContainerId(): number {
    return ++this._last_container_id;
  }

  rootContainer(): Container {
    return <Container>super.rootContainer();
  }

  @ErrorHandler
  protected optimizeBeforeRegister(c: Container, force: boolean): Container {
    const container = super.optimizeBeforeRegister(c, force);
    if (container.id !== 0) {
      let rootContainer = this.rootContainer();
      if (rootContainer.server_dashboard_socket) container.server_dashboard_socket = rootContainer.server_dashboard_socket;
      if (rootContainer.server_editor_socket) container.server_editor_socket = rootContainer.server_editor_socket;
      if (rootContainer.client_socket) container.client_socket = rootContainer.client_socket;
      if (rootContainer.db) container.db = rootContainer.db;
      if (rootContainer.dashboard) container.dashboard = rootContainer.dashboard;
    }
    return container;
  }
}

export class NodeInContainerRegistry extends DefaultSubNodeRegistry<Node> {

  constructor(parent: Container, containerRegistry?: ContainerRegistryMetadata,
              globalNodeRegistry?: GlobalNodeRegistryMetadata) {
    super(parent, containerRegistry, globalNodeRegistry);
  }

  remove(node: Node | number | string, isCaching?: boolean): Promise<Node> {
    let sNode = <SerializedNode>{ cid: node['cid'] ?? this.parent().id, id: node['id'] ?? node, type: '' };
    const nodeId = IdGenerator.createNodeId(sNode.cid, sNode.id);
    this.parent().logInfo(`Removing node ${nodeId} on ${this.parent().side}...`);
    if (isContainerNode(node)) {
      sNode['sub_container'] = (<ContainerNode>node).sub_container;
    }
    return Promise.resolve(isNode(node) ? <Node>node : sNode)
                  .then(n => this.globalNodeRegistry.get(n) ?? n)
                  .then(n => NodeHookHelper.execute(<Node>n, 'onRemoved') ?? n)
                  .then(n => NodeHookHelper.execute(<Node>n, 'beforeRemoveNode') ?? n)
                  .then(n => isCaching ? n : createNodeDbHelper().delete(this.parent().db, sNode).then(_ => n))
                  .then(n => super.remove(<Node>n, isCaching))
                  .then(n => NodeHookHelper.execute(n, 'afterRemoveNode') ?? n)
                  .then(n => {
                    this.parent().logInfo(`Node ${nodeId} is removed on ${this.parent().side}`);
                    return n;
                  });
  }

  protected validate(node: Node, force: boolean, key: string): Node {
    const n = super.validate(node, force, key);
    if (this.parent().container_node && this.parent().container_node.shouldRejectNodeType(n?.type)) {
      throw new RegistryError(`Trying to create non-compatible child node ${n.getReadableId()} with type ` +
                              `${n.type} in container '${this.parent().id}'...`, n);
    }
    return n;
  }

  protected doRegister(k: string | number, node: Node, force: boolean = false, isCaching?: boolean): Promise<Node> {
    this.parent().logInfo(`Creating new node ${node.getReadableId()} on ${this.parent().side}...`);
    return Promise.resolve(node)
                  .then(n => NodeHookHelper.execute(n, 'onCreated') ?? n)
                  .then(n => isCaching ? n : createNodeDbHelper().insert(this.parent().db, n).then(_ => n))
                  .then(n => super.doRegister(k, n, force, isCaching))
                  .then(n => NodeHookHelper.execute(n, 'onAdded') ?? n)
                  .then(n => NodeHookHelper.execute(n, 'afterAdded') ?? n)
                  .then(n => {
                    this.parent().logInfo(`New node ${node.getReadableId()} is created on ${this.parent().side}`);
                    return n;
                  });
  }

  protected parent(): Container {
    return <Container>super.parent();
  }
}

export class Container implements IContainer, Serializable<SerializedContainer>, Deserializable<SerializedContainer> {

  id: number;
  name?: string;
  serializedNodes?: SerializedNode[];
  config: {
    links_ontop?: boolean;
  };
  side: Side;
  parent_container_id?: number;
  parent_id?: number;
  container_node: ContainerNode;
  client_socket: SocketIOClient.Socket; //SocketIOClient.Socket|SocketIO.Server;
  server_dashboard_socket: Namespace;
  server_editor_socket: Namespace;
  db: Database;
  dashboard: Dashboard;
  renderers: Array<Renderer>;
  isRunning: boolean;
  iteration: number;
  tag?: string;
  globaltime: number;
  runningtime: number;
  fixedtime: number;
  fixedtime_lapse: number;
  elapsed_time: number;
  starttime: number;
  execution_timer_id: number | any;
  onStopEvent: Function;
  onExecuteStep: Function;
  onAfterExecute: Function;
  onPlayEvent: Function;
  frame: number;
  private readonly _nodeRegistry: NodeInContainerRegistry;

  protected constructor(side: Side, id?: number, properties?: any,
              containerRegistry?: ContainerRegistryMetadata, globalNodeRegistry?: GlobalNodeRegistryMetadata) {
    this.renderers = null;
    this.side = side;
    this.id = id ?? -1;
    this.name = this.id === 0 ? 'Main' : properties?.name ?? '';
    this.config = Utils.cloneObject(properties?.config);
    this._nodeRegistry = new NodeInContainerRegistry(this, containerRegistry, globalNodeRegistry);
    this.clear();
  }

  static create(side: Side,
                obj?: { id: number; name?: string; config?: any; serializedNodes?: any[] },
                containerRegistry?: ContainerRegistryMetadata,
                globalNodeRegistry?: GlobalNodeRegistryMetadata): Container {
    let container = new Container(side, obj?.id, obj, containerRegistry, globalNodeRegistry);
    container.serializedNodes = obj?.serializedNodes;
    return container;
  }

  /**
   * @deprecated Use {@link NodeTypeRegistry#registerNodeType}
   */
  static registerNodeType(
    type: string,
    node_class: any,
    parentContainerNodeType: string = null,
    show_in_menu: boolean           = true,
    recursive: boolean              = false,
    alwaysInsideContainer: boolean  = false,
  ): void {
    NodeTypeRegistry.registerNodeType(type, node_class, parentContainerNodeType, show_in_menu, recursive,
                                      alwaysInsideContainer);
  }

  nodeRegistry(): NodeInContainerRegistry {
    return this._nodeRegistry;
  }

  /**
   * @deprecated Use {@link #nodeRegistry()}
   * @return {Node[]}
   */
  subNodes() {
    return this.nodeRegistry().list();
  }

  /**
   * @deprecated Use {@link #nodeRegistry()}
   * @param {string | number | Node} node
   * @return {Node}
   */
  getSubNode(node: string | number | Node): Node {
    return this.nodeRegistry().get(node);
  }

  /**
   * Removes all nodes from this container
   */
  clear(): void {
    this.stop();
    this.isRunning = false;

    //nodes
    this.nodeRegistry().clear();

    //iterations
    this.iteration = 0;

    this.config = {};

    //timing
    this.globaltime = 0;
    this.runningtime = 0;
    this.fixedtime = 0;
    this.fixedtime_lapse = 0.01;
    this.elapsed_time = 0.01;
    this.starttime = 0;

    this.sendActionToRenderer('clear');
  }

  /**
   * Stops the execution loop of the container
   */
  stop(): void {
    if (!this.isRunning) return;

    this.isRunning = false;

    if (this.onStopEvent) this.onStopEvent();

    if (this.execution_timer_id != null) clearInterval(this.execution_timer_id);
    this.execution_timer_id = null;

    for (let node of this.nodeRegistry().list()) {
      if (node['onStopContainer']) node['onStopContainer']();
    }
  }

  /**
   * Attach renderer to this container
   * @param renderer
   */
  attachRenderer(renderer: Renderer): void {
    if (renderer.container && renderer.container != this) {
      renderer.deselectAllNodes();
      renderer.container.detachRenderer(renderer);
    }

    renderer.container = this;
    if (!this.renderers) this.renderers = [];
    this.renderers.push(renderer);

    //reset active nodes
    for (let node of renderer.container.nodeRegistry().list()) {
      node.boxcolor = renderer.theme.NODE_DEFAULT_BOXCOLOR;
      node.setDirtyCanvas(true, true);
    }
  }

  /**
   * Detach renderer from this container
   * @param renderer
   */
  detachRenderer(renderer: Renderer): void {
    if (!this.renderers) return;

    let pos = this.renderers.indexOf(renderer);
    if (pos == -1) return;

    renderer.container = null;

    this.renderers.splice(pos, 1);
    if (this.renderers.length == 0) delete this.renderers;
  }

  /**
   * Starts running this container every interval milliseconds.
   * @param interval amount of milliseconds between executions
   */
  //STARTS from here
  run(interval: number = 1): void {
    if (this.isRunning) return;

    this.isRunning = true;

    if (this.onPlayEvent) this.onPlayEvent();

    for (let node of this.nodeRegistry().list()) {
      if (node['onRunContainer']) node['onRunContainer']();
    }

    //launch
    this.starttime = Utils.getTime();
    let that = this;

    //on each loop interval this runStep gets calls for 1 time, currently the inter == 100ms
    this.execution_timer_id = setInterval(() => that.runStep(1), interval);
  }

  /**
   * Run N steps (cycles) of the container
   * @param steps number of steps to run, default is 1
   */
  // THIS block gets called on LOOP_INTERVAL, CURRENTLY this is 100ms on .env and whatever comes in this 100ms inside, those outputs gets ignored, coz we only read value on 100ms
  runStep(steps: number = 1): void {
    let start = Utils.getTime();
    this.globaltime = 0.001 * (start - this.starttime);

    for (let i = 0; i < steps; i++) {
      this.transferDataBetweenNodes(); // this one is the main block where we are doing all operation to see whether our in/out got changed or not //main block to calculate whether our in/out got changed or not

      let now = Date.now();

      for (let node of this.nodeRegistry().list()) {
        // on each 100ms we are looping on this block, and we are seeing whether that interval is exceeded to EXECUTE_INTERVAL or not, if exceeded then call onExecute, so this block is only useful for the operation
        // which should be greater than 100ms to perform some kind of action
        if (
          !node.EXECUTE_INTERVAL ||
          (node.EXECUTE_INTERVAL && !node.executeLastTime) ||
          (node.executeLastTime && now - node.executeLastTime >= node.EXECUTE_INTERVAL)
        ) {
          if (node['onExecute']) node['onExecute']();

          if (node.EXECUTE_INTERVAL) node.executeLastTime = now;
        }

        // this updated is came from transferDataBetweenNodes
        // these UPDATE_INPUTS_INTERVAL is also plays roles as Debounce, normally we don't have value on the node for this variable, so it directly enters into the block
        if (node.isUpdated) {
          if (
            !node.UPDATE_INPUTS_INTERVAL ||
            (node.UPDATE_INPUTS_INTERVAL && !node.updateInputsLastTime) ||
            (node.updateInputsLastTime && now - node.updateInputsLastTime >= node.UPDATE_INPUTS_INTERVAL)
          ) {
            if (node['onInputUpdated']) node['onInputUpdated']();//here we get called our onInputUpdated

            node.isUpdated = false;

            for (let i in node.inputs) if (node.inputs[i].updated) node.inputs[i].updated = false;

            if (node.UPDATE_INPUTS_INTERVAL) node.updateInputsLastTime = now; //this is for restricting multiple values on that interval and to act like Debounce
          }
        }
      }

      this.fixedtime += this.fixedtime_lapse;

      if (this.onExecuteStep) this.onExecuteStep();
    }

    if (this.onAfterExecute) this.onAfterExecute();

    let elapsed = Utils.getTime() - start;
    if (elapsed == 0) elapsed = 1;
    this.elapsed_time = 0.001 * elapsed;
    this.globaltime += 0.001 * elapsed;
    this.iteration += 1;
  }

  transferDataBetweenNodes() {
    // it runs on each 100ms interval so
    //all nodes
    for (let node of this.nodeRegistry().list()) {
      if (!node.outputs) continue;

      //all outputs
      for (let o in node.outputs) {
        let output = node.outputs[o];
        if (!output.updated) continue;// when updated is true from output of each node we dont break the loop and it will go down

        output.updated = false; // make update false, hence we will not get as updated=true on next loop

        if (output.links == null) continue;//if links is not no need to do any operation

        //all output links
        for (let link of output.links) { //loop on all links
          let target_node = this.nodeRegistry().get(link.target_node_id); // this is where we had an issue, coz node was deleted but link wasn't
          if (!target_node) {
            this.debugErr(`Can't transfer data from node ${node.getReadableId()}. Target node not found`);
            continue;
          }

          let target_id = link.target_input_id ?? link.target_slot;
          let target_input = target_node.inputs[target_id];

          if (!target_input) return;
          target_input.data = Utils.parseValue(output.data, target_input.type); // Now change the input of target node, which is connected wit the output

          target_node.isUpdated = true; //now we made the target node also updated as like on setOutputData, so that target node will perform same operation for it's targeted nodes
          target_input.updated = true; // plays important role for onInputUpdated()
        }
      }
    }
  }

  /**
   * Returns the amount of time the container has been running in milliseconds
   * @method getTime
   * @returns number of milliseconds the container has been running
   */
  getTime(): number {
    return this.globaltime;
  }

  /**
   * Sends action to renderer
   * @param action
   * @param params
   */
  sendActionToRenderer(action: string, params?: Array<any>): void {
    if (!this.renderers) return;

    for (let i = 0; i < this.renderers.length; ++i) {
      let c = this.renderers[i];
      if (c[action]) c[action].apply(c, params);
    }
  }

  /**
   * Get nodes
   */
  getNodes(includeSubContainers = false): Array<Node> {
    let nodes = [];

    addNodes(this.nodeRegistry().list());

    function addNodes(_nodes: Node[]) {
      for (let n of _nodes) {
        nodes.push(n);

        if (includeSubContainers && isContainerNode(n)) {
          addNodes((<ContainerNode>n).sub_container.nodeRegistry().list());
        }
      }
    }

    return nodes;
  }

  /**
   * Returns a list of nodes that matches a type
   * @param type the name of the node type
   * @param includeSubContainers
   * @returns a list with all the nodes of this type
   */
  getNodesByType(type: string, includeSubContainers = false): Array<Node> {
    type = type.toLowerCase();
    let r = [];
    //TODO: performance, refactor it
    for (let node of this.nodeRegistry().list()) {
      if (node.type.toLowerCase() == type) r.push(node);
    }

    if (includeSubContainers) {
      for (let node of this.nodeRegistry().list()) {
        if (node instanceof ContainerNode) {
          let nodes = (<ContainerNode>node).sub_container.getNodesByType(type, true);
          nodes.forEach(n => r.push(n));
        }
      }
    }

    return r;
  }

  /**
   * Returns a list of nodes that are an instance of the provided node-class (or one of its subclasses)
   * @returns a list with all the nodes of this type
   * @param nodeClass
   * @param includeSubContainers
   */
  getNodesByClass(nodeClass: Function, includeSubContainers = false): Array<Node> {
    let r = [];
    //TODO: performance, refactor it
    for (let node of this.nodeRegistry().list()) {
      if (node instanceof nodeClass) r.push(node);
    }

    if (includeSubContainers) {
      for (let node of this.nodeRegistry().list()) {
        if (node instanceof ContainerNode) {
          let nodes = (<ContainerNode>node).sub_container.getNodesByClass(nodeClass, true);
          nodes.forEach(n => r.push(n));
        }
      }
    }

    return r;
  }

  getAllNodes(parent?: Node[],
              parentId: number = 0): Array<{ id: string; name: string; isContainer: boolean; list?: Array<{ id: string; name: string }>; }> {
    let list = [];
    let nodes = parent ?? this.nodeRegistry().list();
    nodes.forEach(node => {
      if (node instanceof ContainerNode) {
        list.push({
                    id: node.sub_container.id,
                    name: node.sub_container.name,
                    parentId,
                    list: this.getAllNodes(node.sub_container.nodeRegistry().list(), node.sub_container.id),
                    isContainer: true,
                  });
      } else {
        list.push({
                    id: node.id,
                    parentId,
                    name: node.name ? `${node.name}: ${node.title}` : node.title,
                    isContainer: false,
                  });
      }
    });
    return list.sort((a, b) => {
      if (a.isContainer && b.isContainer) {
        return a.id - b.id;
      } else if (a.isContainer && !b.isContainer) {
        return -1;
      } else {
        return 1;
      }
    });
  }

  /**
   * Set canvas to dirty for update
   * @param foreground
   * @param background
   */
  setDirtyCanvas(foreground?: boolean, background?: boolean): void {
    this.sendActionToRenderer('setDirty', [foreground, background]);
  }

  /**
   * Executes a callback with the editor instance if available
   */
  runWithEditor(f: (Editor) => void) {
    const editor = get(this, 'renderers[0].editor');
    if (editor) f(editor);
  }

  /**
   * @returns serialized container
   */
  serialize(): SerializedContainer {
    return {
      id: this.id,
      name: this.name,
      config: this.config,
      serializedNodes: this.nodeRegistry().list().map(node => SerializableHelper.serialize(node)),
    };
    // let ser_nodes: Array<SerializedNode> = [];
    // for (let id in this._nodes) {
    //   const node = this._nodes[id];
    //   // if (only_dashboard_nodes && !node.isDashboardNode) continue;
    //   ser_nodes.push(node.serialize());
    // }
    // data.serializedNodes = ser_nodes;
    // (<ExtensionContainer>data).last_node_id = this.nodeRegistry().last_node_id;
    // if (this.id === 0) (<any>data).last_container_id = this.nodeRegistry().containerRegistry.last_container_id;
  }

  deserialize(sContainer: SerializedContainer): Container {
    return Container.create(this.side, { ...sContainer });
  }

  logInfo(message: string): void {
    logger.info(`[${this.id}] ${message}`);
  }

  /**
   * Print debug message to console
   * @param message
   */
  debug(message: string): void {
    logger.debug(`[${this.id}] ${message}`);
  }

  /**
   * Print error message to console
   * @param message
   */
  debugWarn(message: string): void {
    logger.warn(`[${this.id}] ${message}`);
  }

  /**
   * Print error message to console
   * @param message
   */
  debugErr(message: string): void {
    logger.error(`[${this.id}] ${message}`);
  }

}
