import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import Utils from '../../utils';
import { NODE_INFO } from '../../node-info';

class StringLatchNode extends Node {
  lastLatchValue: boolean;


  constructor() {
    super();
    this.title = NODE_INFO.latch["string-latch"].title;
    this.description =NODE_INFO.latch["string-latch"].description;
    this.addInput('input', Type.STRING);
    this.addInput('latch', Type.BOOLEAN);
    this.addOutput('output', Type.STRING);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let latch = this.getInputData(1);
    if (latch == null) {
      this.setOutputData(0, null);
      return;
    }
    if (latch == this.lastLatchValue) return;
    this.lastLatchValue = latch;
    if (latch == true) {
      let input = this.getInputData(0);
      if (!Utils.hasInput(input)) {
        this.setOutputData(0, null);
        return;
      }
      if (input === '') input = null;
      this.setOutputData(0, input);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('latch/string-latch', StringLatchNode);
