import * as mqtt from 'mqtt';
import config from '../../../../config';
import { isNull } from '../../../../utils/helper';
import { Container } from '../../../container';
import { Node } from '../../../node';
import { NODE_INFO } from '../../../node-info';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import Utils from '../../../utils';
import CryptoUtils from '../../../utils/crypto-utils';
import { MQTTMessage } from './core/mqtt-model';

const matchMQTT = require('mqtt-match');

//import { themes } from 'src/client/editor/renderer-themes';

class MqttClientNode extends Node {
  client: any;
  topicsCount = 0;
  messageQueue: Array<MQTTMessage> = [];

  constructor() {
    super();
    this.title = NODE_INFO["protocols/mqtt"].client.title;
    this.description = NODE_INFO["protocols/mqtt"].client.description;

    this.addInput('[enable]', Type.BOOLEAN);
    this.addOutput('connected', Type.BOOLEAN);
    this.settings['enable'] = NodeSetting.create({ description: 'Enable', value: false, type: SettingType.BOOLEAN });
    this.settings['broker_group'] = NodeSetting.create({
      description: 'Broker settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['broker_group'] = NodeSetting.create({
      description: 'Broker settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['useEnv'] = NodeSetting.create({ description: 'Dont use env settings', value: false, type: SettingType.BOOLEAN });
    this.settings['server'] = NodeSetting.create({ description: 'Broker URL', value: '0.0.0.0', type: SettingType.STRING });
    this.settings['port'] = NodeSetting.create({ description: 'Broker port', value: '1883', type: SettingType.STRING });
    this.settings['authentication'] = NodeSetting.create({
      description: 'Use Authentication',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['username'] = NodeSetting.create({ description: 'User name', value: '', type: SettingType.STRING });
    this.settings['password'] = NodeSetting.create({ description: 'Password', value: '', type: SettingType.PASSWORD });
    this.settings['topics_group'] = NodeSetting.create({
      description: 'Topic settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['topics_count'] = NodeSetting.create({
      description: 'Number of Topics',
      value: 1,
      type: SettingType.NUMBER,
    });
    this.settings['decimals'] = NodeSetting.create({
      description: 'Decimal Places (Numerics)',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: '0' },
          { value: 1, text: '1' },
          { value: 2, text: '2' },
          { value: 3, text: '3' },
          { value: 4, text: '4' },
          { value: 5, text: '5' },
          { value: 6, text: '6' },
          { value: 7, text: '7' },
          { value: 8, text: '8' },
          { value: 9, text: '9' },
          { value: 10, text: '10' },
        ],
      },
      value: 3,
    });

    this.setSettingsConfig({
      groups: [
        { server: { weight: 3 }, port: { weight: 1 } },
        { useEnv: {}, authentication: {} },
        { username: {}, password: {} },
        { topics_count: {}, decimals: {} },
      ],
      conditions: {
        server: setting => {
          return !!setting['useEnv'].value;
        },
        port: setting => {
          return !!setting['useEnv'].value;
        },
        authentication: setting => {
          return !!setting['useEnv'].value;
        },
        username: setting => {
          return !!setting['authentication'].value;
        },
        password: setting => {
          return !!setting['authentication'].value;
        },
      },
    });
  }

  init() {
    this.changeTopicsCount(this.settings['topics_count'].value);
  }

  onCreated() {
    this.renameInputsOutputs();
  }

  onAdded() {
    this.EXECUTE_INTERVAL = 200;

    this.topicsCount = this.settings['topics_count'].value;
    if (this.side == Side.server) {
      this.setOutputData(0, false);
      // connect if "connect" input disconnected or input data==true
      if (this.settings['enable'].value && (this.inputs[0].link == null || this.inputs[0].data == true)) {
        this.connectToBroker();
      }
    }
  }

  connectToBroker() {
    let { host, port, username, password } = config.mqtt;
    let options: any = {};

    if (this.settings['useEnv'].value) {
      // dont use env
      if (this.settings['server'].value != null && this.settings['server'].value != '')
        options.host = this.settings['server'].value;

      if (this.settings['port'].value != null && this.settings['port'].value != '')
        options.port = this.settings['port'].value;

      if (this.settings['username'].value != null && this.settings['username'].value != '')
        options.username = this.settings['username'].value;

      if (this.settings['password'].value != null && this.settings['password'].value != '')
        options.password = CryptoUtils.decrypt(this.settings['password'].value);
    } else {
      // use use env
      if (isNull(host)) return;
      if (isNull(port)) return;
      options.host = host;
      options.port = port;
      if (this.settings['authentication'].value === true) {
        if (!isNull(options.username)) {
          options.username = username;
        }
        if (!isNull(options.password)) {
          options.password = CryptoUtils.decrypt(password);
        }
      }
    }

    this.disconnectFromBroker();
    this.client = mqtt.connect(options);
    this.client.on('connect', () => {
      this.setOutputData(0, true);
      // subscribe
      for (let i = 1; i <= this.topicsCount; i++) {
        let topic = this.settings['topic' + i].value;
        if (topic && topic != '') {
          this.client.subscribe(topic);
        }
      }
    });

    this.client.on('close', () => {
      this.setOutputData(0, false, true);
      this.setOutputData(1, null, true);
    });

    this.client.on('error', error => {
      this.debugWarn(error);
    });

    this.client.on('message', (topic, message) => {
      let obj: MQTTMessage = { topic: topic, message: message.toString() };
      this.messageQueue.push(obj);
    });
  }

  disconnectFromBroker() {
    if (this.client) this.client.end();
  }

  onInputUpdated() {
    // connect/disconnect
    if (this.inputs[0].updated) {
      if (this.inputs[0].data == false) this.disconnectFromBroker();
      // auto connect when input disconnected
      else if (this.settings['enable'].value && (this.inputs[0].link == null || this.inputs[0].data == true))
        this.connectToBroker();
    }

    // publish message
    if (this.settings['enable'].value && this.client && this.client.connected) {
      for (let i = 1; i < this.getInputsCount(); i++) {
        let data = this.inputs[i].data;
        if (data && this.inputs[i].updated) {
          let isJSON = true;
          try {
            data = JSON.parse(data);
          } catch {
            console.log('Not a valid JSON');
            isJSON = false;
          }
          let topic: string;
          let payload: string = '';

          if (
            isJSON &&
            data.hasOwnProperty('topic') &&
            typeof data.topic === Type.STRING &&
            data.hasOwnProperty('payload')
          ) {
            payload = JSON.stringify(data.payload);
            this.client.publish(data.topic, '' + payload);
          } else {
            topic = this.settings['topic' + i].value;
            if (isJSON) payload = JSON.stringify(data);
            else payload = String(data);
            this.client.publish(topic, '' + payload);
          }
        }
      }
    }
  }

  onAfterSettingsChange() {
    let topics = this.settings['topics_count'].value;
    topics = Utils.clamp(topics, 1, 100);
    this.settings['topics_count'].value = topics;

    if (this.topicsCount != topics) this.changeTopicsCount(topics);

    this.renameInputsOutputs();

    if (this.container.db)
      this.container.db.updateNode(this.id, this.container.id, {
        $set: { inputs: this.inputs, outputs: this.outputs, settings: this.settings },
      });

    if (this.side == Side.server) {
      this.disconnectFromBroker();

      if (this.settings['enable'].value) this.connectToBroker();
    }
  }

  onRemoved() {
    this.disconnectFromBroker();
  }

  changeTopicsCount(target_count) {
    let diff = target_count - this.topicsCount;
    if (diff == 0) return;

    // add inputs, outputs
    this.changeInputsCount(target_count + 1, Type.STRING);
    this.changeOutputsCount(target_count + 1, Type.STRING);

    // change settings for topics
    if (diff > 0) {
      for (let i = this.topicsCount + 1; i <= target_count; i++) {
        this.settings['outputType' + i] = NodeSetting.create({
          description: 'Output Type',
          type: SettingType.DROPDOWN,
          config: {
            items: [
              { value: false, text: 'Payload' },
              { value: true, text: 'JSON' },
            ],
          },
          value: false,
        });
        this.settings['topic' + i] = NodeSetting.create({
          description: 'Topic ' + i,
          value: '',
          type: SettingType.STRING,
        });
        for (let i = this.topicsCount + 1; i <= target_count; i++) {
          this.settingConfigs.groups.push({
            [`outputType${i}`]: { weight: 2 },
            [`topic${i}`]: { weight: 5 },
          });
        }
      }
    } else if (diff < 0) {
      for (let i = this.topicsCount; i > target_count; i--) {
        delete this.settings['topic' + i];
        this.settingConfigs.groups.pop();
      }
    }

    this.topicsCount = target_count;
  }

  renameInputsOutputs() {
    for (let i = 1; i <= this.topicsCount; i++) {
      //this.inputs[i].name = '[' + i + ']';
      let topic = this.settings['topic' + i].value;
      if (topic.length > 20) topic = '...' + topic.substr(topic.length - 20, 20);
      this.inputs[i].name = '' + i + ' | ' + topic;
      this.outputs[i].name = topic + ' | ' + i;
    }

    if (this.side == Side.editor) {
      for (let i = 1; i <= this.topicsCount; i++) {
        this.inputs[i].label = this.inputs[i].name;
        this.outputs[i].label = this.outputs[i].name;
      }
      this.setDirtyCanvas(true, true);
    }
  }

  // TODO: queue for each output
  onExecute() {
    if (this.messageQueue && this.messageQueue.length > 0) {
      this.processQueue(this.messageQueue.shift());
    }
  }

  setMQTTOutput(mqttMessage: MQTTMessage, outputNum: number) {
    if (this.settings['outputType' + outputNum].value) {
      this.setOutputData(outputNum, { topic: mqttMessage.topic, payload: mqttMessage.message }, true);
      return;
    } else {
      let msgAsNum = Number(mqttMessage.message);
      if (isNaN(msgAsNum)) {
        this.setOutputData(outputNum, mqttMessage.message);
        return;
      }
      this.setOutputData(outputNum, Utils.toFixedNumber(msgAsNum, this.settings['decimals'].value).toString(), true);
    }
  }

  processQueue(mqttMessage: MQTTMessage) {
    // look into using mqtt-match library. (https://www.npmjs.com/package/mqtt-match)
    for (let outInd = 1; outInd <= this.topicsCount; outInd++) {
      if (matchMQTT(this.settings['topic' + outInd].value, mqttMessage.topic)) this.setMQTTOutput(mqttMessage, outInd);
    }
  }
}
Container.registerNodeType('protocols/mqtt/client', MqttClientNode);
