import { Node } from '../nodes/node';
import { Side } from '../nodes/node-interface';
import { DispatchNode } from './DispatchNode';
import { UNKNOWN_POINT_NAME } from './IPubSub';
import { pubSubSingleton } from './PubSub';

export class PubSubHelpers {
  static logger = require('logplease').create('pub-sub', { color: 4 });

  static map(that: Node) {
    if (that.side === Side.server && !(that instanceof DispatchNode)) {
      const point = PubSubHelpers.getPoint(that);
      PubSubHelpers.logger.info(`Mapping protocol: ${that.category} having point: ${point}`);
      pubSubSingleton.addPoint(that.category, point, PubSubHelpers.getDisplayPointName(that), false);
    }
  }

  static unMap(that: Node) {
    if (that.side === Side.server && !(that instanceof DispatchNode)) {
      const point = PubSubHelpers.getPoint(that);
      PubSubHelpers.logger.info(`Un-mapping protocol: ${that.category} having point: ${point}`);
      pubSubSingleton.removePoint(that.category, point);
    }
  }

  static renameFunction(that: Node) {
    if (that.side === Side.server && !(that instanceof DispatchNode)) {
      const point: string = PubSubHelpers.getPoint(that);
      PubSubHelpers.logger.info(`Renaming protocol: ${that.category} having point: ${point}`);
      pubSubSingleton.renamePoint(that.category, point, PubSubHelpers.getDisplayPointName(that));
    }
  }

  static dispatch(that: Node, value: any) {
    if (that.side === Side.server && !(that instanceof DispatchNode)) {
      const point = PubSubHelpers.getPoint(that);
      // logger.info(`Dispatching: ${that.protocol} having point: ${point}`);
      pubSubSingleton.dispatch(that.category, point, value);
    }
  }

  static connect(that: DispatchNode) {
    if (that.side === Side.server && that instanceof DispatchNode) {
      const protocol: string = that.getSelectedProtocol();
      const point: string = that.getSelectedPoint();
      const pointName: string = that.getSelectedPointName();
      PubSubHelpers.logger.info(`Subscribing protocol: ${protocol} having point: ${point}`);
      pubSubSingleton.connect(protocol, point, pointName, that);
    }
  }

  static disconnect(that: DispatchNode) {
    if (that.side === Side.server && that instanceof DispatchNode) {
      const protocol: string = that.getSelectedProtocol();
      const point: string = that.getSelectedPoint();
      PubSubHelpers.logger.info(`Un-subscribing protocol: ${protocol} having point: ${point}`);
      pubSubSingleton.disconnect(protocol, point, that);
    }
  }

  static getPoint(that) {
    const { cid, id } = that;
    return `${cid}/${id}`;
  }

  private static getDisplayPointName(that: Node) {
    let names: string[] = [];
    let parentNode = that.getParentNode();
    while (parentNode) {
      names.push(parentNode.title);
      parentNode = parentNode.getParentNode();
    }
    names = names.reverse();
    names.push(that.name || that.title || UNKNOWN_POINT_NAME);
    return names.join('/');
  }
}
