import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import { NODE_INFO } from '../../node-info';

class AnyLatchNode extends Node {
  lastLatchValue: boolean;


  constructor() {
    super();
    this.title = NODE_INFO.latch["any-latch"].title;
    this.description =NODE_INFO.latch["any-latch"].description;

    this.addInput('input', Type.ANY);
    this.addInput('latch', Type.BOOLEAN);
    this.addOutput('output', Type.ANY);
    this.settings['passOnly'] = NodeSetting.create({
      description: 'Only if input value is updated',
      value: false,
      type: SettingType.BOOLEAN,
    });
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const passOnly = this.settings['passOnly'].value
    let latch = this.getInputData(1);
    if (latch == null) {
      if (passOnly) {
        this.setOutputData(0, null);
      } else this.setOutputData(0, null);
      return;
    }
    if (latch == this.lastLatchValue) return;
    this.lastLatchValue = latch;
    if (latch == true) {
      let input = this.getInputData(0);
      if (!Utils.hasInput(input)) {
        if (passOnly) {
          this.setOutputData(0, null);
        } else this.setOutputData(0, null);
        return;
      }
      if (input === '') input = null;
      if (passOnly) {
        this.setOutputData(0, input);
      } else this.setOutputData(0, input);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('latch/any-latch', AnyLatchNode);
