import { isNotNull } from './helper';

/**
 * ID generator
 * //TODO: Workaround until using UUID. To avoid collision
 */
export class IdGenerator {
  static readonly IO_HASH_KEY = 1;
  static readonly CONTAINER_HASH_KEY = 3;
  static readonly NODE_HASH_KEY = 11;
  private static readonly PRIME = 31;

  static genNodeIOId(startingIdx: number = 0, id?: number): number {
    return this.createGuardId(IdGenerator.IO_HASH_KEY * IdGenerator.PRIME, startingIdx ?? 0, id ?? 1);
    // return this.createHashId(IdGenerator.IO_HASH_KEY, startingIdx ?? 0, id ?? ++startingIdx);
  }

  static genNodeId(startingIdx: number = 0, id?: number): number {
    return this.createGuardId(IdGenerator.NODE_HASH_KEY * IdGenerator.PRIME, startingIdx ?? 0, id ?? 1);
    // return this.createHashId(IdGenerator.NODE_HASH_KEY, startingIdx ?? 0, id ?? ++startingIdx);
  }

  static genContainerId(startingIdx: number = 0, id?: number): number {
    return this.createGuardId(IdGenerator.CONTAINER_HASH_KEY * IdGenerator.PRIME, startingIdx ?? 0, id ?? 1);
    // return this.createHashId(IdGenerator.CONTAINER_HASH_KEY, startingIdx ?? 0, id ?? ++startingIdx);
  }

  static createNodeId(cId: number, nId: number) {
    if (isNotNull(cId) && isNotNull(nId)) {
      return `c${cId}n${nId}`;
    }
    return null;
  }

  private static createHashId(hashKey: number, ...values: number[]): number {
    let hash = hashKey;
    for (let v of values) {
      hash = IdGenerator.PRIME * hash + v;
    }
    return hash;
  }

  private static createGuardId(guardKey: number, startIdx: number, step: number = 1): number {
    return guardKey + startIdx + step;
  }

  private IdGenerator() {
  }
}
