import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class ArrayBuilderNode extends FlexibleNode {
  constructor() {
    super();
    this.dynamicInputsType = Type.STRING;
    this.title = NODE_INFO.json["array-builder"].title;
    this.description = NODE_INFO.json["array-builder"].description; 
    this.addOutput('out', Type.STRING);
  }

  onAdded() {
    this.size = this.computeSize();
    this.onInputUpdated();
  }

  onInputUpdated() {
    let out = []
    for (let i = this.dynamicInputStartPosition; i < this.getInputsCount(); i++) {
      out.push(this.getInputData(i))
    }
    this.setOutputData(0, out);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('json/array-builder', ArrayBuilderNode);
