import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathTriggerModulusNode extends Node {
  lastTrigger: boolean;
  constructor() {
    super();

    this.title = NODE_INFO["math-trigger"]["trigger-modulus"].title;
    this.description = NODE_INFO["math-trigger"]["trigger-modulus"].description; 
   
    this.addInput('trigger', Type.BOOLEAN);
    this.addInputWithSettings('in 1', Type.NUMBER, 0, 'in 1');
    this.addInputWithSettings('in 2', Type.NUMBER, 2, 'in 2');
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
    this.lastTrigger = false;
    this.onInputUpdated();
  }

  onInputUpdated() {
    const trigger = this.getInputData(0);
    if (trigger && !this.lastTrigger) {
      let a = this.getInputData(1);
      let b = this.getInputData(2);

      if (a != null && b != null) this.setOutputData(0, a % b);
      else this.setOutputData(0, null);
    }
    this.lastTrigger = trigger;
  }

  dynamicInputStartPosition(): number {
    return 1;
  }
}
Container.registerNodeType('math-trigger/trigger-modulus', MathTriggerModulusNode);
