import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import Utils from '../../utils';
import { NODE_INFO } from '../../node-info';

class StringSwitchNode extends Node {

  constructor() {
    super();
    this.title = NODE_INFO.switch["string-switch"].title;
    this.description = NODE_INFO.switch["string-switch"].description;
    this.addInput('switch', Type.BOOLEAN);
    this.addInputWithSettings('inTrue', Type.STRING, '', 'True Value');
    this.addInputWithSettings('inFalse', Type.STRING, '', 'False Value');
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let select = this.getInputData(0);
    if (!Utils.hasInput(select)) {
      this.setOutputData(0, null);
      return;
    }
    const inA = this.getInputData(1);
    const inB = this.getInputData(2);
    if (select) this.setOutputData(0, inA);
    else this.setOutputData(0, inB);
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('switch/string-switch', StringSwitchNode);
