import { Colors, create } from 'logplease';
import { Container } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

const log = create('node', { color: Colors.Magenta });

// TODO: we don't need this all logic, background does this job
// Show value inside the debug console
export class ConsoleNode extends Node {
  MAX_MESS_PER_SEC = 11;
  messagesPerSec = 0;
  timer: any;

  constructor() {
    super();
    this.title = NODE_INFO.debug.console.title;
    this.description = NODE_INFO.debug.console.description;
    this.addInput('input', Type.ANY);
  }

  onAdded() {
    if (this.side == Side.server) this.updateMessPerSec();
  }

  updateMessPerSec() {
    let that = this;
    this.timer = setInterval(function() {
      if (that.messagesPerSec > that.MAX_MESS_PER_SEC) {
        let dropped = that.messagesPerSec - that.MAX_MESS_PER_SEC;
        log.info(
          'CONSOLE NODE [' +
            that.container.id +
            '/' +
            that.id +
            ']: dropped ' +
            dropped +
            ' messages (data rate limitation)',
        );
        that.sendMessageToEditorSide({ dropped: dropped });
      }

      that.messagesPerSec = 0;
    }, 1000);
  }

  onInputUpdated() {
    if (!this.inputs[0].link) return;

    let val = this.getInputData(0);
    this.isRecentlyActive = true;

    this.messagesPerSec++;
    if (this.messagesPerSec <= this.MAX_MESS_PER_SEC) {
      log.info('CONSOLE NODE [' + this.container.id + '/' + this.id + ']: ' + val);
      this.sendMessageToEditorSide({ value: val });
    }
  }

  onRemoved() {
    if (this.timer) clearInterval(this.timer);
  }

  onGetMessageToEditorSide(data) {
    if (data.value != null) log.info('CONSOLE NODE [' + this.container.id + '/' + this.id + ']: ' + data.value);

    if (data.dropped)
      log.info(
        'CONSOLE NODE [' +
          this.container.id +
          '/' +
          this.id +
          ']: dropped ' +
          data.dropped +
          ' messages (data rate limitation)',
      );
  }
}
Container.registerNodeType('debug/console', ConsoleNode);
