import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class StringBufferNode extends Node {
  useTrigger: boolean;
  bufferData: string;
  lastSend:boolean;
  lastClear:boolean;
  lastAdd:boolean;


  constructor() {
    super();

    this.title = NODE_INFO.string["string-buffer"].title;
    this.description = NODE_INFO.string["string-buffer"].description;

    this.addInput('input', Type.STRING);
    this.addInput('sendBuffer', Type.BOOLEAN);
    this.addInput('clearBuffer', Type.BOOLEAN);
    this.addInput('addToBuffer', Type.BOOLEAN);
    this.addOutput('output', Type.STRING);
    this.addOutput('currentBuffer', Type.STRING);

    this.settings['useTrigger'] = NodeSetting.create({
      description: 'Select a Buffer Input Method',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: true, text: 'Add Input to Buffer When Triggered' },
          { value: false, text: 'Collect all Input Messages (no Trigger)' },
        ],
      },
      value: true,
    });
    this.useTrigger = true;
    this.bufferData = '';
  }

  onCreated() {
    this.lastSend = false;
    this.lastClear = false;
    this.lastAdd = false;
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    const send = this.getInputData(1) || false;
    const clear = this.getInputData(2) || false;
    let addTrigger = false;
    if (this.useTrigger) addTrigger = this.getInputData(3) || false;

    if (this.inputs[1].updated && send && !this.lastSend) {
      this.setOutputData(0, this.bufferData);
      this.bufferData = '';
    }
    this.lastSend = send;

    if (this.inputs[2].updated && clear && !this.lastClear) this.bufferData = '';
    this.lastClear = clear;

    if (
      (!this.useTrigger && this.inputs[0].updated) ||
      (this.useTrigger && this.inputs[3].updated && addTrigger && !this.lastAdd)
    ) {
      const input = this.getInputData(0);
      if (input == null) return;
      this.bufferData += String(input);
    }
    this.lastAdd = addTrigger;

    this.setOutputData(1, this.bufferData);
  }

  onAfterSettingsChange() {
    const triggerInput = this.settings['useTrigger'].value;
    if (triggerInput !== this.useTrigger) {
      if (triggerInput) this.addInput('addToBuffer', Type.BOOLEAN);
      else this.removeInput(3);
    }
    this.useTrigger = triggerInput;
    this.onInputUpdated();
  }
}
Container.registerNodeType('string/string-buffer', StringBufferNode);
