import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class TextCutSubstringNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.string["cut-substring"].title;
    this.description = NODE_INFO.string["cut-substring"].description;
    this.addInput('input', Type.STRING);
    this.addInputWithSettings('start', Type.NUMBER, 0, 'Start', false);
    this.addInputWithSettings('length', Type.NUMBER, 1, 'Length', false);
    this.addOutput('output', Type.STRING);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    const text = this.getInputData(0);
    const start = this.getInputData(1);
    const length = this.getInputData(2);
    if (text != null && start != null && length != null) {
      this.setOutputData(0, text.substr(start, length));
    } else this.setOutputData(0, null);
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('string/cut-substring', TextCutSubstringNode);
