import { isNull } from '../../../utils/helper';
import { IdGenerator } from '../../../utils/id-generator';
import { Container } from '../../container';
import { Node } from '../../node';
import { createNodeDbHelper } from '../../node-db-helper';
import { Type } from '../../node-io';
import { AbstractFolderIONode } from './abstract-folder-io';

class ContainerOutputNode extends AbstractFolderIONode {
  constructor() {
    super('Folder Output', 'Output of the container');
    this.addInput('output', null);
  }

  onCreated() {
    if (!this.isUnderContainer()) return;
    let containerNode = this.container.container_node;
    const id = containerNode.addOutput(this.name, Type.ANY, undefined, { subNodeId: this.id });
    this.name = containerNode.outputs[id].name;
    this.linkHandler.recomputeOutputLinks(IdGenerator.createNodeId(containerNode.cid, containerNode.id));
  }

  onRemoved() {
    if (!this.isUnderContainer()) return;
    let containerNode = this.container.container_node;
    let [outputId, _] = this.findParentOutputById(this.container.container_node, 'outputs');
    if (isNull(outputId)) {
      return;
    }
    containerNode.removeOutput(+outputId);
    this.linkHandler.recomputeOutputLinks(IdGenerator.createNodeId(containerNode.cid, containerNode.id));
    createNodeDbHelper().update(containerNode, ['size']);
  }

  onAfterSettingsChange() {
    if (!this.isUnderContainer()) return;
    const containerNode = this.container.container_node;
    let [outputId, _] = this.findParentOutputById(containerNode, 'outputs');
    if (isNull(outputId)) {
      return;
    }
    containerNode.outputs[outputId].name = this.name;
    createNodeDbHelper().update(containerNode, ['outputs']);
  }

  onInputUpdated() {
    if (!this.isUnderContainer()) return;
    let [outputId, _] = this.findParentOutputById(this.container.container_node, 'outputs');
    if (isNull(outputId)) {
      return;
    }
    this.isRecentlyActive = true;
    this.container.container_node.setOutputData(+outputId, this.getInputData(0));
  }

  protected correctParentIO(containerNode: Node, slot: number) {
    let output = containerNode.outputs[slot];
    if (isNull(output)) {
      return;
    }
    output.subNodeId = this.id;
    createNodeDbHelper().update(containerNode, ['outputs']);
  }

}

export const FOLDER_OUTPUT = 'container/folder-output';
Container.registerNodeType(FOLDER_OUTPUT, ContainerOutputNode, null, true, false, true);
