import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class FiltersOnlyEqualNode extends Node {
  constructor() {
    super();

    this.title = NODE_INFO.filter["only-equal"].title;
    this.description = NODE_INFO.filter["only-equal"].description;

    this.addInput('input', Type.ANY);
    this.addInputWithSettings('match', Type.ANY, '', 'Match Value');
    this.addOutput('output', Type.ANY);
    this.properties['value'];
  }

  onAdded(){
    this.setOutputData(0, this.properties['value'] || null);
    this.onInputUpdated();
  }

  onAfterSettingsChange(){
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    val = (val || '').toString(); // making string null i.e `''` and other `null` same
    if (val == 'false') val = false;
    else if (val == 'true') val = true;

    let match = (this.getInputData(1) || '').toString();
    if (match == 'false') match = false;
    else if (match == 'true') match = true;

    if (val == match) {
      this.setOutputData(0, val);
      this.properties['value'] = val;
      this.persistProperties(false, true);
    }
  }
}
Container.registerNodeType('filter/only-equal', FiltersOnlyEqualNode);
