import * as Bacnet from 'node-bacnet';
import { BACNET_NETWORK } from '../../../constants';
import { Container } from '../../../container';
import { ContainerNode } from '../../../container-node';
import { Node } from '../../../node';
import { NODE_INFO } from '../../../node-info';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import { ADD_DEVICE, GET_BACNET_CLIENT, GET_NETWORK_NODE, REMOVE_DEVICE } from './bacnet-constant';
import BacnetUtils from './bacnet-utils';

export class BACnetNetwork extends ContainerNode {
  bacnetClient: Bacnet = null;
  deviceNodes: Array<Node> = [];

  // node input
  inNetworkEnable: number = 0;
  inDiscover: number = 1;

  // node outputs
  outMsg: number = 0;

  constructor() {
    super();
    this.title = NODE_INFO['protocols/bacnet']['bacnet-network'].title;
    this.description = NODE_INFO['protocols/bacnet']['bacnet-network'].description;

    this.addInputWithSettings('enable', Type.BOOLEAN, false, 'Network Enable', false);
    this.addInput('discover', Type.BOOLEAN);
    this.addOutput('msg', Type.STRING);
    this.settings['interface'] = NodeSetting.create({
      description: 'interface',
      value: '0.0.0.0',
      type: SettingType.STRING,
    });
    this.settings['port'] = NodeSetting.create({
      description: 'port',
      value: 47808,
      type: SettingType.NUMBER,
    });
    this.settings['broadcastAddress'] = NodeSetting.create({
      description: 'broadcastAddress',
      value: '255.255.255.255',
      type: SettingType.STRING,
    });
    this.settings['apduTimeout'] = NodeSetting.create({
      description: 'BACnet apduTimeout',
      value: 6000,
      type: SettingType.NUMBER,
    });
    this.settings['networkNumber'] = NodeSetting.create({
      description: 'BACnet network Number (Set to -1 to disable)',
      value: -1,
      type: SettingType.NUMBER,
    });
    this.settings['networkNumberWhoIs'] = NodeSetting.create({
      description: 'BACnet Network Number for Discover',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: 'This network number' },
          { value: 1, text: 'Global (0xFFFF)' },
          { value: 2, text: 'Blank' },
        ],
      },
      value: 0,
    });
    this.settings['devicePolling'] = NodeSetting.create({
      description: 'Polling time setting in ms',
      value: 2000, // TODO add validation range later
      type: SettingType.NUMBER,
    });
  }

  onAdded() {
    super.onAdded();
    // this.name = `BACnet Network:  cid_${this.container.id.toString()}_sid_${this.sub_container.id.toString()}_id${this.id.toString()}`; //set default node name
    this.initializeBacnetClient();
  }

  onAfterSettingsChange(oldSettings: any) {
    super.onAfterSettingsChange(oldSettings);
    if (this.side !== Side.server) return;
    this.initializeBacnetClient();
  }

  onInputUpdated() {
    if (this.inputs[this.inDiscover].updated) {
      this.iAm();
      this.whoIs();
    }
    if (this.inputs[this.inNetworkEnable].updated) {
      this.initializeBacnetClient();
    }
  }

  whoIs() {
    if (this.side !== Side.server) return;
    const iface = this.settings['interface'].value;
    const networkNumber = this.settings['networkNumber'].value;
    const networkNumberWhoIs = this.settings['networkNumberWhoIs'].value;
    if (this.bacnetClient) {
      if (networkNumberWhoIs === 0) {
        this.bacnetClient.whoIs({ net: networkNumber, interface: iface });
        this.setOutputData(this.outMsg, `INFO: Send a WhoIs on net: ${networkNumber} interface: ${iface}`);
      } else if (networkNumberWhoIs === 1) {
        this.bacnetClient.whoIs({ net: 0xffff, interface: iface });
        this.setOutputData(this.outMsg, `INFO: Send a WhoIs on net: ${0xffff} interface: ${iface}`);
      } else if (networkNumberWhoIs === 2) {
        this.bacnetClient.whoIs({ interface: iface });
        this.setOutputData(this.outMsg, `INFO: Send a WhoIs on net: BLANK interface: ${iface}`);
      }
    } else this.setOutputData(this.outMsg, `ERROR: Failed to send whoIs`);
  }

  iAm() {
    if (this.side !== Side.server) return;
    this.setOutputData(this.outMsg, `INFO: On iAm replay`);
    this.bacnetClient.on('iAm', device => {
      this.setOutputData(this.outMsg, `INFO: On iAm replay from device: ${device.payload.deviceId}`);
    });
  }

  subscribe({ action, payload }) {
    switch (action) {
      case ADD_DEVICE:
        this.deviceNodes.push(payload);
        break;
      case REMOVE_DEVICE:
        this.deviceNodes = this.deviceNodes.filter(pointNode => pointNode.id !== payload.id);
        break;
      case GET_BACNET_CLIENT:
        return this.bacnetClient;
      case GET_NETWORK_NODE:
        return this;
      default:
        this.debugWarn("Request action doesn't match");
    }
  }

  onRemoved() {
    super.onRemoved();
    this.closeBacnetClient();
  }

  private closeBacnetClient() {
    if (this.bacnetClient) {
      this.setOutputData(this.outMsg, `INFO: Closing BACnet client...`);
      this.bacnetClient.close();
    }
  }

  initializeBacnetClient() {
    if (this.side !== Side.server) return; // only execute on server
    this.closeBacnetClient();
    this.setOutputData(this.outMsg, `INFO: Initializing BACnet...`);
    this.bacnetClient = new Bacnet({
      apduTimeout: this.settings['apduTimeout'].value,
      interface: this.settings['interface'].value,
      port: this.settings['port'].value,
      broadcastAddress: this.settings['broadcastAddress'].value,
    });
    if (this.bacnetClient) {
      this.setOutputData(this.outMsg, `INFO: Started BACnet Stack :)`);
    } else this.setOutputData(this.outMsg, `ERROR: Failed to started BACnet Stack :(`);

    for (let deviceNode of this.deviceNodes) {
      BacnetUtils.sendPayloadToChild(deviceNode, {
        bacnetClient: this.bacnetClient,
        networkNode: this,
      });
    }
  }
}
Container.registerNodeType(BACNET_NETWORK, BACnetNetwork);
