import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class GateNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.streams.gate.title;
    this.description = NODE_INFO.streams.gate.description;
    this.addInput('input', Type.STRING);
    this.addInput('open', Type.BOOLEAN);
    this.addOutput('output', Type.STRING);
    this.settings['send-null'] = NodeSetting.create({
      description: 'Send null when closed',
      value: false,
      type: SettingType.BOOLEAN,
    });
  }

  onInputUpdated() {
    let key = this.getInputData(1);

    if (key) this.setOutputData(0, this.getInputData(0));
    else if (this.settings['send-null'].value && this.outputs[0].data != null)
      this.setOutputData(0, null);
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('streams/gate', GateNode);
