import Utils from "../nodes/utils";
import axios from 'axios'

export default class PLAT {

  static port = 1414;
  static url = '127.0.0.1';
  static apiVer = '1.1';
  static EPWiresUpdate = 'wires/update';

  /**
   * wires plat url builder
   * @param endpoint {string} pass in the endpoint
   */
  static wiresPlatUrlBuilder(ep: string) {
    const url = `${Utils.buildUrl(PLAT.url, PLAT.port)}/api/${PLAT.apiVer}/${ep}`;
    return url;
  }

  /**
   * wires plat api call
   * @param endpoint {string} pass in the endpoint
   */
  static async get(url: string): Promise<any> {
    return await axios.get(url);
  }


}
