import { isNumeric } from '../../../../../utils/helper';
import Utils from '../../../../utils';
import ModbusByteOrder from './modbus-point-byte-order';

export default class ModbusMethods {
  static modbusMethods = async (
    thisInstance: any,
    id: number,
    type: number, // type is for example readInputRegisters
    regAddr: number,
    regLength: number,
    nodeInputValue?: number,
    pntDataType?: number, // INT16
    pntDataEndian?: number, // BE-LW
    deviceAddressOffset?: number, // this is the point base offset 1 is default
  ) => {
    // console.log(
    //   `Modbus call @  ${id}, FC: ${type}, regAddr: ${regAddr}, length: ${regLength} , writeVal: ${nodeInputValue} , pntDataType: ${pntDataType}, pntDataEndian: ${pntDataEndian}`,
    // );
    let pointAddress = regAddr - deviceAddressOffset
    if(pointAddress < 0) pointAddress = 0; //make sure address is not less then 0
    let ft = await ModbusMethods.pntSwitch(
      type,
      pointAddress,
      regLength,
      nodeInputValue,
      pntDataType,
      pntDataEndian,
    );
    // console.log(
    //   `Modbus pntSwitch FC-METHOD: ${ft.methodType}, @ ${id}, FC: ${ft.funcType}, regAddr: ${regAddr}, length: ${regLength} , writeVal: ${nodeInputValue} , pntDataType: ${pntDataType}, pntDataEndian: ${pntDataEndian}`,
    // );
    // console.log(JSON.stringify(ft))
    await thisInstance.setID(id);
    await Utils.sleep(5);
    return await new Promise((resolve, reject) => {
      // This blocks requests for Modbus functionality, if it's still unsuccessful of 1 sec, it will return as error
      thisInstance[ft.funcType](...ft.methodType).then((res: any) => {
        // read holing and or input reg
        if (type === 3 || type === 4) {
          if (![0].includes(pntDataType)) {
            const payload = ModbusByteOrder.readByteOrder(res.buffer, 0, pntDataType, pntDataEndian);
            resolve({ res: res, payload: payload });
          } else {
            resolve({ res: res, payload: res.data[0] });
          }
          // read nube BIs and BOs
        } else if (type === 1 || type === 2 || type === 21 || type === 22) {
          const payload = ModbusMethods.readBool(res);
          resolve({ res: res, payload: payload });

          // read nube AIs and AOs
        } else if (type === 23 || type === 24) {
          let nubeType = ModbusMethods.nubeAnalogueRead();
          const payload = ModbusByteOrder.readByteOrder(res.buffer, 0, nubeType[0], nubeType[1]);
          resolve({ res: res, payload: payload });
          // write response for nube DOs and AOs and writeCoil, writeCoil's, writeRegister writeRegister's
        } else if (type === 5 || type === 6 || type === 16 || type === 25 || type === 26) {
          if (res) {
            resolve({ res: res, payload: 'writeOk' });
          }
        }
      });
      setTimeout(() => {
        reject(
          `Fail to execute > id: ${id}, FC: ${ft.funcType}, regAddr: ${regAddr}, length: ${regLength} , writeVal: ${nodeInputValue}`,
        );
      }, 1000);
    });
  };

  private static readBool(res: any) {
    return res.data[0];
  }

  private static nubeAnalogueRead() {
    // this is for support of nube hardware BEB_LEW as a float
    let pntDataType = 2;
    let pntDataEndian = 2;
    return [pntDataType, pntDataEndian];
  }
  private static dataTypeCase(c: number) {
    const type16 = 1;
    const type32 = 2;
    const type64 = 4;
    switch (c) {
      case 1:
      case 2:
        return type16;
      case 3:
      case 4:
      case 5:
        return type32;
      case 8:
        return type64;
    }
  }

  private static async pntSwitch(
    type: number,
    regAddr: number,
    regLength: any,
    nodeInputValue: any,
    pntDataType?: number,
    pntDataEndian?: number,
  ) {
    let funcType = null;
    let methodType = null;
    try {
      // readCoils
      if (type === 1) {
        funcType = 'readCoils'; // (1, 4)  // read the 4 registers starting at address 1
        methodType = [regAddr, regLength];
        // read DO
      } else if (type === 21) {
        // read DO
        funcType = 'readCoils';
        methodType = [regAddr, 1];
        // readDiscreteInputs
      } else if (type === 2) {
        funcType = 'readDiscreteInputs'; // (1, 4)  // read the 4 registers starting at address 1
        methodType = [regAddr, regLength];
        // read DI
      } else if (type === 22) {
        // read D1
        funcType = 'readDiscreteInputs';
        methodType = [regAddr, 1];
        // read readHoldingRegisters
      } else if (type === 3) {
        funcType = 'readHoldingRegisters';
        if (pntDataType !== 0) {
          methodType = [regAddr, ModbusMethods.dataTypeCase(pntDataType)];
        } else methodType = [regAddr, regLength];
        // read AO
      } else if (type === 23) {
        funcType = 'readHoldingRegisters';
        methodType = [regAddr, 2];
        // read readInputRegisters
      } else if (type === 4) {
        funcType = 'readInputRegisters';
        if (pntDataType !== 0) {
          methodType = [regAddr, ModbusMethods.dataTypeCase(pntDataType)];
        } else methodType = [regAddr, regLength];
        // read AI
      } else if (type === 24) {
        funcType = 'readInputRegisters';
        methodType = [regAddr, 2];
        // writeCoil or DO
      } else if (type === 5 || type === 25) {
        funcType = 'writeCoil';
        if (nodeInputValue === 1 || nodeInputValue === 0) {
          methodType = [regAddr, nodeInputValue];
        }
        // writeRegister
      } else if (type === 6) {
        funcType = 'writeRegister'; // (5, 12) "Write 12 to reg 5"
        if (isNumeric(nodeInputValue)) {
          methodType = [regAddr, nodeInputValue];
        };
        // writeCoils
      } else if (type === 16) {
        funcType = 'writeRegisters'; //// example 1, [1, 22, 33, 444])
        if (isNumeric(nodeInputValue)) {
          methodType = [regAddr, ModbusByteOrder.writeValue(nodeInputValue, pntDataType, pntDataEndian)];
        }
      }
      return {
        funcType: funcType,
        methodType: methodType,
      };
    } catch (error) {
      console.log(`buffer readValue issue in modus-point-methods. Buffer.alloc ${error}`);
    }
  }
}
