import { Container } from '../../container';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { Type } from '../../node-io';
import { UiNode } from './ui-node';

export class UiSliderNode extends UiNode {
  constructor(container?: Container, id?: number, properties?: any) {
    super('Slider', 'UiSliderNode', properties);
    this.title = NODE_INFO.dashboard.slider.title;
    this.description = NODE_INFO.dashboard.slider.description;
    this.addInput('input', Type.NUMBER);
    this.addOutput('output', Type.NUMBER);
    this.UPDATE_INPUTS_INTERVAL = 100;
    this.setState(0);
  }

  onAdded() {
    super.onAdded();
    if (this.side == Side.server) this.setOutputData(0, this.getState());
  }

  onGetMessageToServerSide(data) {
    this.setValue(data.state);
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    this.setValue(val);
  }

  setValue(val) {
    if (val > 100) val = 100;
    if (val < 0) val = 0;

    this.setOutputData(0, val);
    this.sendIOValuesToEditor();
    this.isRecentlyActive = true;

    if (this.getState() != val)
      //prevent loop sending
      this.setState(val);
  }
}

Container.registerNodeType('dashboard/slider', UiSliderNode);
