import { PointOutput, PointValue } from '../../../../backend/models/point-model';
import { ErrorHandler } from '../../../../utils/decorators';
import { isFunction } from '../../../../utils/helper';
import { IMessageSubscriber, NodeStoreItem, Observer } from '../../../../utils/pattern';
import { Container } from '../../../container';
import { NODE_INFO } from '../../../node-info';
import { Type } from '../../../node-io';
import { GlobalNodeRegistry } from '../../../node-registry';
import { ConnectionOutput, DependantConnectionNode } from '../connection-node-mixin';
import { ContainerErrorNodeStore, OneIdentifierOneNodeStore } from '../model/container-node-store';
import { CentralizedPointOutputObserver, HasPointOutputObservers } from '../model/point-node';
import { ProtocolDeviceNode } from '../ProtocolDeviceNode';
import BACnetDevice from './core/bacnet-device';
import { BacnetPoint, BacnetPointCreator, PointKind } from './core/bacnet-model';

export const BACNET_SERVER_NODE_TYPE = 'protocols/bacnet-server/bacnet-server';
export const REGISTER_BACNET_POINT = 'REGISTER_BACNET_POINT';
export const UPDATE_BACNET_POINT = 'UPDATE_BACNET_POINT';
export const PUSH_BACNET_POINT_VALUE = 'PUSH_BACNET_POINT_VALUE';
export const UNREGISTER_BACNET_POINT = 'UNREGISTER_BACNET_POINT';

class BACNetCentralizedPointOutputObserver extends CentralizedPointOutputObserver<BacnetPoint> {

  protected convert(data: PointOutput<BacnetPoint>): object {
    let output = {};
    output[data.point.identifier()] = {
      objectId: data.point.objectInstance,
      objectType: data.point.objectType.label,
      objectName: data.point.objectName,
      pointValue: data.point.pointValue,
      status: data.connStatus.status.label,
      error: data.connStatus.error,
    };
    return output;
  }

}

class BacnetNodeServerStore extends OneIdentifierOneNodeStore<PointValue> {

  public getError(): ContainerErrorNodeStore {
    return this.errors;
  }

  public getNodes(): { [identifier: string]: { identifier: string, nodeId: string; enabled: boolean; func: Function } } {
    return this.nodes;
  }

  protected alreadyExistedMsg(payload: NodeStoreItem): string {
    return `Already exist object with ${payload.data?.objectInstance} and type ${payload.data?.objectType}`;
  }
}

class BACnetServerNode extends ProtocolDeviceNode implements IMessageSubscriber, HasPointOutputObservers<BacnetPoint> {

  private readonly _oPointsSlot = 2;
  private device: BACnetDevice = null;
  private store: BacnetNodeServerStore = new BacnetNodeServerStore();

  constructor() {
    super(NODE_INFO['protocols/bacnet-server']['bacnet-server'].title,
          NODE_INFO['protocols/bacnet-server']['bacnet-server'].description);

    this.mixinEnableInputSetting();
    this.addInputWithSettings('address', Type.STRING, '0.0.0.0', 'BACnet Server Address');
    this.addInputWithSettings('broadcast', Type.STRING, '255.255.255.255', 'Broadcast Address');
    this.addInputWithSettings('port', Type.NUMBER, 47808, 'BACnet port');
    this.addInputWithSettings('id', Type.NUMBER, 1, 'BACnet Device Id');
    this.addInputWithSettings('name', Type.STRING, 'nube-bacnet', 'BACnet Device Name');
    this.addInputWithSettings('apdu', Type.NUMBER, 1476, 'BACnet APDU');

    this.addOutput('points', Type.JSON);
  }

  listen({ action, payload }: { action: string; payload: NodeStoreItem }) {
    this.debugInfo(`Handling ${action}:${payload.identifier}...`);
    switch (action) {
      case REGISTER_BACNET_POINT:
        return this.store.register(payload, p => this.afterRegister(p));
      case UPDATE_BACNET_POINT:
        return this.store.update(payload, p => this.afterUpdate(p), (p, force) => this.afterUnregister(p, force));
      case PUSH_BACNET_POINT_VALUE:
        return this.pushValue(payload);
      case UNREGISTER_BACNET_POINT:
        return this.store.unregister(payload, true, p => this.afterUnregister(p, true));
      default:
        this.debugWarn(`Request action doesn't match`);
    }
  }

  connObserverNodes(): Observer<ConnectionOutput>[] {
    return this.store.listNodeIds()
               .map(id => GlobalNodeRegistry.instance().get(id))
               .filter(n => n)
               .map(n => (<DependantConnectionNode><unknown>n).statusObserver());
  }

  pointObservers(): Observer<PointOutput<BacnetPoint>>[] {
    return [new BACNetCentralizedPointOutputObserver(this, this._oPointsSlot)];
  }

  isConnConnected(): boolean {
    return this.device !== null;
  }

  @ErrorHandler
  onReceiveMessage(object: object): void {
    if (!this.getConnectionStatus().isConnected()) {
      return;
    }
    let kind = PointKind.lookupByValue(object['objectType']);
    let bp = BacnetPointCreator.create(this.isEnabled(), object['objectId'], kind, null,
                                       object['presentValue'], object['priority'], object['priorityArray']);
    let pointNode = this.store.lookup(bp.identifier());
    if (pointNode && GlobalNodeRegistry.instance().has(pointNode.nodeId) && isFunction(pointNode.func)) {
      pointNode.func(bp);
    } else {
      this.debug(`Receive message to BACnet Point ${bp.identifier()} but not found node`);
      //TODO: how to add new node from server
    }
  }

  protected applyTitle() {
    super.applyTitle();
    this.broadcastSettingsToClients();
    this.broadcastNameToClients();
  }

  protected createThenStart() {
    this.device = new BACnetDevice({
                                     deviceId: this.settings['id'].value,
                                     name: this.settings['name'].value,
                                     databaseRevision: 1,
                                   }, {
                                     port: this.settings['port'].value,
                                     address: this.settings['address'].value,
                                     broadcast: this.settings['broadcast'].value,
                                     apduTimeout: this.settings['apdu'].value,
                                   }, data => this.onReceiveMessage(data));
    this.device.start();
  }

  protected stop() {
    if (!this.device) {
      return;
    }
    try {
      this.device.stop();
      this.device = null;
    } catch (e) {
      this.debugWarn(`Cannot closed BACnet client ${e}`);
    }
  }

  private afterRegister(payload: NodeStoreItem) {
    if (payload.enabled && this.getConnectionStatus().isConnected()) {
      this.device.addPoint(payload.data);
      return payload.data;
    }
    return null;
  }

  private afterUpdate(payload: NodeStoreItem) {
    if (payload.enabled && this.getConnectionStatus().isConnected()) {
      return this.device.updatePoint(payload.data);
    }
    return null;
  }

  private pushValue(payload: NodeStoreItem) {
    if (payload.enabled && this.getConnectionStatus().isConnected()) {
      return this.device.updateValue(payload.data);
    }
    return null;
  }

  private afterUnregister(payload: NodeStoreItem, force?: boolean) {
    if (payload.data?.objectInstance && this.getConnectionStatus().isConnected()) {
      this.device.delObject(payload.data?.objectInstance, payload.data?.objectType?.value);
    }
    if (force) {
      let output = this.outputs[this._oPointsSlot].data;
      if (output) {
        delete output[payload.identifier];
      }
      this.setOutputData(this._oPointsSlot, output);
    }
  }

}

Container.registerNodeType(BACNET_SERVER_NODE_TYPE, BACnetServerNode);
