import {
  DEVICE_CONTAINER_TYPE,
  EDGE_CONTAINER_TYPE,
  GROUP_CONTAINER_TYPE,
  MODULE_NODE_TYPE,
  NETWORK_CONTAINER_TYPE,
  POINT_NODE_TYPE,
} from '../nodes/constants';

export interface Entry {
  entryId(): string;

  identifier(): string;

  nodeType(): string;

  label(): string;
}

abstract class NormalEntry implements Entry {
  id: string;
  code: string;
  readonly _nodeType: string;

  entryId(): string {
    return this.id;
  }

  identifier(): string {
    return this.code;
  }

  nodeType(): string {
    return this._nodeType;
  }

  label(): string {
    return this.identifier() || this.entryId();
  }
}

export class Edge extends NormalEntry {
  customer_code: string;
  side_code: string;
  model: string;
  firmware_version: string;
  data_version: string;
  readonly _nodeType = EDGE_CONTAINER_TYPE;
}

export class Network extends NormalEntry {
  protocol: string;
  state: string;
  readonly _nodeType = NETWORK_CONTAINER_TYPE;
}

export class Device extends NormalEntry {
  manufacturer: string;
  protocol: string;
  state: string;
  type: string;
  readonly _nodeType = DEVICE_CONTAINER_TYPE;
}

export class Folder extends NormalEntry {
  name: string;
  readonly _nodeType = GROUP_CONTAINER_TYPE;

  label(): string {
    return this.name || super.label();
  }
}

export class Point extends NormalEntry {
  edge: string;
  enabled: boolean;
  kind: string;
  measure_unit: string;
  network: string;
  offset: number;
  precision: number;
  protocol: string;
  type: string;
  readonly _nodeType = POINT_NODE_TYPE;
}

export class Service implements Entry {
  service_id: string;
  service_name: string;
  service_type: string;
  version: string;
  published_by: string;
  state: string;
  readonly created_at: string;
  readonly modified_at: string;
  readonly deploy_id: string;
  readonly deploy_location: string;
  readonly _nodeType = MODULE_NODE_TYPE;

  entryId(): string {
    return this.service_id;
  }

  identifier(): string {
    return this.service_name;
  }

  nodeType(): string {
    return this._nodeType;
  }

  label(): string {
    return `${this.identifier()} ${this.version}` || this.entryId();
  }
}

export class PointData {
  priority: number;
  value: number;
  point: string;
  priority_values: number[];
}
