import { Container, ContainerRegistry } from '../../container';
import { Node } from '../../node';
import Icons from '../../node-icons';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';

const icon = Icons.aiIcon;
export class ConnectionLocalTransmitterNode extends Node {
  constructor() {
    super();

    this.title = NODE_INFO.connection["link-transmitter-single"].title;
    this.description = NODE_INFO.connection["link-transmitter-single"].description;

    this.iconImageUrl = icon;
    this.addInput('input', Type.ANY);
    this.addInputWithSettings('topic', Type.STRING, '1', 'Topic Name', false);
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.settings['ifNew'] = NodeSetting.create({
      description: 'Only transmit on COV',
      value: false,
      type: SettingType.BOOLEAN,
    });
  }

  init() {
    if (!this.properties['topic']) {
      // we are persisting topic input just because, we don't have getInputData(1) on frontend to update title
      this.properties['topic'] = null;
    }
  }

  onAdded() {
    this.onAfterSettingsChange();
    this.updateTitle();
  }

  onInputUpdated() {
    if (this.side !== Side.server) return;

    this.properties['topic'] = this.getInputData(1);
    this.persistProperties(false, true);

    const receivers = this.getReceivers();
    if (this.inputs[1].updated) {
      this.updateTransmitterTopics();
    }
    if (!this.isEnable()) return;
    const val = this.getInputData(0);
    const topic = this.getInputData(1);
    const ifNew = this.settings['ifNew'].value
    receivers.forEach(receiver => {
      if (receiver.properties['topic'] === topic) {
        const blockNull = receiver.settings['blockNull'].value;
        if (receiver.getInputData(0) === true && ((blockNull && val != null) || !blockNull)) {
          receiver.properties['val'] = val;
          receiver.persistProperties(false, true);
          receiver.setOutputData(0, receiver.properties['val'], ifNew);
        }
      }
    });
    this.updateTitle();
  }

  onAfterSettingsChange() {
    this.updateTransmitterTopics();
    this.onInputUpdated();
  }

  onRemoved() {
    this.updateTransmitterTopics();
  }

  updateTransmitterTopics() {
    if (this.side !== Side.server) return;
    setTimeout(() => {
      const receivers = this.getReceivers();
      receivers.forEach(receiver => {
        receiver['triggerTransmitterTopicUpdate']();
      });
    }, 100); // waiting for this node deletion and hence, receiver will not discover this node
  }

  getReceivers(): Array<Node> {
    return ContainerRegistry.instance().rootContainer().getNodesByType('connection/link-receiver-single', true);
  }

  isEnable() {
    return this.getInputData(2);
  }

  updateTitle() {
    this.title = `Link Transmitter Single [${this.properties['topic'] || this.getInputData(1)}]`;
    this.broadcastTitleToClients();
  }
}

Container.registerNodeType('connection/link-transmitter-single', ConnectionLocalTransmitterNode);
