import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import { NODE_INFO } from '../../node-info';

const psychrolib = require('../../lib/psychrolib');

class AtmosphericPressureNode extends Node {
  psychro = new psychrolib();

  constructor() {
    super();
   
    this.title = NODE_INFO.hvac["atmospheric-pressure"].title;
    this.description = NODE_INFO.hvac["atmospheric-pressure"].description;

    this.addInput('altitude', Type.NUMBER);
    this.addOutput('pressure', Type.NUMBER);
    this.settings['units'] = NodeSetting.create({
      description: 'Select Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'SI', text: 'Metric/SI (Meters-> Pa)' },
          { value: 'IP', text: 'Imperial/IP (Feet-> Psi)' },
        ],
      },
      value: 'SI',
    });
    this.settings['precision'] = NodeSetting.create({ description: 'Precision', type: SettingType.NUMBER, value: 2 });
    this.psychro.SetUnitSystem(this.psychro.SI);
  }

  onAdded() {
    this.updateTitleAndFunction();
    this.onInputUpdated();
  }

  onInputUpdated() {
    this.onInputOrSettingsChange();
  }

  onAfterSettingsChange() {
    this.updateTitleAndFunction();
    this.onInputOrSettingsChange();
  }

  private updateTitleAndFunction() {
    const units = this.settings['units'].value;
    if (units == 'IP') {
      this.title = 'Atmospheric Pressure (Feet-> Psi)';
      this.psychro.SetUnitSystem(this.psychro.IP);
    } else {
      this.title = 'Atmospheric Pressure (Meters-> Pa)';
      this.psychro.SetUnitSystem(this.psychro.SI);
    }
  }

  private onInputOrSettingsChange() {
    const altitude = this.getInputData(0);
    if (!Utils.hasInput(altitude)) {
      this.setOutputData(0, null);
      return;
    }
    const pressure = this.psychro.GetStandardAtmPressure(altitude);
    const precision = Math.abs(this.settings['precision'].value);
    this.setOutputData(0, Number(pressure).toFixed(precision));
  }
}
Container.registerNodeType('hvac/atmospheric-pressure', AtmosphericPressureNode);
