import { isNull } from '../../../utils/helper';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class EventCounterNode extends Node {
  constructor() {
    super();

    this.title = NODE_INFO.count["any-event-counter"].title;
    this.description = NODE_INFO.count["any-event-counter"].description;

    this.addInput('input');
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('count', Type.NUMBER);
    this.addOutput('toggle', Type.NUMBER);
    this.settings['null'] = NodeSetting.create({
      description: 'Stop count if input is null',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.properties['count'] = 0;
  }

  onAdded(){
    this.setOutputData(0, 0);
    this.setOutputData(1, false);
    this.onInputUpdated();
  }

  onInputUpdated() {
    const inputUpdated = this.inputs[0].updated;
    const inputData = this.inputs[0].data;
    const reset = this.inputs[1].data
    if (reset && reset == true) this.properties['count'] = 0; //reset count
    if (this.settings['null'].value && this.isNull(inputData)) return
     if (inputUpdated) {
        this.properties['count']++;
        this.setOutputData(1, !this.outputs[1].data); // toggle
      }
    if (this.properties['count'] !== inputData) {
      this.setOutputData(0, this.properties['count']);
      this.persistProperties(false, true);
    }
  }

  isNull(message: any) {
    return isNull(message)
  }
}
Container.registerNodeType('count/any-event-counter', EventCounterNode);
