import PlatformDataSource from '../../../database/datasource/PlatformDataSource';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import OsUtils from '../../utils/os-utils';

class PlatNode extends Node {
  haystackAbout = {
    serverName: OsUtils.hostName(),
    productVersion: OsUtils.getCurrentWiresVersion(),
  };
  constructor() {
    super();
    this.title = NODE_INFO.system.platform.title;
    this.description = NODE_INFO.system.platform.description;
    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('wires-version', Type.STRING);
    this.addOutput('output-json', Type.JSON);
    this.settings['serverName'] = NodeSetting.create({
      description: 'serverName',
      value: '',
      type: SettingType.READONLY,
    });
    this.settings['productVersion'] = NodeSetting.create({
      description: 'productVersion',
      value: '',
      type: SettingType.READONLY,
    });


  }

  async onAdded() {
    await this.haystackAbout.serverName.then(e => {
      this.settings['serverName'].value = e;
      this.broadcastSettingsToClients();
    }).catch(err => {
      this.debugInfo(`PLATFORM NODE: try/catch  serverName info, ${err}`);
    })
    await this.haystackAbout.productVersion.then(e => {
      this.settings['productVersion'].value = e;
      this.broadcastSettingsToClients();
      this.setOutputData(0, e); // wires version
    }).catch(err => {
      this.debugInfo(`PLATFORM NODE: try/catch  productVersion info, ${err}`);
    })

    this.onAfterSettingsChange();
  }

  onInputUpdated() {
    this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    if (this.side !== Side.server) return; // only execute on server
    PlatformDataSource.getPlatform().then(e => {
      this.setOutputData(1, e)
    }).catch(err => {
      this.debugInfo(`PLATFORM NODE: try/catch  getPlatform info, ${err}`);
    })

  }

}
Container.registerNodeType('system/platform', PlatNode);
