import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

export class BoolOutputSelectNode extends FlexibleNode {
  constructor() {
    super();
    this.dynamicOutputsExist = true;
    this.dynamicInputsExist = false;
    this.dynamicSettingsExist = false;
    super.dynamicOutputsType = Type.BOOLEAN;
    this.title = NODE_INFO.switch["bool-output-select"].title;
    this.description = NODE_INFO.switch["bool-output-select"].description;
    this.addInput('select', Type.NUMBER);
    this.addInput('input', Type.BOOLEAN);
  }

  onInputUpdated() {
    let active = this.getInputData(0);
    let val = this.getInputData(1);

    if (active < 0 || active >= this.getOutputsCount()) {
      this.debugWarn('Defined active output does not exist');
      return;
    }

    this.setOutputData(active, val);
  }
}
Container.registerNodeType('switch/bool-output-select', BoolOutputSelectNode);
