import { Colors, create } from 'logplease';
import { Database } from '../database/database';
import {
  EDGES_PARENT_CONTAINER_TYPE,
  MODULES_PARENT_CONTAINER_TYPE,
  SERVICES_PARENT_CONTAINER_TYPE,
} from '../nodes/constants';
import { Container, ContainerRegistry } from '../nodes/container';
import { Node } from '../nodes/node';
import { NodeFactory } from '../nodes/node-factory';
import { SerializedNode } from '../nodes/node-interface';
import { EdgesParentContainerNode } from '../nodes/nodes/IoT/datapoint-node';
import { ModulesParentContainerNode, ServicesParentContainerNode } from '../nodes/nodes/IoT/installer-node';

const logger = create('backend', { color: Colors.Yellow });

export interface BackendSyncConfiguration {
  enabled: boolean,
  baseUrl: string,
  nodeRefreshIntervalSeconds: number,
  pointRefreshIntervalSeconds: number,
  nodeDistance: number
}

export class Backend {
  database: Database;
  backendSyncCfg: BackendSyncConfiguration;
  edgesContainer: EdgesParentContainerNode;
  modulesContainer: ModulesParentContainerNode;
  servicesContainer: ServicesParentContainerNode;

  constructor(database: Database, backendSyncCfg: BackendSyncConfiguration) {
    this.database = database;
    this.backendSyncCfg = backendSyncCfg;
  }

  start(): void {
    if (!this.backendSyncCfg.enabled) {
      return;
    }
    this.ensureParentContainers().then(() => this.refreshPeriodically());
  }

  private ensureParentContainers(): Promise<any> {
    return this.findOrCreateNode(EDGES_PARENT_CONTAINER_TYPE, node => node instanceof EdgesParentContainerNode)
               .then(edgesContainer => (this.edgesContainer = edgesContainer as EdgesParentContainerNode))
               .then(() => this.findOrCreateNode(MODULES_PARENT_CONTAINER_TYPE,
                                                 node => node instanceof ModulesParentContainerNode))
               .then(modulesContainer => (this.modulesContainer = modulesContainer as ModulesParentContainerNode))
               .then(() => this.findOrCreateNode(SERVICES_PARENT_CONTAINER_TYPE,
                                                 node => node instanceof ServicesParentContainerNode))
               .then(servicesContainer => (this.servicesContainer = servicesContainer as ServicesParentContainerNode));
  }

  private findOrCreateNode<N extends Node>(type_name: string, f: (Node) => boolean): Promise<N> {
    const root: Container = ContainerRegistry.instance().rootContainer();
    return new Promise((resolve, reject) => {
      const existingNode = root.getNodes().find(f);
      if (existingNode) {
        resolve(<N>existingNode);
      } else {
        return root.nodeRegistry().register(NodeFactory.create(<SerializedNode>{ type: type_name }, true));
      }
    });
  }

  private refreshPeriodically() {
    setInterval(() => this.refreshParentContainers(), this.backendSyncCfg.nodeRefreshIntervalSeconds * 1000);
  }

  private refreshParentContainers() {
    this.edgesContainer.syncWithBackend();
    this.modulesContainer.syncWithBackend();
    this.servicesContainer.syncWithBackend();
  }
}
