import * as fs from 'fs';
import config from '../../config';
import FileUtils from '../../nodes/utils/file-utils';
import { db_logger as logger } from '../../utils/loggers';

const file = `${config.dataDir}/help-comment.txt`;
export default class HelpDataSource {

  static getHelpComment(): Promise<string> {
    return new Promise((resolve, reject) => {
      FileUtils.createFile(file).then(() => {
        fs.readFile(file, { encoding: 'utf-8' }, (err, data) => {
          if (err) {
            logger.error(err.message);
            reject(err);
          } else {
            resolve(data);
          }
        });
      });
    });
  }

  static changeHelpComment(stringHtml: string): Promise<string> {
    return new Promise((resolve, reject) => {
      FileUtils.createFile(file).then(() => {
        fs.writeFile(file, stringHtml, err => {
          if (err) {
            logger.error(err.message);
            reject(err);
          } else {
            resolve(stringHtml);
          }
        });
      });
    });
  }

}

