import { Api } from '../../../../backend/api';
import { Entry } from '../../../../backend/entry';
import { Node } from '../../../node';
import { NodeSetting, SettingType } from '../../../node-io';
import EntryNode from './entry-node';
import Mixin from './mixin';

/**
 * TODO should rework later
 */
export default abstract class ApiNode<T extends Entry, A extends Api<T>> extends Node implements EntryNode<T> {
  properties: {
    entry: T; // main entry
    is_protected: boolean; // If true the node won't be deleted if the remote counter-part is missing
  };
  settingFields: {};
  clonable = false;
  removable = false;
  movable = false;
  entry = Mixin.prototype.entry.bind(this);
  entryId = Mixin.prototype.entryId.bind(this);
  deactivate = Mixin.prototype.deactivate.bind(this);
  createSettingForm = Mixin.prototype.createSettingForm.bind(this);
  getTakenIdentifiers = Mixin.prototype.getTakenIdentifiers.bind(this);
  makeIdentifier = Mixin.prototype.makeIdentifier.bind(this);
  findFreeIdentifier = Mixin.prototype.findFreeIdentifier.bind(this);
  backend: A;

  protected constructor() {
    super();
    this.description = 'placeholder'; // FIXME
    this.backend = this.initAPI();
    this.settings['name'] = NodeSetting.create({
      description: 'Entry',
      value      : this.title,
      type       : SettingType.READONLY,
    });
  }

  entryFactory = (): T => this.backend.factoryEntry();

  updateEntry = (body: T): Promise<any> => this.backend.update(body, this.entryId());

  deleteEntry = (): Promise<any> => this.backend.delete(this.entryId());

  abstract namePrefix(): string;

  abstract createEntry(): Promise<T>;

  abstract applyEntry(entry: T): void;

  abstract syncWithBackend(): void;

  protected abstract initAPI(): A;
}
