import { Container } from '../../container';
import { Node } from '../../node';
import Icons from '../../node-icons';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

let uiMainIcon = Icons.uiMainIcon;
let uiMainColour = Icons.uiMainColour;

class MathAbsNode extends Node {

  constructor() {
    super();
    this.title = NODE_INFO.math.absolute.title;
    this.description = NODE_INFO.math.absolute.description;
    //this.headerColor = uiMainIcon;
    //this.iconImageUrl = uiMainIcon;

    this.addInput('input', Type.NUMBER);
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    if (val != null) this.setOutputData(0, Math.abs(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math/absolute', MathAbsNode);
