import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class TextStringLengthNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.string["string-length"].title;
    this.description = NODE_INFO.string["string-length"].description;
    this.addInput('input', Type.STRING);
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    if (val != null) this.setOutputData(0, val.length);
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('string/string-length', TextStringLengthNode);
