import axios from 'axios';
import { Colors, create } from 'logplease';
import Utils from '../../../utils';
import { ADD_POINT, REMOVE_POINT, SEND_PAYLOAD_TO_CHILD } from './edge-constant';

const logger = create('edge-utils', { color: Colors.Grey });

export default class Edge28Utils {
  static sendPayloadToChild(childNode, payload) {
    if (childNode && childNode['subscribe']) {
      childNode['subscribe'](this.createMessage(SEND_PAYLOAD_TO_CHILD, payload));
    } else {
      logger.error('Child Node is not available on sendPayloadToChild');
    }
  }

  static addPoint(parentNode, payload) {
    if (parentNode && parentNode['subscribe']) {
      parentNode['subscribe'](this.createMessage(ADD_POINT, payload));
    } else {
      logger.error('Parent Node is not available on addPoint');
    }
  }

  static removePoint(parentNode, payload) {
    if (parentNode && parentNode['subscribe']) {
      parentNode['subscribe'](this.createMessage(REMOVE_POINT, payload));
    } else {
      logger.error('Parent Node is not available on removePoint');
    }
  }

  private static createMessage(action, payload = null) {
    return { action, payload };
  }

  static async writePointValue(
    host: string,
    port: number,
    apiVer: string,
    pointType: string,
    pointId: string,
    val: number,
    priority: number,
  ): Promise<any> {
    const url = `${Utils.buildUrl(host, port)}/api/${apiVer}/write/${pointType}/${pointId}/${val}/${priority}`;
    const pointValue = await axios.get(url);
    return pointValue.data;
  }
}
