import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';
import Statistics from './statistics';
import { NODE_INFO } from '../../node-info';

class MinMaxAvgNode extends Statistics {
  constructor() {
    super();
    this.title = NODE_INFO.statistics["min-max-avg"].title;
    this.description = NODE_INFO.statistics["min-max-avg"].description;
    this.addOutput('Min', Type.NUMBER);
    this.addOutput('Max', Type.NUMBER);
    this.addOutput('Avg', Type.NUMBER);
  }

  onInputUpdated() {
    const data = this.getDefinedInputsOrSettingsValues();
    const min = Math.min.apply(Math, data);
    const max = Math.max.apply(Math, data);
    const count = data.length;
    const sum = Utils.sum(data);
    const avg = sum / count;

    this.setOutputData(0, min);
    this.setOutputData(1, max);
    this.setOutputData(2, avg);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}

Container.registerNodeType('statistics/min-max-avg', MinMaxAvgNode);
