import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class CounterNode extends Node {
  lastUp:boolean;
  lastDown:boolean;
  lastReset:boolean;


  constructor() {
    super();

    this.title = NODE_INFO.count.counter.title;
    this.description = NODE_INFO.count.counter.description;

    this.addInputWithSettings('set value', Type.NUMBER, 0, 'Set Value');
    this.addInput('count up', Type.BOOLEAN);
    this.addInput('count down', Type.BOOLEAN);
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('count', Type.NUMBER);
    this.properties['pointVal'] = 0;
  }

  onCreated() {
  }

  onAdded() {
    this.setOutputData(1, false);
    this.setOutputData(2, false);
    this.lastUp = false;
    this.lastDown = false;
    this.lastReset = false;
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const old = this.outputs[0].data;
    const up = this.getInputData(1) || false;
    const down = this.getInputData(2) || false;
    const reset = this.getInputData(3) || false;

    if (this.inputs[1].updated && up && !this.lastUp) this.properties['pointVal']++;
    this.lastUp = up;

    if (this.inputs[2].updated && down && !this.lastDown) this.properties['pointVal']--;
    this.lastDown = down;

    const setValue = this.getInputData(0);

    if (this.inputs[3].updated && reset && !this.lastReset) this.properties['pointVal'] = setValue;
    this.lastReset = reset;

    if (this.properties['pointVal'] !== old) {
      this.setOutputData(0, this.properties['pointVal']);
      this.properties['pointVal'] = this.properties['pointVal'];
      this.persistProperties(false, true);
    }

    this.setOutputData(0, this.outputs[0].data);
  }
}
Container.registerNodeType('count/counter', CounterNode);
