import { Container } from '../../container';
import { Node} from '../../node';
import { Type } from '../../node-io';
import {isNull } from '../../../utils/helper';
const geohash = require('ngeohash');
import { NODE_INFO } from '../../node-info';

class GeoHashNode extends Node {
  client: any;
  outputsCount = 0;
  constructor() {
    super();
    this.title = NODE_INFO.miscellaneous["geo-hash"].title;
    this.description = NODE_INFO.miscellaneous["geo-hash"].description; 
    this.addInputWithSettings('encode', Type.JSON, '151.1677778, -34.0316667', 'Lon at Lat Values Encode');
    this.addInputWithSettings('decode', Type.STRING, 'ww8p1r4t8, ', 'Hash Decode');
    this.addOutput('encode-out', Type.STRING);
    this.addOutput('decode-out', Type.JSON);
  }
  


  onAdded() {
    this.onInputUpdated()
  }

  onInputUpdated(){
    const encode = this.getInputData(0);
    const decode = this.getInputData(1);

    if (!isNull(encode)) {
      this.setOutputData(0, geohash.encode(parseInt(encode[0]), parseInt(encode[1])))
   
    }
    if (!isNull(decode)) {
      this.setOutputData(1, geohash.decode(decode))
  
    }

  }

  onAfterSettingsChange() {
    this.onInputUpdated()
  }



}
Container.registerNodeType('miscellaneous/geo-hash', GeoHashNode);
