import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class BetweenNode extends Node {

  constructor() {
    super();

    this.title = NODE_INFO.compare.between.title;
    this.description = NODE_INFO.compare.between.description;

    this.addInput('input', Type.NUMBER);
    this.addInputWithSettings('from', Type.NUMBER, 0, 'From Value', false);
    this.addInputWithSettings('to', Type.NUMBER, 1, 'To Value', false);
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('out not', Type.BOOLEAN);
  }

  onAdded() {
    this.setOutputData(0, false);
    this.setOutputData(1, true);
    this.onInputUpdated();
  }


  onInputUpdated() {
    let val = this.getInputData(0);
    let from = this.getInputData(1);
    let to = this.getInputData(2);

    if (val != null && from != null && to != null) {
      //if (val >= from && val <= to) {
      if (val >= Math.min(from, to) && val <= Math.max(from, to)) {
        this.setOutputData(0, true);
        this.setOutputData(1, false);
      } else {
        this.setOutputData(0, false);
        this.setOutputData(1, true);
      }
    } else {
      this.setOutputData(0, false);
      this.setOutputData(1, true);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('compare/between', BetweenNode);
