import * as moment from 'moment-timezone';
import Utils from '../../utils';

export class ScheduleUtils {
  /**
   * @param eventSchedule
   * @param dt: {moment} datetime which is to be compared
   */
  static checkEventsSchedule(eventSchedule, dt: moment) {
    if (eventSchedule && Object.prototype.hasOwnProperty.call(eventSchedule, 'dates')) {
      let eventOutput = false;
      let nextOn = null;
      let nextOff = null;
      let scheduleValue = null;
      for (let i = 0; i < eventSchedule.dates.length; i++) {
        const dtUTC = dt.clone().tz('Etc/UTC'); // remove this line and replace dtUTC with dt once testing is complete
        const startMoment = moment.tz(eventSchedule.dates[i].start, 'Etc/UTC');
        const endMoment = moment.tz(eventSchedule.dates[i].end, 'Etc/UTC');
        //console.log('dtUTC', dtUTC.format('YYYY-MM-DDTHH:MM:ss'));
        //console.log('startMoment', startMoment.format('YYYY-MM-DDTHH:MM:ss'));
        //console.log('endMoment', endMoment.format('YYYY-MM-DDTHH:MM:ss'));
        if (dtUTC.isAfter(startMoment) && dtUTC.isBefore(endMoment)) {
          //return [true, null, endMoment];
          eventOutput = true;
          if (eventSchedule && Object.prototype.hasOwnProperty.call(eventSchedule, 'value'))
            scheduleValue = eventSchedule.value;
          if (nextOff == null || endMoment.isBefore(nextOff)) {
            nextOff = endMoment;
          }
        } else if (dtUTC.isBefore(startMoment) && dtUTC.isBefore(endMoment)) {
          //return [false, startMoment, endMoment];
          if (nextOn == null || startMoment.isBefore(nextOn)) {
            nextOn = startMoment;
          }
          if (nextOff == null || endMoment.isBefore(nextOff)) {
            nextOff = endMoment;
          }
        }
      }
      //return [eventOutput, nextOn, nextOff];
      return {
        scheduleStatus: eventOutput,
        nextStart: nextOn,
        nextStop: nextOff,
        value: scheduleValue,
      };
    }
    //return [false, null, null];
    return { scheduleStatus: false, nextStart: null, nextStop: null, value: null };
  }

  /**
   * @param weeklySchedule
   * @param dt: {moment} datetime which is to be compared
   */
  static checkWeeklySchedule(weeklySchedule, dt: moment) {
    let weeklyOutput = false;
    let nextOn = null;
    let nextOff = null;
    let scheduleValue = null;

    if (weeklySchedule) {
      //console.log(dt)
      //console.log('dt', dt.format());
      const dtUTC = dt.clone().tz('Etc/UTC'); // remove this line and replace dtUTC with dt once testing is complete
      //console.log("dtUTC", dtUTC.format());
      //console.log("dtUTCday", dtUTC.isoWeekday());
      //console.log("weeklySchedule.days", weeklySchedule.days);
      let daysArray = weeklySchedule.days.map(day => {
        return Utils.getISOday(day);
      });
      //console.log("daysArray", daysArray);

      let startArray = weeklySchedule.start.split(':').map(part => {
        return Number(part);
      }); // make the start string into an array of numbers [hour,min]
      let endArray = weeklySchedule.end.split(':').map(part => {
        return Number(part);
      }); // make the end string into an array of numbers [hour,min]

      let rolloverFlag = 0;
      if (startArray[0] > endArray[0]) {
        // schedule period runs into next day on UTC
        rolloverFlag = 1;
      }
      //console.log('rolloverFlag', rolloverFlag)

      const date = dtUTC.format('YYYY-MM-DD'); //current date in local (tz) time.
      //console.log('date', date);

      let startMomentArray = daysArray.map(day => {
        return moment.tz(`${date}T${weeklySchedule.start}:00.000Z`, 'Etc/UTC').isoWeekday(day);
      });
      //console.log("startMomentArray", startMomentArray.map((day)=>{return day.format()}));

      let endMomentArray = daysArray.map(day => {
        return moment
          .tz(`${date}T${weeklySchedule.end}:00.000Z`, 'Etc/UTC')
          .isoWeekday(day + rolloverFlag);
      });
      //console.log("endMomentArray", endMomentArray.map((day)=>{return day.format()}));

      // check if any of the end moments are more than a week past current moment.
      // This is for the edge case where there is a sunday that rolls over into a monday.
      //if (rolloverFlag && endMomentArray.some((end)=>{return dtUTC.add(1, 'week').isBefore(end)})){
      if (rolloverFlag) {
        for (let i = 0; i < endMomentArray.length; i++) {
          if (
            dtUTC
              .clone()
              .add(1, 'weeks')
              .isBefore(endMomentArray[i])
          ) {
            //endMomentArray[i] = endMomentArray[i].subtract(1, 'weeks');
            endMomentArray[i].subtract(1, 'weeks');
            //startMomentArray[i] = startMomentArray[i].subtract(1, 'weeks');
            startMomentArray[i].subtract(1, 'weeks');
            //console.log("Sunday->Monday Rollover Edge Case")
            //console.log("startMomentArray", startMomentArray.map((day)=>{return day.format()}));
            //console.log("endMomentArray", endMomentArray.map((day)=>{return day.format()}));
            break;
          }
        }
      }

      // check if all of the start and end moments are before the current moment
      if (
        startMomentArray.every(start => {
          return start.isBefore(dtUTC);
        }) &&
        endMomentArray.every(end => {
          return end.isBefore(dtUTC);
        })
      ) {
        // if so then add a week to all start and end moments
        //startMomentArray = startMomentArray.map((start)=>{return start.add(1, 'weeks')});
        startMomentArray.map(start => {
          return start.add(1, 'weeks');
        });
        //endMomentArray = endMomentArray.map((end)=>{return end.add(1, 'weeks')});
        endMomentArray.map(end => {
          return end.add(1, 'weeks');
        });
        //console.log("Added a week to all START and END moments")
        //console.log("startMomentArray", startMomentArray.map((day)=>{return day.format()}));
        //console.log("endMomentArray", endMomentArray.map((day)=>{return day.format()}));
      }
      // check if all of the start moments are before the current moment.
      // This means that the last schedule period is active. There will only be one End time after the last start time.
      else if (
        startMomentArray.every(start => {
          return start.isBefore(dtUTC);
        })
      ) {
        for (let j = 0; j < endMomentArray.length; j++) {
          if (endMomentArray[j].isBefore(dtUTC)) {
            endMomentArray[j].add(1, 'weeks');
            startMomentArray[j].add(1, 'weeks');
          }
        }
      }

      // search the Start and End moments for active schedules, and get nextOn and nextOff times.
      for (let x = 0; x < startMomentArray.length; x++) {
        //console.log("dtUTC", dtUTC.format())
        //console.log("startMomentArray[x]", startMomentArray[x].format())
        //console.log("endMomentArray[x]", endMomentArray[x].format())
        if (startMomentArray[x].isBefore(dtUTC) && endMomentArray[x].isAfter(dtUTC)) {
          weeklyOutput = true;
          if (weeklySchedule && Object.prototype.hasOwnProperty.call(weeklySchedule, 'value'))
            scheduleValue = weeklySchedule.value;
          nextOff = endMomentArray[x];
          nextOn = startMomentArray[x].add(1, 'weeks');
        } else if (
          startMomentArray[x].isAfter(dtUTC) &&
          (nextOn == null || startMomentArray[x].isBefore(nextOn))
        ) {
          nextOn = startMomentArray[x];
        }
        if (
          endMomentArray[x].isAfter(dtUTC) &&
          (nextOff == null || endMomentArray[x].isBefore(nextOff))
        ) {
          nextOff = endMomentArray[x];
        }
      }
      //console.log("weeklyOutput", weeklyOutput)
      //console.log("nextOn", nextOn.format())
      //console.log("nextOff", nextOff.format())
    }
    //return [weeklyOutput, nextOn, nextOff];
    return {
      scheduleStatus: weeklyOutput,
      nextStart: nextOn,
      nextStop: nextOff,
      value: scheduleValue,
    };
  }
}

