import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class StopwatchNode extends Node {
  lastRunVal = false;
  lastResetVal = false;
  lastInterval = 100;
  runState = false;
  startTime = null;
  elapsed = 0;
  units = 1;
  digits = 3;
  timeoutFunc;

  constructor() {
    super();
    this.title = NODE_INFO.time.stopwatch.title;
    this.description = NODE_INFO.time.stopwatch.description;
    this.addInputWithSettings('enable', Type.BOOLEAN, 0, 'Enable', false);
    this.addInput('reset', Type.BOOLEAN);

    this.settings['units'] = NodeSetting.create({
      description: 'Output Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 1, text: 'Milliseconds' },
          { value: 1000, text: 'Seconds' },
          { value: 60000, text: 'Minutes' },
          { value: 3.6e6, text: 'Hours' },
          { value: 8.64e7, text: 'Days' },
        ],
      },
      value: 1,
    });
    this.addInputWithSettings('interval', Type.NUMBER, 200, 'Count Interval', false);
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'milliseconds',
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
    this.addOutput('elapsed', Type.NUMBER);

    this.setOutputData(0, null);
    this.elapsed = 0;
  }

  onAdded() {
    this.runState = false;
    this.setOutputData(0, this.elapsed);
    this.inputs[2]['name'] = `[interval] (${this.settings['time'].value})`;
    this.outputs[0]['name'] = '[elapsed] (milliseconds)';
    this.onAfterSettingsChange();
  }

  onInputUpdated() {
    let run = this.getInputData(0);
    let reset = this.getInputData(1);

    if (reset && !this.lastResetVal) {
      this.elapsed = 0;
      this.startTime = Date.now();
      this.setOutputData(0, 0, true);
    }
    this.lastResetVal = reset;

    let interval = this.getInputData(2);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    if (interval != this.lastInterval && this.runState) {
      clearInterval(this.timeoutFunc);
      this.setOutputData(0, Date.now() - this.startTime + this.elapsed, true);
      this.timeoutFunc = setInterval(() => {
        const output = (Date.now() - this.startTime + this.elapsed) / this.units;
        this.setOutputData(0, Utils.toFixedNumber(output, this.digits), true);
      }, interval);
      this.lastInterval = interval;
    }

    if (run && !this.lastRunVal) {
      this.setOutputData(0, this.elapsed, true);
      this.runState = true;
      this.startTime = Date.now();
      this.timeoutFunc = setInterval(() => {
        const output = (Date.now() - this.startTime + this.elapsed) / this.units;
        this.setOutputData(0, Utils.toFixedNumber(output, this.digits), true);
      }, interval);
      this.lastRunVal = run;
    } else if (!run && this.lastRunVal) {
      clearInterval(this.timeoutFunc);
      this.elapsed = Date.now() - this.startTime + this.elapsed;
      this.runState = false;
      this.setOutputData(0, this.elapsed, true);
      this.lastRunVal = run;
    }

    /*
    if (run && run != this.lastRunVal) {
      this.runState = true;
      this.startTime = Date.now();
      this.timeoutFunc = setInterval(() => {
        const output = (Date.now() - this.startTime + this.elapsed) / this.units;
        this.setOutputData(0, Utils.toFixedNumber(output, this.digits), true);
      }, interval);
      this.lastRunVal = run;
    } else if (!run && run != this.lastRunVal) {
      this.elapsed = Date.now() - this.startTime + this.elapsed;
      this.runState = false;
      clearInterval(this.timeoutFunc);
      this.lastRunVal = run;
    }

*/
  }

  onAfterSettingsChange() {
    this.inputs[2]['name'] = `[interval] (${this.settings['time'].value})`;
    this.units = Number(this.settings['units'].value);
    switch (this.units) {
      case 1:
        this.outputs[0]['name'] = '[elapsed] (milliseconds)';
        break;
      case 1000:
        this.outputs[0]['name'] = '[elapsed] (seconds)';
        break;
      case 60000:
        this.outputs[0]['name'] = '[elapsed] (minutes)';
        break;
      case 3.6e6:
        this.outputs[0]['name'] = '[elapsed] (hours)';
        break;
      case 8.64e7:
        this.outputs[0]['name'] = '[elapsed] (days)';
        break;
      default:
        this.outputs[0]['name'] = '[elapsed] (milliseconds)';
    }

    const output = Number(this.outputs[0].data) / this.units;
    this.setOutputData(0, Utils.toFixedNumber(output, this.digits), true);
    this.onInputUpdated();
  }
}
Container.registerNodeType('time/stopwatch', StopwatchNode);
