import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class COVNode extends Node {
  lastValue;
  timeoutFunc;

  constructor() {
    super();
    this.title = NODE_INFO.trigger["change-of-value"].title;
    this.description = NODE_INFO.trigger["change-of-value"].description;
    this.addInput('input', Type.ANY);
    this.addInputWithSettings('interval', Type.NUMBER, 1, 'Interval');
    this.addInputWithSettings('threshold', Type.NUMBER, 0, 'COV Threshold');
    this.addOutput('output', Type.BOOLEAN);
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
  }

  onAdded() {
 
    this.setOutputData(0, false);
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
  }
  
  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const input = this.getInputData(0);
    const isNumber = typeof input === 'number';
    if (this.inputs[0].updated && input !== this.lastValue) {
      if (
        typeof this.lastValue !== 'number' ||
        !isNumber ||
        (isNumber && Math.abs(input - this.lastValue) >= this.getInputData(2))
      ) {
        this.lastValue = input;
        this.setOutputData(0, true);
        const interval = TimeUtils.timeConvert(this.getInputData(1), this.settings['time'].value);
        if (this.timeoutFunc) clearTimeout(this.timeoutFunc);
        this.timeoutFunc = setTimeout(() => {
          this.setOutputData(0, false);
        }, interval);
      }
    }
  }

  onAfterSettingsChange() {
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
  }
}
Container.registerNodeType('trigger/change-of-value', COVNode);
