import * as fs from 'fs';
import * as path from 'path';

export default class FileUtils {
  /**
   * Runs a current dir
   * @returns {string}
   */
  static getDir(): string {
    return path.dirname(__filename);
  }



  /**
   * Runs all files under a dir
   * @param dirPath
   * @returns {string}
   */
  static getAllFiles(dirPath): any {
    if (dirPath === null) return;
    return new Promise((resolve, reject) => {
      fs.readdir(dirPath, (err, files) => {
        if (err) {
          reject(err);
        }
        let obj = [];
        files.forEach(file => {
          obj.push(file);
        });
        resolve(obj);
        return obj;
      });
    });
  }

  /**
   * Checks if a file exists
   * @param fileName
   * @returns {boolean}
   */
  static checkFileExists(fileName): boolean {
    return fs.existsSync(fileName);
  }


  /**
   * create file if doesn't exist
   * @param fileName
   * @returns {any}
   */
  // create file if doesn't exist
  static createFile(fileName) {
    return new Promise((resolve, reject) => {
      fs.open(fileName, 'r', (err, fd) => {
        if (err) {
          fs.writeFile(fileName, '', err => {
            if (err) {
              reject(err);
            }
            let msg = 'The file was created!';
            resolve(msg);
          });
        } else {
          let msg = 'The file exists!';
          resolve(msg);
        }
      });
    });
  }

  /**
   * create directory if doesn't exist
   * @param directoryPath
   * @returns {string}
   */
  static createDirectory(directoryPath) {
    const directory = path.normalize(directoryPath);

    return new Promise((resolve, reject) => {
      fs.stat(directory, error => {
        if (error) {
          if (error.code === 'ENOENT') {
            fs.mkdir(directory, error => {
              if (error) {
                reject(error);
              } else {
                resolve(directory);
              }
            });
          } else {
            reject(error);
          }
        } else {
          resolve(directory);
        }
      });
    });
  }

  /**
   * create folder if doesn't exist
   * @param dirPath
   * @returns {any}
   */
  static ensureDirSync(dirPath) {
    try {
      return fs.mkdirSync(dirPath);
    } catch (err) {
      if (err.code !== 'EEXIST') throw err;
    }
  }

  /**
   * Read content from the filePath
   * @param filePath {string}
   */
  static readFile(filePath: string): Promise<any> {
    return new Promise((resolve, reject) => {
      fs.readFile(filePath, (err, buff) => {
        if (err) {
          reject(err);
        }
        resolve(buff);
      });
    });
  }

  static async checkForOldDirectory(filePath): Promise<boolean> {
    try {
      const fileContent = await FileUtils.readFile(filePath);
      if (fileContent) Boolean(fileContent.toString());
      else return false;
    } catch (e) {
      return false;
    }
  }

  /**
   * read text and return json out
   * @param fileName
   * @returns {any}
   */
  static async readFileReturnJson(fileName: string) {
    //if (!this.checkFileExists(fileName)) return;
    return new Promise((resolve, reject) => {
      fs.readFile(fileName, (err, data) => {
        let obj = [];
        if (err) {
          reject(err);
        }
        data
          .toString()
          .split('\n')
          .forEach(function (line, index, arr) {
            if (line.length === 0) {
              return;
            }
            obj.push(line);
          });
        let objStringify = JSON.stringify(obj);
        let json = JSON.parse(objStringify);
        resolve(json);
      });
    });
  }

  /**
   * append File
   * @param fileName
   * @param input
   * @returns {any}
   */
  static async appendFile(fileName: string, input: string) {
    //if (!this.checkFileExists(fileName)) return;
    return new Promise((resolve, reject) => {
      let msg = 'The file ' + fileName + ' was saved!';
      let errorMsg = 'Cant read file. File name is not defined';
      fs.appendFile(fileName, input, err => {
        if (err) {
          reject(err);
        }
        resolve(msg);
      });
    });
  }

  /**
   * writeFile
   * @param fileName
   * @param input
   * @returns {any}
   */
  static async writeFile(fileName: string, input: string) {
    //if (!this.checkFileExists(fileName)) return false;
    return new Promise((resolve, reject) => {
      let msg = 'The file ' + fileName + ' was saved!';
      let errorMsg = 'Cant read file. File name is not defined';
      fs.writeFile(fileName, input, err => {
        if (err) {
          reject(err);
        }
        resolve(msg);
      });
    });
  }



  /**
   * delete multiple files !!! Not tested well
   * @param files
   * @param callback
   * @returns {any}
   */
  static deleteFiles(files, callback) {
    let i = files.length;
    files.forEach(function (filepath) {
      console.log(filepath)
      if (FileUtils.checkFileExists(filepath)) {
        fs.unlink(filepath, function (err) {
          i--;
          if (err) {
            callback(err);
            return;
          } else if (i <= 0) {
            callback(null);
          }
        });
      } else {
        callback(true);
        console.log(11111)
        console.log(filepath)
      }
    });
  }


}
