import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class MathSubNode extends FlexibleNode {

  constructor() {
    super();
    this.dynamicInputsType = Type.NUMBER;
    
    this.title = NODE_INFO.math.subtract.title;
    this.description = NODE_INFO.math.subtract.description; 

    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
 
    this.size = this.computeSize();
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const inputCount = this.getInputsCount();
    let total = this.getInputData(0);
    for (let i = this.dynamicInputStartPosition + 1; i < inputCount; i++) {
      total = total - this.getInputData(i);
    }
    this.setOutputData(0, total);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('math/subtract', MathSubNode);
