import config from '../../../config';
import { isNull } from '../../../utils/helper';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';

let moment = require('moment-timezone');
const Influx = require('influx');

class InfluxDBNode extends Node {
  inName: number = 0;
  inJsonInput: number = 1;
  outError: number = 0;
  outLastExport: number = 1;
  outMsg: number = 2;
  client;
  constructor() {
    super();
    this.title = NODE_INFO.history["influx-db"].title;
    this.description = NODE_INFO.history["influx-db"].description;
    this.addInput('[name]', Type.STRING);
    this.addInput('json-input', Type.JSON);
    this.addOutput('error', Type.BOOLEAN);
    this.addOutput('outLastExport', Type.STRING);
    this.addOutput('outMsg', Type.STRING);
    this.settings['enable'] = NodeSetting.create({ description: 'Enable', value: false, type: SettingType.BOOLEAN });
    this.settings['useEnv'] = NodeSetting.create({ description: 'Dont use env settings', value: false, type: SettingType.BOOLEAN });
    this.settings['host'] = NodeSetting.create({ description: 'Host', value: '0.0.0.0', type: SettingType.STRING });
    this.settings['port'] = NodeSetting.create({ description: 'Port', value: '8086', type: SettingType.STRING });
    this.settings['authentication'] = NodeSetting.create({
      description: 'Use Authentication',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['user'] = NodeSetting.create({ description: 'Username', value: '', type: SettingType.STRING });
    this.settings['password'] = NodeSetting.create({ description: 'Password', value: '', type: SettingType.STRING });
    this.settings['databaseName'] = NodeSetting.create({
      description: 'Database Name',
      value: '',
      type: SettingType.STRING,
    });
    this.setSettingsConfig({
      groups: [
        { host: { weight: 3 }, port: { weight: 1 } },
        { user: {}, password: {} },
        { period: { weight: 2 }, periodUnits: {} },
      ],
      conditions: {
        host: setting => {
          return !!setting['useEnv'].value;
        },
        port: setting => {
          return !!setting['useEnv'].value;
        },
        authentication: setting => {
          return !!setting['useEnv'].value;
        },
        databaseName: setting => {
          return !!setting['useEnv'].value;
        },
        user: setting => {
          return !!setting['authentication'].value;
        },
        password: setting => {
          return !!setting['authentication'].value;
        },
      },
    });
  }
  onAdded() {
    this.createClient();
  }

  createClient() {
    let { protocol, host, port, databaseName, username, password } = config.influxDb;
    if (this.settings['useEnv'].value) {
      // dont use env
      host = this.settings['host'].value;
      port = this.settings['port'].value;
      this.client = new Influx.InfluxDB({
        host: host || 'localhost',
        port: +(port || 8086),
        protocol: 'http',
        database: this.settings['databaseName'].value || 'undefined',
        username: this.settings['authentication'].value ? this.settings['user'].value : '',
        password: this.settings['authentication'].value ? this.settings['password'].value : '',
      });
    } else {
      // use use env
      this.client = new Influx.InfluxDB({
        host,
        port: +port,
        protocol: protocol === 'https' ? 'https' : 'http',
        database: databaseName,
        username,
        password,
      });
    }
  }

  onInputUpdated() {
    if (this.side !== Side.server) return; // only execute on server
    if (!this.client) return;
    if (!this.settings['enable'].value) {
      this.setOutputData(this.outMsg, `INFLUX-DB NODE: not not enabled`, true);
      return;
    }

    // update node name
    if (this.inputs[0].updated) {
      let nodeName = this.getInputData(this.inName);
      if (!isNull(nodeName)) {
        this.name = nodeName;
        this.broadcastNameToClients();
      }
    }

    let input = this.getInputData(this.inJsonInput);
    if (isNull(input)) return;
    const hasMeasurement = input.hasOwnProperty('measurement');
    const hasMeasurementTags = input.hasOwnProperty('tags');
    const hasMeasurementFields = input.hasOwnProperty('fields');

    if (isNull(input.tags)) {
      this.setOutputData(this.outError, false, true);
      this.setOutputData(this.outMsg, `!tags! is empty,`, true);
      return;
    }
    if (isNull(input.measurement)) {
      this.setOutputData(this.outError, false, true);
      this.setOutputData(this.outMsg, `!measurement! is empty,`, true);
      return;
    }
    if (isNull(input.fields)) {
      this.setOutputData(this.outError, false, true);
      this.setOutputData(this.outMsg, `!fields! is empty,`, true);
      return;
    }
    if (hasMeasurement && hasMeasurementTags && hasMeasurementFields) {
      this.setOutputData(this.outError, false, true);
      this.setOutputData(this.outLastExport, moment().format());
      this.setOutputData(this.outMsg, `influx write to DB`, true);

      Object.keys(input.tags).forEach(key => {
        if (!input.tags[key]) {
          delete input.tags[key];
        }
      });
      this.client.writePoints([input]).catch(err => {
        this.debugInfo(`INFLUX-DB NODE: error on influx write, ${err}`);
        this.setOutputData(this.outError, true, true);
        this.setOutputData(this.outMsg, `error on influx write, ${err}`, true);
      });
    } else {
      this.debugInfo(`INFLUX-DB NODE: input.hasOwnProperty is missing`);
      this.setOutputData(this.outError, true, true);
      this.setOutputData(this.outMsg, `input.hasOwnProperty is missing`, true);
    }
  }

  onAfterSettingsChange() {
    if (this.side !== Side.server) return; // only execute on server
    this.createClient();
    this.onInputUpdated();
  }
}
Container.registerNodeType('history/influx-db', InfluxDBNode);
