import { exec } from 'child_process';
import { isNull } from '../../../utils/helper';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Type } from '../../node-io';

class SystemExecuteNode extends Node {
  lastTrigger:boolean;

  constructor() {
    super();
    this.title = NODE_INFO.system["execute-cmd"].title;
    this.description = NODE_INFO.system["execute-cmd"].description;
    this.addInputWithSettings('command', Type.STRING, 'pwd', 'Linux Command', false);
    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('out stdout', Type.STRING);
    this.addOutput('out err', Type.STRING);
    this.addOutput('out stderr', Type.STRING);
    this.addOutput('error', Type.STRING);
  }

  onCreated(){
    this.lastTrigger = false;
  }

  // example cmd to run a nodejs script cd /home/aidan/ && node lora-reset.js
  onInputUpdated() {
    const trigger = this.getInputData(1) || false;
    if (this.inputs[1].updated && trigger && !this.lastTrigger) {
      const command = this.getInputData(0);
      if(isNull(command))return;

      try {
        exec(command, (err, stdout, stderr) => {
          if (stdout) {
            // this.debugErr(stdout);
            this.setOutputData(0, stdout);
            this.setOutputData(3, false);
          }
          if (err) {
            // this.debugInfo(err);

            this.setOutputData(1, err);
            this.setOutputData(3, true);
          }
          if (stderr) {
            this.setOutputData(2, stderr);
            this.setOutputData(3, true);
          }
        });
      } catch (e) {
        this.setOutputData(3, true);
      }
    }
    this.lastTrigger = trigger;
  }
}
Container.registerNodeType('system/execute-cmd', SystemExecuteNode);
