import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class WaveformNode extends Node {
  lastTime: number;
  startTime: number;
  lastReset: boolean;
  constructor() {
    super();
    this.title = NODE_INFO.count.waveform.title;
    this.description = NODE_INFO.count.waveform.description;
    this.addInputWithSettings('enable', Type.BOOLEAN, 0, 'min', false);
    this.settings['signalType'] = NodeSetting.create({
      description: 'Signal Type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'Ramp', text: 'Ramp' },
          { value: 'Sine', text: 'Sine' },
          { value: 'Square', text: 'Square' },
          { value: 'Triangle', text: 'Triangle' },
          { value: 'Sawtooth', text: 'Sawtooth' },
        ],
      },
      value: 'Ramp',
    });
    this.addInputWithSettings('interval', Type.NUMBER, 1, 'Interval', false);
    this.addInputWithSettings('period', Type.NUMBER, 10, 'Period', false);
    this.addInputWithSettings('amplitude', Type.NUMBER, 1, 'Amplitude', false);
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
    this.settings['intervalTime'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.settings['periodTime'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [
        { interval: { weight: 2 }, intervalTime: {} },
        { period: { weight: 2 }, periodTime: {} },
        { amplitude: {} },
      ],
    });
    this.settings['enable'] = NodeSetting.create({ description: 'Enable', value: true, type: SettingType.BOOLEAN });
    this.settings['invert'] = NodeSetting.create({
      description: 'Invert Output',
      value: false,
      type: SettingType.BOOLEAN,
    });
  }

  onAdded() {
    this.updateTitle();
    this.inputs[1]['name'] = `[interval] (${this.settings['intervalTime'].value})`;
    this.inputs[2]['name'] = `[period] (${this.settings['periodTime'].value})`;
    this.lastReset = false;
    this.startTime = Date.now();
    this.onInputUpdated();
  }

  onExecute() {
    if (!this.getInputData(0)) return;
    let interval = this.getInputData(1);
    interval = TimeUtils.timeConvert(interval, this.settings['intervalTime'].value);
    const now = Date.now();
    if (!this.lastTime) {
      this.lastTime = now;
    }
    if (now - this.lastTime >= interval) {
      this.recalculate();
    }
  }
  onInputUpdated() {
    const reset = this.getInputData(4);
    if (reset && !this.lastReset) {
      this.setOutputData(0, 0, false);
      this.startTime = Date.now();
    }
    this.lastReset = reset;

    if (!this.getInputData(0)) {
      return;
    }
    this.recalculate();
  }

  recalculate() {
    const now = Date.now();
    const signalType = this.settings['signalType'].value || 'Ramp';
    let period = this.getInputData(2);
    period = TimeUtils.timeConvert(period, this.settings['periodTime'].value) / 1000;
    const amplitude = this.getInputData(3);
    const invert = this.settings['invert'].value ? -1 : 1;
    let time = (now - this.startTime) / 1000;
    let t = (time % period) / period;
    let value = 0;
    switch (signalType) {
      case 'Sine':
        value = Math.sin(2 * Math.PI * t);
        break;
      case 'Square':
        value = Math.sign(Math.sin(2 * Math.PI * t));
        if (value === -1) {
          value = 0;
        }
        break;
      case 'Triangle':
        value = 1 - 4 * Math.abs(Math.round(t - 0.25) - (t - 0.25));
        break;
      case 'Sawtooth':
        value = t;
        break;
      case 'Ramp':
        value = 2 * Math.abs(Math.round(t) - t);
        break;
    }
    value = invert * amplitude * value;
    this.lastTime = now;
    this.setOutputData(0, value);
  }

  onAfterSettingsChange() {
    this.updateTitle();
    this.inputs[1]['name'] = `[interval] (${this.settings['intervalTime'].value})`;
    this.inputs[2]['name'] = `[period] (${this.settings['periodTime'].value})`;
  }

  updateTitle() {
    if (!this.settings['signalType'].value) return;
    const signalType = this.settings['signalType'].value;
    this.title = 'Waveform' + ' ' + '(' + signalType + ')';
  }
}
Container.registerNodeType('count/waveform', WaveformNode);
