import * as express from 'express';
import app from '../../app';
import { authMiddleware, schemaMiddleware } from '../middleware';
import CryptoUtils from '../../nodes/utils/crypto-utils';

const Joi = require('@hapi/joi');
let router = express.Router();

const authUserSchema = Joi.object({
  username: Joi.string().min(5),
  password: Joi.string().min(5),
});

const encryptTextSchema = Joi.object({
  text: Joi.string().min(1),
});

router.route('/encrypt').post(schemaMiddleware(encryptTextSchema, 'body'), async (req, res) => {
  try {
    res.status(201).send({ encryptedText: CryptoUtils.encrypt(req.body.text) });
  } catch (e) {
    res.status(500).send(e);
  }
});

router.route('/user').post(schemaMiddleware(authUserSchema, 'body'), async (req, res) => {
  try {
    const authUser = await app.db.addAuthUser(req.body);
    res.status(201).send(authUser);
  } catch (e) {
    res.status(500).send(e);
  }
});

router.route('/user').get(async (req, res) => {
  try {
    const authUser = await app.db.getAuthUser();
    res.status(200).send(authUser);
  } catch (e) {
    res.status(500).send(e);
  }
});

router.route('/users').delete(authMiddleware(), async (req, res) => {
  try {
    const authUser = await app.db.deleteAuthUsers();
    res.status(200).send(authUser);
  } catch (e) {
    res.status(500).send(e);
  }
});

router.route('/login').post(schemaMiddleware(authUserSchema, 'body'), async (req, res) => {
  try {
    const token = await app.db.getAuthToken(req.body);
    res.status(200).send({ token });
  } catch (e) {
    res.status(500).send(e);
  }
});

module.exports = router;
