import { Api } from './api';
import { Service } from './entry';

abstract class ApplicationAPI extends Api<Service> {
  list(_objId?: string): Promise<Service[]> {
    return this.fetchMany(this.url(), 'services');
  }

  get(objId: string): Promise<Service[]> {
    return this.fetchOne(`${this.url()}/${objId}`);
  }

  create(obj: Service, parentId?: string): Promise<any> {
    return this.client.post(this.url(), { obj }).then(() => this.get(parentId));
  }

  update(obj: Service, objId: string): Promise<any> {
    return this.client.patch(`${this.url()}/${objId}`, { service_id: objId, obj });
  }

  delete(objId: string): Promise<any> {
    return this.client.delete(`${this.url()}/${objId}`);
  }

  factoryEntry(): Service {
    return this.newEntry(Service);
  }

  protected abstract url(): string;
}

export class ModuleAPI extends ApplicationAPI {
  protected url(): string {
    return 'modules';
  }
}

export class ServiceAPI extends ApplicationAPI {
  protected url(): string {
    return 'services';
  }
}
