import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class MathDivideNode extends FlexibleNode {

  constructor() {
    super();
    this.dynamicInputsType = Type.NUMBER;
    this.title = NODE_INFO.math.divide.title;
    this.description = NODE_INFO.math.divide.description; 
    
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
 
    this.size = this.computeSize();
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const inputCount = this.getInputsCount();
    let total = 1;
    let div = 0;
    for (let i = this.dynamicInputStartPosition; i < inputCount; i++) {
      div = this.getInputData(i);
      if (!div) {
        // when it's 0 or null
        this.setOutputData(0, 0);
        return;
      }
      if (i === this.dynamicInputStartPosition) {
        total = div;
      } else {
        total = total / div;
      }
    }
    this.setOutputData(0, total);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('math/divide', MathDivideNode);
