import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class MathDecimalPlaceNode extends Node {

  constructor() {
    super();

    this.title = NODE_INFO["num-transform"].round.title;
    this.description = NODE_INFO["num-transform"].round.description; this.addInput('input', Type.NUMBER);

    this.addOutput('output', Type.NUMBER);
    this.settings['decimals'] = NodeSetting.create({
      description: 'Decimal Places (Limit 5)',
      value: 1,
      type: SettingType.NUMBER,
    });
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let decimals = this.settings['decimals'].value;
    if (decimals > 5) decimals = 5;
    this.emitTransformedInput(x => x.toFixed(decimals));
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('num-transform/round', MathDecimalPlaceNode);
