        'reduce-duplicates': { title: 'Reduce Duplicates', description: 'This node reduces the number of duplicated transmitted values. When the value on the input is changed, the node sends it to the output and stops receiving the same value at a specified time interval. The values that come at this time and that are the same are simply ignored, but the last value will be stored if it is enabled in the settings. When the interval passes, the last value is sent to the output. This reduces the number of events, if they are sent too often, but ensures that the node will always send the last actual value. You can disable the sending of the last value in the settings of the node. This may be necessary if you do not want to receive messages late, but then it is not guaranteed that the nodes connected to this node will have the last actual value. If you activate Reset pin, the node will reset the timer and send the last value, if it is enabled in the settings.' },
        'reduce-events': { title: 'Reduce Events', description: 'This node reduces the number of transmitted values. When the value on the input is changed, the node sends it to the output and stops receiving at a specified time interval. The values that come at this time are simply ignored, but the last value will be stored if it is enabled in the settings. When the interval passes, the last value is sent to the output. This reduces the number of events, if they are sent too often, but ensures that the node will always send the last actual value. You can disable the sending of the last value in the settings of the node. This may be necessary if you do not want to receive messages late, but then it is not guaranteed that the nodes connected to this node will have the last actual value. If you activate Reset pin, the node will reset the timer and send the last value, if it is enabled in the settings.' }


