/**
 * Used to chain asynchronous executions and to coordinate them from various class instances while limiting parallelism.
 *
 * Example use case: Executing HTTP requests while limiting number of simultaneous request.
 */
export class PromiseActor {
  private stack: Promise<any> = Promise.resolve(null);

  /**
   * Append an asynchronous computation to the stack. The computation function will be called when all previously chained
   * computations have completed, and must this return a promise to indicate its completion. The next computation will
   * only be executed once the promise completes.
   *
   * Returns a promise that will complete when the provided computation has been called and completed.
   */
  process<T>(f: () => Promise<T>): Promise<T> {
    const callback = () => f(); // hide previous computation from f(x)
    this.stack = this.stack.then(callback, error => {
      console.log(error);
      return callback(); // process next
    });
    return Promise.resolve(this.stack); // defensive copy of promise
  }
}

export const singleton = new PromiseActor();