import { EDGE_28_INPUT, EDGE_28_NETWORK } from '../../../constants';
import { Container } from '../../../container';
import { NodeState } from '../../../node';
import { NODE_INFO } from '../../../node-info';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import Utils from '../../../utils';
import MathUtils, { MATH_FUNC_TYPE } from '../../../utils/MathUtils';
import BACnetEnumsUnits from '../../../utils/points/BACnet-enums-units';
import HistoryBase from '../../history/HistoryBase';
import Edge10kSensorUtils from './edge-10k-sensor-calc';
import { SEND_PAYLOAD_TO_CHILD } from './edge-constant';
import EdgeGPIOUtils from './edge-gpio-utils';
import Edge28Utils from './edge-utils';

enum POINT_TYPE {
  DIGITAL = 1,
  UI_DIGITAL = 2,
  _0_10DC = 3,
  _0_20MA = 4,
  _4_20MA = 5,
  _10K_THERMISTOR = 6,
}

enum POINT_TYPE_TEXT {
  DIGITAL = 'Digital',
  UI_DIGITAL = `UI-Digital`,
  _0_10DC = `0-10dc`,
  _0_20MA = `0-20ma`,
  _4_20MA = `4-20ma`,
  _10K_THERMISTOR = `10k-thermistor`,
}

class Edge28InputPointNode extends HistoryBase {
  out: any;
  constructor() {
    super();
    this.title = NODE_INFO['protocols/nube']['edge-28-input'].title;
    this.description = NODE_INFO['protocols/nube']['edge-28-input'].description;
    this.addOutput('output', Type.NUMBER);
    this.addOutput('output-json', Type.STRING);
    this.addOutput('error', Type.STRING);

    /**
      Default point Settings (Will be in point class)
   */
    this.settings['pointEnable'] = NodeSetting.create({
      description: 'Point enable',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['pointDebug'] = NodeSetting.create({
      description: 'Point Debug',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['pointNumber'] = NodeSetting.create({
      description: 'Point',
      value: 'DI1',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'DI1', text: `DI1` },
          { value: 'DI2', text: `DI2` },
          { value: 'DI3', text: `DI3` },
          { value: 'DI4', text: `DI4` },
          { value: 'DI5', text: `DI5` },
          { value: 'DI6', text: `DI6` },
          { value: 'DI7', text: `DI7` },
          { value: 'UI1', text: `UI1` },
          { value: 'UI2', text: `UI2` },
          { value: 'UI3', text: `UI3` },
          { value: 'UI4', text: `UI4` },
          { value: 'UI5', text: `UI5` },
          { value: 'UI6', text: `UI6` },
          { value: 'UI7', text: `UI7` },
        ],
      },
    });
    this.settings['pointType'] = NodeSetting.create({
      description: 'Point Type',
      value: 1,
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: POINT_TYPE.DIGITAL, text: `Digital` },
          { value: POINT_TYPE.UI_DIGITAL, text: `UI-Digital` },
          { value: POINT_TYPE._0_10DC, text: `0-10dc` },
          { value: POINT_TYPE._0_20MA, text: `0-20ma` },
          { value: POINT_TYPE._4_20MA, text: `4-20ma` },
          { value: POINT_TYPE._10K_THERMISTOR, text: `10k-thermistor` },
        ],
        value: POINT_TYPE.DIGITAL,
      },
    });

    this.settings['value_group'] = NodeSetting.create({
      description: 'Point Settings',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['lowScale'] = NodeSetting.create({
      description: 'Low scale',
      value: 0,
      type: SettingType.NUMBER,
    });
    this.settings['highScale'] = NodeSetting.create({
      description: 'High scale',
      value: 100,
      type: SettingType.NUMBER,
    });
    this.settings['decimals'] = NodeSetting.create({
      description: 'Out value precision',
      value: 2,
      type: SettingType.NUMBER,
    });
    this.settings['offset'] = NodeSetting.create({
      description: 'Out value offset',
      value: 0,
      type: SettingType.NUMBER,
    });
    /**
        Point Math settings
     */
    this.settings['mathFunc'] = NodeSetting.create({
      description: 'Math on output value (optional)',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: MATH_FUNC_TYPE.NA, text: 'na' },
          { value: MATH_FUNC_TYPE.ADD, text: 'add' },
          { value: MATH_FUNC_TYPE.SUBTRACT, text: 'subtract' },
          { value: MATH_FUNC_TYPE.MULTIPLY, text: 'multiply' },
          { value: MATH_FUNC_TYPE.DIVIDE, text: 'divide' },
          { value: MATH_FUNC_TYPE.NUMBER_INVERT, text: 'invert' },
        ],
      },
      value: MATH_FUNC_TYPE.NA,
    });
    this.settings['mathValue'] = NodeSetting.create({
      description: 'Enter a value',
      value: 1,
      type: SettingType.NUMBER,
    });

    this.settings['units_group'] = NodeSetting.create({
      description: 'Units (Save to get units)',
      type: SettingType.GROUP,
    });
    this.settings['unitsType'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: BACnetEnumsUnits.unitCategory,
      },
    });
    this.settings['units'] = NodeSetting.create({
      description: 'Units',
      value: BACnetEnumsUnits.COMMON_METRIC.NO_UNITS,
      type: SettingType.DROPDOWN,
    });

    const isDigitalType = [POINT_TYPE.DIGITAL, POINT_TYPE.UI_DIGITAL];

    this.setSettingsConfig({
      groups: [
        { pointEnable: {}, pointDebug: {} },
        { pointNumber: {}, pointType: {} },
        { decimals: {}, offset: {} },
        { lowScale: {}, highScale: {} },
        { mathFunc: {}, mathValue: {} },
        { unitsType: {}, units: {} },
      ],
      conditions: {
        decimals: setting => {
          const val = setting['pointType'].value;
          return !isDigitalType.includes(val);
        },
        offset: setting => {
          const val = setting['pointType'].value;
          return !isDigitalType.includes(val);
        },
        lowScale: setting => {
          const val = setting['pointType'].value;
          return !isDigitalType.includes(val);
        },
        highScale: setting => {
          const val = setting['pointType'].value;
          return !isDigitalType.includes(val);
        },
        mathValue: setting => {
          const val = setting['mathFunc'].value;
          return ![MATH_FUNC_TYPE.NUMBER_INVERT, MATH_FUNC_TYPE.NA].includes(val);
        },
      },
    });
    this.addHistorySettingsConfig(0, false);
  }

  async onAdded() {
    this.updateTitle();
    super.onAdded();
    if (this.side !== Side.server) return;
    await Utils.sleep(1000); // TODO: Since this node should start after network, import may shows issue if we don't have this
    Edge28Utils.addPoint(this.getParentNode(), this);
  }

  async onAfterSettingsChange() {
    await super.onAfterSettingsChange();
    if (this.side !== Side.server) return;
    this.updateTitle();
    // this.nodeColour();
    const unitsType = this.settings['unitsType'].value;
    this.settings['units'].config = {
      items: BACnetEnumsUnits.unitType(unitsType),
    };
    this.sendPointValue(this.out);
    this.broadcastSettingsToClients();

    setTimeout(() => {
      this.settings['pointDebug'].value = false;
      // this.broadcastSettingsToClients(); // TODO is not updating the settings to false.. fuck knows why
    }, 120 * 1000); // stop debug after 120 sec
  }

  onRemoved() {
    super.onRemoved();
    Edge28Utils.removePoint(this.getParentNode(), this);
  }

  sendJson() {
    return {
      name: this.name,
      pointValue: this.out,
      enable: this.settings['pointEnable'].value,
      units: this.settings['units'].value,
      pointNumber: this.settings['pointNumber'].value,
      pointType: this.settings['pointType'].value,
      pointOffset: this.settings['offset'].value,
    };
  }

  nodeColour() {
    if (this.settings['pointEnable'].value === false) {
      this.setNodeState(NodeState.INFO);
    } else this.setNodeState(NodeState.NORMAL);
  }

  sendPointValue(val) {
    const debug = this.settings['pointDebug'].value;
    if (debug) {
      this.setOutputData(0, val);
      this.setOutputData(1, this.sendJson());
    } else {
      this.setOutputData(0, val);
      this.setOutputData(1, this.sendJson());
    }
  }

  pointOffset(val: any) {
    let offset = this.settings['offset'].value;
    if (offset === null) {
      offset = 0;
    }
    return parseFloat(val) + parseFloat(offset);
  }

  pointTypeText(type) {
    switch (type) {
      case POINT_TYPE.DIGITAL:
        return POINT_TYPE_TEXT.DIGITAL;
      case POINT_TYPE.UI_DIGITAL:
        return POINT_TYPE_TEXT.UI_DIGITAL;
      case POINT_TYPE._0_10DC:
        return POINT_TYPE_TEXT._0_10DC;
      case POINT_TYPE._0_20MA:
        return POINT_TYPE_TEXT._0_20MA;
      case POINT_TYPE._4_20MA:
        return POINT_TYPE_TEXT._4_20MA;
      case POINT_TYPE._10K_THERMISTOR:
        return POINT_TYPE_TEXT._10K_THERMISTOR;

      default:
        this.debugWarn('Unknown type of object');
    }
  }

  updateTitle() {
    const pointNumber = this.settings['pointNumber'].value;
    this.title = `Edge-28-Input: (${pointNumber} as ${this.pointTypeText(this.settings['pointType'].value)})`;
    this.broadcastTitleToClients();
  }

  pointCalcs(val: number) {
    const mathFunc = this.settings['mathFunc'].value;
    const mathValue = this.settings['mathValue'].value;
    const decimals = this.settings['decimals'].value;
    const pointType = this.settings['pointType'].value;
    const isDigitalType = [POINT_TYPE.DIGITAL, POINT_TYPE.UI_DIGITAL];
    if (MathUtils.validateNumbers(val, mathValue)) {
      if (mathFunc !== MATH_FUNC_TYPE.NA) {
        if (!isDigitalType.includes(pointType)) {
          this.out = MathUtils.mathSwitch(mathFunc, val, mathValue);
          this.out = MathUtils.decimals(this.out, decimals);
          this.out = this.pointOffset(this.out);
          this.sendPointValue(this.out);
        } else {
          this.out = MathUtils.mathSwitch(mathFunc, val, mathValue);
          this.out = this.pointOffset(this.out);
          this.sendPointValue(this.out);
        }
      } else {
        if (!isDigitalType.includes(pointType)) {
          this.out = MathUtils.decimals(val, decimals);
          this.out = this.pointOffset(this.out);
        } else {
          this.out = val;
        }
        this.sendPointValue(this.out);
      }
    }
  }

  uiType(type: number, val: number, min_range?: number, max_range?: number) {
    const lowScale = this.settings['lowScale'].value;
    const highScale = this.settings['highScale'].value;
    switch (type) {
      case POINT_TYPE.DIGITAL:
        this.pointCalcs(EdgeGPIOUtils.diInvert(val));
        break;
      case POINT_TYPE.UI_DIGITAL:
        this.pointCalcs(EdgeGPIOUtils.uiAsDI(val));
        break;
      case POINT_TYPE._0_10DC:
        const limitValDc = Utils.clamp(val, 0, highScale);
        this.pointCalcs(EdgeGPIOUtils.scaleFromGPIOValue(limitValDc, lowScale, highScale));
        break;
      case POINT_TYPE._0_20MA:
        const limitValMaZero = Utils.clamp(val, 0, highScale);
        this.pointCalcs(EdgeGPIOUtils.scaleFromGPIOValue(limitValMaZero, lowScale, highScale));
        break;
      case POINT_TYPE._4_20MA:
        const limitValMaFour = EdgeGPIOUtils.scaleUI420ma(val, min_range, max_range);
        this.pointCalcs(EdgeGPIOUtils.scale420maToRange(limitValMaFour, lowScale, highScale));
        break;
      case POINT_TYPE._10K_THERMISTOR:
        this.pointCalcs(Edge10kSensorUtils.calc10kThermistor(val));
        break;
    }
  }

  subscribe({ action, payload }) {
    switch (action) {
      case SEND_PAYLOAD_TO_CHILD:
        if (this.settings['pointEnable'].value === false) return;
        const pointType = this.settings['pointType'].value;
        const pointNumber = this.settings['pointNumber'].value;
        let out: number;
        let min_range: number;
        let max_range: number;
        if (pointType === POINT_TYPE.DIGITAL) {
          if (!['DI1', 'DI2', 'DI3', 'DI4', 'DI5', 'DI6', 'DI7'].includes(pointNumber)) return;
          try {
            for (let val of [payload]) {
              out = val.pointsDI['4_val'][this.settings['pointNumber'].value].val;
            }
            this.uiType(pointType, out, payload);
          } catch (err) {
            this.debugErr(err);
          }
        } else {
          if (!['UI1', 'UI2', 'UI3', 'UI4', 'UI5', 'UI6', 'UI7'].includes(pointNumber)) return;
          try {
            for (let val of [payload]) {
              out = val.pointsUI['4_val'][this.settings['pointNumber'].value].val;
              min_range = val.pointsUI['6_min_range'][this.settings['pointNumber'].value];
              max_range = val.pointsUI['7_max_range'][this.settings['pointNumber'].value];
            }
            this.uiType(pointType, out, min_range, max_range);
          } catch (err) {
            this.debugErr(err);
          }
        }
        break;
      default:
        this.debugWarn("Request action doesn't match");
    }
  }
}

Container.registerNodeType(EDGE_28_INPUT, Edge28InputPointNode, EDGE_28_NETWORK);
