import Debounce from 'lodash-decorators/debounce';
import { Colors, create } from 'logplease';
import { AsyncDbHelper, Database } from '../database/database';
import { UiNode } from '../nodes/nodes/dashboard/ui-node';
import { DashboardServerSocket } from './dashboard-server-socket';

const logger = create('dashboard', { color: Colors.Yellow });

export interface UiPanel {
  name: string;
  title: string;
  icon: string;
  subPanels: Array<UiSubpanel>;
}

export interface UiSubpanel {
  title: string;
  name: string;
  uiElements: Array<UiElement>;
}

export interface UiElement {
  title: string;
  type: string;

  // link to node
  cid: number;
  id: number;
  name: string;
}

export class Dashboard {
  db: Database;
  uiPanels: Array<UiPanel>;
  socket: DashboardServerSocket;

  constructor(socket: DashboardServerSocket) {
    this.socket = socket;
    this.uiPanels = [];
  }

  loadFromDatabase(db: Database): Promise<any> {
    this.db = db;
    return AsyncDbHelper.noArgs<UiPanel[]>(db, db.getUiPanels)
                        .then(panels => this.uiPanels = panels)
                        .catch(err => logger.warn(err));
  }

  onNodeRemoved(node: UiNode) {
    if (!node.isDashboardNode) return;

    this.removeElementForNode(node);
    this.removeEmptyPanels();
    this.sendUiPanel(node.settings['ui-panel'].value);
  }

  onNodeChangePanelOrTitle(node: UiNode, newPanelName: string, newTitle: string) {
    this.removeElementForNode(node);

    //add new element
    const uiElemet: UiElement = {
      title: newTitle,
      type: node.uiElementType,
      cid: node.container.id,
      id: node.id,
      name: node.name,
    };

    let newPanel = this.getUiPanel(newPanelName);
    if (!newPanel) {
      //add to new panel
      newPanel = this.addUiPanel(newPanelName);
      newPanel.subPanels[0].uiElements.push(uiElemet);

      if (this.db) this.db.addUiPanel(newPanel);
    } else {
      //update existing panel
      newPanel.subPanels[0].uiElements.push(uiElemet);
      if (this.db)
        this.db.updateUiPanel(newPanel.name, { $set: { subPanels: newPanel.subPanels } });
    }
    this.removeEmptyPanels();
    this.socket.io.emit('getUiPanelsList', this.getUiPanelsList());
    this.sendUiPanel(node.settings['ui-panel'].value);
  }

  @Debounce(100)
  sendUiPanel(uiPanelValue) {
    this.socket.io.emit('getUiPanel', this.getUiPanel(uiPanelValue));
  }

  removeEmptyPanels() {
    let changed = false;
    for (let p = 0; p < this.uiPanels.length; p++) {
      let panel = this.uiPanels[p];
      if (panel.subPanels.every(s => s.uiElements.length == 0)) {
        // remove panel
        changed = true;
        this.uiPanels = this.uiPanels.filter(pan => pan.name != panel.name);
        if (this.db) this.db.removeUiPanel(panel.name);
      }
    }
    if (changed) this.socket.io.emit('getUiPanelsList', this.getUiPanelsList());
  }

  removeElementForNode(node: UiNode) {
    const oldPanel = this.getUiPanel(node.settings['ui-panel'].value);
    if (oldPanel) {
      //remove old element
      for (let s = 0; s < oldPanel.subPanels.length; s++) {
        const subPanel = oldPanel.subPanels[s];
        for (let e = 0; e < subPanel.uiElements.length; e++) {
          const element = subPanel.uiElements[e];
          if (element.cid == node.container.id && element.id == node.id) {
            subPanel.uiElements.splice(e, 1);

            if (this.db)
              this.db.updateUiPanel(oldPanel.name, { $set: { subPanels: oldPanel.subPanels } });
          }
        }
      }
    }
  }

  getUiPanel(name: string): UiPanel {
    return this.uiPanels.find(p => p.name === name);
  }

  getUiPanelsList(): Array<string> {
    let arr = [];
    this.uiPanels.forEach(p => {
      arr.push({ name: p.name, title: p.title, icon: p.icon });
    });
    return arr;
  }

  addUiPanel(name: string): UiPanel {
    const subPanel: UiSubpanel = {
      title: '',
      name: '',
      uiElements: [],
    };
    const panel: UiPanel = {
      name: name,
      title: name,
      icon: 'label_outline',
      subPanels: [subPanel],
    };
    this.uiPanels.push(panel);
    return panel;
  }
}
