import { Colors, create } from 'logplease';
import { isEmpty, isNull } from '../utils/helper';
import { Node } from './node';
import { createNodeDbHelper, NodeDbHelper } from './node-db-helper';
import { GlobalNodeRegistry } from './node-registry';
import { Deserializable, Serializable } from './serializable';
import Utils from './utils';

const logger = create('node-link', { color: Colors.Magenta });

export class Link implements Serializable<Link>, Deserializable<Link> {

  target_node_id: number;
  target_slot: number;
  target_input_id?: number;

  constructor(target_node_id: number, target_slot: number, target_input_id?: number) {
    this.target_node_id = target_node_id;
    this.target_slot = target_slot;
    this.target_input_id = target_input_id;
  }

  static create(object: { target_node_id: number, target_slot: number, target_input_id?: number }) {
    return new Link(object.target_node_id, object.target_slot, object.target_input_id);
  }

  serialize(): Link {
    return { ...this } as Link;
  }

  deserialize(serializedObj: Link): Link {
    return Link.create(serializedObj);
  }
}

export interface LinkInfo {
  origin_id: number;
  origin_slot: number;
  target_id: number;
  target_slot: number;
  target_input_id?: number;
}

//TODO: must make each method throwable
export interface LinkHandler {
  readonly node: Node;

  /**
   * Create a link
   * @param {LinkInfo} link
   * @return true if create link successfully
   */
  make(link: LinkInfo): boolean;

  /**
   * Disconnect link in node input
   * @param {number} slot input slot
   * @param {number} index? input aka input id
   */
  disconnectInput(slot: number, index?: number): boolean;

  /**
   * Disconnect link in node output
   * @param {number} slot output slot
   * @param {number} index? output aka output id
   */
  disconnectOutput(slot: number, index?: number): boolean;

  /**
   * Recompute Input links
   * @param {string} nodeId
   */
  recomputeInputLinks(nodeId?: string);

  /**
   * Recompute Output links
   * @param {string} nodeId
   */
  recomputeOutputLinks(nodeId?: string);

}

export const createLinkHandler = (node: Node, dbHelper?: NodeDbHelper): LinkHandler => {
  return new DefaultLinkHandler(node, dbHelper ?? createNodeDbHelper());
};

class DefaultLinkHandler implements LinkHandler {
  readonly node: Node;
  private readonly dbHelper: NodeDbHelper;

  constructor(node: Node, dbHelper: NodeDbHelper) {
    this.node = node;
    this.dbHelper = dbHelper;
  }

  make(link: LinkInfo): boolean {
    let target_node = this.node.container.getSubNode(link.target_id);
    if (!target_node) {
      logger.error(`Can't connect, target node not found`);
      return false;
    }

    //check output exist
    if (!this.node.outputs || !this.node.outputs[link.origin_slot]) {
      logger.error(`Cant connect, output not found`);
      return false;
    }

    //check input exist
    let actualSlot = link.target_input_id ?? link.target_slot;

    if (!target_node.inputs || !target_node.inputs[actualSlot]) {
      logger.error(`Can't connect, input not found`);
      return false;
    }

    let output = this.node.outputs[link.origin_slot];
    let input = target_node.inputs[actualSlot];

    //check target node allows connection
    if (target_node['onConnectInput'] && !target_node['onConnectInput'](actualSlot, output)) {
      return false;
    }

    //if target node input already connected
    if (input.link) target_node.disconnectInputLink(link.target_slot, link.target_input_id);

    //create link
    if (!output.links) output.links = [];

    output.links.push(new Link(link.target_id, link.target_slot, link.target_input_id));
    input.link = new Link(link.origin_id, link.origin_slot);

    //update input value
    input.updated = true;
    input.data = Utils.parseValue(output.data, input.type);
    target_node.isUpdated = true;

    // target_node.inputs[actualSlot] = input;
    // this.node.outputs[link.origin_slot] = output;

    if (target_node.id === this.node.id) {
      this.dbHelper.update(this.node, ['inputs', 'outputs']);
    } else {
      this.dbHelper.update(this.node, ['outputs']);
      this.dbHelper.update(target_node, ['inputs']);
    }
    logger.debug('Connected to ' + target_node.getReadableId());
    return true;
  }

  disconnectInput(slot: number, index?: number): boolean {
    // check input exist
    let inputId = index ?? slot;
    if (!this.node.inputs || !this.node.inputs[inputId]) {
      logger.error(`Can't disconnect, input slot: ${inputId} not found on node: ${this.node.id}`);
      return false;
    }
    const input = this.node.inputs[inputId];
    const link = input.link;
    if (!link) return false;

    // disconnect output
    let targetNode = this.node.container.getSubNode(link.target_node_id);
    if (!targetNode) return false;

    let output = targetNode.outputs[link.target_input_id ?? link.target_slot];
    if (!output || !output.links) return false;
    let number = output.links.findIndex(l => this.predicate(l, this.node.id, index, slot));
    if (number !== -1) {
      output.links.splice(number, 1);
    }
    input.link = null;
    input.data = undefined;
    input.updated = true;
    this.node.isUpdated = true;

    this.dbHelper.update(this.node, ['inputs']);
    this.dbHelper.update(targetNode, ['outputs']);
    logger.debug(`Disconnected from ${targetNode.getReadableId()}`);
    return true;
  }

  disconnectOutput(slot: number, index?: number): boolean {
    // check output exist
    if (!this.node.outputs || !this.node.outputs[slot]) {
      logger.error(`Can't disconnect, output slot: ${slot} not found on node: ${this.node.id}`);
      return false;
    }
    const output = this.node.outputs[slot];
    // check links
    if (isEmpty(output.links)) return false;

    let i = output.links.length;
    while (i--) {
      let link = output.links[i];
      // remove link
      const targetNode = this.node.container.getSubNode(link.target_node_id);
      if (!targetNode) {
        logger.error(`Node: ${link.target_node_id} is not available`);
      }
      if (targetNode && targetNode.inputs && targetNode.inputs[link.target_slot] &&
          targetNode.inputs[link.target_slot].link) {
        delete targetNode.inputs[link.target_slot].link;
        // send null to target node input
        targetNode.inputs[link.target_slot].data = undefined;
        targetNode.inputs[link.target_slot].updated = true;
        targetNode.isUpdated = true;
        this.dbHelper.update(this.node, ['inputs']);
      } else {
        logger.error(`Tried to delete input link on: ${targetNode} of slot: ${link.target_slot}`);
      }
      output.links.splice(i, 1);
      logger.debug(`Disconnected from ${targetNode?.getReadableId() ?? link.target_node_id}`);
    }
    delete output.links;

    this.dbHelper.update(this.node, ['outputs']);
    return true;
  }

  recomputeInputLinks(nodeId?: string) {
    let node = GlobalNodeRegistry.instance().get(nodeId) ?? this.node;
    let slot = -1;
    for (const [inputId, input] of Object.entries(node.inputs)) {
      if (input.setting.hidden) {
        continue;
      }
      slot++;
      if (isNull(input.link)) {
        continue;
      }
      let link = input.link;
      let targetNode = node.container.getSubNode(link.target_node_id);
      if (isNull(targetNode)) {
        continue;
      }
      let outputId = link.target_input_id;
      let outputSlot = link.target_slot;
      let output = targetNode.outputs[outputId ?? outputSlot];
      if (isNull(output) || isNull(output.links)) {
        continue;
      }
      let number = output.links.findIndex(l => this.predicate(l, node.id, +inputId, slot));
      if (number === -1) {
        continue;
      }
      output.links.splice(number, 1,
                          Link.create({ target_node_id: node.id, target_slot: slot, target_input_id: +inputId }));
      this.dbHelper.update(targetNode, ['outputs']);
    }
    this.dbHelper.update(node, ['inputs']);
  }

  recomputeOutputLinks(nodeId?: string) {
    let node = GlobalNodeRegistry.instance().get(nodeId) ?? this.node;
    let slot = -1;
    Object.entries(node.outputs).forEach(([oId, output]) => {
      slot++;
      if (isEmpty(output.links)) {
        return;
      }
      output.links.forEach(link => {
        let target = node.container.getSubNode(link.target_node_id);
        const iId = link.target_input_id ?? link.target_slot;
        if (isNull(target) || isNull(target.inputs[iId])) {
          return;
        }
        target.inputs[iId].link = Link.create({ target_node_id: node.id, target_slot: slot, target_input_id: +oId });
        this.dbHelper.update(target, ['inputs']);
      });
    });
    this.dbHelper.update(node, ['outputs']);
  }

  protected predicate(link: Link, targetNodeId: number, targetInputId: number, targetSlot: number): boolean {
    if (link.target_node_id !== targetNodeId) {
      return false;
    }
    if (link.target_input_id && targetInputId && link.target_input_id === targetInputId) {
      return true;
    }
    return link.target_slot === targetSlot;
  }
}
