import { ConnectionOutput } from '../../nodes/nodes/protocols/connection-node-mixin';
import { convertToNumber, isNotNull, isNull, range } from '../../utils/helper';
import { EventData } from '../../utils/pattern';

export interface Model {
}

export interface HasIdentifier extends Model {
  /**
   * Gets model identifier
   * @returns {string} identifier
   */
  identifier(): string;
}

export interface PointValue {
  presentValue: any;
  priority: number;
  priorityArray: { [k: number]: any };

  /**
   * Check point value is reset state
   * @return {boolean} true if this instance has `present value` and `priority` are `null` at same time
   */
  isResetState(): boolean;

  /**
   * Compare this instance with another point value to identify a change
   * @param {PointValue} prev another point value
   * @return {PointValue} change of value. It might `null` if no change.
   */
  changedOfValue(prev: PointValue): PointValue;

  /**
   * Merge this instance from another point value within highest priority computation
   * @param {PointValue} prev another point value
   * @return {PointValue} new non-null point value
   */
  merge(prev: PointValue): PointValue;
}

/**
 * Define point model as input
 */
export interface PointModel extends HasIdentifier {

  enabled: boolean;

  pointValue: PointValue;

  /**
   * Compare another Point to detect might change only value
   * @param point Another Point
   */
  mightOnlyValueChanged(point: PointModel): boolean;

  /**
   * Check another Point is same identifier or not
   */
  equals(point: PointModel): boolean;

}

export interface PointOutput<T extends PointModel> extends Model, EventData {

  connStatus: ConnectionOutput;
  point: T;

}

export class PointValueCreator {
  private constructor() {
  }

  /**
   * Init Point Value without auto correct highest value based on given inputs
   * @param {number} presentValue Point present value
   * @param {number} priority Point priority
   * @param {object | []} priorityArray priority array in JSON or array
   * @returns {PointValue}
   */
  static init = (presentValue: any, priority?: number, priorityArray?: { [k: number]: any } | []): PointValue => {
    if (priority && (priority < 1 || priority > 16)) {
      throw new Error('Priority must be in range [1, 16]');
    }
    let pv = new DefaultPointValue();
    pv.presentValue = presentValue ?? null;
    pv.priority = priority === undefined ? 16 : priority;
    pv.priorityArray = PointValueCreator.normalizePriorityArray(priorityArray);
    return pv;
  };

  /**
   * Init Point Value without auto correct highest value based on priority array
   * @param priorityArray priority array object or array with 16 item
   * @return {PointValue}
   */
  static by = (priorityArray?: { [k: number]: any } | []): PointValue => {
    let pa = PointValueCreator.normalizePriorityArray(priorityArray);
    let highestValue = Object.entries(pa).find(pa => pa[1]) ?? [16, null];
    return PointValueCreator.init(convertToNumber(highestValue[1]), convertToNumber(highestValue[0]), pa);
  };

  /**
   * Create PointValue based on given inputs then auto correct `highest value`
   * @param {any} presentValue
   * @param {number} priority
   * @param {object | []} priorityArray priority array in JSON or array
   * @returns {PointValue}
   */
  static create = (presentValue: any, priority?: number,
                   priorityArray?: { [k: number]: any } | []): PointValue => {
    let pv = PointValueCreator.init(presentValue, priority, priorityArray);
    if (pv.isResetState()) {
      return pv;
    }
    let highestValue = Object.entries(pv.priorityArray).find(pa => pa[1]) ?? [16, null];
    pv.priorityArray[pv.priority] = pv.presentValue;
    if (highestValue && convertToNumber(highestValue[0]) === pv.priority && pv.presentValue === null) {
      highestValue = Object.entries(pv.priorityArray).find(([_, v]) => isNotNull(v));
    }
    if (highestValue && (pv.priority > convertToNumber(highestValue[0]) || pv.presentValue === null)) {
      pv.priority = convertToNumber(highestValue[0]);
      pv.presentValue = highestValue[1];
    }
    return pv;
  };

  private static createPriorityArray = () => {
    return Array.from(range(1, 16)).reduce((o, k) => ((o[k] = null), o), {});
  };

  private static normalizePriorityArray = (priorityArray: { [p: number]: any } | []): { [k: number]: any } => {
    if (!priorityArray) {
      return PointValueCreator.createPriorityArray();
    }
    if (priorityArray instanceof Array) {
      let len = (priorityArray as Array<any>).length;
      if (len === 0) {
        return PointValueCreator.createPriorityArray();
      }
      if (len !== 16) {
        throw new Error('Invalid priority array');
      }
      return priorityArray.reduce((o, k, i) => ((o[i + 1] = convertToNumber(k)), o), {});
    }
    if (typeof priorityArray !== 'object') {
      throw new Error('Invalid priority array');
    }
    let pa = PointValueCreator.createPriorityArray();
    for (let key in pa) {
      if (priorityArray.hasOwnProperty(key)) {
        pa[key] = convertToNumber(priorityArray[key]);
      }
    }
    return pa;
  };
}

class DefaultPointValue implements PointValue {
  presentValue: any;
  priority: number;
  priorityArray: { [p: number]: any };

  isResetState(): boolean {
    return isNull(this.presentValue) && isNull(this.priority);
  }

  changedOfValue(prev: PointValue): PointValue {
    if (!prev) {
      return this;
    }
    if (this.presentValue !== prev.presentValue || this.priority !== prev.priority) {
      return this;
    }
    let pv = new DefaultPointValue();
    for (const priority in this.priorityArray) {
      if (this.priorityArray[priority] !== prev.priorityArray[priority]) {
        pv.priority = convertToNumber(priority);
        pv.presentValue = this.priorityArray[priority];
        pv.priorityArray = this.priorityArray;
        return pv;
      }
    }
    return null;
  }

  merge(prev: PointValue): PointValue {
    if (!prev || !prev.priorityArray) {
      return PointValueCreator.create(this.presentValue, this.priority, this.priorityArray);
    }
    if (this.isResetState()) {
      return PointValueCreator.create(null, 16);
    }
    let arr = {};
    for (const priority in this.priorityArray) {
      arr[priority] = isNull(this.priorityArray[priority]) && isNotNull(prev.priorityArray[priority])
                      ? prev.priorityArray[priority] : this.priorityArray[priority];
    }
    return PointValueCreator.create(this.presentValue, this.priority, arr);
  }
}
