import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

export class InjectNode extends Node {
  lastInterval = 1000;
  runState: boolean = true;
  timeoutFunc;

  constructor() {
    super();
    this.title = NODE_INFO.trigger["interval-inject"].title;
    this.description = NODE_INFO.trigger["interval-inject"].description;
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addInputWithSettings('message', Type.ANY, 'true', 'Message');
    this.addInputWithSettings('interval', Type.NUMBER, 1, 'Interval');
    this.addOutput('output', Type.BOOLEAN);
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
  }

  onAdded() {
    clearInterval(this.timeoutFunc);
    this.inputs[2]['name'] = `[interval] (${this.settings['time'].value})`;
    const enable = this.getInputData(0);
    if (!enable) {
      this.setOutputData(0, null);
      this.runState = false;
      return;
    }
    let interval = this.getInputData(2);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    this.timeoutFunc = setInterval(() => {
      const message = this.getInputData(1);
      this.setOutputData(0, message);
    }, interval);
    this.onInputUpdated();
  }

  onInputUpdated() {
    const enable = this.getInputData(0);
    if (!enable && this.runState) {
      clearInterval(this.timeoutFunc);
      this.setOutputData(0, null, true);
      this.runState = false;
      return;
    } else if (!enable && !this.runState) {
      this.setOutputData(0, null, true);
      this.runState = false;
      return;
    } else if (enable) {
      let interval = this.getInputData(2);
      interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
      if (this.runState) {
        if (interval != this.lastInterval) {
          this.lastInterval = interval;
          clearInterval(this.timeoutFunc);
          this.timeoutFunc = setInterval(() => {
            const message = this.getInputData(1);
            this.setOutputData(0, message);
          }, interval);
        }
        return;
      } else if (!this.runState) {
        this.runState = true;
        this.timeoutFunc = setInterval(() => {
          const message = this.getInputData(1);
          this.setOutputData(0, message);
        }, interval);
      }
    }
  }

  onAfterSettingsChange() {
    this.inputs[2]['name'] = `[interval] (${this.settings['time'].value})`;
    this.onInputUpdated();
  }
}
Container.registerNodeType('trigger/interval-inject', InjectNode);
