import { Container } from '../../../container';
import { Node } from '../../../node';
import { NODE_INFO } from '../../../node-info';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import TimeUtils from '../../../utils/time-utils';

interface LoraSensor {
  id: string;
  voltage: number;
  rssi: number;
  snr: number;
  nodeName: string;
  topic: {}
  lastMessage: any
}
interface AASensor extends LoraSensor {
  pulses: number;
  a1: number;
  a2: number;
  a3: number;
}
interface B0Sensor extends LoraSensor {
  temp: number;
  pressure: number;
  humidity: number;
}
interface B1Sensor extends B0Sensor {
  lux: number;
}
interface B2Sensor extends B1Sensor {
  movement: boolean;
}

enum SensorType {
  Droplet = 'Droplet',
  MicroEdge = 'MicroEdge',
}

export class LoraSensorDecoderNode extends Node {
  watchdogDelay = 3600000;
  nodeID = '';
  timeoutFunc;
  sensorType: string;

  constructor() {
    super();
    this.title = NODE_INFO["protocols/lora-raw"]["lora-sensor-decoder"].title;
    this.description = NODE_INFO["protocols/lora-raw"]["lora-sensor-decoder"].description;
    this.settings['sensorType'] = NodeSetting.create({
      description: 'LORA Sensor Type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: SensorType.Droplet, text: SensorType.Droplet },
          { value: SensorType.MicroEdge, text: SensorType.MicroEdge },
        ],
      },
      value: SensorType.Droplet,
    });
    this.addInput('rawInput', Type.STRING);
    this.addInputWithSettings('nodeID', Type.STRING, '', 'NodeID');
    this.addInputWithSettings('topic', Type.STRING, '', 'Set topic for use elsewhere in wires');
    this.addOutput('json', Type.STRING);
    this.addOutput('id', Type.STRING);
    this.addOutput('voltage', Type.NUMBER);
    this.addOutput('rssi', Type.NUMBER);
    this.addOutput('lowBatteryAlm', Type.BOOLEAN);
    this.addOutput('watchdog', Type.BOOLEAN);
    this.addOutput('temperature', Type.NUMBER);
    this.addOutput('humidity', Type.NUMBER);
    this.addOutput('lux', Type.NUMBER);
    this.addOutput('pressure', Type.NUMBER);
    this.addOutput('movement', Type.BOOLEAN);
    this.settings['microEdgeA1'] = NodeSetting.create({
      description: 'MicroEdge a1 Input Type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: '10k', text: '10k Thermistor' },
          { value: '10vdc', text: '0-10vdc' },
          { value: 'digital', text: 'Digital' },
        ],
      },
      value: '10vdc',
    });
    this.settings['microEdgeA2'] = NodeSetting.create({
      description: 'MicroEdge a2 Input Type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: '10k', text: '10k Thermistor' },
          { value: '10vdc', text: '0-10vdc' },
          { value: 'digital', text: 'Digital' },
        ],
      },
      value: '10vdc',
    });
    this.settings['microEdgeA3'] = NodeSetting.create({
      description: 'MicroEdge a3 Input Type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: '10k', text: '10k Thermistor' },
          { value: '10vdc', text: '0-10vdc' },
          { value: 'digital', text: 'Digital' },
        ],
      },
      value: '10vdc',
    });
    this.settings['battery-warn'] = NodeSetting.create({
      description: 'Low battery warning voltage',
      value: 3.5,
      type: SettingType.NUMBER,
    });
    this.settings['watchdog'] = NodeSetting.create({
      description: 'Watchdog (No message warning) Delay',
      value: 60,
      type: SettingType.NUMBER,
    });
    this.settings['time'] = NodeSetting.create({
      description: 'Time',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'minutes',
    });
    this.setSettingsConfig({
      groups: [{ time: {}, watchdog: { weight: 2 } }],
      conditions: {
        microEdgeA1: setting => {
          return setting['sensorType'].value === SensorType.MicroEdge;
        },
        microEdgeA2: setting => {
          return setting['sensorType'].value === SensorType.MicroEdge;
        },
        microEdgeA3: setting => {
          return setting['sensorType'].value === SensorType.MicroEdge;
        },
      },
    });
  }

  onAdded() {
    this.sensorType = this.settings['sensorType'].value;
    if (this.sensorType === SensorType.MicroEdge) {
      /**
       * Issue: #552
       * On frontend side:
       * Removing this one here, because constructor() and onAdded() function gets called while doing each refresh
       * And always 'movement' slot (slot number: 10) gets displayed even when it shouldn't
       * It's because when sensorType === 'MicroEdge', we are replacing slots 6-9 with new one and leaving 0-5 as is,
       * But we are doing nothing for node 10, so on it displays on frontend side even it doesn't have real value on backend
       */
      this.removeOutput(10);
    }
    this.clearOutputs();
    let nodeID = this.getInputData(1).toUpperCase() || '';
    if (nodeID != this.nodeID) {
      this.nodeID = nodeID;
    }
    this.updateTitle();
  }

  onInputUpdated() {
    const sensorType = this.settings['sensorType'].value;
    let nodeID = this.getInputData(1).toUpperCase() || '';
    if (nodeID != this.nodeID) {
      this.nodeID = nodeID;
      this.updateTitle(); // Update title is also related with nodeID update so
      this.broadcastTitleToClients();
      this.clearOutputs();
      this.resetWatchdog();
    }
    let data: string = this.getInputData(0) || '';
    if (!data) return;
    else if (data.length % 2 === 1 && (data[data.length - 1] === '\r' || data[data.length - 1] === '\n')) {
      data = data.substring(0, data.length - 1);
    } else if (data.substring(data.length - 2, data.length) === '\r\n') {
      data = data.substring(0, data.length - 2);
    }

    if (
      data.length !== 36 &&
      data.length !== 44 &&
      data.substring(2, 4) !== 'AA' &&
      data.substring(2, 4) !== 'B0' &&
      data.substring(2, 4) !== 'B1' &&
      data.substring(2, 4) !== 'B2'
    ) {
      return;
    }

    if (nodeID == data.substring(0, 8) || nodeID == '') {
      if (
        sensorType === SensorType.MicroEdge &&
        data.substring(2, 4) === 'AA' &&
        (data.length === 44 || data.length === 36)
      ) {
        if (this.inputs[0].updated) this.resetWatchdog();
        this.writeOutputs(this.microEdgeDecode(data));
      } else if (
        sensorType === SensorType.Droplet &&
        data.substring(2, 3) === 'B' &&
        (data.length === 44 || data.length === 36)
      ) {
        if (this.inputs[0].updated) this.resetWatchdog();
        this.writeOutputs(this.dropletDecode(data));
      }
    }
  }

  onAfterSettingsChange() {
    let watchdogTime = this.settings['watchdog'].value;
    watchdogTime = TimeUtils.timeConvert(watchdogTime, this.settings['time'].value);
    if (this.watchdogDelay != watchdogTime) {
      this.resetWatchdog();
    }
    this.setOutputs();
    this.updateNodeOutput();
    this.onInputUpdated();
  }

  private writeOutputs(output: LoraSensor) {
    switch (this.settings['sensorType'].value) {
      case SensorType.Droplet:
        this.setOutputData(0, JSON.stringify(output));
        this.setOutputData(1, output['id'] == null ? null : output['id']);
        this.setOutputData(2, output['voltage'] == null ? null : output['voltage']);
        this.setOutputData(3, output['rssi'] == null ? null : output['rssi']);
        this.setOutputData(4, output['voltage'] < this.settings['battery-warn'].value);
        this.setOutputData(6, output['temp'] == null ? null : output['temp']);
        this.setOutputData(7, output['humidity'] == null ? null : output['humidity']);
        this.setOutputData(8, output['lux'] == null ? null : output['lux']);
        this.setOutputData(9, output['pressure'] == null ? null : output['pressure']);
        this.setOutputData(10, output['movement'] == null ? null : output['movement']);
        break;

      case SensorType.MicroEdge:
        this.setOutputData(0, JSON.stringify(output));
        this.setOutputData(1, output['id'] == null ? null : output['id']);
        this.setOutputData(2, output['voltage'] == null ? null : output['voltage']);
        this.setOutputData(3, output['rssi'] == null ? null : output['rssi']);
        this.setOutputData(4, output['voltage'] < this.settings['battery-warn'].value);
        this.setOutputData(6, output['pulses'] == null ? null : output['pulses']);
        const microEdgeOutputs = this.MicroEdgeAnalogs([output['a1'], output['a2'], output['a3']]);
        this.setOutputData(7, microEdgeOutputs[0] == null ? null : microEdgeOutputs[0]);
        this.setOutputData(8, microEdgeOutputs[1] == null ? null : microEdgeOutputs[1]);
        this.setOutputData(9, microEdgeOutputs[2] == null ? null : microEdgeOutputs[2]);
        break;
    }
  }

  private MicroEdgeAnalogs(analogInputs) {
    let output = [];
    for (var i = 0; i < 3; i++) {
      if (typeof analogInputs[i] == 'number') {
        switch (this.settings[`microEdgeA${i + 1}`].value) {
          case '10k':
            const Vref = 3.34;
            const V = (analogInputs[i] / 1024) * Vref;
            const R0 = 10000;
            const R = (R0 * V) / (Vref - V);
            const T0 = 273 + 25;
            const B = 3850;
            const T = 1 / (1 / T0 + (1 / B) * Math.log(R / R0));
            const temp = T - 273.15;
            output.push(Math.round(temp * 100) / 100);
            break;

          case '10vdc':
            output.push((analogInputs[i] / 1024) * 10);
            break;

          case 'digital':
            if (analogInputs[i] === undefined || analogInputs[i] >= 1000) {
              output.push(false);
            } else {
              output.push(true);
            }
            break;
        }
      } else output.push(null);
    }
    return output;
  }

  lastMessageTimeStamp(){
    return  new Date()
  }

  microEdgeDecode(data: string): AASensor {
    const topic = this.getInputData(2)
    const topicsBuilder = {
      id : topic+"/id",
      pulses: topic+"/pulses",
      voltage: topic+"/voltage",
      a1: topic+"/a1",
      a2: topic+"/a2",
      a3: topic+"/a3",
      rssi: topic+"/rssi",
      snr: topic+"/snr"
    }
    let lastMessage = null;
    lastMessage = this.lastMessageTimeStamp()
    return {
      id: data.substring(0, 8),
      pulses: parseInt(data.substring(8, 16), 16),
      voltage: parseInt(data.substring(16, 18), 16) / 50,
      a1: parseInt(data.substring(18, 22), 16),
      a2: parseInt(data.substring(22, 26), 16),
      a3: parseInt(data.substring(26, 30), 16),
      rssi: parseInt(data.substring(data.length - 4, data.length - 2), 16) * -1,
      snr: parseInt(data.substring(data.length - 2, data.length), 16) / 10,
      nodeName: this.name,
      topic: topicsBuilder,
      lastMessage: lastMessage
    };
  }

  dropletDecode(data: string): B0Sensor {
    const topic = this.getInputData(2)
    const topicsBuilder = {
      id : topic+"/id",
      temp: topic+"/temp",
      pressure: topic+"/pressure",
      humidity: topic+"/humidity",
      lux: topic+"/lux",
      movement: topic+"/movement",
      voltage: topic+"/voltage",
      rssi: topic+"/rssi",
      snr: topic+"/snr"
    }
    let lastMessage = null;
    lastMessage = this.lastMessageTimeStamp()

    let output: B0Sensor = {
      id: data.substring(0, 8),
      temp: parseInt(data.substring(10, 12) + data.substring(8, 10), 16) / 100,
      pressure: parseInt(data.substring(14, 16) + data.substring(12, 14), 16) / 10,
      humidity: parseInt(data.substring(16, 18), 16) % 128,
      voltage: parseInt(data.substring(22, 24), 16) / 50,
      rssi: parseInt(data.substring(data.length - 4, data.length - 2), 16) * -1,
      snr: parseInt(data.substring(data.length - 2, data.length), 16) / 10,
      nodeName: this.name,
      topic: topicsBuilder,
      lastMessage: lastMessage
    };
    if (data.substring(2, 4) === 'B1' || data.substring(2, 4) === 'B2') {
      const b1Sensor: B1Sensor = output as B1Sensor;
      b1Sensor.lux = parseInt(data.substring(20, 22) + data.substring(18, 20), 16);
      output = b1Sensor;
      if (data.substring(2, 4) == 'B2') {
        const b2_sensor: B2Sensor = b1Sensor as B2Sensor;
        b2_sensor.movement = parseInt(data.substring(16, 18), 16) > 127;
        output = b2_sensor;
      }
    }
    return output;
  }

  resetWatchdog() {
    this.setOutputData(5, false, true);
    clearInterval(this.timeoutFunc);
    let watchdogTime = this.settings['watchdog'].value;
    watchdogTime = TimeUtils.timeConvert(watchdogTime, this.settings['time'].value);
    this.watchdogDelay = watchdogTime;
    this.timeoutFunc = setTimeout(() => {
      this.setOutputData(5, true, true);
    }, watchdogTime);
  }

  setOutputs() {
    const currentSensorType = this.settings['sensorType'].value;
    if (this.sensorType != currentSensorType) {
      switch (currentSensorType) {
        case SensorType.Droplet:
          this.removeOutput(6);
          this.removeOutput(7);
          this.removeOutput(8);
          this.removeOutput(9);
          if (this.outputs[10]) this.removeOutput(10);
          this.addOutput('temperature', Type.NUMBER);
          this.addOutput('humidity', Type.NUMBER);
          this.addOutput('lux', Type.NUMBER);
          this.addOutput('pressure', Type.NUMBER);
          this.addOutput('movement', Type.BOOLEAN);
          this.clearOutputs();
          break;

        case SensorType.MicroEdge:
          this.removeOutput(6);
          this.removeOutput(7);
          this.removeOutput(8);
          this.removeOutput(9);
          this.removeOutput(10);
          if (this.outputs[11]) this.removeOutput(11);
          this.addOutput('pulses', Type.NUMBER);
          this.addOutput('a1', Type.NUMBER);
          this.addOutput('a2', Type.NUMBER);
          this.addOutput('a3', Type.NUMBER);
          this.clearOutputs();
          break;
      }
    }
    if (currentSensorType == SensorType.MicroEdge) {
      this.outputs[7].name = `a1 (${this.settings['microEdgeA1'].value})`;
      this.outputs[8].name = `a2 (${this.settings['microEdgeA2'].value})`;
      this.outputs[9].name = `a3 (${this.settings['microEdgeA3'].value})`;
    }
    this.sensorType = currentSensorType;
    this.updateTitle();
  }

  updateTitle() {
    if (this.nodeID) this.title = `Lora Sensor Decoder (${this.sensorType} | ${this.nodeID})`;
    else this.title = `Lora Sensor Decoder (${this.sensorType} | ALL)`;
  }

  clearOutputs() {
    this.setOutputData(0, null);
    this.setOutputData(1, null);
    this.setOutputData(2, null);
    this.setOutputData(3, null);
    this.setOutputData(4, null);
    this.setOutputData(5, null);
    this.setOutputData(6, null);
    this.setOutputData(7, null);
    this.setOutputData(8, null);
    this.setOutputData(9, null);
    if (this.settings['sensorType'].value === SensorType.Droplet) this.setOutputData(10, null);
  }
}
Container.registerNodeType('protocols/lora-raw/lora-sensor-decoder', LoraSensorDecoderNode);
