import { Node } from '../nodes/node';
import { DispatcherRegistry } from './DispatcherRegistry';
import { PubSubHelpers } from './PubSubHelpers';

export abstract class DispatchNode extends Node {
  /**
   * `@Dispatch` will call it: for listening dispatched value
   * @param {any} value - value got from `@Dispatch`
   */
  abstract subscribe(value: any): void;

  /**
   * For listening store protocol, point changes
   */
  abstract updateProtocols(): void;

  abstract getSelectedProtocol(): string;

  abstract getSelectedPoint(): string;

  abstract getSelectedPointName(): string;

  onAdded() {
    super.onAdded();
    DispatcherRegistry.instance().register(this, true);
    PubSubHelpers.map(this);
  }

  onRemoved() {
    DispatcherRegistry.instance().remove(this);
    PubSubHelpers.unMap(this);
  }
}
