import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class ToggleNode extends Node {
  state = false;
  lastVal;
  count = 0;
  constructor() {
    super();
    this.title = NODE_INFO.boolean.toggle.title;
    this.description = NODE_INFO.boolean.toggle.description;
    this.addInput('input', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
    //this.addOutput('toggle out', Type.BOOLEAN);
    this.settings['reset-on-disc'] = NodeSetting.create({
      description: 'Reset on disconnected or input is null',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['toggleOnAny'] = NodeSetting.create({
      description: 'Toggle on any input change',
      value: false,
      type: SettingType.BOOLEAN,
    });
  }

  onAdded() {
    this.setOutputData(0, false);
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let input = this.getInputData(0);
    const toggleOnAny = this.settings['toggleOnAny'].value
    const inputUpdated = this.inputs[0].updated;

    if(toggleOnAny){
      if (inputUpdated) {
        this.properties['count']++;
        this.setOutputData(0, !this.outputs[0].data); // toggle
      }
      return
    }
    // sends a true/false on each input update
    if (this.count === 0) {
      //this.setOutputData(1, false);
      this.count = 1;
    } else if (this.count === 1) {
      //this.setOutputData(1, true);
      this.count = 0;
    }

    if (input == null || input == undefined) {
      if (this.settings['reset-on-disc'].value) {
        this.lastVal = false;
        this.setOutputData(0, false);
        this.state = false;
      }
    } else if (input == this.lastVal) return;
    if (input == true) {
      this.setOutputData(0, !this.state);
      this.state = !this.state;
    }
    this.lastVal = input;
  }
}
Container.registerNodeType('boolean/toggle', ToggleNode);
