// source map for node typescript debug
require('source-map-support').install();
import { Colors, create } from 'logplease';
// @formatter:off
//MUST UP
const logger = create('app', { color: Colors.Green });
const path = require('path');
const process = require('process');
const argv = require('minimist')(process.argv.slice(2));
const dataDir = process.env['DATA_DIR'] || argv['datadir'] || './db';
const envFile = argv['envFile'] || '.env';
const envFileAbsPath = path.isAbsolute(envFile) ? envFile : path.resolve(process.cwd(), envFile);
process.env['DATA_DIR'] = path.isAbsolute(dataDir) ? dataDir : path.resolve(process.cwd(), dataDir);
require('dotenv').config({ path: envFileAbsPath });
(<any>global).__rootdirname = path.resolve(__dirname);

logger.info('----------------------------- Rubix Wires -----------------------------');
logger.info(`Process Dir       : '${process.cwd()}'`);
logger.info(`Working Dir       : '${(<any>global).__rootdirname}'`);
logger.info(`Data Dir          : '${process.env.DATA_DIR}'`);
logger.info(`Environment       : '${process.env.NODE_ENV || 'development'}'`);
logger.info(`Environment file  : '${envFileAbsPath}'`);

import { Backend } from './backend/backend';
import conf from './config';
import * as config from './config.json';
import { AsyncDbHelper, Database } from './database/database';
import { Container, ContainerRegistry } from './nodes/container';
import { Node } from './nodes/node';
import { NodeHookHelper } from './nodes/node-hook';
import { SerializedNode, Side } from './nodes/node-interface';
import { NodeRegistryHelper } from './nodes/node-registry';
import { NodeTypeRegistry } from './nodes/node-type-registry';
import { RegistryResult } from './nodes/registry';
import { Dashboard } from './server/dashboard';
import { Server } from './server/server';
import { isEmpty } from './utils/helper';
//---------------------------------------------
// @formatter:on

export class App {
  db: Database;
  dashboard: Dashboard;
  rootContainer: Container;
  server: Server;

  constructor() {
    this.server = new Server((<any>global).__rootdirname, conf.port);
    this.dashboard = new Dashboard(this.server.dashboardSocket);
    this.rootContainer = ContainerRegistry.instance().register(Container.create(Side.server, { id: 0 }), true);
    this.configApp();
    this.registerNodeTypes();
    this.start();
  }

  start() {
    this.connectDatabase()
        .then(db => (this.db = db))
        .then(db => db.asyncLoadDatabase(conf.dataDir).then(_ => db))
        .then(db => this.onDatabaseReady(db))
        .catch(err => this.onDatabaseError(err));
  }

  private configApp(): void {
    this.rootContainer.dashboard = this.dashboard;
    this.rootContainer.server_editor_socket = this.server.editorSocket.io;
    this.rootContainer.server_dashboard_socket = this.server.dashboardSocket.io;
  }

  private registerNodeTypes() {
    require('./nodes/nodes/index');
    logger.info(`Registered ${NodeTypeRegistry.instance().size()} nodes types`);
  }

  private connectDatabase(): Promise<Database> {
    if (!config.dataBase.enable) {
      return Promise.reject(new Error('Not enabled database'));
    }
    if (!config.dataBase.useInternalDb) {
      return Promise.reject(new Error('Unsupported external database'));
    }
    return Promise.resolve(require('./database/neDbDatabase').db);

  }

  private startBackend() {
    new Backend(this.db, conf.backendSync).start();
  }

  private onDatabaseReady(db: Database): Promise<any> {
    return this.initNodeContext(db)
               .then(_ => this.dashboard.loadFromDatabase(db))
               .then(_ => this.startBackend())
               .then(_ => this.rootContainer.run(conf.loopInterval));
  }

  private initNodeContext(db: Database): Promise<any> {
    this.rootContainer.db = db;
    return AsyncDbHelper.noArgs<SerializedNode[]>(db, db.getNodes)
                        .then(serializedNodes => this.registerNodes(serializedNodes));
  }

  private registerNodes(nodes: SerializedNode[]): Promise<RegistryResult<Node>> {
    return NodeRegistryHelper.register(this.rootContainer, nodes, ContainerRegistry.instance(), true)
                             .then(r => {
                               logger.info(`Imported ${ContainerRegistry.instance().size() - 1} containers, ` +
                                           `${r.objects.length}/${nodes.length} nodes successfully from database`);
                               if (!isEmpty(r.errors)) {
                                 logger.warn(`Import failed: [${r.errors.join('\n')}]`);
                               }
                               return r;
                             })
                             .then(r => {
                               r.objects.forEach(node => NodeHookHelper.execute(node, 'onDbReaded') ?? node);
                               return r;
                             });
  }

  private onDatabaseError(err: Error) {
    if (err.message === 'Not enabled database') {
      //TODO is it need? Should remove it
      logger.warn(`Database is not enabled, rubix-wires is running on memory...`);
      this.rootContainer.run(conf.loopInterval);
      return;
    }
    logger.error(`Unable to bootstrap application. Failed at: ${err.stack ?? err}`);
    process.exit(99);
  }
}

const app = new App();
export default app;
