import * as express from 'express';
import app from '../../app';
import conf from '../../config';
import { CLONE, CREATE_NODE, IMPORT, MOVE_TO_CONTAINER, REMOVE_NODES } from '../../shared/socket-events';
import { Container, ContainerRegistry } from '../../nodes/container';
import { SerializedNode } from '../../nodes/node-interface';
import { LinkInfo } from '../../nodes/node-link';
import { NodeValidator } from '../../shared/validator';
import { NotFoundError, RegistryItemExistedError } from '../../utils/errors';
import { reliability_logger } from '../../utils/loggers';
import { NodeCRUDService } from '../service/NodeCRUDService';
import { NodeETLService } from '../service/NodeETLService';

const router = express.Router();
const etlService = new NodeETLService();
const crudService = new NodeCRUDService();

setInterval(updateActiveNodes, 500);

function updateActiveNodes() {
  if (!app.rootContainer) return;

  for (let container of ContainerRegistry.instance().list()) {
    let activeNodesIds = [];

    for (let node of container.subNodes()) {
      if (node.isRecentlyActive) {
        node.isRecentlyActive = false;
        activeNodesIds.push(node.id);
      }
    }

    if (activeNodesIds.length > 0) {
      app.server.editorSocket.io.in('' + container.id).emit('nodes-active', {
        ids: activeNodesIds,
        cid: container.id,
      });
    }
  }
}

/**
 * Get container
 */
router.get('/c/:cid', function (req, res) {
  let container: Container = ContainerRegistry.instance().get(req.params.cid);
  if (!container) return res.status(404).send(`Can't get container. Container id [${req.params.cid}] not found.`);
  res.json(container.serialize());
});

router.get('/c/:cid/file', function (req, res) {
  let container = ContainerRegistry.instance().get(req.params.cid);
  if (!container) return res.status(404).send(`Can't export container. Container id [${req.params.cid}] not found.`);

  let s = container.container_node.serialize();
  //remove links
  if (s.inputs) for (let i in s.inputs) if (s.inputs[i].link) delete s.inputs[i].link;
  if (s.outputs) for (let i in s.outputs) if (s.outputs[i].links) delete s.outputs[i].links;

  let text = JSON.stringify(s);
  let cont_name = container.name.replace(/[^a-z0-9]/gi, '_').toLowerCase();
  let filename = 'shub_' + cont_name + '.json';
  res.set({ 'Content-Disposition': 'attachment; filename="' + filename + '"' });
  res.send(text);
});

//------------------ nodes ------------------------

/**
 * Create node
 */
router.post('/c/:cid/n/', function (req, res) {
  crudService.createNode(+req.params.cid, { type: req.body.type, pos: req.body.position } as SerializedNode)
             .then(result => {
               app.server.editorSocket.io.emit(CREATE_NODE, result.node);
               if (result.isDashboardNode) {
                 app.server.dashboardSocket.io.in(req.params.cid).emit(CREATE_NODE, result.node);
               }
               return res.status(201).send(result.message);
             })
             .catch(err => {
               reliability_logger.warn(err);
               const code = err instanceof NotFoundError ? 404 : err instanceof RegistryItemExistedError ? 409 : 400;
               return res.status(code).send(`Can't create node. Reason: ${err}`);
             });
});

/**
 * Delete nodes
 */
router.delete('/c/:cid/n/', function (req, res) {
  const cid = +req.params.cid;
  crudService.removeNodes(cid, req.body, true)
             .then(result => {
               app.server.editorSocket.io.emit(REMOVE_NODES, result);
               return res.status(200).send(result.message);
             })
             .catch(err => {
               reliability_logger.warn(err);
               return res.status(err instanceof NotFoundError ? 404 : 400).send(`Can't create node. Reason: ${err}`);
             });
});

/**
 * Update node position
 */
router.put('/c/:cid/n/:id/position', function (req, res) {
  let container = ContainerRegistry.instance().get(req.params.cid);
  if (!container) {
    return res.status(404).send(`Can't update node position. Container id [${req.params.cid}] not found.`);
  }

  let node = container.getSubNode(req.params.id);
  if (!node) {
    return res.status(404).send(`Can't update node position. Node id [${req.params.cid}/${req.params.id}] not found.`);
  }

  node.pos = req.body.position;

  if (app.db) app.db.updateNode(node.id, node.container.id, { $set: { pos: node.pos } });

  app.server.editorSocket.io.emit('node-update-position', {
    id: req.params.id,
    cid: req.params.cid,
    pos: node.pos,
  });
  res.send(`Node position updated: type [${node.type}] id [${node.container.id}/${node.id}]`);
});

