import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';

export default abstract class Statistics extends FlexibleNode {
  protected constructor() {
    super();
    this.dynamicInputsType = Type.NUMBER;
  }

  onAdded() {
    this.size = this.computeSize();
    this.onInputUpdated();
  }

  onInputUpdated() {}

  /**
   * Get array of all input values or setting values where values are defined
   */
  getDefinedInputsOrSettingsValues() {
    const output = [];
    for (let i = 0; i < this.getInputsCount(); i++) {
      const input = this.getInputData(i);
      if (input != null) {
        output.push(input);
      }
    }
    return output;
  }
}
