import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { Type } from '../../node-io';
import OsUtils from '../../utils/os-utils';

class HostInfo extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.system["host-info"].title;
    this.description =
      `## Description\n ` +
      NODE_INFO.system["host-info"].description + `   \n ` +
      `## On change of settings\n ` +
      `   \n ` +
      ` On change of settings output ***system-info*** will return data in json format\n ` +
      `## On active Input\n ` +
      `   \n ` +
      ` On an active input ***trigger*** the outputs ***cpu-temp***, ***disc-usage***, ***system-time***, ***system-memory*** will return data in json format\n ` +
      `## The data returned\n ` +
      `   \n ` +
      ` On change of settings output ***system-info*** will return data in json format\n ` +
      `   \n `;
    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('system-info', Type.JSON);
    this.addOutput('cpu-temp', Type.JSON);
    this.addOutput('disc-usage', Type.JSON);
    this.addOutput('system-time', Type.JSON);
    this.addOutput('system-memory', Type.JSON);
    this.addOutput('error', Type.STRING);
  }

  onCreated() {
    this.onAfterSettingsChange();
  }

  callSystemInfo() {
    OsUtils.systemInfo()
      .then(e => this.setOutputData(0, e))
      .catch(err => this.setOutputData(5, err));
  }

  callInfo() {
    OsUtils.cpuTemperature()
      .then(e => this.setOutputData(1, e))
      .catch(err => this.setOutputData(5, err));
    OsUtils.fsSize()
      .then(e => this.setOutputData(2, e))
      .catch(err => this.setOutputData(5, err));
    OsUtils.systemTime()
      .then(e => this.setOutputData(3, e))
      .catch(err => this.setOutputData(5, err));
    OsUtils.systemMem()
      .then(e => this.setOutputData(4, e))
      .catch(err => this.setOutputData(5, err));
  }

  onAfterSettingsChange() {
    if (this.side !== Side.server) return;
    this.callSystemInfo();
  }

  onInputUpdated() {
    if (this.side !== Side.server) return;
    this.callInfo();
  }
}
Container.registerNodeType('system/host-info', HostInfo);
