import { Container } from '../../container';
import { Node, NodeState } from '../../node';
import { NODE_INFO } from '../../node-info';

class NotesNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.debug.comments.title;
    this.description = NODE_INFO.debug.comments.description;
  }

  onAdded() {
    this.nodeColour();
  }

  nodeColour() {
    this.setNodeState(NodeState.INFO);
  }
}
Container.registerNodeType('debug/comments', NotesNode);
