import { Enumify } from 'enumify';

export class InputMethod extends Enumify {
  static VALUE_PRIORITY = new InputMethod('Value and Priority');
  static PRIORITY_ARRAY_LOT = new InputMethod('Priority Array');
  static PRIORITY_ARRAY_JSON = new InputMethod('JSON');
  static _ = InputMethod.closeEnum();

  readonly label: string;

  constructor(label: string) {
    super();
    this.label = label;
  }

  static items(): { text: string; value: any }[] {
    return InputMethod.enumKeys.map(k => {
      return { value: k, text: (<InputMethod>InputMethod.enumValueOf(k)).label };
    });
  }
}
