import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import Utils from '../../utils';
import { NODE_INFO } from '../../node-info';

class EqualNode extends Node {

  constructor() {
    super();
    this.title = NODE_INFO.compare.equal.title;
    this.description = NODE_INFO.compare.equal.description;

    this.addInput('in 1', Type.ANY);
    this.addInputWithSettings('in 2', Type.ANY, null, 'in 2');
    this.addOutput('out equal', Type.BOOLEAN);
    this.addOutput('out not equal', Type.BOOLEAN);
  }

  onAdded() {
    this.setOutputData(0, false);
    this.setOutputData(1, true);
    this.onInputUpdated();
  }


  onInputUpdated() {
    let a = this.getInputData(0);
    if (!Utils.hasInput(a)) {
      this.setOutputData(0, false);
      this.setOutputData(1, true);
      return;
    }
    a = (a || '').toString(); // making string null i.e `''` and other `null` same
    const b = (this.getInputData(1) || '').toString();
    if (a === b) {
      this.setOutputData(0, true);
      this.setOutputData(1, false);
    } else {
      this.setOutputData(0, false);
      this.setOutputData(1, true);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('compare/equal', EqualNode);
