import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class HysteresisNode extends Node {

  constructor() {
    super();

    this.title = NODE_INFO.compare.hysteresis.title;
    this.description = NODE_INFO.compare.hysteresis.description;

    this.addInput('input', Type.NUMBER);
    this.addInputWithSettings('risingEdge', Type.NUMBER, 20, 'Rising Edge', false);
    this.addInputWithSettings('fallingEdge', Type.NUMBER, 10, 'Falling Edge', false);
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('out not', Type.BOOLEAN);
  }

  onAdded() {
    this.setOutputData(0, false);
    this.setOutputData(1, true);
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let inMsg = this.getInputData(0);
    let risingEdge = this.getInputData(1);
    let fallingEdge = this.getInputData(2);

    if (risingEdge != null && fallingEdge != null) {
      if (risingEdge > fallingEdge) {
        if (inMsg <= fallingEdge) {
          this.setOutputData(0, false);
          this.setOutputData(1, true);
        }
        if (inMsg >= risingEdge) {
          this.setOutputData(0, true);
          this.setOutputData(1, false);
        }
      } else if (risingEdge < fallingEdge) {
        if (inMsg >= fallingEdge) {
          this.setOutputData(0, false);
          this.setOutputData(1, true);
        }
        if (inMsg <= risingEdge) {
          this.setOutputData(0, true);
          this.setOutputData(1, false);
        }
      }
    } else {
      this.setOutputData(0, false);
      this.setOutputData(1, true);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('compare/hysteresis', HysteresisNode);
