import { Container, ContainerRegistry } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class FlowExport extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.system["flow-export"].title;
    this.description = NODE_INFO.system["flow-export"].description;
    this.addOutput('output', Type.STRING);
    this.addInput('trigger', Type.BOOLEAN);
  }

  init() {
    if (!this.properties) {
      this.properties['lastTrigger'] = false;
    }
  }

  onInputUpdated() {
    if (this.side != Side.server) return;
    const input: boolean = this.getInputData(0);
    if (this.properties['lastTrigger'] !== input && input) {
      const container = ContainerRegistry.instance().rootContainer();
      container.db.getNodes((err, docs) => {
        if (!err) {
          this.setOutputData(0, JSON.stringify(docs));
        } else {
          this.debugErr(` Node id [ ${this.cid}/${this.id} ] has error: ${err.message}.`);
        }
      });
    }
    this.properties['lastTrigger'] = input;
    this.persistProperties(false, true);
  }
}
Container.registerNodeType('system/flow-export', FlowExport);
