import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class JsonCheckerNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.json["json-checker"].title;
    this.description = NODE_INFO.json["json-checker"].description; 
    this.addInput('input', Type.STRING)
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('msg', Type.STRING);
  }

  onAdded() {
    this.onInputUpdated();
  }

  isJson(item: any): boolean {
    item = typeof item !== 'string' ? JSON.stringify(item) : item;

    try {
      item = JSON.parse(item);
    } catch (e) {
      return false;
    }

    if (typeof item === 'object' && item !== null) {
      return true;
    }

    return false;
  }

  onInputUpdated() {
    const val = this.getInputData(0);
    try {
      if (this.isJson(val)) {
        this.setOutputData(0, true);
        this.setOutputData(1, 'valid JSON');
      } else { 
        this.setOutputData(0, false);
        this.setOutputData(1, 'invalid JSON');
      }
    } catch (err) {
      this.setOutputData(0, false);
      this.setOutputData(1, err);
    }
  }
}
Container.registerNodeType('json/json-checker', JsonCheckerNode);
