import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';

class RandomNode extends Node {
  state = false;
  lastVal;

  constructor() {
    super();
    this.title = NODE_INFO.trigger.random.title;
    this.description = NODE_INFO.trigger.random.description;
    this.settings['digits'] = NodeSetting.create({
      description: 'Number of decimal places',
      value: 3,
      type: SettingType.NUMBER,
    });
    this.addInput('trigger', Type.BOOLEAN);
    this.addInputWithSettings('max', Type.NUMBER, 1, 'Maximum Value');
    this.addInputWithSettings('min', Type.NUMBER, 0, 'Minimum Value');
    this.addOutput('output', Type.NUMBER);
    this.setOutputData(0, null);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let trigger = this.getInputData(0);
    if (trigger == null) {
      this.lastVal = false;
      this.setOutputData(0, null);
      this.state = false;
    } else if (trigger == this.lastVal) return;
    if (trigger == true) {
      const max = this.getInputData(1);
      const min = this.getInputData(2);
      this.settings['digits'].value;
      let digits = this.settings['digits'].value;
      let result = Math.random() * (max - min) + min;
      result = Utils.toFixedNumber(result, digits);
      this.setOutputData(0, result);
    }
    this.lastVal = trigger;
  }
}
Container.registerNodeType('trigger/random', RandomNode);
