import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class QueueNode extends Node {
  data = [];
  lastReset: boolean;

  constructor() {
    super();
    this.title = NODE_INFO.streams.queue.title;
    this.description = NODE_INFO.streams.queue.description;
    this.addInput('input');
    this.addInput('trigger', Type.BOOLEAN);
    this.addInput('clear', Type.BOOLEAN);
    this.addOutput('output');
    this.addOutput('count', Type.NUMBER);
    this.lastReset = false;
  }

  onInputUpdated() {
    const reset = this.getInputData(2);
    if (reset && !this.lastReset) {
      this.data = [];
      this.setOutputData(0, null);
      this.setOutputData(1, 0);
      this.lastReset = reset;
      return;
    } else if (!reset && this.lastReset) {
      this.lastReset = reset;
      return;
    }

    if (this.inputs[0].updated && this.inputs[0].data != null) {
      this.data.unshift(this.inputs[0].data);
      this.setOutputData(1, this.data.length);
    }

    if (this.inputs[1].updated && this.inputs[1].data == true) {
      let val = this.data.length > 0 ? this.data.pop() : null;
      this.setOutputData(0, val);
      this.setOutputData(1, this.data.length);
    }
  }
}
Container.registerNodeType('streams/queue', QueueNode);
