import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { NODE_INFO } from '../../node-info';
import { Type } from '../../node-io';
import Utils from '../../utils';

class StreamMergeNode extends FlexibleNode {
  val: number;
  lastReset: boolean;

  constructor() {
    super();
    this.dynamicSettingsExist = false;
    this.dynamicInputsType = Type.ANY;
    this.title = NODE_INFO.streams["stream-merge"].title;
    this.description = NODE_INFO.streams["stream-merge"].description;
    this.addOutput('output', Type.ANY);
  }

  onAdded() {
    this.onInputUpdated();
    this.size = this.computeSize();
  }

  onInputUpdated() {
    const inputs = this.getInputsCount();
    for (let i = this.dynamicInputStartPosition; i < inputs; i++) {
      let inputValue = this.getInputData(i);
      if (Utils.hasInput(inputValue)) {
        if (this.inputs[i].updated) {
          this.setOutputData(0, this.inputs[i].data);
          return;
        }
      }
    }
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('streams/stream-merge', StreamMergeNode);
