import { isEmpty, isJSON, isNull } from '../../../utils/helper';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

export class JSONEnumBuilderNode extends Node {
  filterKeys = [];

  constructor() {
    super();
    this.title = NODE_INFO.json["json-enum-builder"].title;
    this.description = NODE_INFO.json["json-enum-builder"].description;
    this.addInput('trigger', Type.BOOLEAN);
    this.addInputWithSettings('definition', Type.JSON, '{"pointOne":22, "pointTwo":33}', 'JSON value');
    this.addOutput('output', Type.ANY);
    this.addOutput('valid-json', Type.BOOLEAN);
    this.settings['json'] = NodeSetting.create({
      description: 'Select output',
      type: SettingType.DROPDOWN,
      value: '',
    });
  }

  onAdded() {
    this.onAfterSettingsChange()
  }

  onInputUpdated() {
    this.getInputData(0);
    this.onAfterSettingsChange()
  }

  onAfterSettingsChange() {
    const input = this.getInputData(1);
    if (isNull(input)) return;
    if (isJSON(input) && !isEmpty(input)) {
      const definition = Object.entries(input).map(
        e => {
          return { value: e[1], text: e[0] };
        },
      );
      if (definition) {
        this.settings['json'].config = {
          items: definition,
        };
        const out = this.settings['json'].value
        this.setOutputData(0, out);
        this.setOutputData(1, true);
        this.broadcastSettingsToClients();
      }
    } else this.setOutputData(1, false);
  }




}
Container.registerNodeType('json/json-enum-builder', JSONEnumBuilderNode);
