import { get, isEqual } from 'lodash';
import { Api } from '../../../../backend/api';
import { Entry } from '../../../../backend/entry';
import config from '../../../../config';
import { singleton as actor } from '../../../../promise-actor';
import { IOT_TYPES } from '../../../constants';
import { ContainerNode } from '../../../container-node';
import { BROADCAST, Node } from '../../../node';
import { NodeFactory } from '../../../node-factory';
import { SerializedNode, Side } from '../../../node-interface';
import { NodeSetting, SettingType } from '../../../node-io';
import EntryNode from './entry-node';
import Mixin from './mixin';

/**
 *
 * @param <T> Type of the Entry that this container manages
 */
export default abstract class ApiContainerNode<T extends Entry> extends ContainerNode implements EntryNode<T> {
  extra_node_types = this.childNodeType().filter(v => IOT_TYPES.includes(v));
  properties: {
    entry: T;
    is_protected: boolean;
  };
  clonable = false;
  movable = false;
  settingFields: {};
  entry = Mixin.prototype.entry.bind(this);
  entryId = Mixin.prototype.entryId.bind(this);
  deactivate = Mixin.prototype.deactivate.bind(this);
  createSettingForm = Mixin.prototype.createSettingForm.bind(this);
  getTakenIdentifiers = Mixin.prototype.getTakenIdentifiers.bind(this);
  makeIdentifier = Mixin.prototype.makeIdentifier.bind(this);
  findFreeIdentifier = Mixin.prototype.findFreeIdentifier.bind(this);
  protected api: Api<T>;

  constructor() {
    super();
    this.description = 'placeholder'; // FIXME
    this.api = this.initAPI();
    this.settings['name'] = NodeSetting.create({
                                                 description: 'Entry',
                                                 value: this.title,
                                                 type: SettingType.READONLY,
                                               });
  }

  abstract namePrefix(): string;

  abstract createEntry(): Promise<T>;

  entryFactory(): T {
    return this.api.factoryEntry();
  }

  updateEntry(body: T): Promise<any> {
    return this.api.update(body, this.entryId());
  }

  deleteEntry(): Promise<any> {
    return this.api.delete(this.entryId());
  }

  onAdded() {
    Mixin.prototype.onAdded.call(this);
    super.onAdded();
  }

  applyEntry(entry: T) {
    Mixin.prototype.applyEntry.call(this, entry);
    this.applyTitle();
    this.broadcastTitleToClients();
  }

  onAfterSettingsChange(old) {
    Mixin.prototype.onAfterSettingsChange.call(this);
    super.onAfterSettingsChange(old);
  }

  onCreated() {
    super.onCreated();
    const namePrefix = this.namePrefix();
    const nameSuffix = this.entry() ? ' ' + this.entryId() : '';
    this.settings['name'].value = namePrefix + nameSuffix;
    Mixin.prototype.onCreated.call(this);
  }

  onRemoved() {
    Mixin.prototype.onRemoved.call(this);
  }

  shouldRejectNodeType(type: string): boolean {
    // reject all IoT nodes as children, except the correct hierarchical child node type. Allow all others
    return !this.childNodeType().includes(type);
  }

  syncWithBackend() {
    if (this.side !== Side.server) return;
    actor.process(() => this.fetchChildren().then(children => {
                    const childIds = children.map(v => v.entryId());
                    const childNodes = this.collectChildNodes(this.sub_container.getNodes()).map(
                      n => n as EntryNode<Entry>);
                    const existingChildNodes = childNodes.filter(n => childIds.includes(n.entryId()));
                    const obsoleteChildNodes = childNodes.filter(
                      n => !n.properties.is_protected && !childIds.includes(n.entryId()),
                    );
                    Promise.all(obsoleteChildNodes.map(n => {
                      n.deactivate();
                      //TODO not yet send to client
                      return n.container.nodeRegistry().remove(n);
                    })).then(_ => this.processNextChildEntry(children, existingChildNodes));
                  }),
    );
  }

  onGetMessageToEditorSide({ action, payload }) {
    super.onGetMessageToEditorSide({ action, payload, key: null });
    if (action === BROADCAST.UPDATE_TITLE) this.applyTitle();
  }

  protected applyTitle() {
    super.applyTitle();
    this.container.runWithEditor(e => {
      if (get(e, 'renderer.container.id') === this.container.id) {
        e.emit('rerender-siderbar-containers');
      }
    });
  }

  protected abstract initAPI(): Api<T>;

  protected abstract childNodeType(): string[];

  /**
   * Fetch all current child entries of the main entry from the remote API
   */
  protected abstract fetchChildren(): Promise<Entry[]>;

  /**
   * When given an array of child-nodes, returns only the nodes that manage child entries of the main entry
   */
  protected abstract collectChildNodes(nodes: Node[]): Node[];

  /**
   * Recursively and asynchronously processes the next of the remaining entries from the remote API
   */
  protected processNextChildEntry(remaining: Entry[], childNodes: EntryNode<Entry>[]) {
    if (!remaining || remaining.length === 0) return;
    const entry = remaining.pop();
    let existingNode = childNodes.find(node => node.entryId() === entry.entryId());
    const callback = () => {
      if (existingNode instanceof ApiContainerNode) {
        (<ApiContainerNode<any>>existingNode).syncWithBackend();
      }
      this.processNextChildEntry(remaining, childNodes);
    };
    if (!existingNode) {
      // console.log('Creating child node for ' + this.title + ' ' + (entry.entryId() || ''));
      const maxVerticalPosition = Math.max(100, ...this.sub_container.getNodes(false).map(n => n.pos[1]));
      const pos: [number, number] = [250, maxVerticalPosition + config.backendSync.nodeDistance];
      // existingNode is used above! Leave this reassignment:
      this.sub_container.nodeRegistry()
          .register(NodeFactory.create(<SerializedNode>{ type: entry.nodeType(), properties: { entry }, pos }, true))
          .then(n => {
            existingNode = <EntryNode<Entry>>n;
            callback();
          });
    } else if (!isEqual(existingNode.entry(), entry)) {
      // console.log('Found existing node with outdated entry, updating...');
      existingNode.applyEntry(entry);
      existingNode.persistConfiguration(callback);
    } else {
      callback();
    }
  }
}
