import Debounce from 'lodash-decorators/debounce';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType } from '../../node-io';

export class UiNode extends Node {
  titlePrefix: string; // title on ui panel
  defaultTitle: string;
  template: string;
  uiElementType: string;
  uiPanelValue;

  // onGetMessageToDashboardSide
  // onDashboardElementGetNodeState - this will override default logic in dashboard-server-socket
  // onDashboardElementSetNodeState - this will override default logic in dashboard-server-socket

  constructor(defaultTitle: string, uiElementType: string, properties: any) {
    super();
    this.name = properties && properties.name;
    this.titlePrefix = defaultTitle;
    this.defaultTitle = defaultTitle;
    this.uiElementType = uiElementType;
    this.isDashboardNode = true;
    this.settings['title'] = NodeSetting.create({
      description: 'Title',
      value: this.titlePrefix,
      type: SettingType.STRING,
    });
    this.settings['ui-panel'] = NodeSetting.create({ description: 'Ui Panel Name', type: SettingType.STRING });
  }

  init() {
    if (this.settings['ui-panel']) {
      this.uiPanelValue = this.settings['ui-panel'] && this.settings['ui-panel'].value;
    }
  }

  onCreated() {
    this.settings['ui-panel'].value = this.uiPanelValue || this.container.name;
    if (this.side == Side.server) {
      this.container.dashboard.onNodeChangePanelOrTitle(
        this,
        this.settings['ui-panel'].value,
        this.settings['title'].value,
      );
    }
  }

  onAdded() {
    this.changeTitle(this.settings['title'].value);
  }

  onBeforeSettingsChange(newSettings, newName) {
    if (this.side == Side.server) {
      this.name = newName;
      this.container.dashboard.onNodeChangePanelOrTitle(
        this,
        newSettings['ui-panel'].value,
        newSettings['title'].value,
      );
    }
    this.changeTitle(newSettings['title'].value);
  }

  changeTitle(title: string) {
    if (this.defaultTitle === title) this.title = title;
    else this.title = this.defaultTitle + ': ' + title;
  }

  onRemoved() {
    if (this.side == Side.server) this.container.dashboard.onNodeRemoved(this);
  }

  setState(state: any, sendToDashboard = true) {
    this.properties['state'] = state;

    // if called from constructor
    if (this.container == null) return;

    if (sendToDashboard) {
      let m = { id: this.id, cid: this.container.id, state: state };
      if (this.side == Side.server) {
        // send state to all clients in the room
        let socket = this.container.server_dashboard_socket;
        let panelName = this.settings['ui-panel'].value;
        socket.in('' + panelName).emit('dashboardElementGetNodeState', m);
      }
    }

    this.storeInDatabase();
  }

  @Debounce(1000)
  storeInDatabase() {
    if (this.container && this.container.db) {
      this.container.db.updateNode(this.id, this.container.id, {
        $set: { properties: this.properties },
      });
    }
  }

  sendMessageToDashboardSide(mess: any) {
    let m = { id: this.id, cid: this.container.id, message: mess };
    if (this.side == Side.server) {
      let socket = this.container.server_dashboard_socket;
      let panelName = this.settings['ui-panel'].value;
      socket.in('' + panelName).emit('nodeMessageToDashboardSide', m);
    } else {
      this.container.client_socket.emit('nodeMessageToDashboardSide', m);
    }
  }

  getState() {
    return this.properties['state'];
  }
}
