import { PointValue } from '../../../../backend/models/point-model';
import { NodeStoreItem } from '../../../../utils/pattern';
import { OneIdentifierManyNodeStore, OneIdentifierOneNodeStore } from '../model/container-node-store';

export class MqttPointStoreItem implements NodeStoreItem {

  identifier: string;
  nodeId: string;
  enabled: boolean;
  lenient?: boolean;
  publisher: {
    identifier?: string,
    prev?: NodeStoreItem,
    data?: any,
    callback?: Function
  };
  subscriber: {
    identifier?: string,
    prev?: NodeStoreItem,
    callback?: Function
  };

  constructor(identifier: string, nodeId: string, enabled: boolean,
              publisher: { identifier?: string; prev?: NodeStoreItem; data?: any; callback?: Function },
              subscriber: { identifier?: string; prev?: NodeStoreItem; callback?: Function }) {
    this.identifier = identifier;
    this.nodeId = nodeId;
    this.enabled = enabled;
    this.publisher = publisher;
    this.subscriber = subscriber;
  }

  toPublisher(): NodeStoreItem {
    return this.to(this.publisher as NodeStoreItem);
  }

  toSubscriber(): NodeStoreItem {
    return this.to(this.subscriber as NodeStoreItem);
  }

  private to(ps: NodeStoreItem) {
    return {
      identifier: ps?.identifier ?? this.identifier,
      nodeId    : this.nodeId,
      enabled   : this.enabled,
      prev      : ps?.prev,
      data      : ps?.data,
      lenient   : this.lenient,
      callback  : ps?.callback,
    } as NodeStoreItem;
  }
}

//TODO: current only per MQTT topic per point node. Can enhance to support multiple point nodes with same topic
export class MqttPublisherStore extends OneIdentifierOneNodeStore<PointValue> {

  protected alreadyExistedMsg(payload: NodeStoreItem): string {
    return `Already exist MQTT publisher bind to topic ${payload?.identifier}`;
  }

}

export class MqttSubscriberStore extends OneIdentifierManyNodeStore<any> {

  protected alreadyExistedMsg(payload: NodeStoreItem): string {
    return `Already existed MQTT subscriber bind to topic ${payload?.identifier} with node id ${payload.nodeId}`;
  }

}
