import { Node } from '../../nodes/node';
import { SerializedNode } from '../../nodes/node-interface';
import { SerializableHelper } from '../../nodes/serializable';
import { IdGenerator } from '../../utils/id-generator';
import { db_logger as logger } from '../../utils/loggers';
import { AbstractDatabase } from '../database/BaseDatabase';
import NodeDatabase from '../database/NodeDatabase';

export default class NodeDataSource extends AbstractDatabase implements NodeDatabase {

  addNode(node: Node, callback?: (err?: Error, doc?: SerializedNode) => void) {
    let ser_node = SerializableHelper.serialize<SerializedNode>(node);
    ser_node['_id'] = IdGenerator.createNodeId(ser_node.cid, ser_node.id);

    this.db.insert(ser_node, function (err, doc) {
      if (err) logger.error(err.message);
      if (callback) callback(err, doc);
    });
  }

  batchInsertNodes(nodes: SerializedNode[], callback?: (err?: Error, docs?: SerializedNode[]) => void) {
    this.db.insert(nodes.map(n => {
      n['_id'] = IdGenerator.createNodeId(n.cid, n.id);
      return SerializableHelper.serialize<SerializedNode>(n);
    }), function (err, doc) {
      if (err) logger.error(err.message);
      if (callback) callback(err, doc);
    });
  }

  batchDeleteNodes(query, callback?: (err?: Error, docs?: number) => void) {
    this.db.remove(query, { multi: true }, function (err, docs) {
      if (err) logger.warn(err.message);
      if (callback) callback(err, docs);
    });
  }

  getNodes(callback?: (err?: Error, docs?: Array<SerializedNode>) => void) {
    this.db.find({}, function (err, docs) {
      if (err) logger.warn(err);
      if (callback) callback(err, docs);
    });
  }

  getNode(id: number, cid: number, callback?: (err?: Error, doc?: SerializedNode) => void) {
    let _id = IdGenerator.createNodeId(cid, id);
    this.db.findOne({ _id: _id }, function (err, doc: any) {
      if (err) logger.warn(err.message);
      if (callback) callback(err, doc);
    });
  }

  getNodeType(type: string, callback?: (err?: Error, docs?: Array<SerializedNode>) => void) {
    this.db.find({ type: type }, function (err, docs) {
      if (err) logger.warn(err.message);
      if (callback) callback(err, docs);
    });
  }

  listNodes(query, callback?: (err?: Error, docs?: Array<any>) => void) {
    this.db.find(query, function (err, docs) {
      if (err) logger.warn(err.message);
      if (callback) callback(err, docs);
    });
  }

  updateNode(id: number, cid: number, update: any, callback?: (err?: Error) => void) {
    let _id = IdGenerator.createNodeId(cid, id);
    this.db.update({ _id: _id }, update, {}, function (err, updated) {
      if (err) logger.error(err.message);
      if (updated == 0) logger.warn(`Can't update node [${cid}/${id}]. Document not found.`);
      if (callback) callback(err);
    });
  }

  removeNode(id: number, cid: number, callback?: (err?: Error) => void) {
    let _id = IdGenerator.createNodeId(cid, id);
    this.db.remove({ _id: _id }, {}, function (err, removed) {
      if (err) logger.error(err.message);
      if (removed == 0) logger.warn(`Can't remove. Document not found.`);
      if (callback) callback(err);
    });
  }

  dropNodes(callback?: (err?: Error) => void) {
    this.db.remove({}, { multi: true }, function (err) {
      if (err) logger.error(err.message);
      if (callback) callback(err);
    });
  }

  protected dbName(): string {
    return `nodes`;
  }
}
