import { isNull } from '../../../../utils/helper';
import { EDGE_28_NETWORK, EDGE_28_OUTPUT } from '../../../constants';
import { Container } from '../../../container';
import { NODE_INFO } from '../../../node-info';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import BACnetEnumsUnits from '../../../utils/points/BACnet-enums-units';
import HistoryBase from '../../history/HistoryBase';
import { edgeApiVer, edgeIp, edgePort } from './edge-constant';
import EdgeGPIOUtils from './edge-gpio-utils';
import Edge28Utils from './edge-utils';

enum OUTPUT_POINT_TYPE {
  DO = 1,
  UO_AS_DIGITAL = 2,
  UO_AS_0_10DC = 3,
}

enum OUTPUT_POINT_TYPE_TEXT {
  DO = 'DO',
  UO_AS_DIGITAL = 'UO As Digital',
  UO_AS_0_10DC = 'UO As 0-10dc',
}

class Edge28OutputPointNode extends HistoryBase {
  uuid: Number = null;
  deviceContainerID: Number = null;
  topic: string = null;
  newSetting = false;
  lastSendTime;

  // node inputs
  inInput: number = 0;
  inAlarmTrigger: number = 1;
  inHistoryTrigger: number = 2;

  constructor() {
    super();
    this.title = NODE_INFO["protocols/nube"]["edge-28-output"].title;
    this.description = NODE_INFO["protocols/nube"]["edge-28-output"].description;

    this.addInput('input', Type.ANY);
    this.addOutput('output', Type.NUMBER);
    this.addOutput('output-json', Type.JSON);
    this.properties['pointVal'] = null;

    // Default point Settings (Will be in point class)
    this.settings['pointEnable'] = NodeSetting.create({
      description: 'Point enable',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['pointType'] = NodeSetting.create({
      description: 'Point Type',
      value: OUTPUT_POINT_TYPE.DO,
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: OUTPUT_POINT_TYPE.DO, text: OUTPUT_POINT_TYPE_TEXT.DO },
          { value: OUTPUT_POINT_TYPE.UO_AS_DIGITAL, text: OUTPUT_POINT_TYPE_TEXT.UO_AS_DIGITAL },
          { value: OUTPUT_POINT_TYPE.UO_AS_0_10DC, text: OUTPUT_POINT_TYPE_TEXT.UO_AS_0_10DC },
        ],
      },
    });
    this.settings['pointNumber'] = NodeSetting.create({
      description: 'Point',
      value: 'UO1',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'UO1', text: `UO1` },
          { value: 'UO2', text: `UO2` },
          { value: 'UO3', text: `UO3` },
          { value: 'UO4', text: `UO4` },
          { value: 'UO5', text: `UO5` },
          { value: 'UO6', text: `UO6` },
          { value: 'UO7', text: `UO7` },
          { value: 'DO1', text: `DO1` },
          { value: 'DO2', text: `DO2` },
          { value: 'DO3', text: `DO3` },
          { value: 'DO4', text: `DO4` },
          { value: 'DO5', text: `DO5` },
          { value: 'R1', text: `R1` },
          { value: 'R2', text: `R2` },
        ],
      },
    });
    this.settings['units_group'] = NodeSetting.create({
      description: 'Units (Save to get units)',
      type: SettingType.GROUP,
    });
    this.settings['unitsType'] = NodeSetting.create({
      description: 'Units Category',
      type: SettingType.DROPDOWN,
      config: {
        items: BACnetEnumsUnits.unitCategory,
      },
    });
    this.settings['units'] = NodeSetting.create({
      description: 'Units Type',
      value: BACnetEnumsUnits.COMMON_METRIC.NO_UNITS,
      type: SettingType.DROPDOWN,
    });

    this.setSettingsConfig({
      groups: [
        { pointNumber: {}, pointType: {} },
        { mathFunc: {}, mathValue: {} },
        { unitsType: {}, units: {} },
      ],
      conditions: {
        mathValue: setting => {
          const val = setting['mathFunc'].value;
          return ![10, 0].includes(val);
        },
      },
    });

    this.addHistorySettingsConfig();
  }

  onAdded() {
    super.onAdded();
    this.updateTitle();
    if (this.side !== Side.server) return;
    this.EXECUTE_INTERVAL = 60000;
    this.lastSendTime = new Date().valueOf();
    this.inputChange();
  }

  async onExecute() {
    if (this.side !== Side.server) return;
    if (new Date().valueOf() - 1000 > this.lastSendTime) await this.onInputUpdated();
  }

  pointType(type) {
    switch (type) {
      case OUTPUT_POINT_TYPE.DO:
        return 'do';
      case OUTPUT_POINT_TYPE.UO_AS_DIGITAL:
        return 'uo';
      case OUTPUT_POINT_TYPE.UO_AS_0_10DC:
        return 'uo';
      default:
        this.debugWarn('Unknown type of object');
    }
  }

  pointTypeText(type) {
    switch (type) {
      case OUTPUT_POINT_TYPE.DO:
        return OUTPUT_POINT_TYPE_TEXT.DO;
      case OUTPUT_POINT_TYPE.UO_AS_DIGITAL:
        return OUTPUT_POINT_TYPE_TEXT.UO_AS_DIGITAL;
      case OUTPUT_POINT_TYPE.UO_AS_0_10DC:
        return OUTPUT_POINT_TYPE_TEXT.UO_AS_0_10DC;
      default:
        this.debugWarn('Unknown type of object');
    }
  }

  async onInputUpdated() {
    await super.onInputUpdated();
    this.inputChange();
  }

  onRemoved() {
    super.onRemoved();
  }

  async onAfterSettingsChange() {
    await super.onAfterSettingsChange();
    this.updateTitle();
    const unitsType = this.settings['unitsType'].value;
    this.settings['units'].config = {
      items: BACnetEnumsUnits.unitType(unitsType),
    };
    this.broadcastSettingsToClients();
    this.inputChange();
  }

  inputChange() {
    if (this.side !== Side.server) return;
    const inputVal: number = this.getInputData(this.inInput);
    this.apiCall(inputVal);
  }

  updateTitle() {
    const pointNumber = this.settings['pointNumber'].value;
    this.title = `Edge-28-Output: (${pointNumber} as ${this.pointTypeText(this.settings['pointType'].value)})`;
    this.broadcastTitleToClients();
  }

  sendJson() {
    return {
      name: this.name,
      pointValue: this.properties['pointVal'],
      enable: this.settings['pointEnable'].value,
      units: this.settings['units'].value,
      pointNumber: this.settings['pointNumber'].value,
      pointType: this.settings['pointType'].value,
    };
  }

  apiCall(inputVal: number) {
    if (this.side !== Side.server) return;
    if (this.settings['pointEnable'].value === false) return;
    const pointNumber = this.settings['pointNumber'].value;
    let outVal: number = null;
    const nodeVal = inputVal;
    if (inputVal === undefined || inputVal === null) return;

    if (this.settings['pointType'].value === OUTPUT_POINT_TYPE.DO) {
      // if type = DO 0 is off 1 is on
      if (typeof inputVal === 'boolean') {
        outVal = inputVal ? 1 : 0;
      } else if (typeof inputVal === 'string') {
        if (inputVal === '1' || inputVal === 'true') {
          outVal = 1;
        } else if (inputVal === '0' || inputVal === 'false') {
          outVal = 0;
        }
      } else if (typeof inputVal === 'number') {
        if (inputVal >= 1) {
          outVal = 1;
        } else if (inputVal < 1) outVal = 0; // drive to 0vdc
      } else this.debugInfo(`ERROR: input value must be a int 1 or 0 or a bool`);
    } else if (this.settings['pointType'].value === OUTPUT_POINT_TYPE.UO_AS_DIGITAL) {
      // UO as a DO
      // if type = DO
      if (typeof inputVal === 'boolean') {
        if (inputVal) {
          outVal = 0;
        } else outVal = 100;
      } else if (typeof inputVal === 'string') {
        if (inputVal === '1' || inputVal === 'true') {
          outVal = 0;
        } else if (inputVal === '0' || inputVal === 'false') {
          outVal = 100;
        }
      } else if (typeof inputVal === 'number') {
        if (inputVal >= 1) {
          outVal = 0; // drive to 12vdc
        } else if (inputVal < 1) outVal = 100; // drive to 0vdc
      } else this.debugInfo(`ERROR: input value must be a int 1 or 0 or a bool`);
    } else if (this.settings['pointType'].value === OUTPUT_POINT_TYPE.UO_AS_0_10DC) {
      // if type = UO
      if (typeof inputVal === 'number') {
        if (inputVal >= 100) outVal = EdgeGPIOUtils.scaleToGPIOValue(100, 0, 100);
        // this is to cap writing a value > 12vdc
        else {
          inputVal = inputVal * 0.9839; // * 0.9839 this is to software cal the UOs
          outVal = EdgeGPIOUtils.scaleToGPIOValue(inputVal, 0, 100);
        }
      } else if (typeof inputVal === 'string') {
        this.debugInfo(`ERROR: input value must be a float`);
      }
    }

    if (isNull(outVal)) return;
    const pointType = this.pointType(this.settings['pointType'].value);
    Edge28Utils.writePointValue(edgeIp, edgePort, edgeApiVer, pointType, pointNumber, outVal, 16)
      .then(e => {
        this.properties['pointVal'] = nodeVal;
        this.setOutputData(0, nodeVal);
        this.setOutputData(1, this.sendJson()); // json
        this.lastSendTime = new Date().valueOf();
      })
      .catch(err => {
        this.debugInfo(`ERROR: getting edge point type: ${pointType} ${err}`);
      });
  }
}

Container.registerNodeType(EDGE_28_OUTPUT, Edge28OutputPointNode, EDGE_28_NETWORK);
