import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class RateLimitNode extends Node {
  lastEnable: boolean;
  lastInterval: number;
  currentVal: number;
  timeoutFunc;


  constructor() {
    super();
    this.title = NODE_INFO["num-transform"]["rate-limit"].title;
    this.description = NODE_INFO["num-transform"]["rate-limit"].description; 
    this.addInput('input', Type.NUMBER);
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable', false);
    this.addInputWithSettings('step', Type.NUMBER, 1, 'Step Size', false);
    this.addInputWithSettings('interval', Type.NUMBER, 1, 'Update Interval', false);
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
    this.addOutput('output', Type.NUMBER);
    this.lastInterval = 1000;
    this.lastEnable = true;
    this.currentVal = 0;
  }

  onAdded() {
 
    this.lastInterval = 1000;
    this.lastEnable = true;
    this.currentVal = 0;
    this.setOutputData(0, null);
    this.inputs[3]['name'] = `[interval] (${this.settings['time'].value})`;
    this.timeoutFunc = setInterval(() => {
      this.step();
    }, this.lastInterval);
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let input = this.getInputData(0);
    if (input == null) {
      clearInterval(this.timeoutFunc);
      this.setOutputData(0, null);
      this.lastEnable = false;
      return;
    }
    let interval = this.getInputData(3);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    const enable = this.getInputData(1);
    if (enable && !this.lastEnable) {
      this.currentVal = 0;
      this.timeoutFunc = setInterval(() => {
        this.step();
      }, interval);
    } else if (!enable && this.lastEnable) {
      clearInterval(this.timeoutFunc);
      this.setOutputData(0, null);
    } else if (enable && this.lastEnable && interval !== this.lastInterval) {
      clearInterval(this.timeoutFunc);
      this.timeoutFunc = setInterval(() => {
        this.step();
      }, interval);
    }
    this.lastInterval = interval;
    this.lastEnable = enable;
  }

  step() {
    const step = this.getInputData(2);
    let input = this.getInputData(0);
    if (input >= this.currentVal) {
      this.currentVal = this.currentVal + step;
      if (this.currentVal > input) this.currentVal = input;
      this.setOutputData(0, this.currentVal);
    } else if (input < this.currentVal) {
      this.currentVal = this.currentVal - step;
      if (this.currentVal < input) this.currentVal = input;
      this.setOutputData(0, this.currentVal);
    }
  }

  onAfterSettingsChange() {
    this.inputs[3]['name'] = `[interval] (${this.settings['time'].value})`;
    this.onInputUpdated();
  }
}
Container.registerNodeType('num-transform/rate-limit', RateLimitNode);
