import { isNull } from '../../../../utils/helper';
import { MODBUS_RTU_DEVICE, MODBUS_RTU_NETWORK } from '../../../constants';
import { Container } from '../../../container';
import { ContainerNode } from '../../../container-node';
import { NodeState } from '../../../node';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';

const net = require('net');
class ModbusSerialDeviceNode extends ContainerNode {
  tcpServer = null;
  db = null;
  uuid: Number = null;

  // node outputs
  outStatus = 0;
  outError = 1;
  outMessageJson = 2;

  constructor() {
    super();
    this.title = 'Modbus Device';
    this.description =
      `## Description\n ` +
      ` This node is used as a modbus TCP or RTU(rs485) modbus device.\n ` +
      ` The node is to be added inside a modbus network node (The network details will be added in the network node)\n ` +
      ` The points for the device will be added inside this node \n ` +
      `   \n ` +
      `### Enable\n ` +
      `   \n ` +
      ` This will enable/disable the device from polling \n ` +
      `   \n ` +
      `### Modbus Address\n ` +
      `   \n ` +
      ` If device is RTU type set the slave modbus address\n ` +
      `   \n ` +
      `### Device Timeout\n ` +
      `   \n ` +
      ` This setting is used for the polling. This is the amount of time the polling will wait before moving onto the next register or device to poll\n ` +
      `   \n ` +
      `### Network Type\n ` +
      `   \n ` +
      `If type is TCP set select type TCP and set the target device IP and port \n ` +
      `If type is RTU set the device address as per setting **Modbus Address** \n ` +
      `   \n ` +
      `### Point address starting offset\n ` +
      `   \n ` +
      `Modbus point address offset(Base 1 subtracts 1 to the address) \n ` +
      `If set to Base 1 this will make for example point address 1 (from the manufacture data sheet) equal to 0\n ` +
      `The default setting is Base 1\n ` +
      `   \n ` +
      `## Node outputs\n ` +
      `   \n ` +
      ` If device is TCP when the node is added or after the setting a change a device ping will be sent to see if the device is online\n ` +
      ` If device is online **status** will be sent to true and the **error** will be sent to false. If the device if offline the **status** will be sent to false and the **error** true \n ` +
      `   \n `;
    this.addOutput('status', Type.BOOLEAN);
    this.addOutput('error', Type.STRING);
    this.addOutput('message', Type.JSON);
    this.properties['address'] = 0;
    this.settings['deviceEnable'] = NodeSetting.create({
      description: 'Device enable',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.addInputWithSettings('address', Type.NUMBER, 1, 'Modbus address', false);
    this.settings['deviceTimeout'] = NodeSetting.create({
      description: 'Device Timeout Delay Setting (MS)', //TODO ADD CAP/LIMIT
      value: 2000,
      type: SettingType.NUMBER,
    });
    this.settings['transport'] = NodeSetting.create({
      description: 'TCP or RTU/RS485',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'rtu', text: 'Modbus RTU over Serial 485' },
          { value: 'tcp', text: 'Modbus TCP/IP' },
        ],
      },
      value: 'rtu',
    });
    this.settings['ipAddress'] = NodeSetting.create({
      description: 'Network IP Address',
      value: '0.0.0.0',
      type: SettingType.STRING,
    });
    this.settings['ipPort'] = NodeSetting.create({
      description: 'Network Port Number',
      value: 502,
      type: SettingType.NUMBER,
    });
    this.settings['deviceAddressOffset'] = NodeSetting.create({
      description: 'Point address starting offset',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 1, text: 'Base 1' },
          { value: 0, text: 'Base 0' },
        ],
      },
      value: 1,
    });
    this.setSettingsConfig({
      conditions: {
        ipAddress: setting => {
          return setting['transport'].value === 'tcp';
        },
        ipPort: setting => {
          return setting['transport'].value === 'tcp';
        },
      },
    });
  }

  onAdded() {
    super.onAdded();
    this.onInputUpdated()
    this.broadcastNameToClients();
    this.updateTitle()
    this.nodeColour()
    if (this.side !== Side.server) return;
    this.uuid = this.id;
    if (this.settings['transport'].value === 'tcp') {
      this.ping();
    }
  }


  updateTitle() {
    const address = this.getInputData(0);
    const transport = this.settings['transport'].value;
    const ipAddress = this.settings['ipAddress'].value;
    const ipPort = this.settings['ipPort'].value;
    if (transport === 'tcp') {
      this.title = `Modbus Device (Type: ${this.settings['transport'].value}, AD: ${address} , IP: ${ipAddress}:${ipPort})`;
      this.broadcastSettingsToClients();
    } else {
      this.title = `Modbus Device (Type: ${this.settings['transport'].value}, AD: ${address})`;
      this.broadcastSettingsToClients();
    }
 
  }

  ping() {
    const host = [this.settings['ipAddress'].value, this.settings['ipPort'].value];
    var sock = new net.Socket();
    sock.setTimeout(5000);
    sock
      .on('connect', () => {
        this.setOutputData(this.outStatus, true, true);
        this.setOutputData(this.outError, false, true);
        this.setOutputData(this.outMessageJson, host[0] + ':' + host[1] + ' is up.', true);
        sock.destroy();
      })
      .on('error', e => {
        this.setOutputData(this.outError, true, true);
        this.setOutputData(this.outStatus, false, true);
        this.setOutputData(this.outMessageJson, host[0] + ':' + host[1] + ' is down: ' + e.message, true);
      })
      .on('timeout', e => {
        this.setOutputData(this.outError, true, true);
        this.setOutputData(this.outStatus, false, true);
        this.setOutputData(this.outMessageJson, host[0] + ':' + host[1] + ' is down: timeout', true);
      })
      .connect(host[1], host[0]);
  }



  onAfterSettingsChange(oldSettings: any) {
    this.updateTitle()
    this.onInputUpdated()
    this.nodeColour()
    super.onAfterSettingsChange(oldSettings);
    if (this.side !== Side.server) return;
    if (this.settings['transport'].value === 'tcp') {
      this.ping();
    }


  }


  onInputUpdated() {
      const address = this.getInputData(0);
      if (!isNull(address)) {
        this.properties['address'] = address;
        this.persistProperties(false, true);
      } else {
        this.properties['address'] = 0
        this.persistProperties(false, true);
      }
  }


  subscribeMessage(e) {
    this.setOutputData(0, true, true);
    this.setOutputData(1, false, true);
    this.setOutputData(2, null, true);
    this.nodeColour(false)
  }

  subscribeError(e) {
    if (e === null) return;
    this.setOutputData(0, false, true);
    this.setOutputData(1, true, true);
    this.setOutputData(2, e, true);
    this.nodeColour(true)
  }

  nodeColour(err?: boolean) {
    const deviceEnable = this.settings['deviceEnable'].value
    if (deviceEnable === false) {
      this.setNodeState(NodeState.DISABLED);
    } else if (deviceEnable === true && !err){
      this.setNodeState(NodeState.NORMAL)
    } else if (err === true){
      this.setNodeState(NodeState.ERROR)
    }
  }
}

Container.registerNodeType(MODBUS_RTU_DEVICE, ModbusSerialDeviceNode, MODBUS_RTU_NETWORK);
