import { Entry } from '../../../../backend/entry';
import ApiContainerNode from './api-container-node';

/**
 * Container node that contains a collection of API synced child nodes, but has itself no entity
 * or parent
 */
export default abstract class TopLevelApiContainerNode<C extends Entry> extends ApiContainerNode<C> {
  settingFields = {};

  entryFactory = () => null;

  applyEntry = () => {
    // Top level container's entry is always 'null'
    this.title = this.namePrefix();
    this.sub_container.name = this.title;
  };

  onAdded = () => {
    this.ignore_remove = true;
    this.removable = false;
    this.movable = false;
    this.applyEntry();
    this.syncWithBackend();
  };

  createEntry = () => Promise.resolve(null);

  updateEntry = (body: any) => Promise.resolve(null);

  deleteEntry = () => Promise.resolve(null);

  namePrefix = () => this.title;

  getTakenIdentifiers = () => Promise.resolve([]);

  makeIdentifier = (n: number): string => null;

  findFreeIdentifier = (): Promise<string> => Promise.resolve(null);
}
