import { Container } from '../../container';
import { Node } from '../../node';
import UuidUtils from '../../utils/uuid-utils';
import { NODE_INFO } from '../../node-info';

class UuidNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO["custom-function"].uuid.title;
    this.description =  NODE_INFO["custom-function"].uuid.description;
    this.addInput('trigger');
    this.addOutput('uuid-out-8');
    this.addOutput('uuid-out-12');
    this.addOutput('uuid-out-28');
  }

  onInputUpdated() {
    const out8 = UuidUtils.create8DigId();
    const out12 = UuidUtils.create12DigId();
    const out28 = UuidUtils.createUUID();
    this.setOutputData(0, out8);
    this.setOutputData(1, out12);
    this.setOutputData(2, out28);

  }

  onAfterSettingsChange() {
    this.onInputUpdated()
  }

}

Container.registerNodeType('custom-function/uuid', UuidNode);
