import { Node } from '../../../node';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import { Container } from '../../../container';
import Utils from '../../../utils'
import * as loraRawCrypt from './lora-raw-crypt';

enum LORA_DATA_TYPE_NAMES {
  INT8 = 0,
  UINT8,
  INT16,
  UINT16,
  INT32,
  UINT32,
  FLOAT,
}

const LORA_DATA_TYPE_LENGTHS = [
  1, // INT8
  1, // UINT8
  2, // INT16
  2, // UINT16
  4, // INT32
  4, // UINT32
  4, // FLOAT
];

export class LoRaRawPointsInput extends Node {
  payloadDefinition: Array<LORA_DATA_TYPE_NAMES> = [];
  addressPrefix: string = 'FFFFFF';
  address: string = '';
  keyBuffer: Buffer = null;
  nonce = 0;
  outputOffset = 3; // 3 static outputs before dynamic outputs

  constructor() {
    super();
    this.title = `Nube LoRaRAW Points In`;
    this.description = 'This node parses Nubes LoRa Points protocol payload';
    this.settings['enable'] = NodeSetting.create({
      description: 'enable',
      type: SettingType.BOOLEAN,
      value: true,
    });
    this.settings['senderId'] = NodeSetting.create({
      description: 'sender Id [0-254]',
      type: SettingType.NUMBER,
      value: 0,
    });
    this.settings['port'] = NodeSetting.create({
      description: 'port number [0-254]',
      type: SettingType.NUMBER,
      value: 0,
    });
    this.settings['types'] = NodeSetting.create({
      description: 'Types # = [8, 16, 23]',
      type: SettingType.READONLY,
      // value: 'INT8, UINT8, INT16, UINT16, INT32, UINT32, FLOAT',
      value: 'INT#, UINT#, FLOAT',
    });
    this.settings['definition'] = NodeSetting.create({
      description: 'payload definition (\',\' separated)',
      type: SettingType.STRING,
      value: '',
    });
    this.settings['key'] = NodeSetting.create({
      description: 'encryption key (hex)',
      type: SettingType.STRING,
      value: '',
    });
    this.settings['other_group'] = NodeSetting.create({
      description: 'Advanced',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['addrPrefix'] = NodeSetting.create({
      description: 'Address Prefix [6]',
      type: SettingType.STRING,
      value: 'FFFFFF',
    });
    this.settings['nonceLen'] = NodeSetting.create({
      description: 'Nonce Length',
      type: SettingType.NUMBER,
      value: 2,
    });
    this.settings['frameCounterEnabled'] = NodeSetting.create({
      description: 'frame counters (**TBA**)',
      type: SettingType.BOOLEAN,
      value: false,
    });

    this.addInput('rawInput', Type.STRING);
    this.addOutput('error', Type.STRING);
    this.addOutput('rssi', Type.NUMBER);
    this.addOutput('snr', Type.NUMBER);
  }

  init() {
    this.updateSettings();
    this.updateOutputs(this.payloadDefinition);
  }

  onAdded() {
    this.updateSettings();
  }

  onInputUpdated() {
    if (this.keyBuffer === null) {
      return;
    }
    let data: string = this.getInputData(0) || '';
    if (this.settings['enable'].value === false) return;
    if (!data || data.length < (24 * 2) || data.substring(0, 8).toUpperCase() !== this.address) {
      return;
    }

    // remove newline
    if (data.substring(data.length - 2, data.length) === '\r\n') {
      data = data.substring(0, data.length - 2);
    } else if (data.length % 2 === 1 && (data[data.length - 1] === '\n' || data[data.length - 1] === '\r')) {
      data = data.substring(0, data.length - 1);
    }



    let encryptedBuffer = Buffer.from(data, 'hex');
    let [dataBuffer, err] = loraRawCrypt.loraRawDecryptBuffer(encryptedBuffer.slice(0, 4), encryptedBuffer.slice(4, encryptedBuffer.length - 2), this.keyBuffer);
    if (err !== loraRawCrypt.LORA_RAW_ERROR.LRERR_NOERR) {
      this.setOutputData(0, 'LoRaRaw Crypt ERR: ' + err);
      return;
    }
    let rssi = encryptedBuffer.readInt8(encryptedBuffer.length - 2);
    let snr = encryptedBuffer.readUInt8(encryptedBuffer.length - 1);


    let startPos = this.settings['nonceLen'].value // skip nonce
    const port = dataBuffer[startPos];
    if (port !== this.settings['port'].value) {
      return;
    }

    // PARSING

    const parseError = msg => {
      this.setOutputData(0, 'parse error: ' + msg);
    };

    // convert hex string to buffer
    let buffer = dataBuffer.slice(startPos + 1); //skip port
    let outputValues = new Array<number>(this.payloadDefinition.length);
    let pointParsed = new Array<boolean>(this.payloadDefinition.length);
    pointParsed.fill(false);

    let i = 0;
    for (let pointCount = 0; pointCount < this.payloadDefinition.length; pointCount++) {
      let point = buffer.readUInt8(i++);
      let type_length = 0;

      if (point >= this.payloadDefinition.length) {
        parseError('invalid point ' + point);
        return;
      }
      if (pointParsed[point]) {
        parseError('Bad payload. Point already parsed: ' + point);
        return;
      }

      type_length = LORA_DATA_TYPE_LENGTHS[this.payloadDefinition[point]];

      if (buffer.length - i < type_length) {
        parseError('invalid length');
        return;
      }

      switch (this.payloadDefinition[point]) {
        case LORA_DATA_TYPE_NAMES.INT8:
          outputValues[point] = buffer.readInt8(i);
          break;

        case LORA_DATA_TYPE_NAMES.UINT8:
          outputValues[point] = buffer.readUInt8(i);
          break;

        case LORA_DATA_TYPE_NAMES.INT16:
          outputValues[point] = buffer.readInt16LE(i);
          break;

        case LORA_DATA_TYPE_NAMES.UINT16:
          outputValues[point] = buffer.readUInt16LE(i);
          break;

        case LORA_DATA_TYPE_NAMES.INT32:
          outputValues[point] = buffer.readInt32LE(i);
          break;

        case LORA_DATA_TYPE_NAMES.UINT32:
          outputValues[point] = buffer.readUInt32LE(i);
          break;

        case LORA_DATA_TYPE_NAMES.FLOAT:
          outputValues[point] = buffer.readFloatLE(i);
          break;
      }
      pointParsed[point] = true;
      i += type_length;
    }

    for (let i = 0; i < outputValues.length; i++) {
      this.setOutputData(i + this.outputOffset, outputValues[i]);
    }
    this.setOutputData(1, rssi);
    this.setOutputData(2, snr);
    this.setOutputData(0, null);
  }

  onAfterSettingsChange() {
    if (!this.updateSettings()) {
      return;
    }
    this.updateOutputs(this.payloadDefinition);
    this.onInputUpdated();
  }

  private updateSettings() {
    this.settings['senderId'].value = Utils.clamp(this.settings['senderId'].value, 0, 254);
    this.settings['port'].value = Utils.clamp(this.settings['port'].value, 0, 254);
    this.settings['nonceLen'].value = Utils.clamp(this.settings['nonceLen'].value, 1, 4);
    this.payloadDefinition = this.stringToDataTypes(this.settings['definition'].value);
    let addrPrefix = this.settings['addrPrefix'].value;
    this.addressPrefix = addrPrefix && addrPrefix.length === 6 ? addrPrefix : 'FFFFFF';
    this.address = this.addressPrefix + ('0' + this.settings['senderId'].value.toString(16)).substr(-2);
    this.address = this.address.toUpperCase();
    if (this.settings['key'].value.length !== 32) {
      this.setOutputData(0, 'invalid key');
      this.keyBuffer = null;
      return false;
    }
    this.keyBuffer = Buffer.from(this.settings['key'].value, 'hex');
    this.setOutputData(0, null);
    return true;
  }

  private updateOutputs(data) {
    if (this.getOutputsCount() - this.outputOffset !== data.length) {
      const outputsCount = this.getOutputsCount();
      for (let i = this.outputOffset; i < outputsCount; i++) {
        this.removeOutput(i);
      }
      for (let i = 0; i < data.length; i++) {
        this.addOutput(i.toString(), Type.NUMBER);
      }
      this.updateNodeOutput();
    }
  }

  private stringToDataTypes(str) {
    const data = str
      .toUpperCase()
      .replace(/ /g, '')
      .split(',')
      .filter(x => x !== '');

    if (data && data.length) {
      for (let i = 0; i < data.length; i++) {
        if (LORA_DATA_TYPE_NAMES[data[i]] === undefined) {
          this.setOutputData(0, 'settings parse error');
          return undefined;
        }
        data[i] = LORA_DATA_TYPE_NAMES[data[i]];
      }
    }
    return data;
  }
}
Container.registerNodeType('protocols/lora-raw-v2/lora-raw-points-in', LoRaRawPointsInput);


