export class LoRaRawPointsOutput extends Node {
  payloadDefinition: Array<LORA_DATA_TYPE_NAMES> = [];
  addressPrefix: string = 'FFFFFF';
  address: Buffer = null;
  keyBuffer: Buffer = null;
  nonce = 0;
  // inputsUpdated: Array<boolean> = [];

  errorOutputIndex: number = 1;

  constructor() {
    super();
    this.title = `Nube LoRaRAW Points Out`;
    this.description = 'This node compiles points to Nubes LoRa Points protocol';
    this.settings['enable'] = NodeSetting.create({
      description: 'enable',
      type: SettingType.BOOLEAN,
      value: true,
    });
    // this.settings['sendUpdatedOnly'] = NodeSetting.create({
    //   description: 'send only updated inputs',
    //   type: SettingType.BOOLEAN,
    //   value: true,
    // },
    this.settings['enable'] = NodeSetting.create({
      description: 'enable',
      type: SettingType.BOOLEAN,
      value: true,
    });
    this.settings['senderId'] = NodeSetting.create({
      description: 'sender Id [0-254]',
      type: SettingType.NUMBER,
      value: 0,
    });
    this.settings['port'] = NodeSetting.create({
      description: 'port number [0-254]',
      type: SettingType.NUMBER,
      value: 0,
    });
    this.settings['types'] = NodeSetting.create({
      description: 'Types ( # = [8, 16, 23] )',
      type: SettingType.READONLY,
      // value: 'INT8, UINT8, INT16, UINT16, INT32, UINT32, FLOAT',
      value: 'INT#, UINT#, FLOAT',
    });
    this.settings['definition'] = NodeSetting.create({
      description: 'payload definition (\',\' separated)',
      type: SettingType.STRING,
      value: '',
    });
    this.settings['key'] = NodeSetting.create({
      description: 'encryption key (hex)',
      type: SettingType.STRING,
      value: '',
    });
    this.settings['other_group'] = NodeSetting.create({
      description: 'Advanced',
      value: '',
      type: SettingType.GROUP,
    });
    this.settings['addrPrefix'] = NodeSetting.create({
      description: 'Address Prefix [6]',
      type: SettingType.STRING,
      value: 'FFFFFF',
    });
    this.settings['nonceLen'] = NodeSetting.create({
      description: 'Nonce Length',
      type: SettingType.NUMBER,
      value: 2,
    });
    this.settings['frameCounterEnabled'] = NodeSetting.create({
      description: 'frame counters (**TBA**)',
      type: SettingType.BOOLEAN,
      value: false,
    });

    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('output', Type.ANY);
    this.addOutput('error', Type.STRING);
  }

  init() {
    this.updateSettings();
    this.updateInputs(this.payloadDefinition);
  }

  onAdded() {
    this.updateSettings();
    // this.inputsUpdated = new Array<boolean>(this.payloadDefinition.length);
    // this.inputsUpdated.fill(false);
  }

  onInputUpdated() {
    // for (let i = 1; i < this.getInputsCount(); i++) {
    //   if (this.inputs[i].updated) {
    //     this.inputsUpdated[i - 1] = true;
    //   }
    // }
    if (this.settings['enable'].value === false) return;
    if (!this.inputs[0].updated || this.getInputData(0) !== true) {
      return;
    }

    // PARSING
    const parseError = msg => {
      this.setOutputData(this.errorOutputIndex, 'parse error: ' + msg);
    };

    const checkInputAndType = (value, type) => {
      let isOk = true;
      switch (type) {
        case LORA_DATA_TYPE_NAMES.INT8:
          isOk = value >= -128 && value <= 127;
          break;

        case LORA_DATA_TYPE_NAMES.UINT8:
          isOk = value >= 0 && value <= 0xff;
          break;

        case LORA_DATA_TYPE_NAMES.INT16:
          isOk = value >= -32768 && value <= 32767;
          break;

        case LORA_DATA_TYPE_NAMES.UINT16:
          isOk = value >= 0 && value <= 0xffff;
          break;

        case LORA_DATA_TYPE_NAMES.INT32:
          isOk = value >= -2147483648 && value <= 2147483647;
          break;

        case LORA_DATA_TYPE_NAMES.UINT32:
          isOk = value >= 0 && value <= 0xffffffff;
          break;
      }
      return isOk;
    };

    // if (!this.settings['sendUpdatedOnly'].value) {
    //   this.inputsUpdated.fill(true);
    // }
    // this.inputsUpdated.fill(true);
    let data_lengths = 0;
    for (let i = 0; i < this.payloadDefinition.length; i++) {
      // if (this.inputsUpdated[i]) {
      data_lengths += LORA_DATA_TYPE_LENGTHS[this.payloadDefinition[i]] + 1;
      // }
    }
    if (data_lengths === 0) {
      parseError('No input updates');
      return;
    }

    let buffer = Buffer.alloc(data_lengths + this.settings['nonceLen'].value + 1);

    const nonceLen = this.settings['nonceLen'].value;
    if (nonceLen === 1) {
      buffer.writeUInt8(this.nonce, 0);
      this.nonce = (this.nonce + 1) & 0xFF;
    }
    else if (nonceLen === 2) {
      buffer.writeUInt16LE(this.nonce, 0);
      this.nonce = (this.nonce + 1) & 0xFFFF;
    }
    else if (nonceLen === 4) {
      buffer.writeUInt32LE(this.nonce, 0);
      this.nonce = (this.nonce + 1) & 0xFFFFFFFF;
    }
    buffer.writeUInt8(this.settings['port'].value, nonceLen);

    let point = 0;
    for (let i = nonceLen + 1; i < buffer.length;) {
      // if (!this.inputsUpdated[point]) {
      //   point++;
      //   continue;
      // }
      let type = this.payloadDefinition[point];
      let value = this.getInputData(point + 1);

      if (!checkInputAndType(value, type)) {
        parseError('invalid value input ' + point);
        return;
      }

      buffer.writeUInt8(point, i++);

      switch (type) {
        case LORA_DATA_TYPE_NAMES.INT8:
          buffer.writeInt8(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.UINT8:
          buffer.writeUInt8(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.INT16:
          buffer.writeInt16LE(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.UINT16:
          buffer.writeUInt16LE(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.INT32:
          buffer.writeInt32LE(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.UINT32:
          buffer.writeUInt32LE(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.FLOAT:
          buffer.writeFloatLE(value, i);
          break;
      }

      i += LORA_DATA_TYPE_LENGTHS[type];
      point++;
    }

    let [encBuffer, err] = loraRawCrypt.loraRawEncryptBuffer(this.address, buffer, this.keyBuffer);
    if (err !== loraRawCrypt.LORA_RAW_ERROR.LRERR_NOERR) {
      this.setOutputData(this.errorOutputIndex, 'LoRaRaw Crypt ERR: ' + err);
      return;
    }
    buffer = Buffer.concat([this.address, encBuffer]);

    this.setOutputData(this.errorOutputIndex, null);
    this.setOutputData(0, buffer);

    // this.inputsUpdated.fill(false);
  }

  onAfterSettingsChange() {
    if (!this.updateSettings()) {
      return;
    }
    this.updateInputs(this.payloadDefinition);
    this.setOutputData(this.errorOutputIndex, null);
    this.onInputUpdated();
  }

  private updateSettings() {
    this.settings['senderId'].value = Utils.clamp(this.settings['senderId'].value, 0, 254);
    this.settings['port'].value = Utils.clamp(this.settings['port'].value, 0, 254);
    this.settings['nonceLen'].value = Utils.clamp(this.settings['nonceLen'].value, 1, 4);
    this.payloadDefinition = this.stringToDataTypes(this.settings['definition'].value);
    if (this.settings['key'].value.length !== 32) {
      this.setOutputData(this.errorOutputIndex, 'invalid key');
      this.keyBuffer = null;
      return false;
    }
    this.keyBuffer = Buffer.from(this.settings['key'].value, 'hex');
    let addrPrefix = this.settings['addrPrefix'].value;
    this.addressPrefix = addrPrefix && addrPrefix.length === 6 ? addrPrefix.toUpperCase() : 'FFFFFF';
    this.address = Buffer.from(this.addressPrefix + ('0' + this.settings['senderId'].value.toString(16)).substr(-2), 'hex');
    this.setOutputData(this.errorOutputIndex, null);
    return true;
  }

  private updateInputs(data) {
    if (this.getInputsCount() - 1 !== data.length) {
      const inputsCount = this.getInputsCount();
      for (let i = 1; i < inputsCount; i++) {
        this.removeInput(i);
      }

      // this.inputsUpdated = new Array<boolean>(data.length);
      for (let i = 0; i < data.length; i++) {
        this.addInput(i.toString(), Type.NUMBER);
        // this.inputsUpdated[i] = false;
      }
      this.updateNodeOutput();
    }
  }

  private stringToDataTypes(str) {
    const data = str
      .toUpperCase()
      .replace(/ /g, '')
      .split(',')
      .filter(x => x !== '');

    if (data && data.length) {
      for (let i = 0; i < data.length; i++) {
        if (LORA_DATA_TYPE_NAMES[data[i]] === undefined) {
          this.setOutputData(this.errorOutputIndex, 'settings parse error');
          return undefined;
        }
        data[i] = LORA_DATA_TYPE_NAMES[data[i]];
      }
    }
    return data;
  }
}
Container.registerNodeType('protocols/lora-raw-v2/lora-raw-points-out', LoRaRawPointsOutput);
