import { Container } from '../../../container';
import { Node } from '../../../node';
import { NODE_INFO } from '../../../node-info';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import { LORA_DATA_TYPE_LENGTHS, LORA_DATA_TYPE_NAMES } from './lora-two-way-in';

export class LoRaRawTwoWayOutput extends Node {
  payloadDefinition: Array<LORA_DATA_TYPE_NAMES> = [];
  inputsUpdated: Array<boolean> = [];

  constructor() {
    super();
    this.title = NODE_INFO["protocols/lora-raw"]["lora-raw-two-way-out"].title;
    this.description = NODE_INFO["protocols/lora-raw"]["lora-raw-two-way-out"].description;
    this.settings['enable'] = NodeSetting.create({
      description: 'enable',
      type: SettingType.BOOLEAN,
      value: false,
    });
    this.settings['senderId'] = NodeSetting.create({
      description: 'sender Id',
      type: SettingType.NUMBER,
      value: 0,
    });
    this.settings['port'] = NodeSetting.create({
      description: 'port number',
      type: SettingType.NUMBER,
      value: 0,
    });
    // this.settings['sendUpdatedOnly'] = {
    //   description: 'send only updated inputs',
    //   type: SettingType.BOOLEAN,
    //   value: true,
    // },
    this.settings['types'] = NodeSetting.create({
      description: 'Types',
      type: SettingType.READONLY,
      value: 'INT8, INT16, INT32, FLOAT',
      // value: 'INT8, UINT8, INT16, UINT16, INT32, UINT32, FLOAT',
    });
    this.settings['definition'] = NodeSetting.create({
      description: 'payload definition (JSON)',
      type: SettingType.STRING,
      value: '',
    });
    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('error', Type.STRING);
    this.addOutput('output', Type.ANY);
  }

  init() {
    this.updateOutputs();
  }

  onAdded() {
    this.payloadDefinition = this.stringToDataTypes(this.settings['definition'].value);
    this.inputsUpdated = new Array<boolean>(this.payloadDefinition.length);
    this.inputsUpdated.fill(false);
  }

  onInputUpdated() {
    for (let i = 1; i < this.getInputsCount(); i++) {
      if (this.inputs[i].updated) {
        this.inputsUpdated[i - 1] = true;
      }
    }
    if (this.settings['enable'].value === false) return;
    if (!this.inputs[0].updated || this.getInputData(0) === false) {
      return;
    }

    // PARSING
    const parseError = msg => {
      this.setOutputData(1, 'parse error: ' + msg);
    };

    const checkInputAndType = (value, type) => {
      let isOk = true;
      switch (type) {
        case LORA_DATA_TYPE_NAMES.INT8:
          isOk = value >= -128 && value <= 127;
          break;

        case LORA_DATA_TYPE_NAMES.UINT8:
          isOk = value >= 0 && value <= 0xff;
          break;

        case LORA_DATA_TYPE_NAMES.INT16:
          isOk = value >= -32768 && value <= 32767;
          break;

        case LORA_DATA_TYPE_NAMES.UINT16:
          isOk = value >= 0 && value <= 0xffff;
          break;

        case LORA_DATA_TYPE_NAMES.INT32:
          isOk = value >= -2147483648 && value <= 2147483647;
          break;

        case LORA_DATA_TYPE_NAMES.UINT32:
          isOk = value >= 0 && value <= 0xffffffff;
          break;
      }
      return isOk;
    };

    // if (!this.settings['sendUpdatedOnly'].value) {
    //   this.inputsUpdated.fill(true);
    // }
    this.inputsUpdated.fill(true);
    let data_lengths = 0;
    for (let i = 0; i < this.payloadDefinition.length; i++) {
      if (this.inputsUpdated[i]) {
        data_lengths += LORA_DATA_TYPE_LENGTHS[this.payloadDefinition[i]] + 1;
      }
    }
    if (data_lengths === 0) {
      parseError('No input updates');
      return;
    }
    let buffer = Buffer.alloc(data_lengths + 2);

    buffer.writeUInt8(this.settings['senderId'].value, 0);
    buffer.writeUInt8(this.settings['port'].value, 1);

    let point = 0;
    for (let i = 2; i < buffer.length; ) {
      if (!this.inputsUpdated[point]) {
        point++;
        continue;
      }
      let type = this.payloadDefinition[point];
      let value = this.getInputData(point + 1);

      if (!checkInputAndType(value, type)) {
        parseError('invalid value input ' + point);
        return;
      }

      buffer.writeUInt8(point, i++);

      switch (type) {
        case LORA_DATA_TYPE_NAMES.INT8:
          buffer.writeInt8(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.UINT8:
          buffer.writeUInt8(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.INT16:
          buffer.writeInt16LE(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.UINT16:
          buffer.writeUInt16LE(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.INT32:
          buffer.writeInt32LE(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.UINT32:
          buffer.writeUInt32LE(value, i);
          break;

        case LORA_DATA_TYPE_NAMES.FLOAT:
          buffer.writeFloatLE(value, i);
          break;
      }

      i += LORA_DATA_TYPE_LENGTHS[type];
      point++;
    }

    this.setOutputData(1, null);
    this.setOutputData(0, buffer);

    this.inputsUpdated.fill(false);
  }

  onAfterSettingsChange() {
    this.payloadDefinition = this.updateOutputs();
    this.setOutputData(1, null);
    this.onInputUpdated();
  }

  private updateOutputs() {
    const data = this.stringToDataTypes(this.settings['definition'].value);
    if (this.payloadDefinition.length !== data.length) {
      const inputsCount = this.getInputsCount();
      for (let i = 1; i < inputsCount; i++) {
        this.removeInput(i);
      }

      this.inputsUpdated = new Array<boolean>(data.length);
      for (let i = 0; i < data.length; i++) {
        this.addInput(i.toString(), Type.NUMBER);
        this.inputsUpdated[i] = false;
      }
    }
    return data;
  }

  stringToDataTypes(str) {
    const data = str
      .toUpperCase()
      .replace(/ /g, '')
      .split(',')
      .filter(x => x !== '');

    if (data && data.length) {
      for (let i = 0; i < data.length; i++) {
        if (LORA_DATA_TYPE_NAMES[data[i]] === undefined) {
          this.setOutputData(1, 'settings parse error');
          return undefined;
        }
        data[i] = LORA_DATA_TYPE_NAMES[data[i]];
      }
    }
    return data;
  }
}
Container.registerNodeType('protocols/lora-raw/lora-raw-two-way-out', LoRaRawTwoWayOutput);
