import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class NumbersSumNode extends Node {
  val = 0;
  lastReset: boolean;
  constructor() {
    super();
    this.title = NODE_INFO.streams["stream-sum"].title;
    this.description =
      `## Description\n ` +
      NODE_INFO.streams["stream-sum"].description + ` \n ` +
      ` ## Reset to null\n ` +
      `***Reset to null*** if set to option true and a true value on the node ***reset*** this will reset the node ***output*** to a value of null, If set to false it will keep the last node ***output*** value \n `
    this.addInput('input', Type.NUMBER);
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('output', Type.NUMBER);
    this.settings['null'] = NodeSetting.create({
      description: 'Reset to null',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.lastReset = false;
  }

  onInputUpdated() {
    const reset = this.getInputData(1);
    const resetToNull = this.settings['null'].value;
    if (reset && !this.lastReset) {
      if (resetToNull) {
        this.setOutputData(0, null);
        this.val = null
        this.lastReset = reset;
      } else {
        this.setOutputData(0, this.val);
        this.val = 0
        this.lastReset = reset;
      }
      return;
    } else if (!reset && this.lastReset) {
      this.lastReset = reset;
      return;
    }
    if (this.inputs[0].updated && this.inputs[0].data != null) this.val += this.inputs[0].data;
    this.setOutputData(0, this.val, true);
  }
}
Container.registerNodeType('streams/stream-sum', NumbersSumNode);
