import { isEqual, merge } from 'lodash';
import { Entry } from '../../../../backend/entry';
import { singleton as actor } from '../../../../promise-actor';
import { ContainerRegistry } from '../../../container';
import { Node } from '../../../node';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType } from '../../../node-io';
import EntryNode from './entry-node';

export default abstract class Mixin<T extends Entry> extends Node implements EntryNode<T> {
  properties: {
    entry: T;
    is_protected: boolean;
  };
  settingFields: {};

  abstract namePrefix(): string;

  abstract updateEntry(body: T): Promise<any>;

  abstract deleteEntry(): Promise<any>;

  abstract createEntry(): Promise<T>;

  abstract syncWithBackend(): void;

  abstract entryFactory(): T;

  entry() {
    return this.properties.entry;
  }

  entryId() {
    return this.entry() && this.entry().entryId();
  }

  applyEntry(entry: T) {
    this.properties.entry = Object.assign(this.entryFactory(), entry); // ensures correct inheritance
    this.title = this.namePrefix() + ' ' + this.properties.entry.label();
    this.size = this.computeSize();
    if (this.settings['name']) {
      this.settings['name'].value = this.entryId();
    }
    merge(this.settings, this.createSettingForm(entry));
    this.broadcastSettingsToClients();
    this.broadcastTitleToClients();
  }

  createSettingForm(entry: T): { [name: string]: NodeSetting } {
    return Object.keys(this.settingFields)
                 .map(key => ({
                   [key]: NodeSetting.create({
                                               description: key,
                                               value      : entry ? entry[key] : null,
                                               type       : this.settingFields[key],
                                             }),
                 }))
                 .reduce(merge);
  }

  deactivate() {
    this.properties.entry = null;
  }

  onCreated() {
    if (this.entry()) {
      this.applyEntry(this.entry());
    } else {
      if (this.side !== Side.server) return;
      actor.process(() =>
                      this.createEntry()
                          .then(entry => this.applyEntry(entry))
                          .catch(e => {
                            this.displayError(e, 'Entry not created.');
                            console.log(e);
                            // self-destruct
                            this.deactivate();
                            //TODO not yet send to client
                            return this.container.nodeRegistry().remove(this);
                          }),
      );
    }
  }

  onAdded() {
    if (this.entry()) {
      this.applyEntry(this.entry());
    }
  }

  onAfterSettingsChange() {
    if (this.side !== Side.server) return;
    const update: T = <any>{};
    const current: T = <any>{};
    Object.keys(this.settingFields)
          .filter(key => this.settingFields[key] !== SettingType.READONLY)
          .forEach(key => {
            update[key] = this.settings[key].value;
            current[key] = this.entry() && this.entry()[key];
          });
    // update remotely if there are any changes
    if (!isEqual(current, update)) {
      actor.process(() =>
                      this.updateEntry(update).catch(e => this.displayError(e, 'Unable to update entry remotely.')),
      );
    }
  }

  onRemoved() {
    if (this.side !== Side.server || !this.entry()) return Promise.resolve();
    return actor.process(() =>
                           this.deleteEntry().catch(e => {
                             this.displayError(e, 'Failed to remove entry from backend.');
                             console.log(e);
                           }),
    );
  }

  findFreeIdentifier(): Promise<string> {
    return this.getTakenIdentifiers().then(takenIdentifiers => {
      for (let i = 1; ; i += 1) {
        const identifier = this.makeIdentifier(i);
        if (!takenIdentifiers.includes(identifier)) return identifier;
      }
    });
  }

  getTakenIdentifiers(): Promise<string[]> {
    const globalSiblings = <EntryNode<T>[]>ContainerRegistry.instance().rootContainer().getNodesByClass(this.constructor, true);
    return Promise.resolve(globalSiblings.filter(n => n.entry()).map(v => v.entry().identifier()));
  }

  makeIdentifier(n: number): string {
    return `WIRES_${this.namePrefix().toUpperCase()}_${n}`;
  }
}
