import { Container } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

// TODO: we don't need this all logic, background does this job
// Watch a value in the editor
export class WatchNode extends Node {

  UPDATE_INTERVAL: number
  timer: any;
  lastData: any;
  dataUpdated = false;

  constructor() {
    super();

    this.UPDATE_INTERVAL = 300;

    this.title = NODE_INFO.debug.watch.title;
    this.description = NODE_INFO.debug.watch.description;
    this.addInput('input', Type.ANY);
  }

  onAdded() {
    if (this.side == Side.server) this.startSending();
  }

  startSending() {
    let that = this;
    this.timer = setInterval(function() {
      if (that.dataUpdated) {
        that.dataUpdated = false;
        that.sendMessageToEditorSide({ value: that.lastData });
      }
    }, this.UPDATE_INTERVAL);
  }

  onInputUpdated() {
    this.lastData = this.getInputData(0);
    this.dataUpdated = true;
  }

  onRemoved() {
    if (this.timer) clearInterval(this.timer);
  }

  onGetMessageToEditorSide(data) {
    this.lastData = data.value;
    this.showValueOnInput(data.value);
  }

  showValueOnInput(value: any) {
    // show the current value
    this.inputs[0].label = value;
    this.setDirtyCanvas(true, false);
  }

  updateInputsLabels() {
    this.showValueOnInput(this.lastData);
  }
}
Container.registerNodeType('debug/watch', WatchNode);
