import axios from 'axios';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import SystemUtils from '../system/system-utils';
import { bsaClientConfig } from './bsa-client-config';

class BSACumulocityDeviceNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.cumulocity["cumulocity-device"].title;
    this.description =  NODE_INFO.cumulocity["cumulocity-device"].description;
    this.addInput('createDevice', Type.BOOLEAN);
    this.addOutput('id', Type.NUMBER);
    this.addOutput('message', Type.STRING);

    this.settings['CumulocityDeviceName'] = NodeSetting.create({
      description: 'Cumulocity Device Name',
      value: '',
      type: SettingType.STRING,
    });
  }

  async onAdded() {
    await this.onAfterSettingsChange();
  }

  async onInputUpdated() {
    if (this.side !== Side.server) return;
    if (this.getInputData(0) && this.inputs[0].updated) {
      if (this.outputs[0].data && this.outputs[0].data.length == 0) await this.createDevice();
      await this.checkDeviceExists();
    }
  }

  async onAfterSettingsChange() {
    if (this.side !== Side.server) return;
    await this.checkDeviceExists();
  }

  getDeviceName(): string {
    let deviceName = this.settings['CumulocityDeviceName'].value || 'unknownDevice';
    if (!deviceName.startsWith('c8y_')) deviceName = 'c8y_' + deviceName;
    return deviceName;
  }

  async checkDeviceExists() {
    let deviceName = this.getDeviceName();
    let cfg: any = bsaClientConfig('device');
    cfg = {
      ...cfg,
      method: 'GET',
      url: `${cfg.url}?query=name+eq+${deviceName}`,
    };
    try {
      const response = await axios(cfg);
      const ids = [];
      response.data.managedObjects.forEach(device => {
        ids.push(Number(device.id));
      });
      this.setOutputData(0, ids);
      this.setOutputData(1, null);
    } catch (error) {
      this.setOutputData(1, String(error));
    }
  }

  async createDevice() {
    let clientIdFromPlat;
    let deviceIdFromPlat;
    try {
      clientIdFromPlat = await SystemUtils.getClientIdDb();
    } catch (e) {
      clientIdFromPlat = 'unknownClientID';
    }
    try {
      deviceIdFromPlat = await SystemUtils.getDeviceIdDb();
    } catch (e) {
      deviceIdFromPlat = 'unknownDeviceID';
    }
    let deviceName = this.getDeviceName();
    let cfg: any = bsaClientConfig('device');
    cfg = {
      ...cfg,
      method: 'POST',
      data: {
        name: deviceName,
        type: 'c8y_nube',
        deviceid: deviceIdFromPlat,
        clientid: clientIdFromPlat,
        c8y_IsDevice: {},
      },
    };
    try {
      await axios(cfg);
    } catch (error) {
      this.setOutputData(1, String(error));
    }
  }
}

Container.registerNodeType('cumulocity/cumulocity-device', BSACumulocityDeviceNode);
