import { db_logger as logger } from '../../utils/loggers';
import { AbstractDatabase } from '../database/BaseDatabase';
import UserDatabase from '../database/UserDatabase';
import { User } from '../models/user';

export default class UserDataSource extends AbstractDatabase implements UserDatabase {

  addUser(user: User, callback?: (err?: Error, doc?: User) => void) {
    this.db.insert(user, function (err, doc) {
      if (err) logger.error(err.message);
      if (callback) callback(err, doc);
    });
  }

  getUser(query: object, callback?: (err?: Error, doc?: User) => void) {
    this.db.findOne(query, function (err, doc: any) {
      if (err) logger.warn(err.message);
      if (callback) callback(err, doc);
    });
  }

  getUsers(callback?: (err?: Error, docs?: Array<User>) => void) {
    this.db.find({}, function (err, docs) {
      if (err) logger.error(err);
      if (callback) callback(err, docs);
    });
  }

  getAsyncUsers(): Promise<any> {
    return new Promise((resolve, reject) => {
      this.db.find({}, function (err, doc: any) {
        if (err) {
          logger.warn(err.message);
          reject(err);
        } else {
          resolve(doc);
        }
      });
    });
  }

  updateUser(query: object, user: User, callback?: (err?: Error) => void) {
    this.db.update(query, user, {}, function (err, updated) {
      if (err) logger.error(err.message);
      if (updated == 0) logger.error(`Cat't update user [${name}]. Document not found.`);
      if (callback) callback(err);
    });
  }

  getUsersCount(callback?: (err?: Error, num?: number) => void) {
    this.db.count({}, function (err, num) {
      if (err) logger.warn(err.message);
      if (callback) callback(err, num);
    });
  }

  addOrUpdateUser(query: object, user: User): Promise<any> {
    return new Promise((resolve, reject) => {
      this.getUser(query, (err, usr) => {
        if (usr) {
          this.updateUser(query, user, err => {
            if (err) {
              reject(err);
            } else {
              resolve();
            }
          });
        } else {
          this.addUser(user, err => {
            if (err) {
              reject(err);
            } else {
              resolve();
            }
          });
        }
      });
    });
  }

  removeUser(query: object, callback?: (err?: Error) => void) {
    this.db.remove(query, {}, function (err, removed) {
      if (err) logger.error(err.message);
      if (callback) callback(err);
    });
  }

  dropUsers(callback?: (err?: Error) => void) {
    this.db.remove({}, { multi: true }, function (err) {
      if (err) logger.error(err.message);
      if (callback) callback(err);
    });
  }

  protected dbName(): string {
    return `users`;
  }
}
