import { Container } from '../../container';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import { UiNode } from './ui-node';

export class UiButtonNode extends UiNode {
  interval = null;

  constructor(container?: Container, id?: number, properties?: any) {
    super('Button', 'UiButtonNode', properties);
    this.title = NODE_INFO.dashboard.button.title;
    this.description = NODE_INFO.dashboard.button.description;
    this.addOutput('output', Type.BOOLEAN);
    this.settings['button-text'] = NodeSetting.create({
      description: 'Button Text',
      value: 'ON',
      type: SettingType.STRING,
    });
    this.setState({ buttonText: 'ON' });
  }

  onAfterSettingsChange() {
    if (this.side !== Side.server) return;
  }

  onRemoved() {
    super.onRemoved();
  }

  onGetMessageToServerSide() {
    this.setOutputData(0, true);
    this.sendIOValuesToEditor();
  }
}

Container.registerNodeType('dashboard/button', UiButtonNode);
