import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class TimeFadeNode extends Node {
  updateInterval: number = 500;
  stepSize: number;
  lastEnable: boolean;
  timerFinished: boolean;
  currentVal: number;
  timeoutFunc;


  constructor() {
    super();
    this.title = NODE_INFO["num-transform"].fade.title;
    this.description = NODE_INFO["num-transform"].fade.description; 
   
    this.addInput('enable', Type.BOOLEAN);
    this.addInputWithSettings('interval', Type.NUMBER, 1, 'Interval', false);
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
    this.addInputWithSettings('from', Type.NUMBER, 0, 'To Value', false);
    this.addInputWithSettings('to', Type.NUMBER, 100, 'From Value', false);
    this.addOutput('output');
    this.addOutput('out=to');

    this.updateInterval = 250;
  }

  onAdded() {
 
    this.lastEnable = false;
    this.timerFinished = false;
    this.setOutputData(1, false);
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onAfterSettingsChange() {
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
  }

  onInputUpdated() {
    const enable = this.getInputData(0);
    if (!enable && this.lastEnable) {
      clearInterval(this.timeoutFunc);
      this.setOutputData(0, 0);
      this.setOutputData(1, false);
      this.timerFinished = false;
      this.lastEnable = enable;
      return;
    } else if (!enable) {
      this.setOutputData(0, 0);
      this.setOutputData(1, false);
      return;
    }
    const to = this.getInputData(3);
    if (enable && this.timerFinished) {
      this.setOutputData(0, to);
      this.setOutputData(1, true);
      return;
    }

    const from = this.getInputData(2);
    let interval = this.getInputData(1);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    if (enable && !this.lastEnable) {
      this.stepSize = (to - from) / (interval / this.updateInterval);
      this.currentVal = from;
      this.timeoutFunc = setInterval(() => {
        this.update();
      }, this.updateInterval);
    } else if (!enable && this.lastEnable) {
      clearInterval(this.timeoutFunc);
      this.setOutputData(0, 0);
      this.timerFinished = false;
    }
    this.lastEnable = enable;
  }

  update() {
    let to = this.getInputData(3);
    const val = this.currentVal + this.stepSize;
    if (val >= to) {
      clearInterval(this.timeoutFunc);
      this.setOutputData(0, to);
      this.setOutputData(1, true);
      this.timerFinished = true;
      this.currentVal = 0;
      return;
    } else {
      this.currentVal = val;
      this.setOutputData(0, val);
    }
  }
}
Container.registerNodeType('num-transform/fade', TimeFadeNode);
