

import { exec } from 'child_process';
import OsUtils from '../../nodes/utils/os-utils';
const network = require('network'); //for getting IPs and gateway address
const cp = require('child_process');
const set_ip_address = require('set-ip-address'); //for setting IPs
const ip = require('ip'); //for checking if IPs are valid

export default class IpAddressUtils {
  /**
   * Get current network info
   * @returns {object}
   */
  static getActiveNetworks() {
    return new Promise<Array<any>>(resolve => {
      network.get_interfaces_list(function (err, obj) {
        if (err) resolve();
        if (!obj) resolve();
        resolve(obj);
      });
    });
  }

  /**
   * Runs a system command
   * @param iface {string} Name of the physical system interface
   * @param upDown {string} system command options
   * @returns {Promise}
   *
   */

  static networkUpDown(iface: string, upDown: string) {
    return new Promise((resolve, reject) => {
      let command = `sudo ip link set ${iface} ${upDown}`;
      // let command = 'sudo ip link set' +' '+ iface +' '+upDown
      exec(command, (err, stdout, stderr) => {
        if (err) {
          reject({ err, stderr });
        } else {
          resolve(stdout);
        }
      });
    });
  }

  /** check if new ip is valid
   * @description Method for checking and build an ip address
   * @param {string} newIP pass in an ip address
   * @param {string} netNetmask pass in an ip net mask
   * @param {string} newGateway pass in an  ip gateway
   * @param newNameserver
   * @returns {object} Will return a validIp: true, ipSubnet: '192.168.15.1/24 or if invalid it will return false
   */

  static checkIpValid(newIP: string, netNetmask: string, newGateway: string, newNameserver: string) {
    const checkIP = ip.isV4Format(newIP);
    const checkNetMask = ip.isV4Format(netNetmask);
    const checkGateway = ip.isV4Format(newGateway);
    const nameserver = ip.isV4Format(newNameserver);

    if (checkIP && checkNetMask && checkGateway && nameserver) {
      return true;
    } else return false;
  }

  /**
   * Gets BBB interface details
   * @returns {Promise}
   *
   */
  static bbbGetInterfaceDetails() {
    return new Promise((resolve, reject) => {
      cp.execSync('connmanctl services', (err, stdout, stderr) => {
        if (err) {
          reject({ err, stderr });
        } else {
          resolve(
            stdout
              .toString()
              .replace(/\s/g, '')
              .substring(8),
          );
        }
      });
    });
  }

  /**
   * Set BBB network as DHCP
   * @returns {Promise}
   *
   */
  static bbbSetIPDHCP(iface) {
    return new Promise((resolve, reject) => {
      const setIpDHCP = `sudo connmanctl config ${iface} --ipv4 dhcp`;
      cp.execSync(setIpDHCP, (err, stdout, stderr) => {
        if (err) {
          reject({ err, stderr });
        } else {
          resolve(true);
        }
      });
    });
  }

  /**
   * Set BBB network as fixed IP
   * @returns {Promise}
   *
   */
  static bbbSetIPFixed(iface, ipAddress: string, subnetMask: string, gateway: string) {
    return new Promise((resolve, reject) => {
      const setIP = `sudo connmanctl config ${iface} --ipv4 manual ${ipAddress} ${subnetMask} ${gateway} --nameservers 8.8.8.8 8.8.4.4`;
      cp.execSync(setIP, (err, stdout, stderr) => {
        if (err) {
          reject({ err, stderr });
        } else {
          resolve(true);
        }
      });
    });
  }

  /**
   * Set IP for rubix
   * @returns {Promise}
   * will modify the /etc/dhcpcd.conf 
   */
  static updateIpRubixCompute(interface1Eth, dhcp1Eth: boolean, eth1Ip, eth1Sub, eth1Gate, eth1NameServer, interface2Eth, dhcp2Eth: boolean, eth2Ip, eth2Sub, eth2Gate, eth2NameServer) {
    return new Promise<any>((resolve, reject) => {

      const msgUpdateIp = 'SET: new ip '
      const msgIncorrectHost = 'ERROR: incorrect host type'
      const msgFailToUpdate = 'ERROR: invalid ip settings'
      const host = 'BCM2835'  //PI BCM2835 //aidan's pc 20L5CTO1WW, MS-7A66

      const interfaceEth1 = interface1Eth
      const dhcpEth1 = dhcp1Eth
      const ethIp1 = eth1Ip;
      const ethSub1 = eth1Sub;
      const ethGate1 = eth1Gate;
      const ethNameServer1 = eth1NameServer;

      const interfaceEth2 = interface2Eth
      const dhcpEth2 = dhcp2Eth
      const ethIp2 = eth2Ip;
      const ethSub2 = eth2Sub;
      const ethGate2 = eth2Gate;
      const ethNameServer2 = eth2NameServer;

      const checkIpValidEth1 = IpAddressUtils.checkIpValid(
        ethIp1,
        ethSub1,
        ethGate1,
        ethNameServer1,
      );
      const checkIpValidEth2 = IpAddressUtils.checkIpValid(
        ethIp2,
        ethSub2,
        ethGate2,
        ethNameServer2,
      );

      console.log(interface1Eth, dhcpEth1, eth1Ip, eth1Sub, eth1Gate, eth1NameServer, interface2Eth, dhcpEth2, eth2Ip, eth2Sub, eth2Gate, eth2NameServer, "checkIpValidEth1=", checkIpValidEth1, "checkIpValidEth2=", checkIpValidEth2)
      OsUtils.systemInfo()
        .then(e => {
          if (e.system.model === host) {
            if (checkIpValidEth1 && checkIpValidEth2) {
              //network update
              let interfacesArray = [];
              if (dhcpEth1 === true) {
                //DHCP
                console.log("dhcpEth1 === true")
                const eth1 = {
                  interface: interfaceEth1,
                  dhcp: true
                };
                interfacesArray.push(eth1);
              }
              if (dhcpEth2 === true) {
                //DHCP
                console.log("dhcpEth2 === true")
                const eth2 = {
                  interface: interfaceEth2,
                  dhcp: true
                };
                interfacesArray.push(eth2);
              }

              if (dhcpEth1 === false) {
                //Static
                console.log("dhcpEth1 === false")
                const subnetMaskLength = ip.subnet(ethIp1, ethSub1);
                const eth1 = {
                  interface: interfaceEth1,
                  ip_address: ethIp1,
                  prefix: subnetMaskLength.subnetMaskLength,
                  gateway: ethGate1,
                  nameservers: [ethNameServer1],
                };
                interfacesArray.push(eth1);
              }

              if (dhcpEth2 === false) {
                //Static
                console.log("dhcpEth2 === false")
                const subnetMaskLength = ip.subnet(ethIp2, ethSub2);
                const eth2 = {
                  interface: interfaceEth2,
                  ip_address: ethIp2,
                  prefix: subnetMaskLength.subnetMaskLength,
                  gateway: ethGate2,
                  nameservers: [ethNameServer2],
                };
                interfacesArray.push(eth2);
              }
              console.log(`${msgUpdateIp} ${JSON.stringify(interfacesArray)} @ ${interfaceEth1} @ ${interfaceEth2}`)
              // resolve(`${msgUpdateIp} ${JSON.stringify(interfacesArray)} @ ${interfaceEth1}`)
              // set new IP
              set_ip_address.configure(interfacesArray).then((e) => {
                console.log(`${msgUpdateIp} ${JSON.stringify(interfacesArray)} @ ${interfaceEth1} @ ${interfaceEth2} msg ${e}`)
                resolve(`${msgUpdateIp} ${JSON.stringify(interfacesArray)} @ ${interfaceEth1} msg ${e}`)
              });

            } else {
              console.log(`${msgFailToUpdate} @ ${interfaceEth1}  @ ${interfaceEth2}`)
              reject(`${msgFailToUpdate} @ ${interfaceEth1}  @ ${interfaceEth2}`)
            }
          } else {
            console.log(`${msgIncorrectHost} ${e.system.model} @ ${interfaceEth1} @ ${interfaceEth2}`)
            reject(`${msgIncorrectHost} ${e.system.model} @ ${interfaceEth1} @ ${interfaceEth2}`)
          }
        }).catch(err => {
          console.log(`${msgFailToUpdate} @ ${interfaceEth1} @ ${interfaceEth2} ${err}`)
          reject(`${msgFailToUpdate} @ ${interfaceEth1} @ ${interfaceEth2} ${err}`)
        });
    });
  }



}
