import { isNull } from '../../../utils/helper';
import { Container, ContainerRegistry } from '../../container';
import { Node } from '../../node';
import Icons from '../../node-icons';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';

const icon = Icons.aiIcon;

export class ConnectionLocalReceiverNode extends Node {
  topicList: any = [];

  inputEnable = 0;
  inputNumTopic = 1;

  constructor() {
    super();
    this.title = NODE_INFO.connection["link-receiver-single"].title;
    this.description = NODE_INFO.connection["link-receiver-single"].description;
    this.iconImageUrl = icon;
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.settings['manualTopicOption'] = NodeSetting.create({
      description: 'Set topic manually',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.addInputWithSettings('topic', Type.STRING, '1', 'Set topic', false);
    this.addOutput('output', Type.ANY);
    this.addOutput('topic', Type.STRING);
    this.properties['topic'] = "";
    this.settings['topicDropdown'] = NodeSetting.create({
      description: 'Select Topic',
      config: { items: [] },
      value: '',
      type: SettingType.DROPDOWN,
    });
    this.settings['blockNull'] = NodeSetting.create({
      description: 'Block Null',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.setSettingsConfig({
      conditions: {
        topicDropdown: setting => {
          return setting['manualTopicOption'].value === false;
        }
      },
    });
  }

  init() {
    if (!this.properties['val']) {
      this.properties['val'] = null;
    }
  }

  onAdded() {
    this.onAfterSettingsChange();
    this.setOutputData(0, this.properties['val']);
  }

  onCreated() {
    if (isNull(this.properties['topic'])) {
      this.onAfterSettingsChange();
    }
  }

  async onAfterSettingsChange() {
    await this.setTopic();
    this.updateTitle();
    if (this.side !== Side.server) return;
    this.transmitterTopic();
    this.updateChannel();

  }

  pushSettings() {
    const unitCategory = this.topicList.map((point: any) => {
      return { value: point, text: point };
    });
    this.settings['topicDropdown'].config = {
      items: unitCategory,
    };
    this.persistProperties(true);
    this.updateTitle();
    this.updateChannel();
    this.broadcastSettingsToClients(false);
    this.broadcastTitleToClients(false);
    this.broadcastOutputsToClients(false);
  }

  transmitterTopic() {
    const transmitters = ContainerRegistry.instance().rootContainer().getNodesByType('connection/link-transmitter-single', true);
    this.topicList = [];
    transmitters.forEach(transmitter => {
      this.topicList.push(transmitter.getInputData(1));
      if (this.properties['topic'] === transmitter.getInputData(1)) {
        this.setOutputData(0, transmitter.getInputData(0));
      }
    });
    this.pushSettings();
  }

  updateTitle() {
    this.title = 'Link Receiver Single [' + this.properties['topic'] + ']';
  }

  updateChannel() {
    this.setOutputData(1, this.properties['topic']);
  }


  onInputUpdated() {
    if (this.side !== Side.server) return;
    this.onAfterSettingsChange()
  }

  async setTopic() {
    const manualTopicOption = this.settings['manualTopicOption'].value
    if (manualTopicOption) {
      this.properties['topic'] = this.getInputData(this.inputNumTopic)
    } else if (!manualTopicOption) {
      this.properties['topic'] = this.settings['topicDropdown'].value
    }
  }

  // @Link: Triggers from transmitter on addition and on remove
  triggerTransmitterTopicUpdate() {
    if (this.side !== Side.server) return;
    this.transmitterTopic();
  }
}
Container.registerNodeType('connection/link-receiver-single', ConnectionLocalReceiverNode);
