import * as bodyParser from 'body-parser';
import * as cookieParser from 'cookie-parser';
import * as express from 'express';
import * as http from 'http';
import { Colors, create } from 'logplease';
import * as morgan from 'morgan';
import * as path from 'path';
import * as socket from 'socket.io';
import * as config from '../config.json';
import { DashboardServerSocket } from './dashboard-server-socket';
import { EditorServerSocket } from './editor-server-socket';
import { authMiddleware } from './middleware';
import { PingPongServerSocket } from './ping-pong-server-socket';
import { WireNameServerSocket } from './wire-name-server-socket';

const expressValidator = require('express-validator');

const log = create('server', { color: Colors.Yellow });

const isDev = process.env.NODE_ENV !== 'production';

export class Server {
  express: express.Application;
  server: http.Server;

  editorSocket: EditorServerSocket;
  dashboardSocket: DashboardServerSocket;
  wireNameSocket: WireNameServerSocket;
  private pingPongSocket: PingPongServerSocket;
  private readonly port: number;

  constructor(workingDir: string, port: number) {
    this.port = port;
    this.express = express();
    this.express.locals.moment = require('moment');
    /* webpack-strip-block:removed */
    this.middleware(workingDir);
    this.routes();
    this.handleErrors();
    this.configure();
    this.start_io();
  }

  /* webpack-strip-block:removed */

  private middleware(workingDir: string) {
    if (config.webServer.debug) {
      this.express.use(
        morgan('dev', {
          skip: function (req, res) {
            return res.statusCode < 400;
          },
        }),
      );
    }
    this.express.use(bodyParser.json({ limit: '5mb' }));
    this.express.use(bodyParser.urlencoded({ extended: false }));
    this.express.use(cookieParser());
    this.express.use(expressValidator());
    const publicFolder = path.resolve(workingDir, 'public');
    log.info(`App Server Dir : '${publicFolder}'`);
    this.express.use('/', express.static(publicFolder));
  }

  private routes() {
    // api console logger, after respective apis got called this method will be triggered
    this.express.use('/api/', function (req, res, next) {
      const send = res.send;
      (<any>res).send = function (body) {
        if (![200, 201].includes(res.statusCode)) log.warn(body);
        send.call(this, body);
      };
      next();
    });
    this.express.use('/dashboard', authMiddleware(), require('./routes/dashboard'));
    this.express.use('/api/dashboard', authMiddleware(), require('./routes/api-dashboard'));
    this.express.use('/editor', authMiddleware(), require('./routes/editor'));
    this.express.use('/api/editor', authMiddleware(), require('./routes/api-editor'));
    this.express.use('/api/wire-name', authMiddleware(), require('./routes/api-wire-name'));
    this.express.use('/api/auth', require('./routes/auth'));
    this.express.use('/api/help', authMiddleware(), require('./routes/api-help'));
    this.express.use('/api/platform', authMiddleware(), require('./routes/api-platform'));

  }

  private handleErrors() {
    // Route not found middleware
    this.express.use((req: express.Request, res: express.Response): void => {
      res.status(404);
      res.json({ error: { message: 'Not Found' } });
    });
    // Error handler
    this.express.use(
      (
        err: Error & { status: number },
        req: express.Request,
        res: express.Response,
        next: express.NextFunction,
      ): void => {
        log.warn(`${err}`);
        res.status(err.status || 500);
        res.json({ error: { message: err.message, error: isDev ? err : {} } });
      },
    );
  }

  private configure() {
    const port = normalizePort(this.port);
    this.express.set('port', port);

    this.server = http.createServer(this.express);
    this.server.listen(port);
    this.server.on('error', onError);
    this.server.on('listening', onListening);

    function normalizePort(val: number | string): number | string | boolean {
      const port: number = typeof val === 'string' ? parseInt(val, 10) : val;
      if (isNaN(port)) {
        return val;
      } else if (port >= 0) {
        return port;
      } else {
        return false;
      }
    }

    const that = this;

    function onError(error: NodeJS.ErrnoException): void {
      if (error.syscall !== 'listen') throw error;
      const bind = typeof port === 'string' ? 'Pipe ' + port : 'Port ' + port;
      switch (error.code) {
        case 'EACCES':
          console.error(`${bind} requires elevated privileges`);
          process.exit(1);
          break;
        case 'EADDRINUSE':
          console.error(`${bind} is already in use`);
          process.exit(1);
          break;
        default:
          throw error;
      }
    }

    function onListening(): void {
      const addr = that.server.address();
      const bind = typeof addr === 'string' ? `pipe ${addr}` : `port ${addr.port}`;
      log.info(`Listening on ${bind}`);
    }
  }

  private start_io() {
    const io_root: SocketIO.Server = socket(this.server);
    this.editorSocket = new EditorServerSocket(io_root);
    this.dashboardSocket = new DashboardServerSocket(io_root);
    this.wireNameSocket = new WireNameServerSocket(io_root);
    this.pingPongSocket = new PingPongServerSocket(this.server, io_root);
  }
}
