import { Container, ContainerRegistry } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import { NODE_INFO } from '../../node-info';

export class ConnectionLocalTransmitterNode extends Node {
  constructor() {
    super();

    this.title = NODE_INFO.connection["link-transmitter"].title;
    this.description = NODE_INFO.connection["link-transmitter"].description;

    this.settings['channel'] = NodeSetting.create({
      description: 'Channel Number',
      value: 1,
      type: SettingType.NUMBER,
    });

    this.settings['inputsCount'] = NodeSetting.create({
      description: 'Inputs Count',
      value: 1,
      type: SettingType.NUMBER,
    });
  }

  init() {
    this.properties['inputsCount'] = 0;
    this.changeTopicsCount(this.settings['inputsCount'].value);
  }

  onAdded() {
    this.onAfterSettingsChange();
  }

  onInputUpdated() {
    for (let i in this.inputs) {
      let val = this.inputs[i].data;
      if (val == undefined) val = null;
      let receivers = ContainerRegistry.instance().rootContainer().getNodesByType('connection/link-receiver', true);
      receivers.forEach(receiver => {
        if (receiver.settings['channel'].value == this.settings['channel'].value) {
          if (receiver.outputs.hasOwnProperty(i)) {
            receiver.setOutputData(+i, val);
            if (this.inputs[i].name !== '') receiver.outputs[i].name = this.inputs[i].name;
          }
        }
      });
    }
  }

  onAfterSettingsChange() {
    this.updateTitle();
    let inputCount = this.settings['inputsCount'].value;
    inputCount = Utils.clamp(inputCount, 1, 100);
    if (inputCount !== this.properties['inputsCount']) this.changeTopicsCount(inputCount);
    this.renameInputs();
    this.persistProperties(true, true, true);
    this.onInputUpdated();
  }

  changeTopicsCount(target_count) {
    let diff = target_count - this.properties['inputsCount'];
    if (diff == 0) return;

    //add inputs, outputs
    this.changeInputsCount(target_count, Type.ANY);

    //change settings for topics
    if (diff > 0) {
      for (let i = this.properties['inputsCount'] + 1; i <= target_count; i++) {
        this.settings['in' + i] = NodeSetting.create({
          description: 'Input ' + i,
          value: '',
          type: SettingType.STRING,
        });
      }
    } else if (diff < 0) {
      for (let i = this.properties['inputsCount']; i > target_count; i--) {
        delete this.settings['in' + i];
      }
    }
    this.properties['inputsCount'] = target_count;
  }

  renameInputs() {
    for (let i = 0; i <= this.properties['inputsCount'] - 1; i++) {
      let topic = this.settings['in' + (i + 1)]?.value;
      if (topic?.length > 20) topic = '...' + topic.substr(topic.length - 20, 20);
      this.inputs[String(i)].name = '' + (i + 1) + ' | ' + topic;
    }

    if (this.side == Side.editor) {
      for (let i = 0; i <= this.properties['inputsCount'] - 1; i++) {
        this.inputs[String(i)].label = this.inputs[String(i)].name;
      }
      this.setDirtyCanvas(true, true);
    }
  }

  updateTitle() {
    this.title = 'Link Transmitter [' + this.settings['channel'].value + ']';
  }
}
Container.registerNodeType('connection/link-transmitter', ConnectionLocalTransmitterNode);
