import { ErrorHandler } from '../utils/decorators';
import { IOT_TYPES } from './constants';
import { Container } from './container';
import { Node } from './node';
import { NODE_INFO } from './node-info';
import {
  ContainerRegistryMetadata,
  GlobalNodeRegistryMetadata,
  IContainerNode,
  SerializedContainerNode,
} from './node-interface';
import { NodeSetting, SettingType } from './node-io';
import { Deserializable, Serializable } from './serializable';

/**
 * Container: special node that contains a container of other nodes
 */
export class ContainerNode extends Node implements IContainerNode, Serializable<SerializedContainerNode>,
                                                   Deserializable<SerializedContainerNode> {

  private static NAME: string = 'Folder';
  sub_container: Container;
  /**
   * List of node type identifiers that can be created in specifically in this container node, but are not normally
   * listed in the menu
   */
  extra_node_types: string[];

  constructor() {
    super();
    this.title = NODE_INFO.container.folder.title;
    this.description = NODE_INFO.container.folder.description;
    this.settings['name'] = NodeSetting.create({ description: 'Folder name', type: SettingType.STRING });
    this.sub_container = <Container>{};
  }

  static create(containerNode: SerializedContainerNode, isNew: boolean, containerRegistry?: ContainerRegistryMetadata,
                globalRegistry?: GlobalNodeRegistryMetadata) {
    return new ContainerNode().deserialize(containerNode, isNew, containerRegistry, globalRegistry);
  }

  shouldRejectNodeType(type: string): boolean {
    return IOT_TYPES.includes(type); // IoT nodes can only be created in special container nodes
  }

  onCreated() {
  }

  onAdded() {
    this.setupSubContainer();
    this.applyTitle();
    super.onAdded();
  }

  afterAdded() {
    super.afterAdded();
  }

  onAfterSettingsChange(oldSettings, oldName?: any) {
    this.applyTitle();
  }

  onRemoved() {
  }

  serialize(): SerializedContainerNode {
    return { ...super.serialize(), sub_container: this.sub_container?.serialize() };
  }

  @ErrorHandler
  deserialize(serializedObj: SerializedContainerNode, isNew?: boolean, containerRegistry?: ContainerRegistryMetadata,
              globalRegistry?: GlobalNodeRegistryMetadata): ContainerNode {
    const cNode = <ContainerNode>super.deserialize(serializedObj, isNew);
    cNode.sub_container = Container.create(this.side, serializedObj.sub_container, containerRegistry, globalRegistry);
    return cNode;
  }

  getExtraMenuOptions(renderer, editor) {
    let that = this;
    return [
      {
        content: 'Open',
        callback: function () {
          editor.openContainer(that.sub_container.id);
        },
      },
      null, //null for horizontal line
    ];
  }

  onExecute() {
    if (this.isUpdated) this.isRecentlyActive = true;
    this.sub_container.runStep();
  }

  protected applyTitle() {
    this.settings['name'].value = this.settings.name?.value ?? `${ContainerNode.NAME} ${this.sub_container.id}`;
    this.title = this.title === ContainerNode.NAME ? this.settings['name'].value : this.title;
    this.sub_container.name = this.title;
  }

  @ErrorHandler
  private setupSubContainer() {
    this.sub_container.container_node = this;
    this.sub_container.parent_container_id = this.container.id;
    this.sub_container.parent_id = this.id;
  }

}
