import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';
import Statistics from './statistics';
import { NODE_INFO } from '../../node-info';

class AnyStatAllNode extends Statistics {
  constructor() {
    super();
    this.title = NODE_INFO.statistics["stats-all"].title;
    this.description = NODE_INFO.statistics["stats-all"].description;
    this.addOutput('max', Type.NUMBER);
    this.addOutput('min', Type.NUMBER);
    this.addOutput('range', Type.NUMBER);
    this.addOutput('sum', Type.NUMBER);
    this.addOutput('mean', Type.NUMBER);
    this.addOutput('median', Type.NUMBER);
    this.addOutput('modes', Type.NUMBER);
    this.addOutput('variance', Type.NUMBER);
    this.addOutput('standardDeviation', Type.NUMBER);
    this.addOutput('meanAbsoluteDeviation', Type.NUMBER);
    this.addOutput('zScores', Type.NUMBER);
  }

  onInputUpdated() {
    const a = this.getDefinedInputsOrSettingsValues();
    this.setOutputData(0, Utils.stat.max(a));
    this.setOutputData(1, Utils.stat.min(a));
    this.setOutputData(2, Utils.stat.range(a));
    this.setOutputData(3, Utils.stat.sum(a));
    this.setOutputData(4, Utils.stat.mean(a));
    this.setOutputData(5, Utils.stat.median(a));
    this.setOutputData(6, Utils.stat.modes(a));
    this.setOutputData(7, Utils.stat.variance(a));
    this.setOutputData(8, Utils.stat.standardDeviation(a));
    this.setOutputData(9, Utils.stat.meanAbsoluteDeviation(a));
    this.setOutputData(10, Utils.stat.zScores(a));
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}

Container.registerNodeType('statistics/stats-all', AnyStatAllNode);
