interface findVal {
  valueWithKey: object,
  value: number;
}

enum compareType {
  Equals = 'Equals',
  NotEquals = 'Not Equal',
  GreaterThanEqual = 'Greater Than Equal',
  GreaterThan = 'Greater Than',
  LessThanEqual = 'Less Than Equal',
  LessThan = 'Less Than',
  Includes = 'Includes',
  startsWith = 'Starts With',
  endsWith = 'Ends With'
}

export default class jsonStringCompare {
  /**
   * 
   */
  static compare(self: any, s1: any, s2: any) {
    const inputType = self.settings['function'].value;
    if (self.settings['isNumber'].value) {
      s1 = Number(s1)
      s2 = Number(s2)
    }
    switch (inputType) {
      case compareType.Equals:
        return s1 === s2;
      case compareType.NotEquals:
        return s1 !== s2;
      case compareType.GreaterThanEqual:
        return s1 >= s2;
      case compareType.GreaterThan:
        return s1 > s2;
      case compareType.LessThanEqual:
        return s1 <= s2;
      case compareType.LessThan:
        return s1 < s2;
      case compareType.LessThan:
        return s1 < s2;
      case compareType.Includes:
        return s1.includes(s2);
      case compareType.startsWith:
        return s1.startsWith(s2);
      case compareType.endsWith:
        return s1.endsWith(s2);
      default:
        return s1 === s2;
    }
   
  }

  static findVal(object: { [x: string]: any; }, key: string) {
    let value: findVal;
    Object.keys(object).some(function (k) {
      if (k === key) {
        value = {
          valueWithKey: { [key]: object[k] },
          value: object[k]
        }
      }
      if (object[k] && typeof object[k] === 'object') {
        value = this.findVal(object[k], key);
        return value !== undefined;
      }
    });
    return value;
  }


  

}
