import * as fs from 'fs';
import config from '../../config';
import FileUtils from '../../nodes/utils/file-utils';
import { db_logger as logger } from '../../utils/loggers';

const file = `${config.dataDir}/platform.db`;
const ipDb = `${config.dataDir}/ip.db`;

export default class PlatformDataSource {

  static getPlatform(): Promise<any> {
    return new Promise((resolve, reject) => {
      FileUtils.createFile(file).then(() => {
        fs.readFile(file, { encoding: 'utf-8' }, (err, data) => {
          if (err) {
            reject(err);
            logger.error(err.message);
          } else {
            const obj = data ? JSON.parse(data) : {};
            resolve(obj);
          }
        });
      });
    });
  }

  static updatePlatform(data: any): Promise<any> {
    return new Promise((resolve, reject) => {
      FileUtils.createFile(file).then(() => {
        const json = JSON.stringify(data);
        fs.writeFile(file, json, 'utf-8', err => {
          if (err) {
            reject(err);
            logger.error(err.message);
          } else {
            resolve(json);
          }
        });
      });
    });
  }

  static getEth(): Promise<any> {
    return new Promise((resolve, reject) => {
      FileUtils.createFile(ipDb).then(() => {
        fs.readFile(ipDb, { encoding: 'utf-8' }, (err, data) => {
          if (err) {
            reject(err);
            logger.error(err.stack ?? err.message);
          } else {
            const obj = data ? JSON.parse(data) : {};
            resolve(obj);
          }
        });
      });
    });
  }

  static updateEth(data: any): Promise<any> {
    return new Promise((resolve, reject) => {
      FileUtils.createFile(ipDb).then(() => {
        let json = JSON.stringify(data);
        fs.writeFile(ipDb, json, 'utf-8', err => {
          if (err) {
            reject(err);
            logger.error(err.stack ?? err.message);
          } else {
            resolve(json);
          }
        });
      });
    });
  }
}

