import { Container } from '../../container';
import { Node } from '../../node';
import Icons from '../../node-icons';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

let logicMainColour = Icons.logicMainColour;
let logicAndIcon = Icons.logicAndIcon;
let logicOrIcon = Icons.logicOrIcon;

class XorNode extends Node {

  constructor() {
    super();
    this.title = NODE_INFO.boolean.xor.title;
    this.description = NODE_INFO.boolean.xor.description;

    // this.headerColor = logicMainColour;
    //this.iconImageUrl = logicAndIcon;

    this.addInput('in 1', Type.BOOLEAN);
    this.addInput('in 2', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('out not', Type.BOOLEAN);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const in1 = this.getInputData(0);
    const in2 = this.getInputData(1);
    if ((in1 && !in2) || (!in1 && in2)) {
      this.setOutputData(0, true);
      this.setOutputData(1, false);
    } else {
      this.setOutputData(0, false);
      this.setOutputData(1, true);
    }
  }
}
Container.registerNodeType('boolean/xor', XorNode);
