import { exec } from 'child_process';
import { Container } from '../../../container';
import { Node, NodeState } from '../../../node';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import Utils from '../../../utils';
import IpAddressUtils from '../../../utils/ip-address-utils';
import NodeColours from '../../../utils/nodes/node-colour-change';
import OsUtils from '../../../utils/os-utils';

const set_ip_address = require('set-ip-address'); //for setting IPs
const ip = require('ip'); //for checking if IPs are valid

class UpdateIP extends Node {
  timeStamp = Utils.getTimeStamp();

  interfaceEth0 = 'eth0'; //eth0 //wlp3s0
  interfaceEth1 = 'eth1'; //eth0 //enp0s31f6

  //outputs
  eth0Ip = 0;
  eth1Ip = 1;
  messageOutput = 2;
  errorOutput = 3;

  constructor() {
    super();
    this.title = 'Network Interfaces';
    this.description =
      'This node is used to configure the network (IP) interfaces for the device.  Changes made to settings will only be saved if ‘Save changes on save’ is toggled ON.  In some cases a reboot will be required to save the changes, if ‘Reboot’ is toggled ON, then clicking ‘save’ will reboot the controller.      NOTE: Making changes on a remote device could cause the device to become inaccessible.';
    this.addInput('get interface address', Type.BOOLEAN);
    this.addInput('enable eth0 ', Type.BOOLEAN);
    this.addInput('enable eth1 ', Type.BOOLEAN);
    this.addOutput('ip eth0', Type.STRING);
    this.addOutput('ip eth1', Type.STRING);
    this.addOutput('message', Type.STRING);
    this.addOutput('error', Type.STRING);

    this.settings['writeChanges'] = NodeSetting.create({
      description: 'Save changes on save',
      type: SettingType.BOOLEAN,
      value: false,
    });

    this.settings['reboot'] = NodeSetting.create({
      description: 'Reboot. Warning this will reboot the device',
      type: SettingType.BOOLEAN,
      value: false,
    });

    this.settings['eth0_group'] = NodeSetting.create({
      description: 'ETH-1', //rubix port 1
      value: '',
      type: SettingType.GROUP,
    });

    this.settings['interfaceTypeEth0'] = NodeSetting.create({
      description: 'Interface Type eth0',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: 'DHCP' },
          { value: 1, text: 'Static' },
        ],
      },
      value: 0,
    });
    this.settings['newIpEth0'] = NodeSetting.create({
      description: 'IP',
      value: '192.168.0.40',
      type: SettingType.STRING,
    });

    this.settings['netNetmaskEth0'] = NodeSetting.create({
      description: 'Netmask eth0',
      value: '255.255.255.0',
      type: SettingType.STRING,
    });
    this.settings['newGatewayEth0'] = NodeSetting.create({
      description: 'Gateway',
      value: '192.168.0.1',
      type: SettingType.STRING,
    });

    this.settings['newNameServerEth0'] = NodeSetting.create({
      description: 'Name Servers eth0',
      value: '8.8.8.8',
      type: SettingType.STRING,
    });

    this.settings['eth1_group'] = NodeSetting.create({
      description: 'ETH-2', //rubix port 2
      value: '',
      type: SettingType.GROUP,
    });

    this.settings['interfaceTypeEth1'] = NodeSetting.create({
      description: 'Interface Type eth1',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: 'DHCP' },
          { value: 1, text: 'Static' },
        ],
      },
      value: 0,
    });
    this.settings['newIpEth1'] = NodeSetting.create({
      description: 'IP',
      value: '192.168.1.40',
      type: SettingType.STRING,
    });

    this.settings['netNetmaskEth1'] = NodeSetting.create({
      description: 'Netmask eth1',
      value: '255.255.255.0',
      type: SettingType.STRING,
    });
    this.settings['newGatewayEth1'] = NodeSetting.create({
      description: 'Gateway',
      value: '192.168.1.1',
      type: SettingType.STRING,
    });

    this.settings['newNameServerEth1'] = NodeSetting.create({
      description: 'Name Servers eth1',
      value: '8.8.8.8',
      type: SettingType.STRING,
    });

    this.setSettingsConfig({
      groups: [
        { newIpEth0: {}, netNetmaskEth0: {} },
        { newGatewayEth0: {}, newNameServerEth0: {} },
        { newIpEth1: {}, netNetmaskEth1: {} },
        { newGatewayEth1: {}, newNameServerEth1: {} },
      ],
      conditions: {
        newIpEth0: setting => {
          const val = setting['interfaceTypeEth0'].value;
          return ![0].includes(val);
        },
        netNetmaskEth0: setting => {
          const val = setting['interfaceTypeEth0'].value;
          return ![0].includes(val);
        },
        newGatewayEth0: setting => {
          const val = setting['interfaceTypeEth0'].value;
          return ![0].includes(val);
        },
        newNameServerEth0: setting => {
          const val = setting['interfaceTypeEth0'].value;
          return ![0].includes(val);
        },
        newIpEth1: setting => {
          const val = setting['interfaceTypeEth1'].value;
          return ![0].includes(val);
        },
        netNetmaskEth1: setting => {
          const val = setting['interfaceTypeEth1'].value;
          return ![0].includes(val);
        },
        newGatewayEth1: setting => {
          const val = setting['interfaceTypeEth1'].value;
          return ![0].includes(val);
        },
        newNameServerEth1: setting => {
          const val = setting['interfaceTypeEth1'].value;
          return ![0].includes(val);
        },
      },
    });
  }

  onAdded() {
    setTimeout(() => {
      this.getInterfaces();
    }, 2000);
  }

  onInputUpdated() {
    let enableInterfaceEth0 = this.getInputData(1);
    let enableInterfaceEth1 = this.getInputData(2);

    //refresh the interfaces
    if (this.inputs[0].updated) {
      this.msgOut(this.messageOutput, 'called getInterfaces' + ' ' + this.timeStamp);
      setTimeout(() => {
        this.getInterfaces();
      }, 2000);
    }
    // enable/disable eth0
    if (this.inputs[1].updated) {
      if (enableInterfaceEth0 === true) {
        this.msgOut(this.messageOutput, 'called enable ETH-1 true' + ' ' + this.timeStamp);
        this.enableNetwork(this.interfaceEth0);
      } else if (enableInterfaceEth0 === false) {
        this.msgOut(this.messageOutput, 'called disable ETH-1 false' + ' ' + this.timeStamp);
        this.disableNetwork(this.interfaceEth0);
      }
    }
    // enable/disable eth1
    if (this.inputs[2].updated) {
      if (enableInterfaceEth1 === true) {
        this.msgOut(this.messageOutput, 'called enable ETH-2 true' + ' ' + this.timeStamp);
        this.enableNetwork(this.interfaceEth1);
      } else if (enableInterfaceEth1 === false) {
        this.msgOut(this.messageOutput, 'called disable ETH-2 false' + ' ' + this.timeStamp);
        this.disableNetwork(this.interfaceEth1);
      }
    }
  }

  onAfterSettingsChange() {
    if (this.side !== Side.server) return; // only execute on server
    const newIpEth0 = this.settings['newIpEth0'].value;
    const netNetmaskEth0 = this.settings['netNetmaskEth0'].value;
    const newGatewayEth0 = this.settings['newGatewayEth0'].value;
    const newNameServerEth0 = this.settings['newNameServerEth0'].value;
    const interfaceTypeEth0 = this.settings['interfaceTypeEth0'].value;

    const newIpEth1 = this.settings['newIpEth1'].value;
    const netNetmaskEth1 = this.settings['netNetmaskEth1'].value;
    const newGatewayEth1 = this.settings['newGatewayEth1'].value;
    const newNameServerEth1 = this.settings['newNameServerEth1'].value;
    const interfaceTypeEth1 = this.settings['interfaceTypeEth1'].value;

    const writeChanges = this.settings['writeChanges'].value;
    const reboot = this.settings['reboot'].value;

    const checkIpSettingsEth0 = IpAddressUtils.checkIpValid(
      newIpEth0,
      netNetmaskEth0,
      newGatewayEth0,
      newNameServerEth0,
    );
    const checkIpSettingsEth1 = IpAddressUtils.checkIpValid(
      newIpEth1,
      netNetmaskEth1,
      newGatewayEth1,
      newNameServerEth1,
    );
    //device reboot
    if (reboot) {
      this.msgOut(
        this.messageOutput,
        'called sudo reboot will reboot in 10 sec' + ' ' + this.timeStamp,
      );
      //set setting back to false so its not ticked after reboot
      this.settings['reboot'].value = false;
      this.broadcastSettingsToClients();
      //reboot after 10 sec
      setTimeout(() => {
        this.execCommand(`sudo reboot`);
      }, 10000);
    }

    // check if OS is the PI
    OsUtils.systemInfo()
      .then(e => {
        if (e.system.model === "BCM2835") {
          if (checkIpSettingsEth0 && checkIpSettingsEth1 && writeChanges) {
            //network update
            let interfacesArray = [];
            if (interfaceTypeEth0 == 0) {
              //DHCP
              this.title = 'Adapter' + +' ' + '(DHCP)';
              let eth0 = {
                interface: this.interfaceEth0,
                dhcp: true,
              };
              interfacesArray.push(eth0);
            }
            if (interfaceTypeEth0 == 1) {
              //Static
              this.title = 'Adapter' + ' ' + '(Static)';
              let subnetMaskLength = ip.subnet(newIpEth0, netNetmaskEth0);
              let eth0 = {
                interface: this.interfaceEth0,
                ip_address: newIpEth0,
                prefix: subnetMaskLength.subnetMaskLength,
                gateway: newGatewayEth0,
                nameservers: [newNameServerEth0],
              };
              interfacesArray.push(eth0);
            }

            if (interfaceTypeEth1 == 0) {
              //DHCP
              this.title = 'Adapter' + +' ' + '(DHCP)';
              let eth1 = {
                interface: this.interfaceEth1,
                dhcp: true,
              };
              interfacesArray.push(eth1);
            }

            if (interfaceTypeEth1 == 1) {
              //Static
              this.title = 'Adapter' + ' ' + '(Static)';
              let subnetMaskLength = ip.subnet(newIpEth1, netNetmaskEth1);
              let eth1 = {
                interface: this.interfaceEth1,
                ip_address: newIpEth1,
                prefix: subnetMaskLength.subnetMaskLength,
                gateway: newGatewayEth1,
                nameservers: [newNameServerEth1],
              };
              interfacesArray.push(eth1);
            }
            set_ip_address.configure(interfacesArray).then(() => {
              this.msgOut(this.messageOutput, 'updated' + ' ' + this.timeStamp);
              this.msgOut(this.errorOutput, false);
            });
          } else this.msgOut(this.messageOutput, 'invalid ip settings');
        } else {
          // set node to red to show its the incorrect device type
          NodeColours.nodeColourChange(this, NodeState.ERROR);
          this.msgOut(this.messageOutput, `'ERROR: incorrect device type ${e.system.model}`)
        }
      }).catch(err => this.msgOut(this.messageOutput, `'ERROR: incorrect device type' ${err}`));

    if (!writeChanges) {
      // user didnt toggle the save button
      this.msgOut(this.messageOutput, 'nothing saved' + ' ' + this.timeStamp);
      this.msgOut(this.errorOutput, true);
    }
  }
  //msg output
  msgOut = (num: number, msg: any) => {
    this.setOutputData(num, msg);
  };

  execCommand(command: string) {
    if (this.side !== Side.server) return;
    try {
      exec(command, (err, stdout, stderr) => {
        this.msgOut(this.messageOutput, 'called command' + ' ' + command + ' ' + this.timeStamp);
        if (stdout) {
          this.msgOut(this.messageOutput, stdout + ' ' + this.timeStamp);
          this.msgOut(this.errorOutput, false);
        }
        if (err) {
          this.msgOut(this.messageOutput, err + ' ' + this.timeStamp);
          this.msgOut(this.errorOutput, true);
        }
        if (stderr) {
          this.msgOut(this.messageOutput, stderr + ' ' + this.timeStamp);
          this.msgOut(this.errorOutput, true);
        }
      });
    } catch (e) {
      this.msgOut(this.messageOutput, e + ' ' + this.timeStamp);
      this.msgOut(this.errorOutput, true);
      // this.debugInfo(e);
    }
  }

  getInterfaces() {
    IpAddressUtils.getActiveNetworks()
      .then(list => {
        //eth0
        const getInterfaceEth0 = list.filter(e => {
          return e.name == this.interfaceEth0;
        });
        this.msgOut(this.eth0Ip, getInterfaceEth0[0].ip_address); //ip
        this.msgOut(this.errorOutput, false); //error

        //eth1
        const getInterfaceEth1 = list.filter(e => {
          return e.name == this.interfaceEth1;
        });
        this.msgOut(this.eth1Ip, getInterfaceEth1[0].ip_address); //ip
        this.msgOut(this.errorOutput, false); //error
      })
      .catch(err => {
        this.msgOut(this.messageOutput, err);
        this.msgOut(this.errorOutput, true);
      });
  }

  enableNetwork(iface: string) {
    try {
      IpAddressUtils.networkUpDown(iface, 'up')
        .then(r => {
          console.log(r);
          setTimeout(() => {
            this.getInterfaces();
          }, 5000);
        })
        .catch(e => console.log(e));
    } catch (err) {
      console.log(err);
    }
  }

  disableNetwork(iface: string) {
    try {
      IpAddressUtils.networkUpDown(iface, 'down')
        .then(r => {
          console.log(r);
        })
        .catch(err => console.log(err));
    } catch (err) {
      console.log(err);
    }
  }
}
Container.registerNodeType('system/network-interfaces', UpdateIP);
