import { NodeSetting } from '../node-io';

export default class SettingUtils {
  /**
   * Inserts new setting into specific position in this.settings
   * @returns {object}
   */
  static insertIntoObjectAtPosition(
    object: { [name: string]: NodeSetting },
    insert: object,
    after: string = '',
    before: string = '',
  ): { [name: string]: NodeSetting } {
    if (before === '' && after === '') {
      // add to end
      Object.assign(object, insert);
      return object;
    }
    // Create a temp object and index variable
    const newObj = {};
    let insertAfter = false;
    const matchBoth = before !== '' && after !== '';
    let foundFlag = false;

    // Loop through the original object
    for (let prop in object) {
      if (object.hasOwnProperty(prop)) {
        // look for matches for before and after keys
        if (!foundFlag && matchBoth && insertAfter && before === prop) {
          Object.assign(newObj, insert);
          foundFlag = true;
          insertAfter = false;
        } else if (!foundFlag && !matchBoth && (insertAfter || before === prop)) {
          Object.assign(newObj, insert);
          foundFlag = true;
          insertAfter = false;
        } else if (!foundFlag && prop === after) {
          insertAfter = true;
        }
        // Add the current item in the loop
        newObj[prop] = object[prop];
      }
    }
    if (!foundFlag) Object.assign(newObj, insert);

    return newObj;
  }
}
