import { Colors, create } from 'logplease';
import { connect, IClientPublishOptions, IClientSubscribeOptions, MqttClient } from 'mqtt';
import { MQTTMessage } from './mqtt-model';

const logger = create('mqtt', { color: Colors.Blue });

export interface MqttClientWrapper {

  start(): MqttClientWrapper;

  stop(): void;

  isConnected(): boolean;

  publish(topic: string, payload: string, options?: {}, cb?: any): void;

  subscribe(topic: string, options?: {}, cb?: any): void;

  unsubscribe(topic: string, cb?: any): void;
}

export interface MqttLifecycle {

  /**
   * Emitted on successful (re)connection (i.e. connack rc=0).
   * @param connack received connack packet.
   */
  onConnect(connack): void;

  /**
   * Emitted when a reconnect starts.
   */
  onReconnect(): void;

  /**
   * Emitted after a disconnection.
   */
  onClose(): void;

  /**
   * Emitted after receiving disconnect packet from broker. MQTT 5.0 feature.
   */
  onDisconnect(): void;

  /**
   * Emitted when the client goes offline.
   */
  onOffline(): void;

  /**
   * Emitted when the client cannot connect (i.e. connack rc != 0) or when a parsing error occurs.
   */
  onError(err): void;

  /**
   * Emitted when mqtt.Client#end() is called.
   * If a callback was passed to mqtt.Client#end(), this event is emitted once the callback returns.
   */
  onEnd(): void;

  /**
   * Emitted when the client receives a publish packet
   * @param topic topic of the received packet
   * @param message payload of the received packet
   * @param packet received packet, as defined in mqtt-packet
   */
  onMessage(topic, message, packet): void;

  /**
   * Emitted when the client sends any packet.
   * This includes .published() packets as well as packets used by MQTT for managing subscriptions and connections
   * @param packet received packet, as defined in mqtt-packet
   */
  onPacketSend(packet): void;

  /**
   * Emitted when the client receives any packet.
   * This includes packets from subscribed topics as well as packets used by MQTT for managing subscriptions and connections
   * @param packet received packet, as defined in mqtt-packet
   */
  onPacketReceive(packet): void;

}

export class DefaultMqttClient implements MqttClientWrapper, MqttLifecycle {

  private readonly options: {};
  private readonly callbackWhenConnected: (client: MqttClientWrapper) => void;
  private readonly callbackWhenOffline: (client: MqttClientWrapper, offlineMsg?: string) => void;
  private readonly callbackIncomingMessage: (msg: MQTTMessage) => void;
  private client: MqttClient;
  private connected: boolean = false;
  private clientId: string;

  constructor(options: {}, callbackWhenConnected: (client: MqttClientWrapper) => void,
              callbackWhenOffline: (client: MqttClientWrapper, offlineMsg?: string) => void,
              callbackIncomingMessage: (msg: MQTTMessage) => void) {
    this.options = options;
    this.callbackWhenConnected = callbackWhenConnected;
    this.callbackWhenOffline = callbackWhenOffline;
    this.callbackIncomingMessage = callbackIncomingMessage;
  }

  static init(options: {}, callbackWhenConnected: (client: MqttClientWrapper) => void,
              callbackWhenOffline: (client: MqttClientWrapper, offlineMsg?: string) => void,
              callbackIncomingMessage: (msg: MQTTMessage) => void): MqttClientWrapper {
    return new DefaultMqttClient(options, callbackWhenConnected, callbackWhenOffline, callbackIncomingMessage).start();
  }

  onClose(): void {
    logger.debug(`MQTT Client ${this.clientId} closed`);
    this.connected = false;
  }

  onConnect(connack): void {
    logger.info(`MQTT Client ${this.clientId} connected ${JSON.stringify(connack)}`);
    this.connected = true;
    if (this.callbackWhenConnected) {
      this.callbackWhenConnected(this);
    }
  }

  onDisconnect(): void {
    logger.warn(`MQTT Client ${this.clientId} disconnected`);
    this.connected = false;
    if (this.callbackWhenOffline) {
      this.callbackWhenOffline(this, `Unable connect to server`);
    }
  }

  onEnd(): void {
    logger.info(`MQTT Client ${this.clientId} ended`);
    this.connected = false;
    if (this.callbackWhenOffline) {
      this.callbackWhenOffline(this);
    }
  }

  onError(err): void {
    logger.error(`Error connection in MQTT Client ${this.clientId}: ${err}`);
    this.stop();
  }

  onMessage(topic, message, packet): void {
    logger.info(`MQTT Client '${this.clientId}' dispatch message on topic '${topic}'`);
    this.callbackIncomingMessage({ topic, message: message.toString() } as MQTTMessage);
  }

  onOffline(): void {
    logger.warn(`MQTT Client '${this.clientId}' is offline`);
    this.connected = false;
    if (this.callbackWhenOffline) {
      this.callbackWhenOffline(this, `Unable connect to server`);
    }
  }

  onPacketReceive(packet): void {
    logger.debug(`MQTT Client '${this.clientId}' receives packet '${packet?.cmd}'`);
  }

  onPacketSend(packet): void {
    logger.debug(`MQTT Client '${this.clientId}' sends packet '${packet?.cmd}'`);
  }

  onReconnect(): void {
    logger.info(`MQTT Client '${this.clientId}' reconnected`);
    if (this.callbackWhenConnected) {
      this.callbackWhenConnected(this);
    }
  }

  start(): MqttClientWrapper {
    this.client = connect(this.options);
    this.client.on('connect', connack => this.onConnect(connack));
    this.client.on('close', () => this.onClose());
    this.client.on('disconnect', () => this.onDisconnect());
    this.client.on('end', () => this.onEnd());
    this.client.on('error', err => this.onError(err));
    this.client.on('offline', () => this.onOffline());
    this.client.on('packetreceive', packet => this.onPacketReceive(packet));
    this.client.on('packetsend', packet => this.onPacketSend(packet));
    this.client.on('message', (topic, msg, packet) => this.onMessage(topic, msg, packet));
    this.clientId = this.client?.options?.clientId;
    return this;
  }

  stop(): void {
    if (this.client) {
      this.client.end(true);
    }
  }

  isConnected(): boolean {
    return this.client && this.connected;
  }

  publish(topic: string, payload: string, options?: {}, cb?: any): void {
    if (!this.isConnected()) {
      return;
    }
    this.client.publish(topic, payload, (options || { retain: true }) as IClientPublishOptions, cb);
  }

  subscribe(topic: string, options?: {}, cb?: any): void {
    if (!this.isConnected()) {
      return;
    }
    logger.info(`MQTT Client '${this.clientId}' subscribe topic '${topic}'...`);
    this.client.subscribe(topic, (options || {}) as IClientSubscribeOptions, cb);
  }

  unsubscribe(topic: string, cb?: any): void {
    if (!this.isConnected()) {
      return;
    }
    logger.info(`MQTT Client '${this.clientId}' unsubscribe topic '${topic}'...`);
    if (topic) {
      this.client.unsubscribe(topic, cb);
    }
  }
}
