import axios from 'axios';
import * as moment from 'moment-timezone';
import { Container } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import TimeUtils, { TIME_TYPE } from '../../utils/time-utils';
import { bsaClientConfig } from './bsa-client-config';
import { NODE_INFO } from '../../node-info';

enum HistoryMode {
  COV = 0,
  PERIODIC = 1,
  TRIGGER_ONLY = 2,
}

class BSACumulocityNode extends Node {
  // dynamically calculate position when starting
  histTriggerInput: number;
  clearStoredHisInput: number;
  histErrorOutput: number;
  storedHistCountOutput: number;
  lastHistExportOutput: number;
  // configurable parameters from each extended node for extracting values to store on DB
  valueInput: number = 0;
  // extra parameter for nodes
  interval: number = -1;
  timeoutFunc: any;

  constructor() {
    super();
    this.title = NODE_INFO.cumulocity["cumulocity-point"].title;
    this.description =  NODE_INFO.cumulocity["cumulocity-point"].description;
    this.addInput('input', Type.ANY);
  }

  addHistoryConfiguration() {
    this.addInput('histTrigger', Type.BOOLEAN);
    this.addInput('clearStoredHis', Type.BOOLEAN);
    this.addOutput('histError', Type.ANY);
    this.addOutput('storedHistCount', Type.NUMBER);
    this.addOutput('lastHistExport', Type.STRING);

    /**
     * History settings
     */
    this.settings['enable'] = NodeSetting.create({
      description: 'History Enable',
      value: false,
      type: SettingType.BOOLEAN,
    });

    this.settings['CumulocityDeviceID'] = NodeSetting.create({
      description: 'Cumulocity Device ID (Number)',
      value: '',
      type: SettingType.STRING,
    });

    this.settings['CumulocityPointName'] = NodeSetting.create({
      description: 'Measurement Name',
      value: '',
      type: SettingType.STRING,
    });

    this.settings['CumulocityPointType'] = NodeSetting.create({
      description: 'Measurement Type',
      value: '',
      type: SettingType.STRING,
    });

    this.settings['CumulocitySeries'] = NodeSetting.create({
      description: 'Series Name',
      value: '',
      type: SettingType.STRING,
    });

    this.settings['historyMode'] = NodeSetting.create({
      description: 'History Logging Mode',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: HistoryMode.COV, text: 'Change Of Value (COV)' },
          { value: HistoryMode.PERIODIC, text: 'Periodic' },
          { value: HistoryMode.TRIGGER_ONLY, text: 'Trigger Only' },
        ],
      },
      value: HistoryMode.COV,
    });

    this.settings['dataType'] = NodeSetting.create({
      description: 'Data type for storing',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: Type.NUMBER, text: 'Number' },
          { value: Type.BOOLEAN, text: 'Boolean' },
          { value: Type.STRING, text: 'String' },
        ],
      },
      value: Type.NUMBER,
    });
    this.settings['threshold'] = NodeSetting.create({
      description: 'COV Threshold',
      value: 0,
      type: SettingType.NUMBER,
    });

    this.settings['period'] = NodeSetting.create({
      description: 'Logging Interval',
      value: 15,
      type: SettingType.NUMBER,
    });
    this.settings['periodUnits'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: TIME_TYPE.MILLISECONDS, text: 'Milliseconds' },
          { value: TIME_TYPE.SECONDS, text: 'Seconds' },
          { value: TIME_TYPE.MINUTES, text: 'Minutes' },
          { value: TIME_TYPE.HOURS, text: 'Hours' },
        ],
      },
      value: TIME_TYPE.MINUTES,
    });

    this.settings['storage-limit'] = NodeSetting.create({
      description: 'Local Storage Limit (Max 50)',
      value: 25,
      type: SettingType.NUMBER,
    });
    this.settings['minuteRoundValue'] = NodeSetting.create({
      description: 'Round minutes up in increments of',
      value: 0,
      type: SettingType.NUMBER,
    });
    this.settings['decimals'] = NodeSetting.create({
      description: 'Decimal Places (Limit 5)',
      value: 3,
      type: SettingType.NUMBER,
    });
    this.settings['timeAsString'] = NodeSetting.create({
      description: 'Display Timestamp as',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: false, text: 'Numeric' },
          { value: true, text: 'String' },
        ],
      },
      value: true,
    });

    this.settings['units'] = NodeSetting.create({
      description: 'Units',
      value: '',
      type: SettingType.STRING,
    });

    this.properties['obj'] = [];
  }

  setup() {
    this.addHistoryConfiguration();
  }

  init() {
    this.assignInputsOutputs();
  }

  onAdded() {
    this.assignInputsOutputs();
    this.resetOutputs();
    this.doPeriodicHistoryFunctions();
  }

  async onInputUpdated() {
    if (this.getInputData(this.clearStoredHisInput)) {
      this.properties['obj'] = [];
      this.updateHistoryCountOutput();
    }
    await this.doNonPeriodicHistoryFunctions();
  }

  async onAfterSettingsChange() {
    await this.doNonPeriodicHistoryFunctions();
    this.doPeriodicHistoryFunctions();
  }

  onRemoved() {
    clearInterval(this.timeoutFunc);
  }

  assignInputsOutputs() {
    for (let input in this.inputs) {
      if (this.inputs.hasOwnProperty(input)) {
        if (this.inputs[input].name == 'histTrigger') this.histTriggerInput = Number(input);
        if (this.inputs[input].name == 'clearStoredHis') this.clearStoredHisInput = Number(input);
      }
    }

    for (let output in this.outputs) {
      if (this.outputs.hasOwnProperty(output)) {
        if (this.outputs[output].name == 'histError') this.histErrorOutput = Number(output);
        else if (this.outputs[output].name == 'storedHistCount') this.storedHistCountOutput = Number(output);
        else if (this.outputs[output].name == 'lastHistExport') this.lastHistExportOutput = Number(output);
      }
    }
  }

  resetOutputs() {
    this.updateHistoryCountOutput();
  }

  async doNonPeriodicHistoryFunctions() {
    if (this.side !== Side.server) return;
    if (!this.settings['enable'].value) return;
    const input = this.getInputData(this.valueInput);

    if (this.checkTriggered()) {
      await this.storeLogEntry(input);
    } else if (this.checkCOV(input)) {
      await this.storeLogEntry(input);
    }

    if (this.properties['obj'].length > 0) await this.trySendStoredData();
  }

  async trySendStoredData() {
    let decimals = this.settings['decimals'].value;
    decimals = Utils.clamp(decimals, 0, 5);

    const multiPointPost = [];

    let measurementName = this.settings['CumulocityPointName'].value || 'c8y_unknownPoint';
    if (!measurementName.startsWith('c8y_')) measurementName = 'c8y_' + measurementName;
    let measurementType = this.settings['CumulocityPointType'].value || 'c8y_unknownType';
    if (!measurementType.startsWith('c8y_')) measurementType = 'c8y_' + measurementType;
    const seriesName = this.settings['CumulocitySeries'].value || 'unknownSeries';

    this.properties['obj'].forEach(log => {
      const dataType = this.settings['dataType'].value;
      log.payload = this.convertInput(log.payload, dataType, decimals);
      multiPointPost.push({
        source: {
          id: this.settings['CumulocityDeviceID'].value,
        },
        time: log.timestamp,
        type: measurementType,
        [measurementName]: {
          [seriesName]: {
            value: log.payload,
            unit: String(this.settings['units'].value) || 'undefined',
          },
        },
      });
    });
    this.debugInfo(`SENDING DATA: ${JSON.stringify(multiPointPost)}`);
    let cfg: any = bsaClientConfig('measurement');
    cfg = { ...cfg, method: 'POST', data: { measurements: multiPointPost } };
    try {
      const response = await axios(cfg);
      this.debugInfo(`RESPONSE FROM SERVER: ${JSON.stringify(response.data)}`);
      this.properties['obj'] = [];
      this.persistProperties(false, true);
      this.setOutputData(this.histErrorOutput, '');
      this.settings['timeAsString'].value
        ? this.setOutputData(this.lastHistExportOutput, moment().format(), true)
        : this.setOutputData(this.lastHistExportOutput, moment().valueOf(), true);
      this.updateHistoryCountOutput();
    } catch (e) {
      this.debugErr(`ERROR: ${e}`);
      this.setOutputData(this.histErrorOutput, e);
    }
  }

  async storeLogEntry(input) {
    if (input == null) return;
    // cap the amount of data that can be stored
    const storageLimit = Utils.clamp(this.settings['storage-limit'].value, 0, 50);
    const minuteRound = Utils.clamp(this.settings['minuteRoundValue'].value, 0, 60);
    const now = minuteRound ? TimeUtils.nearestFutureMinutes(minuteRound, moment()) : moment();
    // object data
    const msg = {
      payload: input,
      timestamp: now.toISOString(),
    };
    // build an object
    this.properties['obj'].push(msg);
    while (this.properties['obj'].length > storageLimit) {
      this.properties['obj'].shift();
    }
    this.properties['lastHistoryValue'] = input;
    this.persistProperties(false, true);
    this.updateHistoryCountOutput();
  }

  checkTriggered() {
    return (
      this.settings['historyMode'].value === HistoryMode.TRIGGER_ONLY &&
      this.getInputData(this.histTriggerInput) &&
      this.inputs[this.histTriggerInput].updated
    );
  }

  checkCOV(input) {
    if (this.settings['historyMode'].value === HistoryMode.COV) {
      const threshold = this.settings['threshold'].value;
      if (!isNaN(input) && !isNaN(this.properties['lastHistoryValue'])) {
        return Math.abs(Number(input) - Number(this.properties['lastHistoryValue'])) >= threshold;
      } else {
        return input !== this.properties['lastHistoryValue'];
      }
    }
    return false;
  }

  /**
   * Sets values in periodic period when HistoryMode is `PERIODIC`
   */
  doPeriodicHistoryFunctions() {
    if (this.side !== Side.server) return;
    const enable = this.settings['enable'].value;
    const isPeriodic = this.settings['historyMode'].value === HistoryMode.PERIODIC;
    if (!enable || !isPeriodic) {
      if (this.timeoutFunc) {
        this.interval = -1;
        clearInterval(this.timeoutFunc);
        this.debugInfo('Clearing Periodic interval data insertion...');
      }
    }
    if (enable && isPeriodic) {
      const interval = TimeUtils.timeConvert(this.settings['period'].value, this.settings['periodUnits'].value);
      if (this.interval !== interval) {
        clearInterval(this.timeoutFunc);
        this.timeoutFunc = setInterval(async () => {
          const input = this.getInputData(this.valueInput);
          await this.storeLogEntry(input);
          if (this.properties['obj'].length > 0) await this.trySendStoredData();
        }, interval);
        this.interval = interval;
        this.debugInfo(`Setting Periodic Period of: ${interval} ms`);
      }
    }
  }

  updateHistoryCountOutput() {
    this.setOutputData(this.storedHistCountOutput, (this.properties['obj'] && this.properties['obj'].length) || 0);
  }
}

Container.registerNodeType('cumulocity/cumulocity-point', BSACumulocityNode);
