import * as Bacnet from 'node-bacnet';
import { BACNET_DEVICE, BACNET_NETWORK } from '../../../constants';
import { Container } from '../../../container';
import { ContainerNode } from '../../../container-node';
import { Node } from '../../../node';
import { NODE_INFO } from '../../../node-info';
import { Side } from '../../../node-interface';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import Utils from '../../../utils';
import {
  ADD_POINT,
  GET_NETWORK_NODE,
  GET_POINTS,
  GET_PRESENT_VALUE,
  REMOVE_POINT,
  SEND_PAYLOAD_TO_CHILD,
  WRITE_PRESENT_VALUE,
} from './bacnet-constant';
import { BACnetNetwork } from './bacnet-network';
import BacnetUtils, { singleton as actor } from './bacnet-utils';
import { DeviceObject } from './interface';

enum Poll {
  STOP = 0,
  START = 1,
}

class BACnetDevice extends ContainerNode {
  bacnetClient: Bacnet = null;
  networkNode: BACnetNetwork = null;
  points: Array<DeviceObject> = [];
  pointNodes: Array<Node> = [];
  isFirst: boolean = true;
  isPolling: boolean = false;
  shouldPoll: boolean = true;
  delayBetweenDevice: number = 100;
  delayBetweenPoint: number = 30;
  outputMsg: 1;
  networkNumber: number = null;
  constructor() {
    super();
    this.title = NODE_INFO['protocols/bacnet']['bacnet-device'].title;
    this.description = NODE_INFO['protocols/bacnet']['bacnet-device'].description;
    this.addInput('get-point-list', Type.BOOLEAN);
    this.addOutput('out', Type.STRING);
    this.addOutput('out msg', Type.STRING);
    this.addOutput('error', Type.STRING);
    this.addOutput('points-discovered', Type.STRING);
    this.addOutput('points-to-poll', Type.STRING);
    this.properties['pointsList'] = null;
    this.settings['deviceEnable'] = NodeSetting.create({
      description: 'Device enable',
      value: false,
      type: SettingType.BOOLEAN,
    });

    // device
    this.settings['ip'] = NodeSetting.create({ description: 'ip', value: '192.168.13.13', type: SettingType.STRING });
    this.settings['deviceId'] = NodeSetting.create({ description: 'deviceId', value: 2508, type: SettingType.NUMBER });
    this.settings['port'] = NodeSetting.create({ description: 'port', value: 47808, type: SettingType.NUMBER });

    // device mac address if under a IP to MS/TP router
    this.settings['deviceTypeMstp'] = NodeSetting.create({
      description: 'BACnet MS/TP device',
      value: false,
      type: SettingType.BOOLEAN,
    });
    // device mac address if under a IP to MS/TP router
    this.settings['macAddress'] = NodeSetting.create({
      description: 'BACnet Mac address',
      value: 1,
      type: SettingType.NUMBER,
    });

    this.settings['networkNumber'] = NodeSetting.create({
      description: 'BACnet network number ',
      // value: null,
      type: SettingType.READONLY,
    });
  }

  async onAdded() {
    super.onAdded();
    // this.name = `BACnet Device: cid_${this.container.id.toString()}_sid_${this.sub_container.id.toString()}_id${this.id.toString()}`; //set default node name
    await Utils.sleep(1000); // TODO: Since device should start after network, import may shows issue if we don't have this
    this.setBacnetClient();
    this.setPayloadsToPointNodes();
    if (this.side !== Side.server) return;
    BacnetUtils.addDevice(this.getParentNode(), this);
    this.updateNetworkSettings();
    await this.requestPoll();
  }

  onAfterSettingsChange(oldSettings: any) {
    super.onAfterSettingsChange(oldSettings);
    this.setPayloadsToPointNodes();
    this.requestPoll().then(); // don't await, it will unable to set setting value
  }

  onRemoved() {
    super.onRemoved();
    this.shouldPoll = false;
    BacnetUtils.removeDevice(this.getParentNode(), this);
  }

  updateNetworkSettings() {
    if (this.side !== Side.server) return;
    this.networkNode = BacnetUtils.getNetworkNode(this.getParentNode());
    this.networkNumber = this.networkNode.settings['networkNumber'].value;
    this.settings['networkNumber'].value = this.networkNumber;
    this.broadcastSettingsToClients();
    this.persistSettings();
  }

  onInputUpdated() {
    let pntList = [];
    for (let pointNode of this.pointNodes) {
      const settings = pointNode.settings;
      const id = pointNode.id;
      const cid = pointNode.id;
      const name = pointNode.name;
      let a = {
        id: id,
        cid: cid,
        name: name,
        settings: settings,
      };
      pntList.push(a);
    }
    this.setOutputData(4, JSON.stringify(pntList));
    this.setOutputData(3, JSON.stringify(this.points));
  }

  async requestPoll() {
    if (this.currentPollValue() === Poll.START && !this.isPolling) {
      this.isPolling = true;
      await this.polling();
    }
  }

  // check if network and device are enabled
  currentPollValue(): Poll {
    return this.settings['deviceEnable'].value && this.networkNode.getInputData(this.networkNode.inNetworkEnable)
      ? Poll.START
      : Poll.STOP;
  }
  // send points list to point node
  setPayloadsToPointNodes() {
    if (this.side !== Side.server) return; // only execute on server
    this.setOutputData(this.outputMsg, `INFO: Scan devices`);
    actor
      .process<Array<DeviceObject>>(() => this.scanDeviceFunc())
      .then(points => {
        this.points = points;
        this.sendPayloadToPointNodesFunc();
      });
    actor.process(async () => await Utils.sleep(this.delayBetweenDevice)).then();
  }

  // send points list to point node
  sendPayloadToPointNodesFunc() {
    for (let pointNode of this.pointNodes) {
      BacnetUtils.sendPayloadToChild(pointNode, {
        points: this.points,
        networkNode: this.networkNode,
      });
    }
  }

  // get bacnet client from network node
  setBacnetClient() {
    if (this.side !== Side.server) return;
    this.bacnetClient = BacnetUtils.getBacnetClient(this.getParentNode());
  }

  // bacnet lib point write function
  writePresentValue(deviceAddress, type, instance, value, priority, tag) {
    if (!this.bacnetClient) return;
    if (this.settings['deviceTypeMstp'].value) {
      this.bacnetClient.writeProperty(
        {
          address: deviceAddress,
          net: this.networkNumber,
          adr: [this.settings['macAddress'].value],
        },
        { type: type, instance: instance },
        85,
        [{ type: tag, value: value }],
        { priority: priority },
        (err, value) => {
          console.log(err, value);
        },
      );
    } else {
      this.bacnetClient.writeProperty(
        deviceAddress,
        { type: type, instance: instance },
        85,
        [{ type: tag, value: value }],
        { priority: priority },
        (err, value) => {},
      );
    }
  }
  // bacnet lib get points list function
  readObjectList(deviceAddress, deviceId, callback) {
    // Read Device Object
    const requestArray = [
      {
        objectId: { type: Bacnet.enum.ObjectType.DEVICE, instance: deviceId },
        properties: [{ id: Bacnet.enum.PropertyIdentifier.OBJECT_LIST }],
      },
    ];
    if (!this.bacnetClient) return;
    if (this.settings['deviceTypeMstp'].value) {
      this.bacnetClient.readPropertyMultiple(
        {
          address: deviceAddress,
          net: this.networkNumber,
          adr: [this.settings['macAddress'].value],
        },
        requestArray,
        requestArray,
        callback,
      );
    } else this.bacnetClient.readPropertyMultiple(deviceAddress, requestArray, callback);
  }

  readObjectFull(deviceAddress, type): Promise<any> {
    return this.readObject(deviceAddress, type, [
      { id: Bacnet.enum.PropertyIdentifier.OBJECT_IDENTIFIER },
      { id: Bacnet.enum.PropertyIdentifier.OBJECT_NAME },
      { id: Bacnet.enum.PropertyIdentifier.OBJECT_TYPE },
      { id: Bacnet.enum.PropertyIdentifier.DESCRIPTION },
      { id: Bacnet.enum.PropertyIdentifier.UNITS },
      { id: Bacnet.enum.PropertyIdentifier.PRESENT_VALUE },
      // { id: Bacnet.enum.PropertyIdentifier.PRIORITY_ARRAY },
      // { id: Bacnet.enum.PropertyIdentifier.PRIORITY_FOR_WRITING },
    ]);
  }
  // bacnet lib point read function
  readObject(deviceAddress, obj, properties): Promise<any> {
    return new Promise(resolve => {
      const requestArray = [
        {
          objectId: { type: obj.type, instance: obj.instance },
          properties: properties,
        },
      ];
      if (!this.bacnetClient) return;
      if (this.settings['deviceTypeMstp'].value) {
        this.bacnetClient.readPropertyMultiple(
          {
            address: deviceAddress,
            net: this.networkNumber,
            adr: [this.settings['macAddress'].value],
          },
          requestArray,
          (error, value) => {
            resolve({
              error: error,
              value: value,
            });
          },
        );
      } else {
        this.bacnetClient.readPropertyMultiple(deviceAddress, requestArray, (error, value) => {
          resolve({
            error: error,
            value: value,
          });
        });
      }
    });
  }

  // bacnet lib point read function
  readProperty(deviceAddress, obj): Promise<any> {
    return new Promise(resolve => {
      if (this.settings['deviceTypeMstp'].value) {
        this.bacnetClient.readProperty(
          { address: deviceAddress, net: this.networkNumber, adr: [this.settings['macAddress'].value] },
          { type: obj.type, instance: obj.instance },
          Bacnet.enum.PropertyIdentifier.PRESENT_VALUE,
          (error, value) => {
            resolve({
              error: error,
              value: value,
            });
          },
        );
      } else {
        this.bacnetClient.readProperty(
          deviceAddress,
          { type: obj.type, instance: obj.instance },
          Bacnet.enum.PropertyIdentifier.PRESENT_VALUE,
          (error, value) => {
            resolve({
              error: error,
              value: value,
            });
          },
        );
      }
    });
  }

  readPresentValue(obj): any {
    return this.readProperty(this.settings['ip'].value, obj);
  }

  findValueById(properties, id) {
    const property = properties.find(function(element) {
      return element.id === id;
    });
    if (property && property.value && property.value.length > 0) {
      if (property.value[0].value.errorClass) {
        return null;
      }
      return property.value[0].value;
    } else {
      return null;
    }
  }

  mapToDeviceObject(object): DeviceObject {
    if (!object || !object.values) {
      return null;
    }
    const objectInfo = object.values[0].objectId.instance;
    const objectProperties = object.values[0].values;
    const name = this.findValueById(objectProperties, Bacnet.enum.PropertyIdentifier.OBJECT_NAME);
    const description = this.findValueById(objectProperties, Bacnet.enum.PropertyIdentifier.DESCRIPTION);
    const type = this.findValueById(objectProperties, Bacnet.enum.PropertyIdentifier.OBJECT_TYPE);
    const units = this.findValueById(objectProperties, Bacnet.enum.PropertyIdentifier.UNITS);
    const presentValue = this.findValueById(objectProperties, Bacnet.enum.PropertyIdentifier.PRESENT_VALUE);

    return { objectInfo, name, description, type, units, presentValue };
  }

  async scanDeviceFunc(): Promise<Array<DeviceObject>> {
    const device = {
      deviceId: this.settings['deviceId'].value,
      address: this.settings['ip'].value,
    };
    this.setOutputData(1, `INFO: Scan device ID: ${JSON.stringify(device)}`);
    this.setOutputData(2, ``);
    return new Promise((resolve, reject) => {
      this.readObjectList(device.address, device.deviceId, async (err, result) => {
        if (!err) {
          const objectArray = result.values[0].values[0].value;

          this.setOutputData(1, `INFO: Scan device ID: ${JSON.stringify(device)}`);
          this.setOutputData(2, ``);
          const outputs = [];
          for (const object of objectArray) {
            try {
              const output = await this.readObjectFull(device.address, {
                type: object.value.type,
                instance: object.value.instance,
              });
              outputs.push(output);
            } catch (error) {
              this.setOutputData(1, ``);
              this.setOutputData(2, `ERROR: while fetching objects: ${JSON.stringify(error)}`);
              reject(error);
            }
            await Utils.sleep(this.delayBetweenDevice);
          }
          const successfulResults = outputs.filter(element => !element.error);
          const deviceObjects = successfulResults.map(element => {
            return this.mapToDeviceObject(element.value);
          });
          this.debugInfo(`BACNET-NETWORK-POINT-NODE: msg from device ${JSON.stringify(deviceObjects)}`);
          this.setOutputData(1, `INFO: Scan device ID: ${JSON.stringify(deviceObjects)}`);
          resolve(deviceObjects);
        } else {
          this.setOutputData(1, ``);
          this.setOutputData(2, `ERROR: while fetching objects: ${JSON.stringify(err)}`);
          reject(err);
        }
      });
    });
  }

  subscribe({ action, payload }) {
    switch (action) {
      case ADD_POINT:
        this.pointNodes.push(payload);
        break;
      case REMOVE_POINT:
        this.pointNodes = this.pointNodes.filter(pointNode => pointNode.id !== payload.id);
        break;
      case GET_NETWORK_NODE:
        return BacnetUtils.getNetworkNode(this.getParentNode());
      case GET_POINTS:
        return this.points;
      case GET_PRESENT_VALUE:
        return this.readPresentValue(payload);
      case SEND_PAYLOAD_TO_CHILD:
        this.bacnetClient = payload.bacnetClient;
        this.networkNode = payload.networkNode;
        this.updateNetworkSettings();
        if (!this.isFirst) {
          // We can check settings with older settings if we want
          this.setPayloadsToPointNodes();
        }
        this.isFirst = false;
        this.requestPoll().then();
        break;
      case WRITE_PRESENT_VALUE:
        this.writePresentValue(this.settings['ip'].value, payload[0], payload[1], payload[2], payload[3], payload[4]);

        break;
      default:
        this.debugWarn("Request action doesn't match");
    }
  }

  persistSettings() {
    if (!this.container.db) return;
    this.container.db.updateNode(this.id, this.container.id, {
      $set: {
        settings: this.settings,
        properties: this.properties,
      },
    });
  }

  async polling() {
    if (this.currentPollValue() !== Poll.START || !this.shouldPoll) {
      this.isPolling = false;
      return;
    }
    await actor.process<any>(() => this.pollingFuncOrTimeout());
    await actor.process<any>(async () => await Utils.sleep(this.delayBetweenDevice));
    await Utils.sleep(this.networkNode.settings['devicePolling'].value);
    await this.polling();
  }

  /**
   * If any reason await function got to wait indefinitely long then timeout such function
   */
  pollingFuncOrTimeout(): Promise<any> {
    const that = this;
    return Promise.race([
      this.pollingFunc(),
      new Promise((_, reject) =>
        setTimeout(() => reject(new Error('timeout')), this.networkNode.settings['devicePolling'].value),
      ),
    ]).catch(function(err) {
      that.debugErr(`Error: ${err}`);
    });
  }

  async pollingFunc() {
    for (let pointNode of this.pointNodes) {
      let selectPoint = pointNode.settings['pointsList'].value;
      const manualPoint = pointNode.settings['manualPoint'].value;
      const objectType = pointNode.settings['objectType'].value;
      const objectInstance = pointNode.settings['objectInstance'].value;
      if (manualPoint) {
        selectPoint = { type: objectType, instance: objectInstance };
      }
      if (selectPoint) {
        const value = await this.readPresentValue(selectPoint);
        this.debugInfo(`BACNET-NETWORK-DEVICE-NODE: msg from readPresentValue ${JSON.stringify(value)}`);
        BacnetUtils.sendPointValue(pointNode, value);
        await Utils.sleep(this.delayBetweenPoint);
      }
    }
  }
}
Container.registerNodeType(BACNET_DEVICE, BACnetDevice, BACNET_NETWORK);
