import * as NeDBDataStore from 'nedb';
import { db_logger as logger } from '../../utils/loggers';
import { AbstractDatabase } from '../database/BaseDatabase';
import ScheduleDatabase from '../database/ScheduleDatabase';
import { Schedule } from '../models/schedule';

export default class ScheduleDataSource extends AbstractDatabase implements ScheduleDatabase {
  db: NeDBDataStore;

  addSchedule(schedule: Schedule, callback?: (err?: Error, doc?: Schedule) => void) {
    this.db.insert(schedule, function (err, doc) {
      if (err) logger.error(err.message);
      if (callback) callback(err, doc);
    });
  }

  updateSchedule(
    query: object,
    schedule: Schedule,
    callback?: (err?: Error) => void,
    updatedCallback?: (updated?: number) => void,
  ) {
    this.db.update(query, schedule, {}, function (err, updated) {
      if (err) {
        logger.error(err.message);
      } else {
        updatedCallback(updated);
      }
      if (updated == 0) logger.error(`Can't update schedule [${name}]. Document not found.`);
      if (callback) callback(err);
    });
  }

  //remove
  removeSchedule(
    query: object,
    callback?: (err?: Error) => void,
    removedCallback?: (removed?: number) => void,
  ) {
    this.db.remove(query, {}, function (err, removed) {
      if (err) {
        logger.error(err.message);
      } else {
        removedCallback(removed);
      }
      if (removed == 0) logger.debug('Cat\'t remove. Schedule Document not found.');
      if (callback) callback(err);
    });
  }

  protected dbName(): string {
    return `schedule`;
  }
}
