import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';

class FiltersPreventDuplicatesNode extends Node {
  lastVal: any;

  constructor() {
    super();

    this.title = NODE_INFO.filter["prevent-duplicates"].title;
    this.description = NODE_INFO.filter["prevent-duplicates"].description;

    this.addInput('input');
    this.addOutput('output');
    this.properties['value'];
  }

  onAdded(){
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    if (val === '') val = null;

    if (this.lastVal == val) return;

    this.lastVal = val;
    this.setOutputData(0, val, true);
    this.properties['value'] = val;
    this.persistProperties(false, true);
  }
}
Container.registerNodeType('filter/prevent-duplicates', FiltersPreventDuplicatesNode);
