import { Container } from '../../../container';
import { FlexibleNode } from '../../../flexible-node';
import { Type } from '../../../node-io';
import { NODE_INFO } from '../../../node-info';

class MQTTTopicBuilderNode extends FlexibleNode {
  constructor() {
    super();
    this.dynamicInputsType = Type.STRING;
    super.dynamicInputsStartName = 'string';
    this.title = NODE_INFO["protocols/mqtt"]["topic-builder"].title;
    //TODO Should re-update
    this.description = NODE_INFO["protocols/mqtt"]["topic-builder"].description; 
   
    this.addOutput('output', Type.STRING);
    this.setOutputData(0, null);
  }

  onAdded() {
    this.size = this.computeSize();
    this.onInputUpdated();
  }

  onInputUpdated() {
    const inputs = this.getInputsCount();
    let output = '';
    for (let i = this.dynamicInputStartPosition; i < inputs; i++) {
      const value = (this.getInputData(i) || '').toString(); // making string null i.e `''` and other `null` same
      if (value != ''){
        if(output != '') output = output.concat('/');
        output = output.concat(value);
        //if(i !== (inputs - 1)) output = output.concat('/');
      }
    }
    this.setOutputData(0, output, true);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}

Container.registerNodeType('protocols/mqtt/topic-builder', MQTTTopicBuilderNode);
