import { AsyncDbHelper, Database } from '../database/database';
import { isEmpty } from '../utils/helper';
import { IdGenerator } from '../utils/id-generator';
import { db_logger as logger } from '../utils/loggers';
import { Node } from './node';
import { INode, SerializedNode, Side } from './node-interface';
import { SerializableHelper } from './serializable';

export interface NodeDbHelper {

  listByIds(database: Database, ids: string[]): Promise<SerializedNode[]>;

  /**
   * Query Nodes in container by container id
   * @param {Database} database
   * @param {number} cId Container id
   * @return {Promise<SerializedNode[]>}
   */
  listByContainer(database: Database, cId: number): Promise<SerializedNode[]>;

  /**
   * Async update node properties
   * @param {Node} node
   * @param {string} props one or many properties
   * @return {Promise<void>}
   */
  updateAsync(node: Node, ...props: string[]): Promise<void>;

  /**
   * Update node properties
   * @deprecated should use {@link NodeDbHelper#updateAsync}
   * @param {Node} node
   * @param {string[]} props
   */
  update(node: Node, props: string[]): void;

  /**
   * Insert new node
   * @param {Database} database
   * @param {SerializedNode} node
   * @return {Promise<SerializedNode>}
   */
  insert(database: Database, node: SerializedNode | INode | Node): Promise<SerializedNode>;

  delete(database: Database, node: SerializedNode): Promise<void>;

  batchInsert(database: Database, nodes: (SerializedNode | INode | Node)[]): Promise<SerializedNode[]>;

  batchDeleteByIds(database: Database, ids: string[]): Promise<number>;

}

export const createNodeDbHelper = (): NodeDbHelper => {
  return new DefaultNodeDbHelper();
};

class DefaultNodeDbHelper implements NodeDbHelper {

  listByIds(database: Database, ids: string[]): Promise<SerializedNode[]> {
    logger.debug(`Query nodes by ids: [${ids}]...`);
    if (database) {
      return AsyncDbHelper.oneArgs(database, database.listNodes, { _id: { $in: ids } });
    }
    return Promise.resolve([]);
  }

  listByContainer(database: Database, cId: number): Promise<SerializedNode[]> {
    if (database) {
      logger.debug(`Query nodes in container id '${cId}'...`);
      return AsyncDbHelper.oneArgs(database, database.listNodes, { cid: cId });
    }
    return Promise.resolve([]);
  }

  /**
   * Persist node in database by UPDATE operation based on properties
   * @param {Node} node
   * @param props node properties, If not provided, whole node will be serialized then updated
   */
  update(node: Node, props: string[]): void {
    if (node.side === Side.server && node.container.db) {
      let data = isEmpty(props) ? node.serialize()
                                : props.reduce((o, p) => ((o[p] = SerializableHelper.serialize(node[p])), o), {});
      node.container.db.updateNode(node.id, node.container.id, { $set: data });
    }
  }

  updateAsync(node: Node, ...props: string[]): Promise<void> {
    const db = node?.container?.db;
    if (node?.side === Side.server && db) {
      logger.debug(`Updating [${props}] of node '${node.getReadableId()}'...`);
      let data = isEmpty(props) ? node.serialize()
                                : props.reduce((o, p) => ((o[p] = SerializableHelper.serialize(node[p])), o), {});
      return AsyncDbHelper.threeArgs(db, db.updateNode, node.id, node.cid, data);
    }
    return Promise.resolve();
  }

  insert(database: Database, node: SerializedNode): Promise<SerializedNode> {
    if (database) {
      logger.debug(`Inserting node '${IdGenerator.createNodeId(node.cid, node.id)}' into database...`);
      return AsyncDbHelper.oneArgs(database, database.addNode, SerializableHelper.serialize(node));
    }
    return Promise.resolve(node);
  }

  batchInsert(database: Database, nodes: (SerializedNode | INode | Node)[]): Promise<SerializedNode[]> {
    if (database) {
      logger.debug(`Inserting ${nodes.length} nodes into database...`);
      return AsyncDbHelper.oneArgs<SerializedNode[]>(database, database.batchInsertNodes,
                                                     nodes.map(n => SerializableHelper.serialize(n)))
                          .then(insertedNodes => {
                            logger.debug(`Inserted ${insertedNodes.length} nodes in database`);
                            return insertedNodes;
                          });
    }
    return Promise.resolve(nodes);
  }

  batchDeleteByIds(database: Database, ids: string[]): Promise<number> {
    if (database) {
      logger.debug(`Deleting node ids: [${ids}] in database...`);
      return AsyncDbHelper.oneArgs<number>(database, database.batchDeleteNodes, { _id: { $in: ids } })
                          .then(total => {
                            logger.debug(`Deleted ${total} nodes in database`);
                            return total;
                          });
    }
    return Promise.resolve(0);
  }

  delete(database: Database, node: SerializedNode): Promise<void> {
    if (database) {
      logger.debug(`Deleting node '${IdGenerator.createNodeId(node.cid, node.id)}' in database...`);
      return AsyncDbHelper.twoArgs(database, database.removeNode, node.id, node.cid);
    }
    return Promise.resolve();
  }
}

