import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { isNull } from '../../../utils/helper';
import { NODE_INFO } from '../../node-info';
const jsonata = require('jsonata');

class JsonQueryNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.json["json-query"].title;
    this.description = NODE_INFO.json["json-query"].description;
    this.addInput('input', Type.JSON);
    this.addInputWithSettings('query', Type.STRING, `deviceID &  ', ' & clientId`, 'Query', false);
    this.addOutput('output', Type.STRING);
    this.addOutput('error', Type.STRING);
  }

  onInputUpdated() {
    let input = this.getInputData(0);
    let queryInput = this.getInputData(1);
    if (isNull(input)) return
    if (isNull(queryInput)) return
    try {
      let expression = jsonata(queryInput);
      let result = expression.evaluate(input);
      this.setOutputData(0, result);
      this.setOutputData(1, null);
    } catch (e) {
      this.setOutputData(0, null);
      this.setOutputData(1, e);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('json/json-query', JsonQueryNode);
