import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { NODE_INFO } from '../../node-info';
import { Type } from '../../node-io';
import Utils from '../../utils';

class StringSelectNode extends FlexibleNode {

  constructor() {
    super();
    this.dynamicInputsType = Type.STRING;
    super.dynamicInputStartPosition = 1;
    this.title = NODE_INFO.switch["string-select"].title;
    this.description = NODE_INFO.switch["string-select"].description; 
    this.addInput('select', Type.NUMBER);
    this.addOutput('output', Type.STRING);
  }

  onAdded() {
 
    this.size = this.computeSize();
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const select = this.getInputData(0);
    if (
      !Utils.hasInput(select) ||
      select < this.dynamicInputStartPosition ||
      select > this.getInputsCount() - this.dynamicInputStartPosition
    ) {
      this.setOutputData(0, null);
      return;
    }
    this.setOutputData(0, this.getInputData(select));
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('switch/string-select', StringSelectNode);
