import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class DeadbandNode extends Node {
  constructor() {
    super();

    this.title = NODE_INFO.hvac.deadband.title;
    this.description = NODE_INFO.hvac.deadband.description;

    this.addInput('input', Type.NUMBER);
    this.addInputWithSettings('setpoint', Type.NUMBER, 0, 'Setpoint');
    this.addInputWithSettings('deadband', Type.NUMBER, 0, 'Deadband');
    this.addOutput('out', Type.BOOLEAN);
    this.addOutput('out not', Type.BOOLEAN);
    this.settings['invert'] = NodeSetting.create({
      description: 'Reverses ON direction when true',
      value: false,
      type: SettingType.BOOLEAN,
    });
  }

  onAdded() {
    this.setOutputData(0, false);
  }

  onInputUpdated() {
    const input = this.getInputData(0);
    const setpoint = this.getInputData(1);
    let deadband = this.getInputData(2);

    const invert = this.settings['invert'].value;
    if (invert == true) deadband = deadband * -1;

    const risingEdge = setpoint + deadband / 2;
    const fallingEdge = setpoint - deadband / 2;

    if (risingEdge > fallingEdge) {
      if (input <= fallingEdge) {
        this.setOutputData(0, false, true);
      }
      if (input >= risingEdge) {
        this.setOutputData(0, true, true);
      }
    } else if (risingEdge < fallingEdge) {
      if (input >= fallingEdge) {
        this.setOutputData(0, false, true);
      }
      if (input <= risingEdge) {
        this.setOutputData(0, true, true);
      }
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('hvac/deadband', DeadbandNode);
