import { UiPanel } from '../../server/dashboard';
import { db_logger as logger } from '../../utils/loggers';
import { AbstractDatabase } from '../database/BaseDatabase';
import DashboardDatabase from '../database/DashboardDatabase';

export default class DashboardDataSource extends AbstractDatabase implements DashboardDatabase {

  addUiPanel(panel: UiPanel, callback?: (err?: Error, doc?: UiPanel) => void) {
    this.db.insert(panel, function (err, doc) {
      if (err) logger.error(err.message);
      if (callback) callback(err, doc);
    });
  }

  getUiPanels(callback?: (err?: Error, docs?: UiPanel[]) => void) {
    this.db.find({}, function (err, docs) {
      if (err) logger.error(err);
      if (callback) callback(err, docs);
    });
  }

  getUiPanel(name: string, callback?: (err?: Error, doc?: UiPanel) => void) {
    this.db.findOne({ name: name }, function (err, doc: any) {
      if (err) logger.warn(err.message);
      if (callback) callback(err, doc);
    });
  }

  updateUiPanel(name: string, update: any, callback?: (err?: Error) => void) {
    this.db.update({ name: name }, update, {}, function (err, updated) {
      if (err) logger.error(err.message);
      if (updated == 0) logger.error(`Cat't update dashboard panel [${name}]. Document not found.`);
      if (callback) callback(err);
    });
  }

  removeUiPanel(name: string, callback?: (err?: Error) => void) {
    this.db.remove({ name: name }, {}, function (err, removed) {
      if (err) logger.error(err.message);
      if (removed == 0) logger.error('Cat\'t remove. Document not found.');
      if (callback) callback(err);
    });
  }

  dropUiPanels(callback?: (err?: Error) => void) {
    this.db.remove({}, { multi: true }, function (err) {
      if (err) logger.error(err.message);
      if (callback) callback(err);
    });
  }

  protected dbName(): string {
    return `dashboard`;
  }
}
