import { exec } from 'child_process';
import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

const pkg = require('../../../../package.json');

class WiresVersion extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.system["system-build-version"].title;
    this.description = NODE_INFO.system["system-build-version"].description;
    this.addInput('in1', Type.STRING);
    this.addOutput('wires-version', Type.STRING);
    this.addOutput('nodejs-version', Type.STRING);
    this.addOutput('npm-version', Type.STRING);
    this.addOutput('json out', Type.STRING);
    this.addOutput('error', Type.STRING);
  }

  getCurrentWiresVersion() {
    return new Promise(function(resolve) {
      return resolve({
        version: pkg.version,
      });
    });
  }

  getNodeJsVersion() {
    return new Promise((resolve, reject) => {
      exec('node -v', (err, stdout, stderr) => {
        if (err) {
          reject(err);
          return;
        } else {
          resolve({
            stdout,
          });
        }
      });
    });
  }

  getNPMVersion() {
    return new Promise((resolve, reject) => {
      exec('npm -v', (err, stdout, stderr) => {
        if (err) {
          reject(err);
          return;
        } else {
          resolve({
            stdout,
          });
        }
      });
    });
  }

  onInputUpdated() {
    let arr = [];

    // wires build
    this.getCurrentWiresVersion()
      .then(r => {
        r = JSON.stringify(r)
          .replace(/\\n/g, '')
          .split('version')
          .join('wires version');
        arr.push(r);
        this.setOutputData(0, r);
      })
      .catch(e => {
        this.setOutputData(4, e);
      });
    // nodejs version
    this.getNodeJsVersion()
      .then(r => {
        r = JSON.stringify(r)
          .replace(/\\n/g, '')
          .split('stdout')
          .join('nodejs version');
        arr.push(r);
        this.setOutputData(1, r);
      })
      .catch(e => {
        this.setOutputData(4, e);
      });
    // npm version
    this.getNPMVersion()
      .then(r => {
        r = JSON.stringify(r)
          .replace(/\\n/g, '')
          .split('stdout')
          .join('npm version');
        arr.push(r);
        this.setOutputData(3, arr); //json out
        this.setOutputData(2, r);
      })
      .catch(e => {
        this.setOutputData(4, e);
      });
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('system/system-build-version', WiresVersion);
