import { isNull, unionToArray } from '../../../../utils/helper';
import { NodeStoreItem } from '../../../../utils/pattern';

export interface ContainerNodeStore<T> {

  checkExistence(payload: NodeStoreItem): void;

  lookup(identifier: string): any;

  register(payload: NodeStoreItem, afterUpdate?: (p) => T): T;

  update(payload: NodeStoreItem, afterUpdate?: (p) => T, afterUnregisterIfExisted?: (p, force?: boolean) => void): T;

  unregister(payload: NodeStoreItem, force?: boolean, afterUnregister?: (p, force?: boolean) => void): void;

  listNodeIds(): string[];

}

export interface ContainerErrorNodeStore {

  hasError(nodeId: string): boolean;

  addError(nodeId: string): void;

  removeError(nodeId: string): void;

  listErrors(): Set<string>;

}

export interface IOneIdentifierManyNodeStore<T> extends ContainerNodeStore<T> {

  has(identifier, predicate: (key, value) => boolean): boolean;

}

export class DefaultContainerErrorNode implements ContainerErrorNodeStore {
  private nodeIdErrors: Set<string> = new Set<string>();

  hasError(nodeId: string): boolean {
    return this.nodeIdErrors.has(nodeId);
  }

  addError(nodeId: string): void {
    this.nodeIdErrors.add(nodeId);
  }

  removeError(nodeId: string): void {
    this.nodeIdErrors.delete(nodeId);
  }

  listErrors(): Set<string> {
    return this.nodeIdErrors;
  }
}

export abstract class AbstractContainerStore<T> implements ContainerNodeStore<T> {

  abstract checkExistence(payload: NodeStoreItem): void;

  abstract lookup(identifier: string): any;

  abstract unregister(payload: NodeStoreItem, force?: boolean, cb?: (p, force?: boolean) => void): void;

  abstract listNodeIds(): string[];

  register(payload: NodeStoreItem, cb?: (p) => T): T {
    this.checkExistence(payload);
    this.add(payload);
    return cb && cb(payload);
  }

  update(payload: NodeStoreItem, afterUpdate?: (p) => T, afterUnregisterIfExisted?: (p, force?: boolean) => void): T {
    if (payload?.identifier !== payload?.prev?.identifier) {
      try {
        this.checkExistence(payload?.prev);
      } catch (e) {
        this.unregister(payload?.prev, true, (p) => afterUnregisterIfExisted && afterUnregisterIfExisted(p, true));
      }
      return this.register(payload, afterUpdate);
    }
    if (!payload?.enabled) {
      this.unregister(payload, false, (p) => afterUnregisterIfExisted && afterUnregisterIfExisted(p, false));
      return payload?.data;
    }
    this.add(payload);
    return afterUpdate && afterUpdate(payload);
  }

  protected abstract add(payload: NodeStoreItem): string;

}

export abstract class OneIdentifierOneNodeStore<T> extends AbstractContainerStore<T> {

  protected nodes: { [identifier: string]: { identifier: string, nodeId: string; enabled: boolean; func: Function } } = {};
  protected errors: ContainerErrorNodeStore = new DefaultContainerErrorNode();

  checkExistence(payload: NodeStoreItem): void {
    if (this.nodes[payload?.identifier]) {
      if (payload.lenient && this.nodes[payload.identifier].nodeId === payload.nodeId) {
        return;
      }
      if (this.nodes[payload.identifier].nodeId !== payload.nodeId) {
        this.errors.addError(payload.nodeId);
      }
      throw new Error(this.alreadyExistedMsg(payload));
    }
  }

  lookup(identifier: string): { identifier: string, nodeId: string; enabled: boolean; func: Function } {
    return this.nodes[identifier] ?? null;
  }

  update(payload: NodeStoreItem, afterUpdate?: (p) => T, afterUnregisterIfExisted?: (p, force?: boolean) => void): T {
    this.errors.removeError(payload?.nodeId);
    return super.update(payload, afterUpdate, afterUnregisterIfExisted);
  }

  unregister(payload: NodeStoreItem, force?: boolean, cb?: (p, force?: boolean) => void): void {
    if (isNull(payload)) {
      return null;
    }
    this.errors.removeError(payload?.nodeId);
    let item = this.lookup(payload.identifier);
    if (isNull(item)) {
      return null;
    }
    if (item.nodeId !== payload.nodeId) {
      return null;
    }
    if (force) {
      delete this.nodes[payload.identifier];
    } else {
      this.nodes[this.add(payload)].enabled = false;
    }
    return cb && cb(payload);
  }

  listNodeIds(): string[] {
    return unionToArray(Object.values(this.nodes).map(v => v.nodeId), this.errors.listErrors());
  }

  protected add(payload: NodeStoreItem): string {
    this.errors.removeError(payload?.nodeId);
    this.nodes[payload.identifier] = {
      identifier: payload.identifier,
      nodeId    : payload.nodeId,
      enabled   : payload.enabled,
      func      : payload.callback,
    };
    return payload.identifier;
  }

  protected abstract alreadyExistedMsg(payload: NodeStoreItem): string;

}

export abstract class OneIdentifierManyNodeStore<T> extends AbstractContainerStore<T>
  implements IOneIdentifierManyNodeStore<T> {

  private nodes: { [identifier: string]: { [nodeId: string]: { enabled: boolean; func?: Function } } } = {};

  checkExistence(payload: NodeStoreItem): void {
    if (this.nodes[payload?.identifier] && this.nodes[payload?.identifier][payload?.nodeId]) {
      if (payload?.lenient) {
        return;
      }
      throw new Error(this.alreadyExistedMsg(payload));
    }
  }

  lookup(identifier: string): { [nodeId: string]: { enabled: boolean; func?: Function } } {
    return this.nodes[identifier] ?? {};
  }

  unregister(payload: NodeStoreItem, force?: boolean, afterUnregister?: (p, force?: boolean) => void): void {
    let item = this.lookup(payload.identifier);
    if (isNull(item) || isNull(item[payload?.nodeId])) {
      return null;
    }
    if (force) {
      if (this.nodes[payload?.identifier]) {
        delete this.nodes[payload?.identifier][payload?.nodeId];
      }
    } else {
      this.nodes[this.add(payload)][payload?.nodeId].enabled = false;
    }
    return afterUnregister && afterUnregister(payload);
  }

  listNodeIds(): string[] {
    return unionToArray(...Object.values(this.nodes).map(v => Object.keys(v)));
  }

  has(identifier, predicate: (key, value) => boolean): boolean {
    return Object.entries(this.lookup(identifier)).some(object => predicate(object[0], object[1]));
  }

  protected add(payload: NodeStoreItem): string {
    this.nodes[payload?.identifier] = this.nodes[payload?.identifier] ?? {};
    this.nodes[payload?.identifier][payload?.nodeId] = {
      enabled: payload?.enabled,
      func   : payload?.callback,
    };
    return payload?.identifier;
  }

  protected abstract alreadyExistedMsg(payload: NodeStoreItem): string;
}
