import { Container } from '../../container';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import { NODE_INFO } from '../../node-info';

class AddressHelperString extends Node {
  client: any;
  outputsCount = 0;
  constructor() {
    super();
    this.title = NODE_INFO.miscellaneous["append-to-strings"].title;
    this.description = NODE_INFO.miscellaneous["append-to-strings"].description; 

    this.addInputWithSettings('prepend', Type.STRING, '', 'Prepend Value');
    this.addInputWithSettings('append', Type.STRING, '', 'Append Value');

    this.settings['outputs_count'] = NodeSetting.create({
      description: 'Number of Outputs',
      value: 1,
      type: SettingType.NUMBER,
    });

    this.setSettingsConfig({
      groups: [{ outputs_count: {} }],
    });
  }

  init() {
    this.changeOutputsAndSettings();
  }

  onCreated() {
    this.renameInputsOutputs();
  }

  onAdded() {
    this.outputsCount = this.settings['outputs_count'].value;
    this.onAfterSettingsChange()
  }

  onInputUpdated(){
    this.renameInputsOutputs();
  }

  onAfterSettingsChange() {
    this.changeOutputsAndSettings();
    this.renameInputsOutputs();
    //update db
    if (this.container.db)
      this.container.db.updateNode(this.id, this.container.id, {
        $set: { outputs: this.outputs, settings: this.settings },
      });
  }

  private changeOutputsAndSettings() {
    let outputs = this.settings['outputs_count'].value;
    outputs = Utils.clamp(outputs, 1, 100);
    this.settings['outputs_count'].value = outputs;
    // change outputs count
    if (this.outputsCount != outputs) this.changeMyOutputsCount(outputs);
  }

  changeMyOutputsCount(target_count) {
    const diff = target_count - this.outputsCount;
    if (diff == 0) return;

    // add inputs, outputs
    this.changeOutputsCount(target_count + 1, Type.STRING);

    // change settings for outputs
    if (diff > 0) {
      for (let i = this.outputsCount + 1; i <= target_count; i++) {
        this.settings['output' + i] = NodeSetting.create({
          description: 'Output ' + i,
          value: '',
          type: SettingType.STRING,
        });
        for (let i = this.outputsCount + 1; i <= target_count; i++) {
          this.settingConfigs.groups.push({
            [`output${i}`]: { weight: 5 },
          });
        }
      }
    } else if (diff < 0) {
      for (let i = this.outputsCount; i > target_count; i--) {
        delete this.settings['output' + i];
        this.settingConfigs.groups.pop();
      }
    }

    this.outputsCount = target_count;
  }

  renameInputsOutputs() {
    const prepend = this.getInputData(0);
    const append = this.getInputData(1);
    for (let i = 1; i <= this.outputsCount; i++) {
      const output = this.settings['output' + i].value || '';
      this.outputs[i].name = '' + i + ' | ' + output;
      this.setOutputData(i, '' + prepend + output + append);
    }
  }
}
Container.registerNodeType('miscellaneous/append-to-strings', AddressHelperString);
