import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';
import ModbusByteOrder from '../protocols/modbus/modbus-fc/modbus-point-byte-order';

class ByteArrayToIntNode extends Node {
  value = null;
  constructor() {
    super();
    this.title = NODE_INFO.conversion["buffer-array-to-int"].title;
    this.description = NODE_INFO.conversion["buffer-array-to-int"].description;
    this.addInput('in 1', Type.ANY);
    this.addOutput('out', Type.JSON);
    this.addOutput('out buffer', Type.JSON);
    this.settings['dataType'] = NodeSetting.create({
      description: 'Set data type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: 'na' }, // use reg length from setting
          { value: 1, text: 'INT-16' }, // 1 reg
          { value: 2, text: `UINT-16` }, // 1 reg
          { value: 3, text: 'INT-32' }, // 2 reg
          { value: 4, text: 'UINT-32' }, // 2 reg
          { value: 5, text: 'FLOAT-32' }, // 2 reg
          { value: 8, text: 'DOUBLE-64' }, // 4 reg
        ],
      },
      value: 0,
    });
    this.settings['dataEndian'] = NodeSetting.create({
      description: 'Set byte order',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 0, text: 'LE-Byte BE-Word' },
          { value: 1, text: 'LE-Byte LE-Word' },
          { value: 2, text: 'BE-Byte LE-Word' },
          { value: 3, text: 'BE-Byte BE-Word' },
        ],
      },
      value: 0,
    });
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    const input = this.getInputData(0);
    try {
      const buffer = Buffer.from(input);
      const dataEndian = this.settings['dataEndian'].value;
      const dataType = this.settings['dataType'].value;
      const out = ModbusByteOrder.readByteOrder(buffer, 0, dataType, dataEndian);
      this.setOutputData(0, JSON.stringify(out));
      this.setOutputData(1, JSON.stringify(buffer));
    } catch (error) { }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('conversion/buffer-array-to-int', ByteArrayToIntNode);
