import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class MathTriggerDivideNode extends FlexibleNode {
  lastTrigger: boolean;

  constructor() {
    super();
    this.dynamicInputsType = Type.NUMBER;
    super.dynamicInputStartPosition = 1;
    this.title = NODE_INFO["math-trigger"]["trigger-divide"].title;
    this.description = NODE_INFO["math-trigger"]["trigger-divide"].description; 
   
    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
    this.size = this.computeSize();
    this.lastTrigger = false;
    this.onInputUpdated();
  }

  onInputUpdated() {
    const trigger = this.getInputData(0);
    if (trigger && !this.lastTrigger) {
      const inputCount = this.getInputsCount();
      let total = 1;
      let div = 0;
      for (let i = this.dynamicInputStartPosition; i < inputCount; i++) {
        div = this.getInputData(i);
        if (div === 0) {
          this.setOutputData(0, 0);
          return;
        }
        if (i === this.dynamicInputStartPosition) {
          total = div;
        } else {
          total = total / div;
        }
      }
      this.setOutputData(0, total);
    }
    this.lastTrigger = trigger;
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('math-trigger/trigger-divide', MathTriggerDivideNode);
