import { Container } from '../../container';
import { Node } from '../../node';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import { NODE_INFO } from '../../node-info';

class LinearShaperNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO["num-transform"]["linear-shaper"].title;
    this.description = NODE_INFO["num-transform"]["linear-shaper"].description; 
    this.addInput('input', Type.NUMBER);
    this.settings['inputs'] = NodeSetting.create({
      description: 'Inflection Points Count',
      value: 3,
      type: SettingType.NUMBER,
    });
    this.settings['in1'] = NodeSetting.create({
      description: 'output value @ input = 0',
      value: 0,
      type: SettingType.NUMBER,
    });
    this.settings['in2'] = NodeSetting.create({
      description: 'output value @ input = 50',
      value: 0,
      type: SettingType.NUMBER,
    });
    this.settings['in3'] = NodeSetting.create({
      description: 'output value @ input = 100',
      value: 0,
      type: SettingType.NUMBER,
    });
    this.addInput('[0]', Type.NUMBER);
    this.addInput('[50]', Type.NUMBER);
    this.addInput('[100]', Type.NUMBER);
    this.addOutput('output', Type.NUMBER);
  }

  onAfterSettingsChange() {
    const inputCountBefore = this.getInputsCount();
    let inputs = this.settings['inputs'].value;
    inputs = Utils.clamp(inputs, 3, 100);
    inputs = inputs + 1;
    if (inputs == inputCountBefore) return;
    this.changeInputsCount(inputs, Type.NUMBER);

    if (inputs > 4) {
      for (let i = 4; i < inputs; i++) {
        if (!this.settings['in' + i]) {
          this.settings['in' + i] = NodeSetting.create({
            description: 'in ' + i,
            value: 0,
            type: SettingType.NUMBER,
          });
        }
      }
    }
    for (let j = inputs; j < inputCountBefore; j++) {
      delete this.settings['in' + j];
    }
    this.renameInputsAndSettings(inputs);

    if (this.side == Side.editor) this.updateInputsLabels();
    this.onInputUpdated();
  }

  renameInputsAndSettings(count) {
    count = count - 1;
    this.inputs[1].name = '[0]';
    for (let i = 1; i < count; i++) {
      let point = (100.0 / (count - 1)) * i;
      let pointFormatted = point.toFixed(2).replace(/[.,]00$/, '');
      this.inputs[i + 1].name = '[' + pointFormatted + ']';
      this.settings['in' + (i + 1)] = NodeSetting.create({
        description: 'output value @ input = ' + pointFormatted,
        value: 0,
        type: SettingType.NUMBER,
      });
    }
    this.updateNodeInput();
  }

  onInputUpdated() {
    const input = this.getInputData(0);
    if (input == null) {
      this.setOutputData(0, null, true);
      return;
    }
    const position = Utils.clamp(input, 0, 100);
    const pointsCount = this.getInputsCount() - 1;
    const stepSize = 100.0 / (pointsCount - 1);
    const stepIndex = position / stepSize;
    if (position % stepSize == 0) {
      let valueAtPosition = this.getInputData(stepIndex + 1);
      if (!Utils.hasInput(valueAtPosition)) {
        valueAtPosition = this.settings['in' + (stepIndex + 1)].value;
      }
      valueAtPosition = Number(valueAtPosition);
      this.setOutputData(0, valueAtPosition);
      return;
    }
    const positionInStep = stepIndex - Math.floor(stepIndex);
    const startValueIndex = Math.floor(stepIndex);
    console.log(stepIndex, 'stepIndex');
    console.log(startValueIndex, 'startValueIndex');
    console.log('in' + (startValueIndex + 1), "'in' + (startValueIndex + 1)");
    let startValue = this.getInputData(startValueIndex + 1);
    if (!Utils.hasInput(startValue)) startValue = this.settings['in' + (startValueIndex + 1)].value;
    let endValue = this.getInputData(startValueIndex + 2);
    if (!Utils.hasInput(endValue)) endValue = this.settings['in' + (startValueIndex + 2)].value;
    let output = Utils.remap(positionInStep, 0, 1, startValue, endValue);
    this.setOutputData(0, output, true);
  }
}
Container.registerNodeType('num-transform/linear-shaper', LinearShaperNode);
