import config from '../../config';
import CryptoUtils from '../../nodes/utils/crypto-utils';
import { db_logger as logger } from '../../utils/loggers';
import AuthUserDatabase from '../database/AuthUserDatabase';
import { AbstractDatabase } from '../database/BaseDatabase';
import { AuthUser } from '../models/AuthUser';

const jwt = require('jsonwebtoken');

export default class AuthUserDataSource extends AbstractDatabase implements AuthUserDatabase {

  addAuthUser(authUser: AuthUser): Promise<AuthUser> {
    return new Promise((resolve, reject) => {
      this.db.find({}, (err, doc: Array<AuthUser>) => {
        if (doc.length) {
          logger.warn('User is already inserted');
          reject('User is already inserted');
          return;
        }
        if (!config.secretKey) {
          const error = 'Please provide secret key on environment';
          logger.error(error);
          reject(error);
          return;
        }
        authUser.password = CryptoUtils.encrypt(authUser.password);
        this.db.insert(authUser, (err, doc) => {
          if (err) {
            logger.error(err.message);
            reject(err);
          } else {
            resolve(doc);
          }
        });
      });
    });
  }

  getAuthUser(): Promise<AuthUser> {
    return new Promise((resolve, reject) => {
      this.db.findOne({}, (err, doc) => {
        if (err) {
          logger.error(err.message);
          reject(err);
        } else {
          resolve(doc);
        }
      });
    });
  }

  deleteAuthUsers(): Promise<string> {
    return new Promise((resolve, reject) => {
      this.db.remove({}, { multi: true }, err => {
        if (err) {
          logger.error(err.message);
          reject(err);
        } else {
          resolve('Successfully removed');
        }
      });
    });
  }

  getAuthToken(authUser: AuthUser): Promise<string> {
    return new Promise((resolve, reject) => {
      this.db.findOne({ username: authUser.username }, (err, doc) => {
        if (err) {
          logger.error(err.message);
          reject(err);
        } else if (doc && CryptoUtils.decrypt(doc.password) === authUser.password) {
          try {
            const token = jwt.sign({ username: doc.username }, config.secretKey, { expiresIn: 18000 });
            resolve(`${token}`);
          } catch (e) {
            logger.error(`Error: ${e.message}`);
            reject(e.message);
          }
        } else {
          logger.warn('Invalid Credentials');
          reject('Invalid Credentials');
        }
      });
    });
  }

  isAuthenticated(token: string): Promise<any> {
    return new Promise((resolve, reject) => {
      jwt.verify(token, config.secretKey, (err, decoded) => {
        if (err) {
          logger.error(err);
          reject(err);
        } else {
          resolve(decoded);
        }
      });
    });
  }

  protected dbName(): string {
    return `auth`;
  }

}
