import { Container } from '../../container';
import { Node } from '../../node';
import { Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class ThermostatNode extends Node {
  clgMode: boolean;
  htgMode: boolean;


  constructor() {
    super();

    this.title = NODE_INFO.hvac.thermostat.title;
    this.description = NODE_INFO.hvac.thermostat.description; 
   
    this.addInput('input', Type.NUMBER);
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addInputWithSettings('setpoint', Type.NUMBER, 20, 'Setpoint');
    this.addInputWithSettings('CLG offset', Type.NUMBER, 1, 'CLG offset');
    this.addInputWithSettings('HTG offset', Type.NUMBER, 1, 'HTG offset');
    this.addOutput('CLG Mode', Type.BOOLEAN);
    this.addOutput('HTG Mode', Type.BOOLEAN);
    this.setOutputData(0, false);
    this.setOutputData(1, false);
    this.clgMode = false;
    this.htgMode = false;
  }

  onAdded() {
 
    this.setOutputData(0, false);
    this.setOutputData(1, false);
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let enable = this.getInputData(1);
    if (!enable) {
      this.setOutputData(0, false);
      this.setOutputData(1, false);
      this.clgMode = false;
      this.htgMode = false;
      return;
    }
    const input = this.getInputData(0);
    const setpoint = this.getInputData(2);
    const clgOffset = this.getInputData(3);
    const htgOffset = this.getInputData(4);

    const clgSP = setpoint + clgOffset;
    const htgSP = setpoint - htgOffset;

    if (input < htgSP) {
      this.setOutputData(0, false);
      this.setOutputData(1, true);
      this.htgMode = true;
      this.clgMode = false;
    } else if (input > clgSP) {
      this.setOutputData(0, true);
      this.setOutputData(1, false);
      this.clgMode = true;
      this.htgMode = false;
    } else if ((this.clgMode && input < setpoint) || (this.htgMode && input > setpoint)) {
      this.setOutputData(0, false);
      this.setOutputData(1, false);
      this.clgMode = false;
      this.htgMode = false;
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('hvac/thermostat', ThermostatNode);
