import { Colors, create } from 'logplease';
import { ConnectionStatus } from '../../../utils/enums';
import { Constructor, isFunction } from '../../../utils/helper';
import { IdGenerator } from '../../../utils/id-generator';
import { EventData, Observer } from '../../../utils/pattern';
import { Node, NodeSettingConfig } from '../../node';
import { Type } from '../../node-io';
import { AbleEnableNode, HasEnabled } from '../node-mixin';

const logger = create('observer', { color: Colors.Yellow });

export class ConnectionOutput implements EventData {

  status: ConnectionStatus;
  error?: string;

  constructor(status: ConnectionStatus, error?: string) {
    this.status = status;
    this.error = error;
  }

}

export interface ExternalConnectionNode extends HasEnabled {

  isConnConnected(): boolean;

  getConnectionStatus(): ConnectionStatus;

  statusObserver(): Observer<ConnectionOutput>;

}

export interface DependantConnectionNode extends ExternalConnectionNode {

  parentConnectionNode(): ExternalConnectionNode;

}

export class ConnectionOutputObserver implements Observer<ConnectionOutput> {

  node: Node;
  outputSlots: { status: number, error: number };

  constructor(node: Node, outputSlots: { status: number, error: number }) {
    this.node = node;
    this.outputSlots = outputSlots;
  }

  update(data: ConnectionOutput, eventSourceId?: string): void {
    let current = ConnectionStatus.lookupByLabel(this.node.outputs[this.outputSlots.status].data);
    let nodeId = IdGenerator.createNodeId(this.node.cid, this.node.id);
    let itselfConnected = isFunction(this.node['isConnConnected']) ? this.node['isConnConnected']() : true;
    if (eventSourceId && nodeId !== eventSourceId && (current?.isError() || !itselfConnected)) {
      return;
    }
    logger.debug(`Node ${this.node.getReadableId()} receives connection status '${data.status.label}' ` +
                 `from source id '${eventSourceId ?? nodeId}'`);
    this.node.setOutputData(this.outputSlots.status, data.status.label);
    this.node.setOutputData(this.outputSlots.error, data.error ?? '');
  }

}

export function ExternalConnectionNodeMixin<TBase extends Constructor<Node>>(Base: TBase) {
  abstract class ExternalConnectionNodeMixinBase extends AbleEnableNode(Base) implements ExternalConnectionNode {

    protected connectionStatus: ConnectionStatus = ConnectionStatus.UNCONNECTED;

    statusObserver(): Observer<ConnectionOutput> {
      return new ConnectionOutputObserver(this, { status: this.statusOutputIdx(), error: this.errorOutputIdx() });
    }

    getConnectionStatus(): ConnectionStatus {
      if (!this.isEnabled()) {
        return (this.connectionStatus = ConnectionStatus.UNCONNECTED);
      }
      if (this.connectionStatus?.isError()) {
        return this.connectionStatus;
      }
      this.connectionStatus = this.isConnConnected() ? ConnectionStatus.CONNECTED : ConnectionStatus.DISCONNECTED;
      return this.connectionStatus;
    }

    abstract isConnConnected(): boolean;

    protected abstract statusOutputIdx(): number;

    protected errorOutputIdx(): number {
      return this.statusOutputIdx() + 1;
    }

    protected mixinConnectionStatusOutput(): NodeSettingConfig {
      this.addOutput('status', Type.STRING);
      this.addOutput('error', Type.STRING);
      this.setOutputData(this.statusOutputIdx(), this.getConnectionStatus().label);
      return {};
    }

  }

  return ExternalConnectionNodeMixinBase;
}

export function DependantConnectionNodeMixin<TBase extends Constructor<Node>>(Base: TBase) {
  abstract class DependantConnectionNodeMixinBase extends ExternalConnectionNodeMixin(Base)
    implements DependantConnectionNode {

    onAdded(): void {
      this.executeFunction('onAdded');
    }

    onAfterSettingsChange(oldSettings, oldName?): void {
      this.executeFunction('onAfterSettingsChange', oldSettings, oldName);
    }

    onInputUpdated(): void {
      this.executeFunction('onInputUpdated');
    }

    parentConnectionNode(): ExternalConnectionNode {
      let parentNode = this.getParentNode();
      if (parentNode && isFunction(parentNode['getConnectionStatus'])) {
        return <ExternalConnectionNode><unknown>parentNode;
      }
      return null;
    }

    isConnConnected(): boolean {
      let isParentConnected = this.parentConnectionNode()?.getConnectionStatus().isConnected() ?? false;
      return this.isEnabled() && isParentConnected;
    }

    protected handleError(err: Error, func: string): void {
      this.debug(`Error when executing function ${func} in node ${this.getReadableId()} - ${err.stack}`);
      this.statusObserver().update(new ConnectionOutput(this.getConnectionStatus(), err.message));
    }

    protected executeFunction(func: string, ...args) {
      try {
        if (super[func] && isFunction(super[func])) {
          super[func](...args);
        }
        this.connectionStatus = null;
        this.statusObserver().update(new ConnectionOutput(this.getConnectionStatus()));
      } catch (err) {
        this.connectionStatus = ConnectionStatus.ERROR;
        this.handleError(err, func);
      }
    }
  }

  return DependantConnectionNodeMixinBase;
}
